.news-events-strip__events {
    > :last-child .event {
        border-bottom-width: 0;
    }

    .event {
        padding-bottom: 15px;
        border-bottom: 1px solid $gray-light;
        margin-bottom: 15px;

        .date {
            width: 50px;
            margin: 0;
        }
        .date-month,
        .date-day-no {
            background: $red2;
            border: 0;
            color: white;
            font-size: small;
            line-height: 1em;
            font-weight: 600;
            font-family: 'ff-meta-web', Helvetica, sans-serif;
        }

        .date-month {
            background: $red2;
            padding: 8px 0 6px;
        }
        .date-day-no {
            background: $red1;
            border: 0;
            color: white;
            font-size: 14px;
            padding: 6px 0;
        }

        .event-details {
            margin-left: 70px;
            min-height: 53px;

            h2 {
                font-size: 16px;
                margin: 0 0 0.2em;
                font-family: Arial, Helvetica, sans-serif;
            }
            p {
                font-size: 15px;
                font-family: Arial, Helvetica, sans-serif;
                font-weight: bold;
                margin-bottom: 0;
                color: #555;
            }

            &,
            h2 a,
            p {
                line-height: 1.2em;
            }
        }
    }
}

.news-events-strip__events .event .event-details div.extra {
    display: none;
}

.news-events-strip {
    $tx-time: 0.2s ease;
    $controls-bottom: 20px;
    $controls-hpos: 20px;
    $controls-button-size: 40px;
    $controls-button-icon-size: 16px;
    $controls-button-hmargin: 0px;
    $controls-pager-size: 20px;
    $controls-pager-hmargin: 5px;
    $controls-pager-hmargin-sm: 5px;

    .bx-wrapper {
        position: relative;

        .bx-controls {
            display: block;
        }

        .bx-pager {
            display: none;
        }

        .bx-controls-direction {
            display: block;
            position: static;
            width: 0;
            height: 0;

            a {
                &,
                &.active {
                    &,
                    &:focus {
                        background: none;
                        border: 0;
                        text-decoration: none;
                    }
                }

                transition: background $tx-time;
                &:hover {
                    background: rgba(255, 255, 255, 0.3);
                }

                position: absolute;
                top: 0;
                margin: 0;
                width: 50px;
                padding: 0 0 66.85%;
                height: 0;
                border: 0;
                display: block;

                &.bx-prev {
                    left: 0;
                }
                &.bx-next {
                    right: 0;
                }

                i {
                    position: absolute;
                    text-align: center;
                    transition: color $tx-time;

                    &.bg {
                        top: 50%;
                        left: 50%;
                        height: $controls-button-size;
                        width: $controls-button-size;

                        line-height: $controls-button-size + 2;
                        font-size: $controls-button-size;
                        margin: (-0.5 * $controls-button-size) auto auto
                            (-0.5 * $controls-button-size);
                        color: white;
                    }
                    &.ic {
                        top: 50%;
                        left: 50%;
                        width: $controls-button-icon-size;
                        height: $controls-button-icon-size;
                        line-height: $controls-button-icon-size;
                        font-size: $controls-button-icon-size;
                        margin-left: -0.5 * $controls-button-icon-size;
                        margin-top: -0.5 * $controls-button-icon-size + 1;
                        color: $red1;

                        &.fa-chevron-right,
                        &.fa-chevron-left {
                            margin-top: -0.5 * $controls-button-icon-size + 2;
                        }

                        &.fa-play,
                        &.fa-chevron-right {
                            margin-left: -0.5 * $controls-button-icon-size + 1;
                        }
                        &.fa-chevron-left {
                            margin-left: -0.5 * $controls-button-icon-size - 1;
                        }
                    }
                }
                &:hover i,
                &.active i {
                    &.bg {
                        color: $red1;
                    }
                    &.ic {
                        color: white;
                    }
                }

                &:hover {
                    color: white;
                }

                &.active {
                    color: $red1;
                }
            }
        }
    }
}
