.embed-container {
    position: relative;

    .youtube-preview {
        position: static;

        img {
            cursor: pointer;
            position: absolute;

            &.preview {
                width: 100%;
                min-height: 100%;
                top: 0;
                left: 0;
            }

            $play-button-width: 100px;
            $play-button-height: $play-button-width * 0.7;
            &.play {
                width: $play-button-width;
                position: absolute;
                left: 50%;
                top: 50%;
                margin-left: -$play-button-width / 2;
                margin-top: -$play-button-height / 2;
            }
        }

        .play-hover {
            display: none;
        }

        &:hover {
            .play {
                display: none;
            }
            .play-hover {
                display: block;
            }
        }

        > a {
            display: block;
            &:focus img.preview {
                outline-offset: -2px;
                outline: #b02a1a solid 2px;
            }
        }
    }
}
