.openday-cta {
    $openday-red: #a22833;
    .openday-cta__main {
        text-align: center;
        background: $openday-red;
        color: white;
        padding: 30px 0;
    }

    .openday-cta__main__heading {
        margin-bottom: 10px;
    }

    .openday-cta__main__date {
        font-weight: 600;
        font-size: 1.5em;
        margin-bottom: 10px;
    }

    .openday-cta__main__link {
        a {
            text-transform: uppercase;
            font-size: 1.1em;

            &:link,
            &:visited {
                color: #eee;
                border-bottom: 2px dotted #ccc;
            }

            &:hover {
                color: #fff;
                border-bottom: 2px solid #fff;
            }

            &:focus,
            &:active {
                color: #555;
                background-color: #fff;
                border-bottom: 2px solid white;
            }
        }
    }

    .openday-cta__ical {
        margin: 10px 0;
        .fa {
            color: $openday-red;
            font-size: 2em;
        }
        a {
            border: none;
            margin-left: 10px;
            padding: 3px 8px;
            background: #eee;
            color: #333;
            position: relative;
            display: inline-block;
            top: -5px;
            border-radius: 2px;

            &:hover span {
                border-bottom: 1px dotted #333;
            }

            &:active,
            &:focus {
                color: white;
                background: $openday-red;
                span {
                    border-bottom: none;
                }
            }
        }
    }
}

.free-html .openday-cta-widget {
    $openday-red: #a22833;
    $openday-red-hover: lighten($openday-red, 10%);
    background: $openday-red;
    color: white;
    display: block;
    padding: 4em 1em 1em;
    .openday-cta-widget__icon {
        text-align: center;
        font-size: 2em;
    }
    .openday-cta-widget__main__heading {
        background: $openday-red;
        font-size: 2em;
        text-align: center;
    }
    .openday-cta-widget__dates__heading {
        font-size: 1.4em;
    }
    .openday-cta-widget__dates__list {
    }
    &:link {
    }
    &:visited {
    }
    &:hover {
        background: $openday-red-hover;
        .openday-cta-widget__main__heading {
            background: $openday-red-hover;
            text-decoration: underline;
        }
    }
    &:focus,
    &:active {
        background: $openday-red-hover;
        .openday-cta-widget__main__heading {
            background: white;
            color: #555;
            text-decoration: underline;
        }
    }
}

a.cta-button-link {
    display: inline-block;
    text-transform: uppercase;
    border: none;
    padding: 20px 25px;
    border-radius: 5px;
    font-weight: 700;
    font-size: 1.2em;

    &:link,
    &:visited,
    &:hover {
        background: #c00f23;
        color: white;
    }

    &:hover .cta-button-link__label {
        border-bottom: 1px dotted white;
    }

    &:active,
    &:focus {
        color: #c00f23;
        background: white;
    }

    .fa {
        margin-left: 30px;
    }
}

/* TODO: update after making links branch live */
.cta-label-icon {
    display: block;

    &:link {
        border: none;
        background: none;

        .label {
            color: #b91b1b;
            border-bottom: 1px dotted #999999;
        }
    }
    &:visited .label {
        color: #670003;
    }

    &:hover .label {
        border-bottom-style: solid;
        border-bottom-color: #670003;
    }

    &:active,
    &:focus {
        .label {
            background-color: #b91b1b;
            color: #ffffff;
            border-bottom-color: transparent;
        }
    }

    .fa {
        display: block;
        padding-left: 1px;
        font-size: 2em;
    }
}
