.library-lede {
    p:empty {
        display: none;
    }
}

#library .hallway-new {
    margin-top: 0;
}

.no-js #library .dropdown-select {
    display: none;
}

.js .search-refine-wrapper {
    display: none;
}

.library-home-background {
    position: relative;
    background: #eee;
    padding: 2px 10px 0;
    margin: -15px -10px 20px 0;
    @include breakup-breakpoint('wide') {
        padding: 2px 0 0;
        margin: 0 0 20px 0;
    }
    &--search {
        margin-top: -35px;
    }
}

.library-login {
    text-align: center;

    p {
        text-align: left;
    }
}

.library-login-link {
    &,
    &:link {
        background-color: $red;
        color: $white;
        height: 3.5em;
        display: inline-block;
        margin: 0 auto 0;
        padding: 0 1em;
        border: 0;
        outline: 0;
        font-weight: 600;
        line-height: 3.4em;
        text-align: center;
        border-radius: 2px;
        cursor: pointer;
        @include transition(all 0.2s ease);
        @include breakup-breakpoint('thinish_extended_2') {
            margin: 2em auto 0;
        }
        @include breakup-breakpoint('medium') {
            margin: 5em auto;
        }
    }
    &:hover,
    &:focus,
    &:active {
        text-decoration: underline;
    }
}
.opening-times--home {
    padding: 0;
    @include breakup-breakpoint('medium') {
        padding-bottom: 1.5em;
    }
}

.opening-times--home-controls {
    display: block;
    text-align: center;
    @include breakup-breakpoint('medium') {
        text-align: left;
    }

    h2,
    form {
        display: inline-block;
    }

    h2 {
        margin: 0;
    }

    form {
        width: 118px;
        padding-left: 2em;
        vertical-align: top;

        select {
            font-size: 1.5em;
            padding: 0.167em 0;
        }
    }

    time {
        color: #444;
        margin-left: 0.5em;
        display: block;
        font-size: 0.8em;
        @include breakup-breakpoint('medium') {
            display: inline;
        }
    }

    .opening-times--home-controls-link {
        clear: both;
        @include breakup-breakpoint('medium') {
            float: right;
            margin-top: 10px;
        }
    }
}

.opening-times--home-list {
    width: 100%;
    display: block;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
    @include breakup-breakpoint('medium') {
        display: table;
        table-layout: fixed;
        text-align: left;
    }

    .opening-times--home-controls + & {
        padding-top: 0.5em;
    }

    li {
        background-color: transparent;
        display: block;
        padding: 1.5em 0;
        border: 0;
        vertical-align: top;
        margin-bottom: 1em;
        &:nth-child(odd) {
            @include breakup-breakpoint('medium') {
                background-color: transparent;
            }
        }
        @include breakup-breakpoint('medium') {
            width: 20%;
            display: table-cell;
        }
    }

    h3 {
        @include font-family-meta-sans;
        padding-bottom: 0.4em;
        margin: 0;
        font-size: 1.143em; // 16px = 14px * 1.143
        font-weight: 600;
        @include breakup-breakpoint('medium') {
            width: 80%;
        }

        a {
            border-bottom-width: 2px;
            border-bottom-style: dotted;
            text-decoration: none;
            &:link,
            &:visited {
                border-bottom-color: $gray-light;
            }
            &:link {
                color: $red;
            }
            &:visited {
                color: $red4;
            }
            &:hover {
                color: $red;
                border-bottom-color: $red4;
                border-bottom-style: solid;
            }
            &:focus,
            &:active {
                background-color: $red;
                color: $white;
                border-bottom-color: transparent;
            }
        }
    }

    time,
    span {
        font-size: 1.1em;
    }
}

.opening-times--home-list-item-times {
    color: #444;
}

.opening-times-list {
    list-style: none;
    margin: 0;

    .opening-times-list-card {
        width: 100%;
        padding: 1em 1em 1.5em;
        margin-bottom: 2em;
        border-radius: 0.25em;
        box-shadow: 0 0 0.5em rgba($black, 0.25);
        @include breakup-breakpoint('thinish_extended_2') {
            float: left;
        }
        @include breakup-breakpoint('wide_extended') {
            float: left;
        }
    }

    h2 {
        margin: 0 0 0.75em;
    }

    h3 {
        margin: 14px 0;

        &:first-of-type {
            margin-top: 0;
        }
    }

    img {
        width: 100%;
        height: auto;
        display: none;
        margin-bottom: 1em;
    }
}

.js .opening-times-list,
.js .opening-times--home-list {
    .opening-times-busy {
        opacity: 0.5;

        .calendarNavLink {
            cursor: default;
        }
    }
}

.js .library-aside-call-to-action.opening-times-busy {
    opacity: 0.5;

    .calendarNavLink {
        cursor: default;
    }
}

.opening-times-panel {
    width: 100%;
    display: block;
    @include breakup-breakpoint('thinish_extended_2') {
        width: 50%;
        display: inline-block;
        float: left;
    }
    @include breakup-breakpoint('medium') {
        width: 100%;
        display: block;
    }
    @include breakup-breakpoint('wide_extended') {
        width: 50%;
        display: inline-block;
        float: left;
    }
}
.opening-times-panel--left {
    @include breakup-breakpoint('thinish_extended_2') {
        padding-right: 0.75em;
    }
    @include breakup-breakpoint('medium') {
        padding-right: 0;
    }
    @include breakup-breakpoint('wide_extended') {
        padding-right: 0.75em;
    }

    & > h3 {
        display: none;
    }
}
.opening-times-panel--right {
    padding-top: 1.5em;
    @include breakup-breakpoint('thinish_extended_2') {
        padding-top: 0;
        padding-left: 0.75em;
    }
    @include breakup-breakpoint('medium') {
        padding-top: 1.5em;
        padding-left: 0;
    }
    @include breakup-breakpoint('wide_extended') {
        padding-top: 0;
        padding-left: 0.75em;
    }
}

.opening-times-info {
    margin-top: 1.5em;
    border: 1px solid $gray-light;
    padding: 0.75em;

    h3 {
        border-bottom: 1px solid $gray-light;
        &:not(:first-of-type) {
            margin-top: 1em;
        }
    }

    p {
        margin-bottom: 0;
    }
}

.opening-times-table {
    margin-bottom: 0;

    tbody {
        width: 100%;
        display: table;
    }

    tr {
        user-select: all;

        &:nth-child(even) {
            background-color: $grey1;
        }
    }
    /*tr*/
    .is-today {
        border: 2px solid $red;
        position: relative;

        td {
            padding: 0.5em 0.786em;
        }
    }

    td {
        padding: 0.643em 0.786em; // 9px 11px
        border: 0;

        &:last-child {
            text-align: right;
        }

        &,
        &::selection {
            background-color: transparent !important;
        }

        span,
        time {
            &::selection,
            & em::selection {
                background: $black !important;
                color: $white !important;
            }
        }
    }
}

.opening-times-navigation {
    width: 100%;
    display: table;
    margin-bottom: 0.643em; // 9px
    line-height: 1;

    .calendarNavLink,
    .opening-times-navigation-date-range {
        background-color: $red;
        color: $white !important;
        text-align: center;
        transition: all 0.15s ease;
    }

    .calendarNavLink {
        width: 2.429em;
        height: 2.429em;
        display: block;
        padding: 0.5em 1.25em;
        border-bottom-width: 0 !important;
        position: relative;
        overflow-x: hidden;
        text-indent: 10em;
        white-space: nowrap;
        user-select: none;

        &:hover,
        &:focus {
            background-color: $red2;
            cursor: pointer;
        }

        &:before {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translateX(-50%) translateY(-50%);
            text-indent: 0;
        }
    }

    .opening-times-navigation-previous .calendarNavLink {
        border-radius: 0.25em 0 0 0.25em;
        border-right: 1px solid $white;

        &:before {
            @include fa-pseudo-icon(caret-left);
        }
    }

    .opening-times-navigation-next .calendarNavLink {
        border-radius: 0 0.25em 0.25em 0;
        border-left: 1px solid $white;

        &:before {
            @include fa-pseudo-icon(caret-right);
        }
    }

    .opening-times-navigation-date-range {
        width: 100%;
        display: table-cell;
        overflow: hidden;
        font-weight: 600;
        vertical-align: middle;
        user-select: all;

        &::selection {
            background-color: transparent !important;
        }

        time {
            &::selection {
                background-color: $black !important;
            }
        }
    }
    .js & .opening-times-navigation-date-range time {
        color: transparent;
        transition: color 0.15s ease;

        &[data-toggle='on'] {
            color: $white;
        }

        span {
            display: none;
        }
    }
}

.full-width-background {
    background-color: $gray-lighter;
    padding-left: 0;
    padding-right: 0;
    position: relative;

    &:before,
    &:after {
        content: '';
        background-color: inherit;
        width: 9600px; // must be exactly 9600px to avoid Blink bug
        position: absolute;
        top: 0;
        bottom: 0;
        right: 100%;
    }

    &:after {
        left: 100%;
    }
}

.library-focus-container {
    float: left;
    padding-top: 1.5em;
    padding-bottom: 1.5em;
    margin-bottom: 1.5em;
}

.library-focus-list {
    width: 100%;
    float: left;
    margin: 0;
    list-style: none;

    h2 {
        display: inline-block;
        font-size: 22px;

        a {
            border-bottom-width: 2px;
            border-bottom-style: dotted;
            text-decoration: none;
            &:link,
            &:visited {
                border-bottom-color: $gray-light;
            }
            &:link {
                color: $red;
            }
            &:visited {
                color: $red4;
            }
            &:hover {
                color: $red;
                border-bottom-color: $red4;
                border-bottom-style: solid;
            }
            &:focus,
            &:active {
                background-color: $red;
                color: $white;
                border-bottom-color: transparent;
            }
        }
    }

    p {
        color: $text-color;
    }
}

.connect-with-us {
    margin-bottom: 2em;
}

.library-aside-call-to-action {
    background-color: $city-purple;
    color: $white;
    display: block;
    padding: 2em 1em;
    margin-bottom: 0.667em;
    font-size: 1.5em;
    font-family: 'ff-meta-web', Arial, Helvetica, sans-serif;

    a {
        display: inline-block;
        border-bottom-width: 2px;
        border-bottom-style: dotted;
        text-decoration: none;
        &:link,
        &:visited {
            color: $gray-lighter;
            border-bottom-color: $grey3;
        }
        &:hover {
            color: $white;
            border-bottom-color: $white;
            border-bottom-style: solid;
        }
        &:focus,
        &:active {
            background-color: $white;
            color: $grey5;
            border-bottom-color: transparent;
        }
    }
    p {
        margin-top: 0.667em;
        font-size: 0.667em;
    }
}

.library-news-campaign-item {
    background-color: $white;
    padding: 1em 0;

    .thumbnail {
        text-align: center;
    }
}

.library-hallway-list {
    width: 100%;
    margin: 0;

    &,
    li {
        list-style: none;
    }

    li {
        width: 100%;
        display: inline-block;
        margin-bottom: 1.5em;
        float: left;
        clear: both;
    }

    h3 {
        margin-top: 0;
        margin-bottom: 0.75em;
        line-height: 1;
    }

    img {
        display: block;
        margin-right: 1.5em;
        margin-bottom: 1.25em;
        @include breakup-breakpoint('thinish_extended_2') {
            display: inline-block;
            float: left;
            margin-bottom: 0.75em;
        }
    }
}

.library-search {
    .search-section-button {
        font-size: 1.2em;
        height: 3em;
        line-height: 3em;
        .fa {
            padding-left: 8px;
        }
    }
}

body#library {
    overflow-x: hidden;
}
