// Event n pages
.single-event {
    padding: 0;

    .event-header {
        @include clearfix;
        margin-bottom: 14px;
    }

    .extra-logo {
        text-align: left;
        margin-left: 17px;

        a {
            border: none;
        }
    }

    .date {
        margin: 0 14px 0 0;
    }

    .from-start-end {
        float: left;
        display: inline;
        margin: 20px 14px 0 0;
    }

    .event-title {
        margin: 0;
    }

    .event-meta {
        @include clearfix;
        margin-bottom: 14px;
    }

    .event-meta,
    .single-event-widget {
        // all icons should be grey
        i,
        span.fa {
            color: #666;
        }
        // except these guys
        .mid-cta,
        .hard-cta {
            i,
            span.fa {
                color: #fff;
            }
        }
    }

    .event-time,
    .event-category,
    .event-audience {
        margin: 0 10px 14px 0;
    }
    .event-speaker {
        clear: left;
    }
    .event-share {
        h3 {
            float: left;
            margin: 0 20px 0 0;
            line-height: 2.2em;
        }

        .social-flat-links {
            clear: both;
        }

        .social-btn {
            margin: 0 2% 10px 0;
            width: auto;
            clear: none;

            a {
                padding: 10px;
            }
        }
    }

    .strong {
        font-weight: bold;
    }

    .single-event-widget {
        padding: 20px;
        margin-bottom: 20px;
        background: #eae9e7;

        h2 {
            margin: 0 0 14px 0;
        }

        dl {
            margin: 0;
            padding: 0;
        }

        dt {
            background: transparent;
            padding: 0;
            margin-bottom: 5px;
            font-family: $font-family-sans;
        }

        dd {
            padding: 0;
            margin: 0 0 14px 0;
        }
        a:hover {
            border-bottom: 1px solid #eae9e7;
        }
        .cta {
            font-size: 0.9em;
        }
        .soft-cta {
            a:hover {
                border-bottom: 1px solid #c6c6c6;
            }
        }
        .mid-cta {
            a:hover {
                border-bottom: 1px solid #00759b;
            }
        }
        .hard-cta {
            a:hover {
                border-bottom: 1px solid #670003;
            }
        }
    }

    .single-event-widget__warning {
        clear: both;
        margin: 10px 0;
        padding: 10px;

        background-color: #ffbaba;
        &,
        & span.fa {
            color: #d8000c;
        }
    }

    // call to action box
    .sign-up {
        @include clearfix;
        padding: 17px;

        .ticket-info {
            float: left;
            width: 60%;
            margin-right: 80px;
        }

        .cta {
            font-size: 1.2em;
            margin: 0;
            display: block;
            float: left;

            a {
                padding: 10px 20px;
            }
            i,
            span.fa {
                float: none;
                margin-left: 7px;
            }
        }
    }

    // event detail block - blue background
    .event-detail {
        background: #e3f0fd;

        .date-time {
            .start-time,
            .end-time {
                margin: 5px 0;
            }
            .full-date {
                margin: 5px 0 0 5px;
            }
            .end-date {
            }
        }

        .location {
            margin-bottom: 20px;
            @include clearfix;

            i,
            span.fa {
                display: block;
                float: left;
                margin: 5px 10px 0 0;
            }

            span {
                display: block;
            }
        }

        .icalendar {
            background: #e3f0fd;

            p {
                margin: 0;
            }

            .fa-calendar {
                font-size: 1em;
                line-height: 1.6em;
                margin-right: 7px;
                vertical-align: middle;
                float: left;
            }
        }
        .static-map-image {
            margin-bottom: 10px;
        }
        .static-map-link {
            margin-bottom: 14px;

            a {
                border-bottom: none;

                i,
                span.fa {
                    color: #666;
                    border-bottom: 0;
                }
                &:hover span {
                    border-bottom: 1px solid #e3f0fd;
                }
            }

            span {
                border-bottom: 1px solid #ccc;
            }
        }
    }
    // .event-detail

    .events-extra {
        ul {
            margin: 0;

            li {
                margin: 0 0 7px 0;

                i,
                span.fa {
                    margin-right: 10px;
                }
            }
        }
    }

    // contact
    .alternative-contact {
        padding: 20px;
        @include clearfix;

        .contact {
        }
        .fn {
            margin: 0;
        }
        i,
        span.fa {
            margin-right: 10px;
        }
        .cta {
            margin: 14px auto;
            text-align: center;
            float: left;

            i,
            span.fa {
                float: left;
            }
        }
    }
}

@include breakup-breakpoint('wide') {
    // 980px
    .single-event {
        .event-share {
            .social-flat-links {
                clear: none;
            }
        }
    }
}

// .single-event

.js {
    .event-form {
        @include visuallyhidden;

        &.show {
            border: 0;
            clip: none;
            height: auto;
            margin: 0;
            position: relative;
            width: auto;
            background: #eae9e7;
            padding: 20px;
            @include clearfix;

            .wFormContainer {
                padding: 0;
            }

            .wForm {
                background: #fff;
            }
        }
    }
}

#related-events {
    margin: 0 0 20px;
    background-color: #333333;
    padding: 0 70px;

    h2 {
        color: #ffffff;
        font-size: 1.7em;
        padding: 20px 0 0;
        text-align: center;
    }
    h3 {
        margin-bottom: 5px;
        font-size: 1.1em;
    }
    ul {
        margin: 0;
    }
    .event {
        color: $white;
        margin-bottom: 20px;
        overflow: hidden;

        a {
            color: $white;
        }
        .duration {
            color: #333;
        }
    }
    .date-day {
        color: #333333;
    }
    .date {
        margin: 10px 10px 10px 0;
        width: 50px;
    }
    .date-day-no {
        border: none;
        padding-bottom: 4px;
    }
    .bx-wrapper {
        margin: 0 45px;
        padding: 0 0 20px;
    }
    .bx-controls {
        display: block;
        position: absolute;
        top: -34px;
        width: 100%;
        height: 0;
    }
    .bx-controls-direction a {
        background-color: #666666;
        border: medium none;
        color: #ffffff;
        display: block;
        outline: 0 none;
        padding: 40px 10px;
        font-size: 2em;
        width: 30px;
        position: relative;
        text-align: center;
        transition-duration: 0.5s;
        transition-property: background-color;
        transition-timing-function: ease-out;
        z-index: 1;

        &:hover {
            background-color: #999999;
        }
        &.disabled {
            opacity: 0.4;
            background-color: #666666;
            cursor: default;
        }
    }
    .bx-prev {
        left: -65px;
        float: left;
    }
    .bx-next {
        right: -65px;
        float: right;
    }

    @include breakup-breakpoint('medium') {
        // 767px
        h3 {
            font-size: 1.3em;
        }
    }
}

// end Event n pages
