.academic-unit {
    .main-thumbnail {
        display: none;
        position: relative;

        img {
            display: block;
            margin: 0;
        }
    }
    .description {
        font-size: 1.1em;
    }
    .tabs {
        .ui-tabs-nav h2 {
            font-size: 1em;
            font-family: $font-family-sans;
            margin: 0;
        }
        .featured-title {
            background-color: #333;
            color: $white;
            margin-bottom: 0;
            padding: 10px;
        }
        .person {
            padding: 10px 15px 15px;
            display: block;
            background-color: #333;
            color: $white;

            h3 {
                font-size: 1.3em;
            }
            .thumbnail-wrapper {
                background-color: #333;
                border: none;
                box-shadow: none;
                text-align: center;
            }
            .thumbnail {
                text-align: center;
                vertical-align: middle;
                max-height: 200px;
                margin-bottom: 0;
            }
            .name {
                margin: 15px 0 15px 0;
            }
            .description {
                margin: 0;
                font-family: inherit;
                font-size: inherit;
                font-style: inherit;
                line-height: inherit;
            }
        }
    }

    .social-widget {
        padding: 5px 15px;
        background-color: $white;

        ol {
            margin: 0;
        }
        li {
            list-style-type: none;
            margin: 0 0 30px 0;
        }
        h4 {
            margin-bottom: 0;
        }
    }

    @include breakup-breakpoint('thinish') {
        // 480px
        .person .thumbnail-wrapper {
            float: none;
            width: auto;
        }
    }
    @include breakup-breakpoint('thinish_extended') {
        // 500px
        .main-thumbnail {
            margin: 0 0 -50px;
            display: block;
        }
        h1 {
            padding: 10px 20px 10px 10px;
            position: relative;
            bottom: 36px;
            background-color: rgba(0, 0, 0, 0.87);
            color: $white;
            display: inline-block;
        }
    }
    @include breakup-breakpoint('wide') {
        // 980px
        .presentation {
            width: 70%;
        }
    }
}

.academic-units {
    @mixin normal-link($underline: false) {
        border: none;
        transition: color 0.1s, background 0.1s;

        &:hover {
            color: $red1;
            background: transparent;
            @if $underline {
                text-decoration: underline;
            }
        }
        &:active,
        &:focus {
            background: $red1;
            color: white;
        }
    }

    h1,
    h2,
    h3 {
        font-family: 'ff-meta-web', Georgia, sans-serif;
    }

    #site-home a {
        font-family: 'ff-meta-web', Georgia, sans-serif;
    }

    .responsive-wrapper {
        padding-top: 0;
    }

    @include breakup-breakpoint('wide') {
        .responsive-wrapper {
            padding-top: 20px;
        }
        h1 {
            display: none;
        }
    }

    .academic-unit-listing {
        img {
            margin: 0;
        }

        h2 {
            margin: 15px 0;
            font-size: 1.4em;
            font-weight: 600;

            a {
                color: black;
                font-weight: 600;
                @include normal-link(true);
            }
        }

        ul {
            list-style-type: none;
            margin: 0;
        }
        li {
            a {
                color: #333;
                @include normal-link;
            }
        }

        .academic-unit-centre {
            .link-list,
            ul {
                margin: 0 0 60px 0;
            }
            li {
                padding-bottom: 6px;
                border-bottom: 1px solid #ccc;
                margin-bottom: 6px;
            }
        }

        .academic-unit-other {
            padding: 10px 20px;
            background: #eee;
            margin-bottom: 30px;

            ul {
                margin: 0;
            }
            li {
                margin-bottom: 13px;
            }

            .academic-support {
                margin-top: 40px;
            }
        }

        .academic-unit-listing__item {
            position: relative;
        }
    }

    .academic-units-slider {
        margin: 0;

        .stat {
            width: 100%;
            background: #00203c;
            padding: 20px 30px;
        }

        .stat-head {
            font-family: ff-meta-web, 'Helvetica Neue', Arial, Helvetica,
                sans-serif;
            font-size: 3.4em;
            font-weight: 400;
            margin-bottom: -10px;
            line-height: 1.5em;
        }

        .stat-sub {
            font-family: ff-meta-web, 'Helvetica Neue', Arial, Helvetica,
                sans-serif;
            font-size: 1.15em;
            margin-top: 0;
        }

        .stat-source {
            font-family: Georgia, sans-serif;
            font-style: italic;
            font-size: 1.15em;
        }
    }
}
