/**
 * Category Colours
 */
$arts-and-culture: #3f175c;
$business-and-finance: #11365c;
$campus-life: #ad2b1a;
$health: #5a8054;
$politics-and-law: #5d5d5d;
$science-and-technology: #00434a;
// These classes are applied to `.news-card` and `.news-article`
$categories: all $red, arts-and-culture $arts-and-culture,
    business-and-finance $business-and-finance, campus-life $campus-life,
    health $health, politics-and-law $politics-and-law,
    science-and-technology $science-and-technology;

@each $category in $categories {
    $class: nth($category, 1);
    $color: nth($category, 2);

    .#{$class} {
        .news-card-topic,
        .news-article-topic,
        .news-widget-topic,
        & + aside .news-widget-topic {
            color: #{$color};

            &:hover,
            &:focus,
            &:active {
                background-color: #{$color};
                color: $white;
            }
        }

        .tweet-content a {
            color: #{$color};
        }

        & + aside .news-article-widget-related-articles {
            border-top-color: #{$color};
        }
    }
}

.news-article-hashtag {
    font-style: normal;
    font-family: $font-family-sans;
    margin-right: 3px;
}

/**
 * Cards List
 * Used to display articles in a list-type format
 */
// Cards - List Container
.news-articles-container {
    clear: both;

    .news-articles-title {
        border-top: 5px solid $gray-lighter;
        padding-top: 1em;
        box-shadow: inset 0 1px lighten($gray-light, 20%);
    }
}

// Cards - List
.news-articles-list {
    padding: 0;
    margin: 0.5em -10px 0;
    list-style: none;

    & > li:not(.clearfix) {
        margin-bottom: (2 * $grid-gutter-width);
    }
}

/**
 * Card
 */
.news-card-wrapper h2 {
    margin-top: 0;
}

.news-card {
    background-color: $white;
    display: block;
    padding-bottom: 1em;
    position: relative;
}

// Card - Resets
.news-card-media,
.news-card-categories,
.news-widget-categories,
.news-card-content {
    width: 100%;
    display: block;
    position: relative;
    border-bottom: 0 !important;
    z-index: 2;

    &:hover,
    &:focus {
        background-color: transparent;
    }
}

// Card - Media
.news-card-media {
    background-color: $white;
    position: relative;
    overflow: hidden;

    &:empty {
        display: none;
    }

    &:after {
        @include transition(opacity 0.2s ease);
        content: '';
        background: $white;
        opacity: 0;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 3;
    }

    .news-card:hover &:after {
        opacity: 0.1;
    }

    img,
    svg,
    video,
    iframe {
        display: block;
        margin: 0;
    }
    img,
    svg {
        width: 100%;
        height: auto;
        border-radius: 1px;
    }

    .news-card:hover & img,
    .news-card:hover & svg {
        -webkit-filter: saturate(120%);
    }
}

.news-card-text {
    padding: 10px 10px 0;

    p:last-of-type {
        margin-bottom: 0;
    }
}

// Card & Widget - Categories
.news-card-categories,
.news-widget-categories {
    width: 100%;
    color: $grey5;
    float: left;
    padding-bottom: 0.5em;
    border-bottom: 1px solid $grey5;
    margin-bottom: 0.5em;
    position: relative;
    overflow-x: hidden;
    z-index: 3;

    .news-card-media + &,
    .home-news & {
        padding-top: 0.5em;
        border-bottom-width: 0;
        margin-bottom: 0;
    }

    .course__example-research & {
        position: relative;
        overflow: visible;
        padding-left: 2.5em;
        padding-bottom: 0;
        margin-bottom: 1em;

        &:before {
            content: '\f0ca';
            background-color: $grey5;
            color: $white;
            width: 1.5em;
            height: 1.5em;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 50%;
            font-family: 'FontAwesome';
            font-size: 1.25em;
            line-height: 1.5;
            text-align: center;
        }
    }
}

.news-card-topic,
.news-card-series,
.news-card-subject,
.news-article-topic,
.news-article-series-family,
.news-widget-topic {
    display: inline-block;
    border-bottom: 0 !important;
    position: relative;
    padding: 0.25em 0;
    font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica, sans-serif;
    line-height: 1.25em;
    text-decoration: none;
    text-transform: uppercase;

    &:focus {
        background-color: transparent;
    }
}

.news-card-topic,
.news-article-topic,
.news-widget-topic {
    @include transition(
        background-color 0.2s ease,
        color 0.2s ease,
        padding 0.2s ease,
        margin 0.2s ease
    );
    margin-right: 1.25em;
    font-weight: 600;
    border-radius: 1px;

    &:hover,
    &:focus,
    &:active {
        padding: 0.25em 0.5em;
        margin-right: 0.25em;

        & + .news-article-series:before,
        & + .news-card-series:before {
            @include transition(visiblity 0 linear 0.05s);
            visibility: hidden;
        }
    }

    & + .news-article-series:before,
    & + .news-card-series:before {
        @include transition(visibility 0 linear 0.15s);
        content: '';
        background-color: $grey5;
        width: 1px;
        height: 75%;
        position: absolute;
        top: 12.5%;
        left: -0.625em;
    }
}

.news-article-topic,
.news-widget-topic {
    margin-right: 1em;

    &:hover,
    &:focus,
    &:active {
        margin-right: 0;
    }

    & + .news-article-series:before {
        height: 60%;
        top: 20%;
    }
}

.news-card-series,
.news-card-subject {
    color: $gray !important;

    &:hover,
    &:focus,
    &:active {
        text-decoration: underline;
    }
}

// Card - Content
.news-card-content {
    color: black !important;
    padding-top: 0.5em;

    .news-card-content__title,
    h3 {
        margin: 0 0 14px;
        font-size: 16px;
    }

    .news-card:hover & h3,
    .news-card:focus & h3,
    .news-card:active & h3 {
        text-decoration: underline;
    }

    a {
        border-bottom: 0;
        word-wrap: break-word;

        &:hover,
        &:focus,
        &:active {
            text-decoration: underline;
        }
    }

    p {
        margin-bottom: 0;
    }

    time {
        color: $grey5;
        display: block;
        margin-top: 1em;
    }

    &.instagram-content {
        color: $grey5 !important;

        time {
            display: inline;
        }
    }

    .tweet-content {
        font-size: 1.6em;
        font-family: $font-family-serif;
        line-height: 1.4em;
    }

    .tweet-title,
    .tweet-meta {
        margin-top: 1em;
    }

    .tweet-meta {
        width: 100%;

        time {
            float: left;
        }
    }

    .twitter-username {
        color: $grey5;
        font-style: italic;
    }

    .tweet-meta-actions {
        float: right;
        list-style: none;

        li {
            display: inline-block;

            & + li {
                margin-left: 0.25em;
            }
        }

        a {
            display: block;
        }
    }

    p:empty {
        display: none;
    }
}

.tweet-meta-reply,
.tweet-meta-favorite,
.tweet-meta-retweet {
    background-image: url('i/everything-sprite.png');
    background-repeat: no-repeat;
    width: 16px;
    height: 16px;
    overflow: hidden;
    white-space: nowrap;
    text-indent: 100%;

    &:focus {
        background-color: transparent;
    }
}

.tweet-meta-reply {
    background-position: 0 center;

    &:hover,
    &:focus {
        background-position: -16px center;
    }
}

.tweet-meta-favorite {
    background-position: -32px center;

    &:hover,
    &:focus {
        background-position: -48px center;
    }

    &:active {
        background-position: -64px center;
    }
}

.tweet-meta-retweet {
    width: 19px;
    background-position: -80px center;

    &:hover,
    &:focus {
        background-position: -99px center;
    }

    &:active {
        background-position: -118px center;
    }
}

/**
 * Navigation
 * Navigate between pages underneath News
 */
.news-navigation {
    padding-top: 0;
    padding-bottom: 0;
    position: relative;
}

// Navigation - Title
.news-navigation-title {
    @include font-family-sans-serif();
    color: $grey5;
    display: inline-block;
    margin: 0;
    padding-bottom: 1em;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.071em;
    line-height: 1.867em; // equivalent of 2em at 14px

    @include breakup-breakpoint('medium') {
        padding-bottom: 0;
    }
}

// Navigation - List
.news-navigation-list {
    display: block;
    margin: 0;
    list-style: none;
    overflow: hidden;

    @include breakup-breakpoint('medium') {
        display: inline-block;
        float: right;
    }

    li {
        display: inline-block;
        margin-left: -0.75em;

        @include breakup-breakpoint('medium') {
            margin-left: 0;
            margin-right: -0.75em;
        }
    }

    a {
        @include transition(all 0.2s ease);
        color: black;
        display: block;
        padding: 0.5em 0.75em;
        border-bottom: 0;
        line-height: 1em;
        text-decoration: none;

        &:hover,
        &:focus,
        &:active {
            span {
                text-decoration: underline;
            }

            i {
                text-decoration: none;
            }
        }

        &:focus,
        &:active {
            &,
            i {
                color: $white;
            }

            span {
                text-decoration: none;
            }
        }

        i {
            @include transition(all 0.2s ease);
            width: auto;
            color: $red;
            padding-left: 0.25em;
        }
    }
}

.see-all-wrapper {
    @include breakup-breakpoint('medium') {
        position: relative;
        text-align: center;
        padding-top: 2.5em;

        &:before {
            content: '';
            background-color: $gray-lighter;
            height: 0.25em;
            display: block;
            position: absolute;
            top: 0;
            left: 10px;
            right: 10px;
        }
    }
}

.see-all-button {
    [class*='icon'],
    [class*='fa fa'] {
        font-size: 2em;
        line-height: 0.5em;
    }
}

/**
 * Article
 */
.news-article {
    .embed-wrapper {
        margin-bottom: 1.5em;
    }
}

.news-article-hero {
    min-height: 2.5em;
    margin-bottom: 1em;
    position: relative;

    img {
        display: block;
        margin-bottom: 0;
        border-radius: 0.357em;
    }
}

.news-article-title {
    font-family: sans-serif;
    font-size: 26px;
    font-weight: 300;
}

.news-article-standfirst {
    font-family: sans-serif;
    font-size: 18px;
    line-height: 1.4em;
    margin-bottom: 8px;

    p {
        margin: 0;
    }
}

.news-article-categories {
    background-color: $white;
    color: $grey5;
    padding-right: 0.75em;
    position: absolute;
    bottom: 0;
    left: 0;
    line-height: 2.5em;
    word-spacing: 0.25em;
    border-radius: 0 0.357em 0 0;

    a,
    .news-article-topic,
    .news-article-series-family {
        font-weight: 600;
        border-bottom: 0;
        word-spacing: normal;
    }

    .news-article-series-family {
        color: $grey5;

        &:hover,
        &:focus,
        &:active {
            text-decoration: underline;
        }
    }

    span {
        display: inline-block;
        position: relative;
    }
}

.news-article-date,
.news-article-press-officer {
    &,
    a,
    & + span:not([class]) {
        color: $grey5;
    }

    a {
        @include transition(all 0.2s ease);
        padding: 0 0.25em;

        &:focus,
        &:active {
            color: $white;
        }
    }
}

.tweet-blockquote {
    width: 80%;
    padding: 1em 0;
    margin: 1.5em auto;
    border-top: 0.357em solid #d9ecfa;
    border-bottom: 0.357em solid #d9ecfa;
    text-align: center;

    blockquote {
        background: none;
        color: inherit;
        width: 80%;
        padding: 0;
        margin: 0 auto;
        font-size: 1.6em;
        line-height: 1.4em;
        quotes: '\201C' '\201D' '\2018' '\2019';

        &:before {
            content: open-quote;
        }

        &:after {
            content: close-quote;
        }
    }

    .tweet-link {
        color: $grey5;
        display: inline-block;
        margin-top: 1em;
        border-bottom: 0;
        font-family: $font-family-serif;
        font-style: italic;
        text-transform: lowercase;

        &:hover,
        &:focus,
        &:active {
            background: none;
            text-decoration: underline;
        }

        .fa-twitter {
            color: #55aeed;
            display: inline-block;
            padding-right: 0.5em;
            font-size: 1.8em;
            vertical-align: middle;
            text-decoration: none !important;
        }
    }
}

.media-definition {
    background-color: #f5f4f2;
    width: 100%;
    display: table;
    padding: 1em;
    margin: $grid-gutter-width 0;
}

.media-definition-title {
    color: $grey5;
    width: auto;
    min-width: 8em;
    display: block;
    margin-bottom: 0.5em;
    font-family: $font-family-serif;
    font-style: italic;
    font-weight: 600;

    @include breakup-breakpoint('medium') {
        display: inline-block;
        margin-bottom: 0;
    }

    &:empty {
        display: none;
    }
}

.media-definition-content {
    width: 100%;
    display: block;
    overflow: hidden;

    @include breakup-breakpoint('medium') {
        display: table-cell;
    }

    p {
        margin: 0;
    }
}

.news-article-bottom-meta {
    color: $grey5;
    padding-top: 1.5em;
    margin: 3em 0 1.5em;
    position: relative;
    font-family: $font-family-serif;
    font-style: italic;
    font-weight: 600;
    line-height: 2em;

    &:after {
        content: '';
        background-color: $black;
        width: 8em;
        height: 0.357em;
        position: absolute;
        top: 0;
        left: 0;
    }

    span {
        display: inline-block;
        padding-right: 0.25em;
    }

    a {
        color: $grey5;
        border-bottom: 0;
        font-family: $font-family-sans;
        font-style: normal;

        &:hover,
        &:focus,
        &:active {
            text-decoration: underline;
        }
    }
}

.news-article-meta-social {
    width: 100%;
    padding-bottom: ($grid-gutter-width / 2);
    border-bottom: 1px solid $grey5;
    margin: $grid-gutter-width 0;

    .news-article-bottom-meta & {
        margin-top: 0.5em;
        border-bottom: 0;
    }
}

/**
 * Author
 * Information about authors
 */
.news-author-info {
    margin-bottom: 1.5em;
}

// Author - Photo
.news-author-info,
.news-author-list {
    img {
        height: auto;
        display: block;
        margin: 0 auto;
        border-radius: 0.375em;
    }
}

.news-author-info .fa-user,
.news-author-list .fa-user {
    background-color: $gray-lighter;
    width: 100%;
    height: 0;
    display: block;
    padding-bottom: 66.8%;
    position: relative;
    text-align: center;
    border-radius: 0.375em;

    &:before {
        color: $gray;
        width: 100%;
        margin-top: -0.5em;
        position: absolute;
        top: 50%;
        left: 0;
        font-size: 5em;
        line-height: 1em;
    }
}

.news-author-info,
.news-author-list {
    img + .fa-user {
        display: none;
    }
}

// Author - Name Container
.news-author-name-container {
    background-color: $white;
    width: 100%;
    max-width: 257px;
    padding: 0.5em 0;
    margin: -1.5em auto 0;
    border-bottom: 1px solid $gray-lighter;
    position: relative;
    line-height: 1em;
    text-align: center;
}

// Author - Name
.news-author-name {
    color: $gray;
    margin: 0;
    font-size: 1.143em; // 16px
}

// Author - Title
.news-author-title,
.news-author-name-container span {
    color: $red;
    line-height: 1.143em;

    &:empty {
        display: none;
    }
}

// Author - Social
.news-author-social {
    list-style: none;
    margin: 0 1em;

    .news-author-name-container + & {
        margin-top: 1.5em;
    }

    li {
        display: block;
    }

    a {
        color: $gray;
        display: block;
        border: 0;
        line-height: 3em;
        text-decoration: none;

        &:hover,
        &:focus,
        &:active {
            text-decoration: underline;

            [class*='fa-'] {
                color: $gray;
            }
        }
    }

    [class*='fa-'] {
        color: $grey5;
        width: 1.5em;
        font-size: 1.5em;
        line-height: 2em;
        text-align: left;
        text-decoration: none !important;
    }

    .news-author-phone {
        white-space: nowrap;
    }
}

.news-author-about {
    padding-bottom: 1.5em;
}

/**
 * News Pagination
 */
.news-pagination {
    width: 100%;
    margin: 0 0 1em;
    list-style: none;
    text-align: center;

    li {
        display: inline-block;
        margin-bottom: 1em;
    }

    a,
    span {
        min-width: 2em;
        height: 2em;
        display: block;
        padding: 0 0.75em;
        margin: 0 0.25em;
        line-height: 2em;
        text-align: center;
        border: 1px solid $grey5;
        border-radius: 2px;
    }

    a {
        @include transition(all 0.15s ease);

        &:hover,
        &:focus,
        &:active {
            background-color: transparent;
            border-color: $red;
        }

        &:focus,
        &:active {
            background-color: $red;
            color: $white;
        }
    }

    span {
        color: $grey5;
    }
}

/**
 * Article Widgets
 */
.news-widget-call-to-action {
    @include transition(color 0.2s ease);
    color: $gray !important;
    display: block;
    padding: $grid-gutter-width;
    border-top: 1px solid $gray-lighter;
    border-bottom: 1px solid $gray-lighter !important;
    margin-bottom: 1em;
    position: relative;
    font-size: 16px;
    font-weight: 600;
    text-decoration: none;

    &:hover,
    &:focus,
    &:active {
        background: none;

        span {
            text-decoration: underline;
        }

        i {
            right: ($grid-gutter-width - 14px);
        }
    }

    i {
        @include transition(right 0.2s ease);
        color: $grey5;
        font-size: 2em;
        line-height: 1em;
        position: absolute;
        right: $grid-gutter-width;
        top: 50%;
        margin-top: -0.5em;
    }
}

.news-article-widget {
    padding: 0 $grid-gutter-width $grid-gutter-width;
    border-bottom: 1px solid $gray-lighter;
    margin-bottom: 2em;

    aside &:last-child {
        border-bottom: 0;
    }
}

.news-article-widget-related-experts {
    h2 span {
        color: $red1;
    }

    .find-an-expert-link {
        @include transition(padding 0.2s ease);
        color: $gray;
        display: block;
        margin-top: 1em;
        border-bottom: 0;
        font-size: 15px;
        text-decoration: none;

        &:hover,
        &:focus,
        &:active {
            background: none;
            padding-left: 0.25em;

            span {
                text-decoration: underline;
            }

            i {
                padding-left: 0.75em;
            }
        }

        i {
            @include transition(padding 0.2s ease);
            color: $red;
            padding-left: 0.5em;
            font-size: 20px;
            line-height: 15px;
        }
    }
}

.related-experts-list,
.related-subjects-list,
.related-courses-list,
.related-articles-list,
.news-date-list {
    margin: 0;
    list-style: none;

    a {
        @include transition(padding 0.2s ease);
        color: $gray-dark;
        display: block;
        padding: 0.5em 0.5em 0.5em 1em;
        border-bottom: 0;
        position: relative;
        line-height: 1.25em;
        text-decoration: none;

        &:hover,
        &:focus,
        &:active {
            background: none;
            color: $gray-dark;
            padding-left: 1.5em;
            padding-right: 0;

            span {
                text-decoration: underline;
            }

            i {
                padding-left: 0.25em;
                text-decoration: none;
            }
        }

        i {
            @include transition(padding 0.2s ease);
            width: auto;
            color: $red;
            position: absolute;
            top: 0;
            left: 0;
            line-height: 2em;
        }
    }
}

.related-experts-list,
.related-articles-list,
.news-date-list {
    li,
    span {
        display: block;
    }

    a {
        padding-left: 0;

        &:hover,
        &:focus,
        &:active {
            padding-left: 0.5em;

            i {
                padding-left: 0.5em;
            }
        }

        i {
            padding-left: 0.25em;
            position: relative;
        }
    }
}

.related-experts-list,
.related-articles-list,
.news-date-list {
    li + li {
        border-top: 1px solid $gray-lighter;
    }
}

.related-experts-list,
.related-articles-list {
    a {
        padding-top: 0.75em;
        padding-bottom: 0.75em;

        i {
            line-height: 2.5em;
        }
    }

    .news-widget-topic {
        display: inline-block;
        padding: 0.25em 0;
        font-size: 0.929em;

        &:hover,
        &:focus,
        &:active {
            padding: 0.25em 0.5em;
        }

        & + a {
            padding-top: 0.5em;
            margin-bottom: 0.75em;
        }
    }

    li:last-child .news-widget-topic + a {
        padding-bottom: 0.5em;
        margin-bottom: 0;
    }
}

.news-article-widget-related-subjects,
.news-article-widget-related-courses,
.news-article-widget-text {
    background: $gray-lighter;
    padding: $grid-gutter-width;
    border-bottom: 0;
}

.news-article-widget-related-subjects + .news-article-widget-related-courses,
.news-article-widget-text + .news-article-widget-text {
    margin-top: -48px; // (2em + $grid-gutter-width)
    padding-bottom: 2em;
}

.news-article-widget-related-articles {
    border-top: 5px solid $gray-lighter;
    padding: 0.5em 0 $grid-gutter-width;
}

.news-widget-topic {
    &:empty {
        display: none;
    }
}

.news-date-list {
    margin-bottom: 1.5em;

    @include breakup-breakpoint('medium') {
        margin-bottom: 0;
    }

    a {
        white-space: nowrap;
    }

    ul {
        display: none;
        margin: 0;
        list-style: none;

        li + li {
            border-top: 0;
        }

        a {
            padding: 0.25em 0.5em 0.25em 1em;

            &:hover,
            &:focus,
            &:active {
                padding-left: 1.5em;
            }
        }
    }
}

.news-date-list > .news-date-item--active,
.news-date-list-2016 .news-date-item-2016,
.news-date-list-2015 .news-date-item-2015,
.news-date-list-2014 .news-date-item-2014,
.news-date-list-2013 .news-date-item-2013,
.news-date-list-2012 .news-date-item-2012,
.news-date-list-2011 .news-date-item-2011,
.news-date-list-2010 .news-date-item-2010 {
    & > a {
        padding-bottom: 0.25em;
        font-weight: 600;
    }

    ul {
        display: block;
    }
}

.news-date-item--active > ul > .news-date-item--active,
.news-date-list-jan .news-date-item-jan,
.news-date-list-feb .news-date-item-feb,
.news-date-list-mar .news-date-item-mar,
.news-date-list-apr .news-date-item-apr,
.news-date-list-may .news-date-item-may,
.news-date-list-jun .news-date-item-jun,
.news-date-list-jul .news-date-item-jul,
.news-date-list-aug .news-date-item-aug,
.news-date-list-sep .news-date-item-sep,
.news-date-list-oct .news-date-item-oct,
.news-date-list-nov .news-date-item-nov,
.news-date-list-dec .news-date-item-dec {
    font-weight: 600;
}

.news-article-widget-featured-cta {
    background-color: #04284c;
    color: $white;
    padding-top: 1.429em;

    a {
        color: $white;
        display: block;
        border: 0;

        &:hover,
        &:focus,
        &:active {
            background: inherit;
        }

        &:hover h2,
        &:focus h2 {
            text-decoration: underline;
        }

        &:focus h2,
        &:active h2 {
            color: $red1;
        }
    }

    h2,
    h3,
    p {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
    }

    h2 {
        margin: 5px 0;
        font-size: 1.2em;
        font-weight: 600;
    }

    h3 {
        color: #aaaaaa;
        margin: 0;
        font-size: 1em;
    }

    p {
        font-size: 1.2em;
        font-weight: 300;
    }

    i {
        font-size: 1.4em;
    }
}

// Author - List
.news-author-list {
    list-style: none;

    & > li:not(.clearfix) {
        margin-bottom: $grid-gutter-width;
    }

    a {
        display: block;
        border-bottom: 0;
        text-decoration: none;

        &:hover,
        &:focus,
        &:active {
            opacity: 0.8;
        }
    }

    img {
        width: 100%;
        height: auto;
    }
}

/**
 * Archive News Items
 */
.index-archive {
    ol {
        padding: 0;
        margin: 0;

        li {
            border-bottom: 1px solid #cccccc;
            margin-left: 0;
            padding: 15px 0;
            overflow: hidden;
            list-style-type: none;
        }

        h2 {
            font-size: 1.4em;
            margin: 0;
        }

        img {
            display: inline;
            float: left;
            margin: 5px 13px 5px 0;
        }

        .pub-date {
            font-size: 0.9em;
        }
    }
}

/** BEM update */

.news-finder__query-wrapper {
    margin-top: -40px;
    background: #cccccc;
}

.news-finder__form-wrapper {
    background: #eeeeee;
}

.news-finder__query {
    .query-box {
        padding-right: 44px;
        position: relative;
    }
    input {
        padding: 10px 10px 9px 10px;
        width: 100%;
        border: 1px solid #a9a9a9;
        font-size: 16px;
    }
    button {
        position: absolute;
        top: 0;
        right: 0;
        width: 45px;
        height: 45px;
        background: white;
        border: 1px solid #a9a9a9;
    }

    .filter-box {
        margin-top: 1em;
    }

    .filter-box__float {
        margin-bottom: 0;
    }

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        .filter-box__inner {
            background: none;
            padding: 0;
        }

        .filter-box__filter {
            margin-bottom: 0;
        }
    }
}
