////
/// Course Level-Specific Styles
/// @group courses
////

/// @type Color
$color-flamingo: #ffffff !default;
/// @type Color
$color-everyman: #eeeeee !default;
/// @type Color
$color-carnage: #cccccc !default;
/// @type Color
$color-doomsday: #666666 !default;
/// @type Color
$color-eobard: #333333 !default;
/// @type Color
$color-reaper: #222222 !default;
/// @type Color
$color-achebe: #a22833 !default;
/// @type Color
$color-buffalo: #8d182a !default;
/// @type Color
$color-buffalo2: #b91b1b !default;
/// @type Color
$color-chucky: #c00f22 !default;
/// @type Color
$color-purple-dark: #2e1827 !default;
/// @type Color
$color-purple: #370332 !default;
/// @type Color
$color-purple-light: #4d2942 !default;
/// @type Color
$color-blue-dark: #1c3040 !default;
/// @type Color
$color-blue: #243d51 !default;
/// @type Color
$color-blue-light: #395062 !default;

/// @type Map
/// @prop {Color} color - Background Default
/// @prop {Color} color - Background Dark
/// @prop {Color} color - Background Light
/// @prop {Color} color - Course News
$course-levels: (
    'ug': (
        'background-default': '#a42833',
        'background-dark': '#8d182a',
        'background-light': '#ae3c44',
        'course-news': '#b91b1c',
    ),
    'pg': (
        'background-default': '#3e1031',
        'background-dark': '#402337',
        'background-light': '#5e3b53',
        'course-news': '#b91b1c',
    ),
    'rd': (
        'background-default': $color-blue,
        'background-dark': $color-blue-dark,
        'background-light': $color-blue-light,
        'course-news': $color-blue,
    ),
) !default;

@each $name, $colors in $course-levels {
    $background-default: #{map-get($colors, 'background-default')};
    $background-dark: #{map-get($colors, 'background-dark')};
    $background-light: #{map-get($colors, 'background-light')};
    $course-news: #{map-get($colors, 'course-news')};

    .course-#{$name} {
        .course__subheader:after,
        .course__key-information {
            background-color: $background-default;
        }

        .course__key-information h3 {
            border-bottom-color: $background-light;
        }

        .course__red-links .row > :not(:first-child) .course__red-links:before {
            background-color: $background-light;
        }

        .course__red-links__link {
            background-color: $background-default;

            &:hover {
                background-color: $background-dark;
            }

            &:focus,
            &:active {
                background-color: $background-light;
            }
        }

        .course__news-item__cat,
        .course__event__date {
            background-color: $course-news;
        }

        .course__testimonials__wrapper {
            background-color: $background-default;
        }

        .course__profiles__item__title a {
            &:focus,
            &:active {
                color: $background-default;
            }
        }
    }
}
