// event homepage, calendar page and event n page styles

.events-intro {
    font-family: $font-family-serif;
    font-style: italic;
    font-size: 1.3em;
    line-height: 1.6em;
}

.events-home .event-featured {
    height: 450px;
}

#primary-nav-toggler.events-home-title {
    font-size: 1.8em;
    a {
        background: transparent;
        border: none;
        padding: 0;

        &:hover {
            border: none;
        }

        &:active {
            box-shadow: none;
        }
    }
    .icon-reorder {
        display: none;
    }
}

.event-calendar {
    background: #eae9e7;
    padding: 4.4% 5%;
    margin-bottom: 2.43902%;

    h2 {
        margin: 0 0 10px 0;
    }
    .filter-list {
        list-style: none;
        margin: 0 0 14px 0;
        padding: 0;
        @include clearfix;

        li {
            float: left;
            margin-right: 10px;

            &:last-child {
                margin-right: 0;
            }
        }
    }
}

#calendar {
    margin-bottom: 10px;

    .ui-datepicker {
        width: 100%;
    }

    table {
        display: table;
    }
}

.calendar-link,
.event-archive {
    &:hover {
        border-color: #eae9e7;
    }
}

#category-links {
    width: 50%;
    float: left;

    .dropdown-menu li:last-child a {
        border-bottom: none;
    }
}

.event-archive {
    display: block;
    float: right;
    margin: 4px 0;
}

#day-links,
#category-select {
    list-style: none;
    margin: 0 0 10px 0;

    li {
        padding: 0;
        margin: 0;
        float: left;
        margin-right: 10px;
    }
}

.featured-events-title {
    text-align: center;
    margin: 0 auto 14px auto;
}

.events-heading {
    padding: 5px 10px;
    margin: 0 0 2.43902% 0;
    background: #eae9e7;
}

//square event or story, four in a row on desktop
.event-featured,
.story {
    margin-bottom: 2.43902%;
    position: relative;

    a:link {
        color: #333;
        border: none;
        display: block;
        border: none;
    }
    a:visited {
        color: #333;
    }
    a:hover {
        color: #333;
        img {
            opacity: 0.8;
        }
    }

    img {
        margin: 0;
        display: block;
        width: 100%;
    }

    .event-title,
    .story-title {
        font-weight: bold;
    }
    .detail {
        position: relative;
        min-height: 40px;
        width: 100%;
        background: #e3f0fd;

        p,
        span {
            padding: 10px;
            margin: 0;
        }
        span {
            font-size: 0.9em;
            display: block;
        }
        &:after,
        &:before {
            bottom: 100%;
            border: solid transparent;
            content: ' ';
            height: 0;
            width: 0;
            position: absolute;
            pointer-events: none;
        }
        &:after {
            border-color: rgba(136, 183, 213, 0);
            border-bottom-color: #e3f0fd;
            border-width: 10px;
            left: 15%;
            margin-left: -10px;
        }
        &:before {
            border-color: rgba(194, 225, 245, 0);
            border-bottom-color: #e3f0fd;
            border-width: 16px;
            left: 15%;
            margin-left: -16px;
        }
    }
}

.event-main,
.event-featured {
    .date {
        position: absolute;
        top: 10px;
        right: 10px;
        width: 50px;
        margin: 0;

        .date-month {
            border: none;
        }
        .date-day-no {
            font: 2em/1.1em Georgia, serif;
            border: none;
            margin-bottom: 5px;
        }
    }
}

// main event, top of the page, large image
.event-main {
    overflow: hidden;
    margin: 0 0 2.43902% 0;
    background: #e3f0fd;
    position: relative;

    a:link,
    a:hover,
    a:visited {
        color: #333;
    }
    a:hover {
        img {
            opacity: 0.8;
        }
    }
    .detail {
        position: relative;
        width: 100%;
        bottom: auto;
        height: 100%;
        background: #e3f0fd;

        span {
            position: relative;
            left: auto;
            display: block;
            padding: 0 10px 10px 10px;
        }
        .event-title {
            padding: 10px 10px 0 10px;
            font-weight: bold;
            font-size: 1.2em;
        }
        &:after,
        &:before {
            bottom: 100%;
            border: solid transparent;
            content: ' ';
            height: 0;
            width: 0;
            position: absolute;
            pointer-events: none;
        }
        &:after {
            border-color: rgba(136, 183, 213, 0);
            border-bottom-color: #e3f0fd;
            border-width: 16px;
            left: 15%;
            margin-left: -16px;
        }
        &:before {
            border-color: rgba(194, 225, 245, 0);
            border-bottom-color: #e3f0fd;
            border-width: 16px;
            left: 15%;
            margin-left: -16px;
        }
    }
    a {
        border: none;
    }
    img {
        width: 100%;
        margin: 0;
        display: block;
    }
}

.events-social {
    margin-bottom: 2.43902%;
    background: #eae9e7;
    padding: 2.43902%;

    h2 {
        margin-top: 0;
    }
    .events-social-list {
        margin: 0;
        padding: 0;
        list-style: none;
    }
    li {
        margin-right: 0;
        margin-bottom: 10px;
        display: block;

        &:last-child {
            margin-bottom: 0;
        }

        a {
            display: block;
        }
    }
    .cta {
        a {
            font-weight: normal;
        }
        i,
        span.fa {
            float: left;
            margin: 0 5px 0 0;
        }
    }

    @include breakup-breakpoint('thinish') {
        // 480px
        li {
            display: inline-block;
            margin-right: 2.43902%;
            // ie 7 fix, shudder
            zoom: 1;
            *display: inline;

            a {
                display: inline-block;
            }
        }
    }
}

.featured-events,
.stories {
    .event,
    .story {
        //max-width: 300px;
        //margin: 0 auto 2.43902% auto;
    }
}

// link to view all upcoming events
.all-events {
    text-align: right;
    p {
        margin: 0 0 20px 0;
    }
}

@include breakup-tweakpoint('(max-width: 1200px)', 'medium', false) {
    .events-home .event-main img {
        margin-top: 10px;
    }
}

@include breakup-tweakpoint('(max-width: 980px)', 'medium', false) {
    .events-home {
        .event-main img {
            margin-top: 0px;
        }

        .event-main .detail {
            bottom: 134px;
        }

        .event-main {
            height: 345px;
        }
    }
}

@include breakup-tweakpoint('(max-width: 770px)', 'thinish', false) {
    .events-home .event-main .detail {
        bottom: 200px;
    }
}

@include breakup-tweakpoint('(max-width: 640px)', 'thinish', false) {
    .events-home .event-main .detail {
        bottom: 100px;
    }
}

@include breakup-tweakpoint('(max-width: 480px)', 'thin', false) {
    .events-home {
        .event-featured {
            height: auto;
        }

        .event-main {
            height: auto;
        }

        .event-main .detail {
            bottom: 0px;
        }
    }
}

@include breakup-breakpoint('medium') {
    // 767px
    .featured-events {
        .event {
            max-width: auto;
            margin: 0 auto 60px auto;
        }
    }

    .event-featured {
        margin-bottom: 60px;

        .detail {
            position: absolute;
            bottom: -35px;

            span {
                border: 0;
                clip: rect(0 0 0 0);
                height: 1px;
                margin: -1px;
                overflow: hidden;
                position: absolute;
                width: 1px;
                display: block;
            }
        }
        a:hover .detail span {
            clip: auto;
            height: auto;
            margin: 0;
            overflow: visible;
            position: static;
            width: auto;
        }
    }

    .events-home .event-featured {
        height: auto;
    }
}

@include breakup-breakpoint('wide') {
    // 980px

    .event-main {
        .detail {
            width: 35%;
            float: left;
        }
        .detail:after {
            left: 100%;
            border: solid transparent;
            content: ' ';
            height: 0;
            width: 0;
            position: absolute;
            pointer-events: none;
            border-color: rgba(227, 240, 253, 0);
            border-left-color: #e3f0fd;
            border-width: 20px;
            top: 20%;
            margin-top: -20px;
            margin-left: 0;
        }
        img {
            float: right;
            width: 65%;
            margin-left: -1px;
        }
    }

    .events-home .event-calendar {
        height: 366px;
    }
}
