.timeline__verical-line-start,
.timeline__verical-line-stop {
    width: 50%;
    position: absolute;
}

.timeline.grouped {
    .timeline__verical-line-stop::before {
        content: '';
        position: absolute;
        left: 12px;
        height: 4px;
        width: 16px;
        background: #000;
        z-index: -1;
        top: 20px;
        @include breakup-breakpoint('wide') {
            left: auto;
            right: -8px;
        }
    }
    .timeline__content__block {
        &:nth-child(1) {
            margin-top: 40px;
        }
        @include breakup-breakpoint('wide') {
            margin-top: -5%;

            &:nth-child(even) .timeline__content__block__item {
                position: relative;
                left: 55%;
            }
            &:nth-child(even) .timeline__content__block__item__connector {
                left: -11%;
                span {
                    left: 80px;
                }
            }
        }
    }
}

.timeline__collection {
    position: relative;

    .timeline__collection__header {
        a {
            background-color: #8d182a;
            color: #eee;
            border: 1px dotted transparent;
            &:hover {
                color: #8d182a;
                background-color: #eee;
            }
            &:active,
            &:focus {
                background-color: #eee;
                color: #8d182a;
                border: 1px dotted transparent;
            }
        }
    }

    &.open {
        .timeline__content {
            position: static;
            &.expanded {
                &::before {
                    /* this is the vertical line */
                    content: '';
                    position: absolute;
                    left: 18px;
                    height: 100%;
                    width: 4px;
                    background: #000;
                    z-index: -1;
                    top: 20px;

                    @include breakup-breakpoint('wide') {
                        left: 50%;
                        margin-left: -2px;
                    }
                }
            }
        }
    }

    &.closed {
        .collapsed {
            display: none;
        }
    }

    .timeline__collection__header {
        position: relative;
        display: inline-block;
        z-index: 2;
        margin: 0 0 10px 0;
        width: 50%;
        height: 40px;

        a {
            text-decoration: none;
            border: none;
            display: inline-block;
            width: 250px;
            height: 40px;
            @include breakup-breakpoint('wide') {
                position: absolute;
                right: -25px;
            }

            h2 {
                display: inline-block;
                margin: 8px;
                font-size: 1.4em;
                font-weight: 400;
                @include breakup-breakpoint('wide') {
                    margin: 8px 8px 8px 24px;
                }
            }
        }
    }
}
.timeline__collection__header__icon {
    position: relative;
    width: 40px;
    padding: 0px 13px;
    display: inline-block;
    font-size: 1.4em;

    @include breakup-breakpoint('wide') {
        float: right;
        padding: 10px 9px;
    }
}

.timeline.not-grouped {
    margin-top: 100px;
    .timeline__verical-line-start::before {
        content: '';
        position: absolute;
        left: 12px;
        height: 4px;
        width: 16px;
        background: #000;
        z-index: -1;
        @include breakup-breakpoint('wide') {
            left: auto;
            right: -8px;
        }
    }
    .timeline__verical-line-stop::before {
        content: '';
        position: absolute;
        left: 12px;
        height: 4px;
        width: 16px;
        background: #000;
        z-index: -1;
        @include breakup-breakpoint('wide') {
            left: auto;
            right: -8px;
        }
    }
    .timeline__content__block {
        @include breakup-breakpoint('wide') {
            margin-top: -5%;

            &:nth-child(odd) .timeline__content__block__item {
                position: relative;
                left: 55%;
            }
            &:nth-child(odd) .timeline__content__block__item__connector {
                left: -11%;
                span {
                    left: 80px;
                }
            }
        }
    }
}

.timeline__content {
    position: relative;
    &.expanded {
        &::before {
            /* this is the vertical line */
            content: '';
            position: absolute;
            left: 18px;
            height: 100%;
            width: 4px;
            background: #000;
            z-index: -1;

            @include breakup-breakpoint('wide') {
                left: 50%;
                margin-left: -2px;
            }
        }
    }
}

.timeline__content__block {
    width: 100%;
    padding-bottom: 20px;
    position: relative;
}

.timeline__content__block__item {
    border: 1px solid #ccc;
    padding: 20px 20px;
    margin: 0 20px 0 45px;

    @include breakup-breakpoint('medium') {
        padding: 20px 60px;
    }

    @include breakup-breakpoint('wide') {
        margin: 0 45px 0 0;
        width: 45%;
    }

    h2 {
        font-weight: bold;
        font-size: 1.49em;
    }
}

.timeline__content__block__item__connector {
    border-top: solid 2px black;
    position: relative;
    left: -45px;
    top: 30px;
    width: 30px;
    height: 1px;

    @include breakup-breakpoint('medium') {
        border-top: solid 2px black;
        width: 50px;
        left: -85px;
    }
    @include breakup-breakpoint('wide') {
        position: absolute;
        width: 80px;
        left: auto;
        right: 50%;
    }

    span {
        width: 7px;
        height: 7px;
        border: solid 1px black;
        border-radius: 50%;
        display: block;
        color: transparent;
        position: relative;
        top: -4px;
        left: 30px;

        @include breakup-breakpoint('medium') {
            width: 10px;
            height: 10px;
            border: solid 2px black;
            top: -6px;
            left: 50px;
        }
        @include breakup-breakpoint('wide') {
            left: auto;
            right: 10px;
        }
    }
}

.timeline__content__block__item__year {
    color: #8d182a;
    font-weight: bold;
    font-size: 1.2em;
}
.timeline {
    @include breakup-breakpoint('wide') {
        .timeline__content__block:nth-child(even) {
            animation: timeline-bounce-2 0.6s;
        }
        .timeline__content__block:nth-child(odd) {
            animation: timeline-bounce-2-inverse 0.6s;
        }
    }
}
@keyframes timeline-bounce-2 {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }

    60% {
        opacity: 1;
        transform: translateX(20px);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes timeline-bounce-2-inverse {
    0% {
        opacity: 0;
        transform: translateX(100px);
    }

    60% {
        opacity: 1;
        transform: translateX(-20px);
    }

    100% {
        transform: translateX(0);
    }
}

.no-js .timeline {
    .collapsed {
        display: block;
    }
}
