#promo-wrapper .banner.banner-start-here {
    padding: 0;

    .banner-media {
        display: block;

        float: none;
        margin: 0;

        img {
            width: 100%;
            margin: 0;
        }
    }
}

.start-here {
    .tile-filters {
        padding: 20px;
        background: #dedad6;

        .filter-heading,
        .filter-reset {
            line-height: 33px;
            font-weight: bold;
        }

        .filter-heading {
            padding-right: 0;
        }

        .filter-reset {
            a {
                background: none;
                color: black;
                border-bottom: 0;

                &.disabled {
                    color: grey;
                }
            }

            @include breakup-breakpoint('medium') {
                text-align: right;
                a {
                    white-space: nowrap;
                    padding-left: 20px;
                    border-left: 1px solid #c3c3c3;
                }
            }
        }

        .filter-select {
            .city-dropdown {
                float: left;
                height: 35px;
                margin: 0;
                position: relative;
                width: 100%;

                a.display {
                    border: 1px solid #e3e3e3;
                    font-weight: bold;
                    color: $red1;
                    display: block;
                    height: 33px;
                    line-height: 33px;
                    padding: 0 15px;
                    background: white;

                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;

                    i {
                        position: absolute;
                        top: 0;
                        right: 10px;
                        vertical-align: baseline;
                        line-height: inherit;
                    }
                }

                .options {
                    display: none;
                    background: white;
                    border: 1px solid #e3e3e3;
                    border-bottom: none;
                    position: absolute;
                    z-index: 1000;
                    min-width: 100%;
                    margin: 0;

                    a {
                        display: block;
                        color: $red1;
                        border-bottom: 1px solid #e3e3e3;
                        background: #efefef;
                        padding: 5px 15px;

                        &:hover {
                            background: white;
                        }

                        &.selected {
                            color: gray;
                        }
                    }
                }

                &.active {
                    .options {
                        display: block;
                    }
                }

                &.disabled {
                    a.display {
                        color: grey;
                    }
                    .options {
                        display: none;
                    }
                }
            }
        }
    }
}

.no-js .start-here {
    .tile-filters .filter-select .city-dropdown {
        &:hover .options {
            display: block;
        }

        &.disabled .options {
            display: none;
        }
    }
}

//I'm starting

.imstarting {
    .ready-banner {
        padding: 20px 20px;
        p {
            text-align: center;
            font-weight: bold;
        }
    }

    .start-text {
        width: 50%;
        margin: 30px auto;
        text-align: center;
        font-weight: bold;
        button {
            background: #fff;
            border: 1px solid #390334;
            padding: 10px;

            a {
                border-bottom: none;
                color: #390334;
                font-weight: 600;
            }
        }
    }

    .image-subtitle-container {
        display: table;
        height: 100%;
        left: 0;
        top: 0;
        position: relative;
        margin-bottom: 0;
        width: 100%;
    }

    .image-subtitle-block {
        padding-bottom: 0;
        border-bottom: 0;
        vertical-align: bottom;
        display: table-cell;
        text-align: center;
    }

    .image-subtitle {
        background: #333;
        background: rgba(0, 0, 0, 0.7);
        border-radius: 5px;
        bottom: 24px;
        color: white;
        font-weight: bold;
        margin: 0 6%;
        padding: 5px 10px;
        position: absolute;
        width: 88%;
    }

    .selfie-action-image {
        background-color: #eae9e7;
        margin-left: -10px;
        margin-right: -10px;
        margin-bottom: 15px;
    }

    .explore-footer {
        background: #390334;
        padding: 50px;
        height: auto;
        margin: 0 -10px;
        .explore-text {
            color: #fff;
            a {
                color: #fff;
                border-bottom: none;
                display: block;
            }
            p.explore-heading {
                font-size: 16px;
            }
        }
    }

    .red {
        color: #b02a1a;
    }

    .column {
        text-align: center;
    }

    #container-grid-images {
        margin-top: 50px;
    }

    @include breakup-breakpoint('medium') {
        .selfie-action-text {
            height: 90px;
        }
    }

    @include breakup-breakpoint('wide') {
        .selfie-action-text {
            height: 70px;
        }
    }

    @include breakup-breakpoint('wider') {
        .selfie-action-text {
            height: 55px;
        }
    }
}
