/// Courses V2 2018

.course__entries {
    position: relative;
}

.course__entries__text {
    display: inline-block;
    margin-right: 5px;
}

select[title='Select entry year'] {
    border: none;
    border-radius: 0px;
    outline: 2px solid #979797;
    font-size: 1.2em;
    line-height: 1.5;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -o-appearance: none;
    padding: 0 5px;
    cursor: pointer;
    width: 120px;

    &::-ms-expand {
        display: none;
    }
}

.select__arrow__entry-year {
    position: absolute;
    top: 11px;
    right: 10px;
    width: 0;
    height: 0;
    pointer-events: none;
    border-style: solid;
    border-width: 8px 6px 0 6px;
    border-color: #2c2c2c transparent transparent transparent;
}

/// CTAs

.course__red-links {
    padding: 0 0 $measure-larger;

    .row > :not(:first-child) .course__red-links__link:before {
        content: ' ';
        position: absolute;
        width: 1px;
        height: 60%;
        top: 20%;
        left: ($measure-small * -1);
        margin-top: 0;
    }
}

a.course__red-links__link {
    padding: $measure-large $measure-small;
    display: block;
    border: none;
    position: relative;
    text-align: center;

    &,
    &:link,
    &:visited,
    &:hover,
    &:focus,
    &:active {
        color: $white;
        border: none;
    }

    &:hover {
        .course__red-links__link__title {
            border-bottom: 1px solid $white;
        }
    }
}

a.course__red-links__link--cta {
    font-size: 1.2em;
    margin-bottom: 1.2em;
    padding: 1.2em 3em;

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        padding: 1.2em 1.4em;
    }

    @include breakup-breakpoint('wide') {
        // 768px
        padding: 1.2em 1.2em;
    }
}

.course__red-links__link__icon {
    font-size: 3em;
    margin-bottom: $measure-small;
}

a.course__red-links__link.course__red-links__link--apply {
    text-align: centre;
    .course__red-links__link__icon {
        position: relative;
        bottom: 10px;
    }
}

.course__red-links__link--cta__icon {
    float: left;
    font-size: 1.4em;
    position: relative;
    bottom: 4px;
}

.course__red-links__link__icon--cta--left-align {
    float: right;
}

.course__red-links__link__title {
    font-weight: 600;
    font-size: 12px;

    @include breakup-breakpoint('thinish') {
        // 480px
        font-size: 14px;
    }
}

.course__red-links__link__title--cta {
    font-weight: 600;
    display: inline-block;
}

.course__red-links__link__description {
    display: none;

    @include breakup-breakpoint('medium') {
        // 768px
        display: block;
    }
}

/// course menu
.course__menu__wrapper--v2 {
    .course__menu {
        background: #fff;
        padding: $measure-smaller $measure-small;
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
        position: relative;
        margin-left: 26px;
    }

    .course__menu__title {
        h4,
        a {
            padding-top: 1px;
        }

        h4 {
            font-weight: 700;
            position: absolute;
            display: inline;
        }

        a {
            float: right;
            text-decoration: underline;
            border-bottom-style: none;
            margin-top: 6px;
            &:hover {
                border-bottom: 0;
                text-decoration: underline;
            }
            &:focus,
            &:active {
                color: #670003;
                background: #fff;
                border-bottom-color: #999;
            }
        }

        svg {
            left: -54px;
            top: -29px;
            position: absolute;
        }

        span.course__menu__title__icon {
            color: white;
            font-size: 23px;
            left: -26px;
            position: relative;
            z-index: 2;
            top: 5px;
        }
    }
}

.js {
    .course__menu__wrapper--v2 {
        .course__menu__content {
            display: block;
            ul {
                margin-top: 10px;
                li {
                    margin-bottom: $measure-smaller;
                    list-style-type: none;

                    a:link,
                    a:visited {
                        color: #555;
                        border-bottom: 1px dotted #666;
                    }

                    a:hover {
                        color: #000;
                        border-bottom: 1px solid #000;
                    }

                    a:active,
                    a:focus {
                        background-color: #888;
                        color: #fff;
                        border-bottom: 2px solid #666;
                    }
                }
            }

            li:before {
                content: '\2022';
                color: #df0031;
                padding-right: 0.5em;
            }
        }
    }
}

.course__subheader.course__subheader--v2:after {
    width: 100px;
}
