/*image galleries*/

#content .gallery img {
    border: none;
}

.gallery {
    margin-bottom: 20px;
    background: black;
    overflow: hidden;

    .cg-caption {
        padding: 0.5em 10px 0;
        color: #f0f0f0;
    }
}

.js .gallery-nav {
    display: none;
}

.js .gallery {
    position: relative;

    .loading {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: rgba(255, 255, 255, 0.3);

        .loading__icon {
            $size: 50px;

            position: absolute;
            top: 50%;
            left: 50%;
            font-size: $size;
            width: $size;
            height: $size;
            margin-top: (-$size / 2);
            margin-left: (-$size / 2);
            color: #b02a1a;
        }
    }
}
