.hallway-new {
    margin-top: 30px;

    $tx-time: 0.2s ease;
    .hallway-item {
        height: auto;

        margin-bottom: 30px;
        @include breakup-breakpoint('wide') {
            margin-bottom: 45px;
        }

        a {
            display: block;
            border: none;
            text-decoration: none;

            img,
            h2,
            h3 {
                margin: 0;
            }

            h2,
            h3 {
                font-family: 'ff-meta-web', Arial, Helvetica, sans-serif;
                font-weight: 500;

                color: black;
                @include transition(color 200ms);
                font-size: 20px;
                margin-right: 15px;
                position: relative;
                padding: 0;
                background: none;

                span {
                    border-bottom: dotted 2px transparent;
                }

                .title-icon {
                    height: 100%;
                    right: -15px;
                    top: 0;
                    position: absolute;
                    vertical-align: middle;
                    width: auto;

                    &:before {
                        height: 100%;
                        content: '';
                        display: inline-block;
                        vertical-align: middle;
                        width: 0;
                    }
                    span.fa {
                        display: inline-block;
                        margin: 0 auto;
                        vertical-align: middle;
                        border-bottom-color: transparent !important;
                    }
                }
            }
            p {
                font-size: 15px;
                color: #444;
                display: none;

                @include breakup-breakpoint('wide') {
                    // 980px
                    display: block;
                }
            }

            .content {
                position: relative;
                z-index: 2;
                margin: -30px 14px 0;
                padding: 10px 14px 0;
                background: white;

                p {
                    margin-right: 15px;
                    margin-bottom: 0;
                }
            }

            .image {
                position: relative;
                img {
                    width: 100%;
                    opacity: 1;
                    @include transition(opacity $tx-time);
                    -webkit-backface-visibility: hidden;
                }

                &:after {
                    content: '';
                    background: $white;
                    opacity: 0;
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 2; // MIGHT NEED TO FIDDLE WITH THIS A BIT
                    @include transition(opacity 0.2s ease);
                }
            }

            &:hover {
                h2,
                h3 {
                    color: $anchor-color;
                    span {
                        border-bottom-color: $anchor-color;
                        border-bottom-style: solid;
                    }
                }
                .image img {
                    opacity: 0.75;
                }
            }

            &:active,
            &:focus {
                background: none;
                h2,
                h3 {
                    color: $anchor-color;
                    span {
                        border-bottom-color: $anchor-color-alt--light;
                        border-bottom-style: dotted;
                    }
                }
            }
        }
    }
}

.text-hallway-nav {
    margin-top: 20px;
    margin-bottom: 0;

    div {
        margin-bottom: 20px;
    }

    .item {
        a {
            font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                sans-serif;
            font-weight: 500;
            color: #b91b1b;
            font-size: 20px;

            border-bottom: 1px dotted #999;

            &:visited {
                color: #670003;
                border-bottom: 2px dotted #999;
            }

            &:hover {
                border-bottom: 1px solid #670003;
            }

            &:active,
            &:focus {
                color: white;
                background: #b91b1b;
            }
        }

        p {
            margin-top: 0.25em;
            font-size: 15px;
            display: none;
        }

        padding: 0 10px;

        @include breakup-breakpoint('medium') {
            // 767px
            padding: 0;

            p {
                display: block;
            }
        }
    }
}

.fw-section-wrapper.section-hallway-nav-wrapper {
    padding: 0;
    background: #eee;
    margin-bottom: 60px;
}

.text-hallway-nav-content {
    margin-bottom: 60px;
}

.hallway-section-nav {
    margin: 20px 0;
    padding: 7.5px 0;
    border-width: 3px 0;
    border-style: solid;
    border-color: #ddd;

    > div {
        margin-top: 5px;
        margin-bottom: 5px;
    }

    span {
        color: #aaa;
    }

    a {
        color: $red1;
        border-bottom: 1px dotted #999;

        &:hover {
            border-bottom: 1px solid $red1;
        }

        &:active,
        &:focus {
            color: white;
        }
    }

    .selected a {
        border: none;
        color: #333;
    }
}
