.section-uol-experts-wrapper {
    padding-top: 0;
}

.section-uol-experts,
.section-uol-red-border {
    position: relative;

    padding-top: 35px;

    &:before {
        position: absolute;
        content: ' ';
        display: block;
        background: #a7281d;
        width: 100px;
        max-width: 25%;
        height: 3px;
        top: 0;
    }
}

.section-uol-red-border {
    padding-bottom: 35px;
}

.uol-academic-listing {
    .uol-academic {
        .info-content,
        .quote {
            background: #333;
            color: white;
            cursor: pointer;
        }

        .info-content {
            min-height: 380px;
        }

        .quote {
            display: none;
            position: absolute;
            padding: 10px 10px 0;
            z-index: 1;
            margin-top: -15px;
        }

        .info {
            position: relative;

            h3 {
                padding: 0 10px;
            }

            p {
                padding: 0 10px 10px;
            }
        }

        img {
            width: 100%;
            margin: 0;
        }
    }
}

.uol-ribbon-wrapper {
    background: #333;
}

.image-banner#promo-wrapper .banner.banner-uol .banner-text {
    background: rgba(51, 51, 51, 0.7);
}
