$tile-gray: #dedad6;

.tile-wrapper {
    margin: 30px 0;

    > h2 {
        font-size: 1.25em;
        margin-bottom: 20px;
    }

    .tile-listing {
        a {
            border: none;
        }

        img {
            margin: 0;
        }

        .tile {
            margin-bottom: 30px;
            overflow: hidden;
            position: relative;

            .tile-content {
                position: relative;
            }

            .tile-subject {
                background-color: #b81b1a;
                padding: 0.4em 0.9em;
                position: relative;
                color: $white;
                display: inline;
            }

            .tile-subject-icon {
                background: #b5975a;
                bottom: -1px;
                color: #333;
                display: none;
                font-size: 14px;
                font-weight: bold;
                left: 0;
                padding: 4px 8px;
                pointer-events: none;
                position: absolute;
                z-index: 1;

                @include breakup-breakpoint('thinish') {
                    // 480px
                    padding: 6px 10px;
                }
                @include breakup-breakpoint('medium') {
                    // 767px
                    padding: 4px 8px;
                }
                @include breakup-breakpoint('wide') {
                    // 980px
                    padding: 6px 10px;
                }
            }

            &.tile--featured {
                .tile-subject-icon {
                    display: block;
                }

                .tile-canvas {
                    border: 3px solid #b5975a;
                }
            }

            .tile-text {
                background: white;
                padding: 0 10px 10px;
                font-size: 15px;
                color: #444;
            }

            .tile-time {
                background: white;
                padding: 5px 10px 10px;
                font-size: 14px;
                color: #666;
                display: block;
            }

            .tile-media {
                position: relative;
            }

            .tile-heading {
                background: white;
                padding: 10px 10px 5px;
            }

            .tile-heading span {
                color: #000;
                font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                    sans-serif;
                font-weight: 500;
                font-size: 16px;
            }

            a .tile-heading span {
                border-bottom: 1px dotted #000;
                &:hover {
                    border-bottom: 1px solid #000;
                }
            }

            a.tile-img-heading {
                text-decoration: none;

                span {
                    border-bottom: dotted 1px #666;
                }

                &:visited .tile-heading span {
                    color: #000;
                    border-bottom: dotted 1px #000;
                }
                &:hover .tile-heading {
                    span {
                        color: #000;
                        border-bottom: solid 1px #000;
                    }
                    img {
                        opacity: 0.75;
                    }
                }
                &:focus .tile-heading span {
                    color: #fff;
                    background: #888;
                }

                .tile-image {
                    position: relative;
                }
            }

            &.tile-preview .tile-heading {
                &:after,
                &:before {
                    content: '*';
                }
            }

            .tile-centered-container {
                position: absolute;
                left: 0;
                top: 0;

                display: table;
                height: 100%;
                width: 100%;

                .title-text-block {
                    display: table-cell;
                    vertical-align: middle;

                    > div {
                        padding: 0 6%;

                        @include breakup-breakpoint('wide') {
                            padding: 0 12%;
                        }
                    }

                    text-align: center;

                    .tile-title {
                        font-size: 1.5em;
                        margin-bottom: 10px;
                    }

                    .tile-text,
                    .tile-link {
                        line-height: 1.4em;
                    }

                    @include breakup-breakpoint('thinish') {
                        // 480px
                        .tile-title {
                            font-size: 2em;
                        }

                        .tile-text,
                        .tile-link {
                            font-size: 1.2em;
                        }
                    }
                    @include breakup-breakpoint('medium') {
                        // 767px
                        .tile-title {
                            font-size: 1.5em;
                        }

                        .tile-text,
                        .tile-link {
                            font-size: 1em;
                        }
                    }
                    @include breakup-breakpoint('wide') {
                        // 980px
                        .tile-title {
                            font-size: 2em;
                        }

                        .tile-text,
                        .tile-link {
                            font-size: 1.2em;
                        }
                    }

                    .tile-link {
                        margin-bottom: 10px;
                    }

                    .tile-content-icon {
                        $icon-size: 40px;
                        display: inline-block;
                        .tile-icon-inner {
                            display: block;
                            width: $icon-size;
                            img {
                                width: 100%;
                            }
                        }
                    }
                }
            }

            &.tile-type-youtube {
                .tile-media {
                    padding-top: 55%; /* aspect ratio */
                    overflow: hidden;
                }

                .tile-youtube-preview {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    left: 0;
                    cursor: pointer;

                    img {
                        position: absolute;

                        &.preview {
                            width: 100%;
                            min-height: 100%;
                            top: 0;
                            left: 0;
                        }

                        $play-button-width: 60px;
                        $play-button-height: $play-button-width * 0.7;
                        &.play {
                            width: $play-button-width;
                            position: absolute;
                            left: 50%;
                            top: 50%;
                            margin-left: -$play-button-width / 2;
                            margin-top: -$play-button-height / 2;
                        }
                    }

                    a:focus img.preview {
                        outline-offset: -2px;
                        outline: #b02a1a solid 2px;
                    }

                    .play-hover {
                        display: none;
                    }

                    &:hover {
                        .play {
                            display: none;
                        }
                        .play-hover {
                            display: block;
                        }
                    }
                }

                .embed-container {
                    position: relative;
                    height: 100%;
                    width: 100%;
                    margin: 0;
                    padding: 0;
                    overflow: visible;

                    iframe {
                        z-index: 2;
                    }
                }

                iframe {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
            }

            &.tile-type-tweet {
                .tile-media {
                    padding-top: 55%; /* aspect ratio */
                }

                .tile-centered-container {
                    .title-text-block {
                        color: black;

                        .tile-title {
                            font-size: 1.25em;

                            .tile-tweet-userpic,
                            .tile-tweet-userinfo {
                                display: inline-block;
                                vertical-align: top;
                            }

                            $userpic_size: 40px;

                            .tile-tweet-userpic {
                                width: $userpic_size;
                                height: $userpic_size;

                                background-repeat: no-repeat;
                                background-size: contain;
                                position: relative;
                                overflow: hidden;
                                margin-right: 5px;

                                img {
                                    position: absolute;
                                    width: 100%;
                                    height: 100%;
                                    left: 0;
                                    right: 0;
                                }
                                div {
                                    $bw: $userpic_size / 2;
                                    $p: -$bw;
                                    $s: $userpic_size + 2 * $bw;
                                    position: absolute;
                                    left: $p;
                                    top: $p;
                                    width: $s;
                                    height: $s;
                                    border: solid $bw $tile-gray;
                                    border-radius: 2 * $bw;
                                }
                            }

                            .tile-tweet-userinfo {
                                text-align: left;
                                line-height: 1.1em;

                                .tile-tweet-username {
                                    font-weight: bold;
                                }
                            }
                        }
                        .tile-text {
                            font-size: 1.4em;
                        }
                    }
                }
            }
            &.tile-type-cityconnect {
                .tile-media {
                    padding-top: 55%; /* aspect ratio */
                }

                .tile-cityconnect-background {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                }
                .tile-centered-container {
                    position: absolute;
                }

                .title-text-block {
                    color: white;

                    .tile-link a {
                        color: white;
                        text-decoration: underline;
                    }
                }
            }

            &.tile-type-image,
            &.tile-type-campaign,
            &.tile-type-selfie {
                img {
                    width: 100%;
                }
            }
        }

        &.tile-grid .tile .tile-subject-icon {
            position: absolute;
            bottom: -1px;
            left: 10px;
        }
    }

    .read-more {
        text-align: center;

        a {
            display: inline-block;
            text-align: center;
            background: $purple;

            transition: color 0.1s, background 0.1s;

            color: white;
            padding: 10px 50px;
            border: 1px solid $purple;
            font-weight: bold;
            text-decoration: none;

            span {
                margin-right: 20px;

                transition: border-bottom-color 0.1s;
            }

            &:hover {
                span {
                    border-bottom: 1px solid white;
                }

                background: black;
            }

            &:active,
            &:focus {
                color: $purple;
                background: white;

                span {
                    border-bottom: 1px solid $purple;
                }
            }
        }
    }
}

.tile-listing--intranet {
    margin-top: 3em;
    margin-bottom: -3em;

    li {
        margin: 0;
        list-style-type: none;
    }

    & > li:not(.clearfix) {
        margin-bottom: 40px;
    }
}

.tile-wrapper {
    $tile-themes: ((undergraduate #3e1031), (postgraduate #00284c));

    @each $theme in $tile-themes {
        &.tile-wrapper--#{nth($theme, 1)} {
            .filter-box__inner {
                background: nth($theme, 2);
            }

            // .white-box--cards-intro {
            //     border-top-color: nth($theme, 2);
            // }

            .read-more a {
                &:not(:hover):not(:active):not(:focus) {
                    background: nth($theme, 2);
                    border-color: nth($theme, 2);
                }

                &:active,
                &:focus {
                    color: nth($theme, 2);
                }
            }
        }
    }
}
