.load-more {
    text-align: center;
    display: block;

    a {
        display: inline-block;
        text-align: center;
        background: #f2f2f2;

        transition: color 0.1s, background 0.1s;

        color: #000;
        padding: 10px 50px;
        border: 1px solid #aaa;
        font-weight: bold;
        text-decoration: none;

        span {
            margin-right: 20px;

            transition: border-bottom-color 0.1s;
        }

        &:hover {
            color: white;
            border: 1px solid #000;
            span {
                border-bottom: 1px solid white;
            }

            background: black;
        }

        &:active,
        &:focus {
            color: #000;
            background: white;
            border-bottom: 1px solid #000;

            span {
                border-bottom: 1px solid #000;
            }
        }
    }

    &.load-more--academics {
        a {
            background-color: #f9f9f9;
            color: #444;
            border: 1px solid #d8d8d8;

            &:hover {
                color: white;
                border: 1px solid #000;
                span {
                    border-bottom: 1px solid white;
                }

                background: black;
            }

            &:active,
            &:focus {
                color: #000;
                background: white;
                border-bottom: 1px solid #000;

                span {
                    border-bottom: 1px solid #000;
                }
            }
        }
    }
}
