////
/// Academic Expert (2017)
/// @group experts
////
.academic-experts-profile {
    .personal-details {
        border: 1px solid #dfdfdf;
        margin-bottom: 28px;
        padding: 16px;

        .expert-name,
        .photo,
        .contact-lists {
            padding-left: 0;
        }

        .expert-name {
            .department-name {
                .fa {
                    padding-left: 4px;
                    padding-right: 8px;
                    width: 20px;
                }
            }
        }

        .contact-lists {
            .contact-list {
                h2 {
                    font-size: $font-size-h4;
                }

                ul {
                    margin-left: 4px;

                    li {
                        list-style-type: none;

                        .fa {
                            padding-right: 8px;
                            width: 20px;
                            text-align: center;
                        }
                    }
                }
            }
        }
    }

    .academic-details {
        padding: 0;

        .academic-details__content {
            ol,
            ul {
                margin-left: 0;

                li {
                    list-style-type: none;
                    margin-bottom: 14px;
                    margin-left: 0;
                }
            }

            dl {
                background-color: #f8f8f8;
                padding: 8px;

                dt {
                    font-family: $font-family-meta-sans;
                    font-size: $font-size-h4;
                    font-weight: 500;
                    margin-bottom: 0;
                    padding: 0;
                }

                dd {
                    margin-left: 0;
                }
            }

            .expertise-list {
                margin-left: 0;

                li {
                    border: 1px solid #333;
                    display: block;
                    float: left;
                    list-style-type: none;
                    margin-bottom: 8px;
                    margin-right: 8px;
                    padding: 4px;
                }

                & + h4 {
                    clear: left;
                }
            }

            .dark-background a:link,
            .widget a:link,
            [class*='widget-'] a:link {
                border-bottom-width: 1px;
            }

            #favourites > li {
                border: 1px solid #e0dbd7;
                padding: 8px 8px 28px;
                background: #f9f9f9;
                position: relative;

                &:after {
                    content: 'Featured publication';
                    position: absolute;
                    bottom: 8px;
                    right: 8px;
                    font-size: 12px;
                    line-height: 12px;
                    color: #999;
                }
            }
        }
    }
}
