@import 'accordion';

#contact-widget {
    @include font-family-meta-sans;
    background-color: $sandyGrey;
    padding: 1em 0;
    margin-bottom: 1.429em; // 20px
    font-size: 1.072em; // 15px
    .widget-title,
    h2 {
        padding: 0 1.25em; // = ((15 * 1.5) / 18)
    }
    dl {
        width: 100%;
        margin: 1em 0 0;
        overflow: hidden;
        dt,
        dd {
            margin: 0;
        }
        dd {
            padding-bottom: 0.5em;
            vertical-align: top;
            break-word: break-all;
        }
        .widget-content-label {
            background-color: transparent;
            width: auto;
            float: left;
            clear: left;
            padding: 0 0.5em 0.5em 0;
            font-family: inherit;
            font-size: inherit;
            line-height: inherit;
            &:after {
                content: '\003A';
            }
            &--location {
                width: 100%;
                padding-right: 0;
                &,
                & + dd {
                    padding-bottom: 0;
                }
            }
        }
    }
    .widget-title,
    .widget-content-label {
        font-weight: 700;
    }
    .widget-content-title,
    .widget-content-subtitle {
        font-weight: 600;
    }
    a {
        padding: 2px 2px 0;
        text-decoration: none;
        @include transition(all 0.35s ease);
        &:link,
        &:visited {
            border-bottom: 2px dotted $grey4;
        }
        &:link {
            color: #b91b1b;
        }
        &:visited {
            color: $red4;
        }
        &:hover {
            color: #b91b1b;
            border-bottom: 2px solid #b91b1b;
        }
        &:focus,
        &:active {
            background-color: #b91b1b;
            color: $white;
            border-bottom: none;
        }
    }
    .widget-content {
        margin: 1em 1.5em 0;
        font-size: 1em;
        border-bottom: 1px solid #ccc;
        padding-bottom: 1em;

        &:last-child {
            border-bottom: 0;
        }
        p:last-child {
            margin-bottom: 0;
        }
    }
    .widget-content-list {
        padding: 0;
        &,
        li {
            list-style: none;
            margin: 0;
        }
    }
}

//default widget
.widget {
    @include font-family-meta-sans;
    margin: 0 0 20px 0;
    padding: 0;
    position: relative;
    @include clearfix;

    &.scrollable {
        box-sizing: content-box;
    }
    @include breakup-breakpoint('medium') {
        &.scrollable {
            box-sizing: border-box;
        }
    }

    .widget-title,
    h2 {
        @include font-family-meta-sans;
        background-color: $sandyGrey;
        display: block;
        margin: 0;
        padding: 10px 10px;
        font-size: 1.2em; // 18px
        font-weight: 600;
        // deprecated July 2015
        // color: $grey6;
        // @include font-sans-serif(1em, bold, 1.7em);
        &.controls-direction {
            padding-right: 70px;
        }
    }
    .bx-viewport {
        @include transition(height 200ms);
        padding: 0;
    }
    .bx-controls-direction {
        position: absolute;
        top: 5px;
        right: 5px;

        a {
            position: relative;
            background: $red;
            display: block;
            padding: 7px 12px;
            color: $white;
            float: left;
            cursor: pointer;
            border-bottom: none;
            text-align: center;
            margin-left: 1px;

            &:visited {
                color: $white;
            }
            &:hover {
                background-color: $logo-red;
            }
            &:focus {
                background-color: $red2;
            }
            &:active {
                background-color: $red3;
            }
            i {
                font-size: 1em;
                vertical-align: middle;
                font-weight: normal;
            }

            &:first-child {
                margin-left: 0;
            }
        }

        a.disabled {
            background-color: #cc887e;
            cursor: default;
        }
        a.prev,
        a.bx-prev {
            display: block;
            border-radius: 3px 0 0 3px;
        }
        a.all,
        a.bx-all,
        a.refresh {
            font-size: 1em;
            text-align: center;
            background-image: none;
        }
        a.next,
        a.bx-next {
            display: block;
            border-radius: 0 3px 3px 0;
        }
    }
    .bxslider {
        display: block;
    }

    .widget-content {
        @include clearfix;
        margin: 0;
        padding: 0;
        background: $sandyGrey;
        a {
            &:hover {
                border-color: $sandyGrey;
            }
        }
        ul {
            margin-right: 5px;
        }
        ol li {
            list-style-type: none;
            margin: 0;
        }
        h2 {
            font-size: 0.9em;
        }
        h3,
        h4 {
            @include font-sans-serif(1.1em, bold, inherit);
        }
        h2,
        h3 {
            margin: 0 0 5px 0;
            padding: 0;
            border: none;
            color: $grey6;
            text-transform: none;
        }
        blockquote {
            clear: both;
            @include font-family-serif;
            font-style: italic;
            background: transparent;
            padding: 0;
            margin: -19px 0 10px 0;
            text-indent: 35px;
        }
        img.right {
            margin: 0 0 5px 5px;
        }
        img.left {
            margin: 0 5px 5px 0;
        }
        .vcard {
            border-bottom: 1px solid $grey3;
            padding: 10px 5px;

            .fn {
                font-weight: bold;
            }

            .tel span,
            .fax span {
                font-weight: bold;
            }
            &:last-child {
                border-bottom: none;
            }
            .email-wrapper {
                word-break: break-all;
            }
        }
    }
    // .widget-content
}

@include breakup-breakpoint('medium') {
    // 767px
    .widget {
        &.widget-invisible {
            display: block;
        }
        h2 {
            padding: 9px 10px;

            &.controls-direction {
                padding-right: 60px;
            }
        }
        .widget-controls a,
        .bx-controls-direction a {
            padding: 5px 10px;
        }
    }
}

// .widget

//show/hide widgets based on screen size
.widget-invisible {
    display: none;
}

.free-text {
    p {
        margin: 0 0 10px 0;
    }
    .widget-content {
        padding: 0.5em 1em;

        blockquote {
            font-family: $font-family-serif;
            font-style: italic;
            padding: 0 0 0 40px;
            margin: 10px 0 20px 0;
            background: transparent url('i/quote.png') no-repeat left top;
            clear: both;
            border: none;
            text-indent: 0;
        }
    }
}

// courses widget
#courses-widget {
    .widget-content {
        margin: 0;
        padding: 5px;
        border-bottom: none;
        background: #f8f8f8;

        h3 a {
            font-size: 1.2em;
            background-color: $grey2;
        }
        h4 a {
            font-weight: bold;
            border-bottom: none;
        }

        @include accordion(12px, 12px, 10px, 1px);

        .ui-accordion-header {
            .ui-icon {
                width: 12px;
                height: 12px;
            }
        }

        .ui-accordion-content {
            //padding: 0px 1px;

            padding: 0px 5px;

            li.course {
                background: $white;
                //border-bottom: 1px solid $grey2;
                padding: 5px;

                h4 {
                    margin: 0;
                    font-size: 1em;
                }
            }
        }
    }

    .ui-state-active {
        background-color: $sandyGrey;
        a {
            border-width: 0 0 1px 0;
        }
    }
    .ui-state-default {
        background-color: $sandyGrey;
    }
}

// gallery widget
#gallery-widget {
    .widget-content {
        padding: 5px;
        overflow: hidden;
    }

    .scrollable {
        @include size(150px, 100px);
        min-height: 250px;
        overflow: visible !important;
        margin: 0 0 0 76px;

        .items {
            .item {
                float: left;
                width: 145px;
                margin: 0 5px;

                img {
                    border: 1px solid $white;
                }
            }
            .active {
                img {
                    border: 1px solid $black;
                }
            }
            .disabled {
                img {
                    /* for IE */
                    filter: alpha(opacity=60);
                    /* CSS3 standard */
                    opacity: 0.6;
                }
                .name,
                .department,
                .job-title {
                    color: $grey3;

                    a {
                        color: $grey3;
                    }
                }
            }
        }
    }
}

//RSS widget
#rss-widget {
    .rss-meta {
        @include clearfix;
        padding: 10px;
        float: none;

        img {
            float: left;
            margin-right: 10px;
        }
    }
}

//Twitter timeline
#sidebar .twitter-timeline {
    margin-bottom: 20px;
}

li.with-pic {
    @include background-size('48px 48px');
    p {
        padding-left: 60px;
    }
}

.tweet-time,
.tweet-retweet,
.tweet-reply {
    margin-right: 1em;
}

// maps widget
#map-widget {
    .map {
        border: none;
        position: relative;
        cursor: pointer;
        border: 1px solid $white;
        border-width: 0px 0px 1px 0px;

        .magnifier {
            position: absolute;
            bottom: 0;
            z-index: 1;
            display: block;
            @include size(20px, 20px);
            right: 0;
            background: transparent url('../i/icon-magnifier.png') no-repeat
                right bottom;
        }
        &:hover {
            .magnifier {
                background-position: 0 bottom;
            }
        }
        img {
            border: none;
            position: relative;
            margin-bottom: -3px;
        }
    }
    #mapJsData {
        display: none;
    }
    .vcard {
        margin: 0 5px;
    }
}

.map-parameters {
    display: none;
}

//flickr widget
#flickr-widget {
    .widget-content {
        padding: 0 3px 3px;

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
        }
    }

    li {
        background: $white;
        margin: 0;
        padding: 0 5px;
    }
    .imageurl {
        display: none;
    }

    li > a,
    li > a img {
        display: block;
        margin: 0 auto;
        border: 2px solid $white;
        background-color: transparent;
        float: none;
    }

    $img-meta-x-padding: 10px;
    $img-meta-y-padding: 10px;

    .image-meta {
        min-height: 50px;
        padding: $img-meta-x-padding $img-meta-y-padding $img-meta-x-padding
            $img-meta-y-padding + 60px;
        margin: 0;
        background: $white no-repeat $img-meta-y-padding $img-meta-x-padding;
        position: absolute;
        bottom: 0;
        left: 0;
        display: none;
        @include opacity(0.95);
    }

    li {
        position: relative;

        &:hover {
            z-index: 99;

            .image-meta {
                display: block;
            }
        }
    }
}

// testimonial widget
#testimonials-widget {
    &.widget-one-item .widget-content {
        padding: 10px;
    }
    .bx-wrapper {
        padding: 0;
        margin: 0 10px;
    }
    ul {
        list-style-type: none;
        padding: 0;
        margin: 0;

        li {
            padding: 0;
            margin: 0;
            @include clearfix;
        }
    }
    .fa-quote-left {
        color: #666666;
        font-size: 1.6em;
    }
    .details {
        margin: 0 0 10px 0;
        overflow: hidden;
    }
    .name {
        font-weight: bold;
        margin-bottom: 0;
    }
    .position,
    .course,
    .year {
        margin-bottom: 0;
    }
    .course {
        margin-top: 0;
    }

    img {
        float: left;
        display: inline;
        padding: 0 10px 0 0;
    }
}

#testimonial-dialog {
    .details {
        margin: 0 0 10px 0;
        overflow: hidden;
        width: auto;
        float: none;
        p {
            margin: 0 0 5px 0;
        }
        .name {
            font-weight: bold;
        }
        img {
            float: left;
            display: inline;
            margin: 0 10px 0 0;
        }
    }
}

//profiles widget
#profiles-widget {
    .widget-content {
        padding: 10px 0;
    }
    ul {
        padding: 0 10px;
        margin: 0;
        list-style-type: none;
    }

    li > a {
        display: block;
        border: none;

        span {
            border-bottom: 1px solid #cccccc;
        }

        &:hover span {
            border: none;
        }

        &:active,
        &:focus {
            background: none;

            span {
                border: none;
                background: $red1;
            }
        }
    }

    .thumbnail-wrapper {
        background-color: #f9f9f9;
        display: block;
        margin: 0 10px 10px;
        &:hover {
            border: 1px solid #cccccc;
        }

        @include breakup-breakpoint('thin') {
            // 320px
            width: 50%;
        }
        @include breakup-breakpoint('thinish') {
            // 480px
            float: left;
        }
        @include breakup-breakpoint('thinish_extended') {
            // 500px
            width: 40%;
        }
        @include breakup-breakpoint('medium') {
            // 767px
            width: auto;
            float: none;
        }
    }
    .person {
        margin-bottom: 15px;
        overflow: hidden;
    }
    .thumbnail {
        width: 100%;
        vertical-align: middle;
        margin-bottom: 0;
    }
    .name {
        font-size: 1.2em;
        margin: 0 10px;
    }
    .description,
    .job-title,
    .role-description,
    .email,
    .tel {
        font-size: 1.2em;
        margin: 10px 10px 0 10px;
    }
    .job-title {
        font-weight: 600;
    }

    .scrollable {
    }
}

