#gallery-widget .widget-content {
    padding: 5px;
}

#social-widget .widget-content {
    padding: 10px;
}

/*list of social media channels with icons*/
#social-list {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
        margin: 0 0 10px 0;
        min-height: 30px;
    }
    a {
        padding: 0;
        border-bottom: none;
        display: block;
        float: none;
        min-height: 30px;

        &.osw {
            background: transparent
                url('i/vector-social-media-icons/social-list.png') no-repeat 0 0;
            line-height: 32px;
            height: 32px;

            &.twitter {
                background-position: 0 -52px;
            }
            &.google {
                background-position: 0 -104px;
            }
            &.youtube {
                background-position: 0 -156px;
            }
            &.linkedin {
                background-position: 0 -208px;
            }
            &.pinterest {
                background-position: 0 -260px;
            }
            &.instagram {
                background-position: 0 -312px;
            }
        }

        span {
            border-bottom: 1px solid #ccc;
            line-height: 28px;
        }

        &:hover {
            text-decoration: none;
            span {
                border-bottom: none;
            }
        }

        &:focus span {
            background: #b02a1a;
        }
    }
}

.widget.navigation-widget {
    h2,
    .widget-content {
        background: none;
    }

    h2 {
        border-bottom: 2px solid #ddd;
        padding: 0;
    }

    ul {
        list-style-type: none;
        margin: 0;
    }

    li {
        border-bottom: 1px solid #ddd;
    }

    a {
        display: block;
        padding: 5px 10px;
        position: relative;

        &::before {
            position: absolute;
            top: 2px;
            left: 0;
            height: 26px;
            width: 4px;
            background: $red1;
        }

        &:hover {
            background: #eee;
            color: $red1;
        }

        border: none;

        &:visited {
            color: $red1;
        }

        &:focus {
            background: $red1;
            color: white;
        }

        &:active {
            background: #eee;
            color: #333;
            border: none;

            &::before {
                content: ' ';
            }
        }
    }

    li.selected {
        a {
            background: #eee;
            color: #333;
            border: none;

            &::before {
                content: ' ';
            }
        }
    }
}

/* widgets loading functionality */
.js .widget-loading .widget-content {
    visibility: hidden;
    max-height: 50px;
}

.js #sidebar .widget-loading,
.js .opening-times.widget-loading {
    @include loading-fa-icon-content;
    min-height: 100px;
    background: #e0dbd7;

    i.loading-fa-icon {
        top: 70px;

        $size: 30px;

        font-size: $size;
        width: $size;
        height: $size;
        text-align: center;
        line-height: $size;
        margin-top: (-$size / 2);
        margin-left: (-$size / 2);
    }
}

.widget-warning {
    background-color: #a22833;
    padding: 7px 14px 15px;
    color: $white;

    h2 {
        background-color: transparent;
        padding: 9px 0;
    }
}
