@media print {
    // set our page dimensions
    // @page {
    //     margin: 0.5cm;
    // }
    // @page:right {
    //     @bottom-right {
    //         content: counter(page);
    //     }
    // }
    // @page:left {
    //     @bottom-left {
    //         content: counter(page);
    //     }
    // }

    // reset our background and text
    * {
        background: transparent !important;
        color: #000 !important; // Black prints faster: h5bp.com/s
        box-shadow: none !important;
        text-shadow: none !important;
    }

    // set some basic typography changes
    h2,
    h3,
    h4,
    h5,
    h6 {
        clear: both;
    }
    p {
        clear: left;
    }
    h2,
    h3,
    table,
    figure,
    img {
        page-break-after: avoid;
    }
    p,
    h2,
    h3 {
        orphans: 3;
        widows: 3;
    }
    img {
        max-width: 100% !important;
        &.left {
            float: left;
            margin-right: 10px;
            margin-bottom: 10px;
        }
        &.right {
            float: right;
            margin-left: 10px;
            margin-bottom: 10px;
        }
    }
    thead {
        display: table-header-group; // h5bp.com/t
    }
    a,
    a:visited {
        text-decoration: underline;
        #header & {
            text-decoration: none;
        }
    }
    a[href]:after {
        content: ' (' attr(href) ')';
    }
    // Don't show links for images, or javascript/internal links
    .ir a:after,
    a[href^='javascript:']:after,
    a[href^='#']:after {
        content: '';
    }
    abbr[title]:after {
        content: ' (' attr(title) ')';
    }
    pre,
    blockquote {
        border: 1px solid #999;
        page-break-inside: avoid;
    }

    // hide/show specific components
    .hidden,
    #global-nav,
    #search,
    #login-opener,
    #login_form_login_prompt,
    #site-home,
    #primary-nav,
    #primary-breadcrumb,
    #breadcrumb,
    #secondary-nav,
    #sidebar,
    #footer,
    #courses-form,
    #news-form,
    #events-form,
    #filter,
    .ui-tabs-nav,
    .date,
    .sign-up,
    #promo-wrapper,
    .noprint,
    #cross-domain,
    #mast-1,
    #util-nav,
    #primary-nav-toggler,
    #slice-navigation,
    #global-nav-toggler,
    #intranet .footer {
        display: none;
    }
    #header #logo a,
    #header #logo a span,
    #header #global-nav-1 {
        display: none !important;
    }
    .ui-accordion-content {
        display: block !important;
    }

    #intranet {
        h1 {
            font-size: 2.1em;
        }

        .footer2016,
        .primary-menu-wrapper,
        .header__search__toggle,
        .global-nav__wrapper {
            display: none !important;
        }
    }
}
