.see-all-button {
    background-color: $red;
    color: $white !important;
    min-height: 2.45em;
    display: inline-block;
    padding: 0.5em;
    border: 0;
    outline: none;
    font-weight: 600;
    text-align: center;
    line-height: 1.45em;
    border-radius: 2px;
    cursor: pointer;
    @include transition(all 0.2s ease);
    &:hover,
    &:focus,
    &:active {
        span {
            text-decoration: underline;
        }
    }
    &:after {
        content: '\00a0\f105';
        padding-left: 0.2em; // .25rem
        font-family: FontAwesome;
        font-size: 1.25em;
        line-height: 0.8em;
    }
}

.button {
    display: inline-block;
    background-color: #390132;
    padding: 15px 15px;
    text-decoration: none;
    border: none;
    font-size: 1.2em;
    border-radius: 3px;

    span {
        color: $white;
    }
    i {
        color: $white;
        float: right;
        margin-right: 15px;
        font-size: 1.3em;
    }
    &:link {
        border: none;
    }
    &:hover {
        border: none;
    }
}

.button-lg {
    width: 100%;
}

@include breakup-breakpoint('thin') {
    .button-lg {
        width: 240px;
    }
}

@include breakup-breakpoint('wider') {
    .button-lg {
        width: 380px;
    }
}
