/* Code from Fabrikbrands.com ------------ */

/*
    Each widget has the class 'widget'
    Each inpage box has the class 'inpage'

    Additional classes:
        w2013   a wrapper to distinguish these widgets
                from any other campaigns sharing the
                'widget' class

    Additional responsive styles are in the meda query sections at the bottom of this page

*/

$purple: #381d29; /*4d2938;*/
$purple-dark: #24131b; /*3a1e2a;*/
$red: #cc3333;

.widget,
.inpage {
    &.w2013 {
        /* override existing style from city uni css */
        margin: 0 0 20px 0;
        border-top: 4px solid $red;
        position: relative;

        a {
            display: block;
            background-color: $purple;
        }

        a:hover {
            background: $purple-dark;
        }

        .content {
            padding: 15px;
            width: auto;
            height: 100%;
        }

        h4 {
            font-weight: normal;
            line-height: 1.2em;
            color: $red;
            margin-top: 0;

            span {
                display: block;
                color: white;
            }
        }

        p {
            margin: 20px 0;
            color: white;
        }

        img {
            position: absolute;
            bottom: 15px;
            right: 15px;
        }
    }
}

/*  Tablet Layout and above: >= 768px. */

@media only screen and (min-width: 768px) {
    /*} and (max-width: 991px) {*/

    .widget {
        &.w2013 {
            width: 100%;
            max-width: 700px;

            h4 {
                font-size: 30px;
            }
            p {
                width: 40%;
                max-width: 250px;
            }
            img {
                margin-right: 0;
            }
        }
    }
    .inpage {
        &.w2013 {
            width: 100%;
            max-width: 420px;
            height: 140px;

            .content {
                height: 140px;
            }

            h4 {
                font-size: 22px;
            }
            p {
                width: 70%;
                max-width: 300px;
                margin: 10px 0 0 0;
            }
            img {
                height: 90px;
                margin-right: 0px;
                margin-bottom: 0px;
            }
        }
    }
}

/*      Wide Mobile Layout: 480px - 767px. */

@media only screen and (min-width: 480px) and (max-width: 767px) {
    .widget {
        &.w2013 {
            width: 100%;
            max-width: 700px;

            h4 {
                font-size: 30px;
            }
            p {
                width: 40%;
                max-width: 250px;
            }
        }
    }
    .inpage {
        &.w2013 {
            width: 100%;
            max-width: 420px;
            height: 140px;

            .content {
                height: 140px;
            }

            h4 {
                font-size: 22px;
            }
            p {
                width: 70%;
                max-width: 300px;
                margin: 10px 0 0 0;
            }
            img {
                height: 90px;
                margin-right: 0px;
                margin-bottom: 0px;
            }
        }
    }
}

/*      Mobile Layout: 320px - 479px. */
@media only screen and (min-width: 320px) and (max-width: 479px) {
    .widget {
        &.w2013 {
            width: 100%;
            max-width: 329px;

            h4 {
                font-size: 30px;
            }
            p {
                float: left;
                width: 150px;
                margin: 30px 10px auto 0;
            }
            img {
                position: relative;
                float: right;
                right: 0;
                bottom: 0;
                margin-left: 20px;
            }
        }
    }

    .inpage {
        &.w2013 {
            width: 100%;
            max-width: 420px;
            height: 140px;

            .content {
                height: 140px;
            }

            h4 {
                font-size: 20px;
                line-height: 1.1em;
                margin-right: 80px;
            }
            p {
                font-size: 13px;
                line-height: 1.2em;
                margin: 10px 80px 0 0;
            }
            img {
                height: 90px;
                right: 10px;
                bottom: 10px;
            }
        }
    }
}

/*      Mobile Layout: < 320px. */

@media only screen and (max-width: 319px) {
    .widget {
        &.w2013 {
            width: 100%;
            max-width: 200px;

            h4 {
                font-size: 23px;
            }
            p {
                float: left;
                width: 80%;
                max-width: 170px;
                margin: 30px auto;
            }
            img {
                position: relative;
                float: right;
                right: 0;
                bottom: 0;
                margin-left: 20px;
            }
        }
    }

    .inpage {
        &.w2013 {
            width: 100%;
            max-width: 200px;

            h4 {
                font-size: 20px;
                line-height: 1.1em;
                margin-right: 80px;
            }
            p {
                font-size: 13px;
                line-height: 1.2em;
                margin: 10px 80px 0 0;
            }
            img {
                height: 90px;
                margin-right: 0px;
                margin-bottom: 0px;
            }
        }
    }
}
