@mixin loading-info {
    @include loading-fa-icon-content-bg;
}

.yt-module.loading {
    @include loading-info;
}

// whole block is js dependant
.js .yt-module {
    position: relative;
    display: block;
    background: #333;
    color: #fff;
    margin-bottom: 20px;
    padding-top: 20px;

    h1 {
        padding: 0 20px 0 20px;
        margin: 0 0 20px 0;
    }

    // featured video section
    .yt-featured-video {
        .yt-video-player {
            padding: 0 20px 20px 20px;
        }
        .video-meta {
            padding: 0 20px 20px 20px;

            .video-title {
                margin-top: 0;
            }
            .video-views {
                font-size: 0.9em;
                color: #ccc;
            }
        }
    }

    // videos carsouel section
    .yt-videos-list {
        list-style: none;
        overflow: hidden;
        margin: 0;
        padding: 0;

        .video {
            padding: 10px;
            background: #333;
            color: #fff;

            a {
                text-decoration: underline;
                color: #fff;

                &:hover {
                    text-decoration: none;
                }
                &:visited,
                &:active,
                &:focus {
                }
            }
            .video-title {
                margin: 0;
            }
            .video-views {
                font-size: 0.9em;
                color: #ccc;
            }
        }
        .video.active {
            background: #e3f0fd;
            color: #333;

            a {
                color: #333;
            }
            .video-views {
                color: #333;
            }
        }
        a:link {
            display: block;
            border-bottom: none;

            &:hover {
                img {
                    opacity: 0.9;
                }
            }
        }
        img {
            margin: 0;
            padding: 0;
        }
    }
    .bx-wrapper {
        margin: 0 40px;
        padding: 0 40px;
    }
    .bx-viewport {
    }
    .bx-controls {
        display: block;
    }
    .bx-pager {
        //no need for pager on carousel style
        display: none;
    }
    .bx-controls-direction {
        a {
            outline: 0;
            z-index: 1;
            padding: 0;
            background-color: #666;
            display: block;
            color: #fff;
            text-align: center;
            top: 10px;
            position: absolute;
            padding: 5px;
            display: block;
            transition-property: background-color;
            transition-duration: 0.5s;
            transition-timing-function: ease-out;
            border: none;

            &:hover {
                background-color: lighten(#666, 20%);
            }
            i {
                padding: 5px 10px;
            }
        }
        .bx-next {
            right: 0;
        }
        .bx-prev {
            left: 0;
        }
    }

    @include breakup-breakpoint('medium') {
        // 767px
        #video-player {
            padding: 20px 0 20px 20px;
        }
        .featured-video {
            .video-meta {
                padding: 20px 20px 20px 0;
            }
        }
    }
}

.social-flat-links {
    .share-title {
        display: block;
        margin-bottom: 10px;
    }
    .social-btn {
        float: left;
        margin: 0 2% 0 0;
        background: #ccc;

        a {
            padding: 5px;
            display: block;
            transition-property: background-color;
            transition-duration: 0.5s;
            transition-timing-function: ease-out;
        }
        a:link,
        a:hover,
        a:visited {
            color: #fff;
            text-decoration: none;
            border-bottom: none;
            background-color: transparent;
        }
    }

    i,
    span.fa {
        margin-right: 5px;
    }
    .facebook {
        background: $facebook-blue;

        a:hover {
            background: lighten($facebook-blue, 10%);
        }
    }
    .twitter {
        background: $twitter-blue;

        a:hover {
            background: lighten($twitter-blue, 10%);
        }
    }
    .google {
        background: $googleplus-orange;

        a:hover {
            background: lighten($googleplus-orange, 10%);
        }
    }
    .linkedin {
        background: $linkedin-blue;

        a:hover {
            background: lighten($linkedin-blue, 10%);
        }
    }
}

