#wow-widget {
    label.required:before {
        @include fa-pseudo-icon(asterisk);
        color: $red1;
        margin-right: 3px;
        font-size: 0.6em;
        vertical-align: text-top;
    }

    .addnominees,
    .removenominees {
        margin-left: 5px;
    }

    textarea[name='nomination'] {
        width: 100%;
        min-height: 200px;
    }
}
