/* facebook, twitter, google+ icon links list */
.social-icons-list {
    list-style: none;
    li {
        display: inline-block;
        + li {
            margin-left: 0.5em;
        }
    }
    a {
        background-color: $white !important;
        color: $grey5 !important;
        width: 36px;
        height: 36px;
        display: block;
        border-bottom: 0;
        position: relative;
        border: 1px solid $grey5 !important;
        overflow: hidden;
        font-size: 1.5em;
        line-height: 36px;
        text-align: center;
        white-space: nowrap;
        border-radius: 50%;
        @include transition(all 0.2s ease);
        &:hover,
        &:focus,
        &:active {
            color: $gray;
            border-color: $gray;
            text-decoration: none !important;
        }
        &.facebook-link {
            &:hover,
            &:focus,
            &:active {
                color: #3b5998 !important;
                border-color: #3b5998 !important;
            }
        }
        &.twitter-link {
            &:hover,
            &:focus,
            &:active {
                color: #55acee !important;
                border-color: #55acee !important;
            }
        }
        &.google-plus-link {
            &:hover,
            &:focus,
            &:active {
                color: #d73d32 !important;
                border-color: #d73d32 !important;
            }
        }
        i {
            text-decoration: none;
        }
    }
}

/* international social media channels */
.int-social-list {
    list-style: none;
    padding: 0;
    margin: 0 0 0 5px;

    li {
        margin: 0 0 5px 0;
        float: left;

        a {
            padding: 0 0 0 45px;
            background: transparent url('i/icon-weibosocial.png') no-repeat
                center 2px;
            display: block;
            line-height: 32px;
            height: 32px;
            border-bottom: none;
            border: none;

            &:hover {
                background-position: center -40px;
            }
        }
        .youku {
            background-position: center -167px;

            &:hover {
                background-position: center -209px;
            }
        }
        .sina {
            background-position: center -84px;

            &:hover {
                background-position: center -127px;
            }
        }
    }
}

.widget {
    .social-panel__icons {
        @include breakup-breakpoint('medium') {
            display: inline;
            margin: 0;
        }
    }
    .link {
        border: none;

        &,
        &:link,
        &:visited {
            color: #000;
        }

        &:hover {
            border-bottom: 1px solid #000;
        }

        &:focus,
        &:active {
            background: #fff;
            border-bottom: 1px dotted #000;
        }
    }

    .link--social-icon {
        font-size: 1.7em;
        margin: 0 5px 5px 0;
        display: inline-block;

        &,
        &:link,
        &:visited {
            color: #000;
        }
        &:hover {
            opacity: 0.25;
            border: none;
        }
        &:focus,
        &:active {
            background: #fff;
            color: #555;
            border: none;
        }
    }

    .link--social-text,
    .link--location,
    .link--goto {
        &,
        &:link,
        &:visited {
            color: #fff;
        }
        &:hover {
            border-bottom: 1px solid #fff;
        }
        &:focus,
        &:active {
            background: #fff;
            color: #555;
            border: none;
        }
    }
}
