// year listing
#events-archive li a {
    background-color: #666666;
    display: block;
    padding: 15px 10px;
    border: none;
    margin-bottom: 5px;
    color: $white;
    transition-duration: 0.5s;
    transition-property: background-color;
    transition-timing-function: ease-out;

    &:hover {
        background-color: #999999;
    }
}

// month listing
#events-list-archive {
    li {
        padding: 20px 0;

        &:last-child {
            border-bottom: none;
        }
    }
    h2 {
        font-family: $headings-font-family;
        font-size: 1.4em;
    }
    .date {
        width: 50px;
    }
    .date-day-no {
        border-bottom: 1px solid #ccc;
        padding-bottom: 5px;
    }
    .event-summary {
        clear: both;
    }
}

.js #event-book-modal {
    display: none;
}

#concert-events,
#opendays-events {
    display: block;
}
