@mixin icon-after {
    text-indent: -99999px;
    font-family: FontAwesome;
    font-weight: normal;
    font-style: normal;
    text-decoration: inherit;
    -webkit-font-smoothing: antialiased;
    display: inline;
    width: auto;
    height: auto;
    line-height: normal;
    vertical-align: middle;
    background-image: none;
    background-position: 0% 0%;
    background-repeat: repeat;
    margin-top: 0;
    color: #666;
    margin-left: 10px;
    font-size: 1.2em;
    position: relative;
}

.clearing-gradient,
.clearing-gradient-wrapper {
    background-color: #a22833;
    background: linear-gradient(
        to right,
        rgba(153, 0, 15, 0.8) 40%,
        rgba(54, 0, 31, 0.8)
    );
}

.clearing-gradient-wrapper {
    padding-top: 10px;
    padding-bottom: 10px;

    .clearing-gradient {
        background: none;
    }
    h3 {
        color: $white;
    }
}

.promo-wrapper--clearing {
    margin-top: 20px;
    position: relative;
    margin-bottom: -5px;

    .image-under {
        @extend .sr-only;

        @include breakup-breakpoint('medium') {
            // 767px
            clip: inherit;
            margin: 0;
            position: relative;
            width: 100%;
            height: 100%;
        }
    }

    .text-over {
        padding: 5%;
        font-size: 1.7em;
        margin-top: 10px;
    }
}

.home .home-studying-clearing {
    @extend .sr-only;

    @include breakup-breakpoint('medium') {
        // 767px
        color: #ffffff;
        width: 100%;
        height: 269px;
        display: block;
        font-size: 1.7em;
        padding-top: 0;
        clip: inherit;
        position: relative;
        margin: 0;

        &:link {
            border: none;
        }
        &:hover,
        &:focus,
        &:active {
            background-color: transparent;

            .image-under {
                opacity: 0.93;
            }
        }
        .text-over {
            padding: 9% 13% 5% 10%;
        }
        h2 {
            color: $white;
        }
        p {
            margin-bottom: 0;
        }
    }

    @include breakup-breakpoint('wide') {
    }

    h3 {
        color: #ffffff !important;
    }

    img {
    }
}

.media-ribbon.clearing-ribbon-wrapper,
.home .clearing-ribbon-wrapper {
    color: $white;

    .text {
        font-size: 1.3em;
        line-height: 1.3em;
        @include breakup-breakpoint('wide') {
            max-width: 100%;
        }
    }
    h3 {
        color: $white;
        font-size: 1.4em;
        margin-bottom: 20px;
        text-transform: uppercase;
    }
    .tel {
        font-size: 1.3em;
        display: block;
    }

    @include breakup-breakpoint('thinish') {
        .tel {
            display: inline;
        }
    }
    @include breakup-breakpoint('medium') {
        padding-top: 0;
        padding-bottom: 0;
    }
}

.button-clearing-course,
.button-clearing-open-day {
    float: left;
    clear: both;
    margin-bottom: 0;

    @include breakup-breakpoint('wide') {
        float: right;
    }
}

.button-clearing-course {
    margin-top: 20px;

    @include breakup-breakpoint('medium') {
        margin-top: 80px;
    }
    @include breakup-breakpoint('wide') {
        margin-top: 100px;
    }
    @include breakup-breakpoint('wider') {
        margin-top: 130px;
    }
}

.button-clearing-open-day {
    margin-top: 20px;
}

.standout-warning .tel {
    font-size: 1.4em;
    color: #a22833;
    font-weight: bold;
}

.widget-clearing {
    font-size: 1.2em;
    p {
        margin-bottom: 0;
    }
    h2 {
        font-size: 2em;
    }
    .tel {
        font-size: 1.5em;
        margin-bottom: 10px;
        display: block;
        border: none;
        color: $white;
    }
    .international {
        font-size: 1em;
    }
}

#clearing-courses_filter {
    padding: 20px 10px;
    background: #333333;
    margin-bottom: 5px;

    label {
        color: $white;
        font-size: 1.4em;
    }

    input {
        font-size: 1em;
        background-color: #f7f7f7;
        border: medium none;
        font-size: 1em;
        height: 27px;
        line-height: 27px;
        min-height: 27px;
        padding: 4px 5px;
        width: 60%;
        margin-left: 10px;
    }
}

.clearing-courses-title {
    margin-top: 30px;
}

#clearing-courses {
    margin-bottom: 0;

    th {
        padding: 15px 40px 15px 10px;
        border: none;

        &:hover {
            cursor: pointer;
        }
        &.sorting {
            &::after {
                @include icon-after;
                content: '\f0dc';
            }
        }
        &.sorting_desc {
            &::after {
                @include icon-after;
                content: '\f0dd';
                top: -4px;
            }
        }
        &.sorting_asc {
            &::after {
                @include icon-after;
                content: '\f0de';
                top: 2px;
            }
        }
    }
    .column-course {
        width: 40%;
    }
    .column-ucas {
        width: 15%;
    }
    .column-keyword {
        width: 40%;
    }
    .column-places {
        width: 5%;
    }
    tr {
        td {
            background-color: $white;
            padding: 5px 0 5px 10px;
            border: none;
            vertical-align: top;
        }
        a {
            display: block;
            padding: 10px;
            text-decoration: none;
            border: none;
            color: #666666;
            margin-left: 40px;
        }
        .course-vacancy-status,
        .course-keywords,
        .course-ucas {
            a {
                padding: 0;
                margin-left: 0;
                margin-top: 10px;
            }
        }
        .course-vacancy-status {
            font-weight: bold;
        }
        &:hover {
            td {
                background-color: #eae9e7;
            }
        }
    }
    .row-detail {
        td {
            padding-bottom: 15px;
        }
    }
    // added on small screens
    .course-extra {
        list-style-position: inside;
        margin-bottom: 0;
        margin-left: 10px;
        color: #666666;

        a {
            display: inline;
        }
    }
    .course-link {
        a {
            font-weight: bold;
            padding-left: 0;
        }
    }
    .dataTables_empty {
        padding: 20px 10px;
    }
}

.dataTables_info {
    background: #eae9e7;
    padding: 20px 10px;
    margin-top: 5px;
    font-size: 1em;
    margin-bottom: 20px;
}

.responsiveExpander {
    padding-right: 5%;
    cursor: pointer;
    display: block;
    float: left;
    position: relative;
    top: 12px;

    &::after {
        @include icon-after;
        content: '\f067';
        padding-bottom: 40px;
        margin-left: 5px;
    }
}

.detail-show {
    .responsiveExpander {
        &::after {
            @include icon-after;
            content: '\f068';
            margin-left: 5px;
        }
    }
}

@include breakup-breakpoint('thinish') {
    // 480px
    .responsiveExpander {
        display: none;
    }
    #clearing-courses tr a {
        margin-left: 0;
    }
    .course-vacancy-status a {
        font-weight: normal;
    }
}

@include breakup-breakpoint('medium') {
    // 767px
    #clearing-fixed {
        position: fixed;
    }
    .dataTables_info {
        margin-bottom: 20px;
    }
}
