.election-microsite {
    .responsive-wrapper {
        padding-top: 0;
    }

    #container {
        margin-bottom: 0;
    }

    .election-microsite__intro {
        padding-top: 0;
    }

    .social-icons-list {
        float: right;
        margin-top: 7px;
    }

    @mixin red-partial-border {
        position: relative;

        &:before {
            position: absolute;
            content: ' ';
            display: block;
            background: #a7281d;
            width: 100px;
            max-width: 25%;
            height: 3px;
            top: 0;
        }
    }

    .election-microsite__experts-wrapper {
        padding-top: 0;
        padding-bottom: $section-padding - 20;
    }

    .election-microsite__news,
    .election-microsite__experts--top {
        @include red-partial-border;
    }

    .election-microsite__experts {
        padding-top: 20px;
        padding-bottom: 0;
    }

    .election-microsite__experts--top {
        padding-top: 35px;
    }

    .election-microsite__experts--top-wrapper {
        padding-bottom: 20px;
        padding-top: 0;
    }

    .election-microsite__experts--info-wrapper {
        display: none;
        background: #eee;

        .row {
            position: relative;
        }

        &.election-microsite__experts--info-wrapper--open {
            display: block;
        }
    }

    .election-microsite__news {
        margin-top: $section-padding;
        padding-top: $section-padding / 2;
        padding-bottom: 0;

        h2 a {
            &:link,
            &:visited {
                i {
                    color: #888;
                }
            }

            &:hover,
            &:active {
                i {
                    color: inherit;
                }
            }

            i {
                margin-left: 10px;
                font-size: 0.7em;
            }
        }

        .news-card time {
            display: none;
        }

        .read-more {
            @include red-partial-border;
            padding-top: 10px;

            a {
                border: 0;
                font-weight: 600;
                color: $red1;

                &:active,
                &:focus {
                    color: white;
                }
            }

            h3 {
                font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                    sans-serif;
            }
        }
    }

    $selected-color: #80b8eb;

    .election-microsite__academics__item {
        margin-top: 0;
        border: none;
        margin-bottom: 20px;
        position: relative;

        a {
            border: none;
            margin: 0;
            padding: 0;
            display: block;
        }

        img {
            width: 100%;
            margin: 0;
        }

        .meta {
            background: $section-color-blue;

            vertical-align: middle;
            height: 60px;
            position: relative;
            color: white;
            transition: background 0.2s, color 0.2s;

            .text-center {
                position: absolute;
                bottom: 0;
                width: 100%;

                .text {
                    padding: 10px;
                    display: inline-block;
                    vertical-align: middle;

                    p {
                        margin-bottom: 0;
                    }
                }
            }

            h3 {
                font-family: Arial, Helvetica, sans-serif;
                font-size: 14px;
                font-weight: 600;
                margin: 0;
            }
        }

        .info {
            display: none;
        }

        &:hover a {
            .meta {
                background: $section-color-red;
                color: white;
            }
        }

        &.selected {
            .meta {
                background: white;
                color: $red1;
            }
        }
    }

    .iwd-listing {
        .meta {
            height: auto;
            min-height: 60px;

            .text-center {
                position: relative;
                bottom: auto;
            }

            a {
                color: $white;
                display: inline-block;
                border-bottom: 1px dotted currentColor;

                &:hover {
                    border-bottom-color: $grey5;
                }

                &:focus,
                &:active {
                    border-bottom-color: transparent;
                }
            }
        }
    }

    .election-microsite__academics {
        position: relative;
    }

    .election-microsite__academics__item__info {
        background: white;
        z-index: 1;
        padding: 20px;
        opacity: 0;
        width: 0;
        height: 0;
        border-color: #003a6f;
        border-style: solid;
        border-width: 0;

        &.open {
            opacity: 1;
            transition: opacity 0.2s;
        }
    }

    .election-microsite__experts--info__img img {
        margin: 0;
    }

    .election-microsite__experts--info__close {
        position: absolute;
        border: 0;
        right: 10px;
        font-size: 1.5em;
        display: block;
        z-index: 1;

        &:hover {
            color: $section-color-red;
        }
    }

    .info-content {
        color: black;
        margin-right: 25px;

        .info-name {
            font-weight: 600;
            @include font-family-meta-sans();
            font-size: 1.2em;
            margin-bottom: 0;
        }

        .job-title,
        .specialisms,
        .school {
            margin-bottom: 0.8em;
        }
        .label {
            font-weight: 600;
        }
        .label,
        .mq-label,
        .mq {
            margin: 0;
        }
        .mq-label {
            color: $red1;
            font-weight: 600;
        }
    }

    .election-microsite__tweets {
        @include red-partial-border;
        padding-top: $section-padding / 2;

        .city-press-office-tweeter > a {
            font-weight: 600;
            color: gray;
            line-height: 27px;
            border: none;

            &:hover {
                text-decoration: underline;
            }
        }

        .tweeter-icon {
            background: url('i/home/social.png') no-repeat -32px 0;
            float: left;
            width: 30px;
            height: 27px;
            margin-right: 10px;
        }
    }

    .election_microsite__ribbon {
        h2 {
            font-size: 22px;
            font-weight: normal;
            line-height: 1.5em;
        }
    }

    #promo-wrapper .banner-n {
        margin-bottom: 40px;
    }
}

#promo-wrapper .banner-n.general-election__banner {
    .banner-text {
        background-color: #00284c;
        color: $white;
        width: 100%;
        padding: 20px;
        margin: 0;
        @include breakup-breakpoint('wide') {
            background-color: rgba(#00284c, 0.7);
            width: auto;
            max-width: 50%;
            position: absolute;
            top: 1.429em;
            left: 1.429em;
        }

        h2 {
            margin: 0;
            line-height: 1.25em;
            font-family: ff-meta-web, 'Helvetica Neue', Arial, Helvetica,
                sans-serif;
            font-size: 2.286em;
            font-weight: 200;
        }
    }
}
