.primary-menu-wrapper {
    background: #8d182a;
    padding: 3px;
    margin-top: 15px;

    @include breakup-breakpoint('wide') {
        display: none;
    }

    .primary-menu-toggle {
        display: block;
        text-decoration: none;
        text-align: center;
        width: 100%;
        line-height: 20px;
        padding: 10px 0;
        background: #8d182a;
        color: #fff;
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-size: 1.4em;
        border-bottom: 1px solid rgba(0, 0, 0, 0.05);

        &--open {
            background: #fff;
            color: #000;
        }

        &__icon {
            margin-right: 7px;
        }
    }

    .primary-menu {
        margin: 0;
        padding-left: 0;
        list-style-type: none;
        background: #fff;
        color: #000;
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-size: 1.2em;

        .js & {
            display: none;
        }

        &__item {
            position: relative;
            border-top: 1px solid rgba(0, 0, 0, 0.1);
            overflow: hidden;
        }

        &__item-interior {
            position: relative;
        }

        &__item-link {
            display: inline-block;
            position: relative;
            padding: 13px 60px 13px 10px;
            width: 100%;
            text-decoration: none;
            border: none;
            color: #000;

            &:focus,
            &:active {
                background: none;
            }

            &:active {
                .primary-menu__item-text {
                    border-bottom: 1px solid;
                }
            }

            &--selected {
                background: #000;
                color: #fff;
                font-weight: bold;

                &:focus,
                &:active {
                    background: #000;
                }
            }
        }

        &__item-toggle {
            position: absolute;
            display: block;
            right: 4px;
            top: 0;
            bottom: 0;
            margin-top: auto;
            margin-bottom: auto;
            width: 50px;
            height: 38px;
            line-height: 38px;
            z-index: 10;
            text-align: center;
            text-decoration: none;
            font-family: FontAwesome;
            font-size: 2.4em;
            font-weight: bold;
            background: #fff;
            color: #555;
            border: 1px solid #555;

            &:before {
                content: '\f107';
            }

            &--open {
                background: #555;
                color: #fff;

                &:before {
                    content: '\f106';
                }
            }
        }

        &__submenu {
            padding: 0;
            margin-left: 0;
            list-style-type: none;
            display: none;

            &--level-1 {
                background: #e8e8e8;
            }

            &--level-2 {
                background: #d0d0d0;
            }

            &--level-3 {
                background: #b8b8b8;
            }

            &--level-4 {
                background: #a0a0a0;
            }

            &--level-5 {
                background: #888888;
            }

            &--level-6 {
                background: #707070;
            }

            &--level-7 {
                background: #585858;
            }

            &--level-4,
            &--level-5,
            &--level-6,
            &--level-7 {
                .primary-menu__item-link {
                    color: #fff;
                }
            }
        }
    }
}

.no-js .primary-menu-wrapper {
    display: none;
}

.skip {
    left: -99999px;
    position: absolute;
}

.skip:focus {
    left: 0;
    background-color: #ff0;
    color: #000;
    position: absolute;
    padding: 0 5px;
    z-index: 10000;
}

.header {
    padding-bottom: 0.3em;
    margin: 0 -10px;
    @include breakup-breakpoint('wide') {
        margin: 0;
    }
}

.header__util {
    display: none;
    background-color: #8f182e;
}

.header__util,
.header__logo-nav-wrapper {
    padding: 0 10px;
    @include breakup-breakpoint('wide') {
        padding: 0;
    }
}

@include breakup-breakpoint('wide') {
    .header {
        margin-bottom: 1em;
        padding-bottom: 0;
    }
    .header__util {
        display: block;
    }

    .header__logo-nav-wrapper {
        border-bottom: 1px solid #eee;
        position: relative;
    }
}

.header__util__misc {
    float: left;
    list-style-type: none;
    padding: 1.1em 0;
    font-size: 1.2em;
}

.header__util__audience {
    float: right;
    list-style-type: none;
    margin: 0;
    font-size: 1.2em;
}

@include breakup-breakpoint('wide') {
    .header__util__misc {
        margin: 6px 0 0 0;
        font-size: 1.1em;

        li {
            display: inline-block;
        }
        a {
            &:link,
            &:visited {
                color: $white;
                border: none;
                margin-right: 4em;
            }

            &:hover {
                border-bottom: 1px solid $white;
            }
            &:focus,
            &:active {
                border: none;
                background: $white;
                color: #555;
            }
        }
    }

    .header__util__audience {
        font-size: 1.1em;
    }
}

.header__util__students,
.header__util__staff {
    display: inline-block;
    color: $white;
    border-right: 2px solid #831226;
    cursor: pointer;
    position: relative;
    float: left;

    &:first-child {
        border-left: 2px solid #831226;
    }
}

.header__util__audience {
    .header__util__audience__button-link {
        color: #fff;
        line-height: 1em;
    }
}

.header__util__audience__button {
    margin: 1.5em 1.2em;
    padding: 0;
    display: block;

    &:after {
        content: '\f0d7';
        font-family: 'FontAwesome';
        float: right;
        margin-right: 1em;
        padding: 0.2em 0.4em 0.1em;
    }

    @include breakup-breakpoint('wide') {
        .header__util__audience__button-link:focus & {
            background: white;
            color: #555;
            border-color: white;
        }
    }
}

@include breakup-breakpoint('wide') {
    .header__util__audience__button-link:hover {
        .header__util__audience__button__text {
            border-bottom: 1px solid;
        }
    }

    .header__util__audience__button-link:focus {
        .header__util__audience__button__text {
            border-bottom: none;
        }
    }
}

@include breakup-breakpoint('wide') {
    .header__util__audience__button {
        &:after {
            margin-left: 1em;
            margin-right: 0;
            content: '\f0d7';
            display: inline-block;
            float: none;
        }
    }
}

.header__util__audience {
    .header__util__staff__list,
    .header__util__students__list {
        padding: 0;
        border: 0;
        overflow: hidden;
        list-style-type: none;
        display: none;

        height: auto;
        clip: initial;
        right: 0;
        margin: 1.2em 0 0;
        position: relative;
        background-color: #a34654;
        width: auto;

        li {
            border: none;
            display: block;
            margin: 0;
            padding: 0.5em 0 0.5em 0.5em;
            border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        }

        a {
            color: $white;
            border: none;
            display: inline;

            &:hover .header__hoverable {
                border-bottom: 1px solid $white;
            }
            &:focus,
            &:active {
                color: #555;
                background: $white;
            }
        }

        .fa {
            padding-left: 7px;
            font-size: 0.8em;
        }
    }
}

.no-js .header__util__audience > li {
    .header__util__staff__list,
    .header__util__students__list {
        display: block;
    }
    .header__util__students__list__item {
        display: inline-block;
        border-bottom-width: 0;
    }
    .header__util__students__list__item--dup {
        display: none;
    }

    @include breakup-breakpoint('wide') {
        .header__util__staff__list,
        .header__util__students__list {
            visibility: hidden;
            opacity: 0;
            transition: opacity 0.5s, visibility 0.5s;
        }

        .header__util__students__list__item,
        .header__util__students__list__item--dup {
            display: block;
            border-bottom-width: 1px;
        }

        &:hover {
            transition: visibility 0s, opacity 0.1s;
            .header__util__staff__list,
            .header__util__students__list {
                visibility: visible;
                opacity: 1;
            }
        }
    }
}

.header__util__item--active {
    .header__util__staff__list,
    .header__util__students__list {
        display: block;
    }

    .header__util__audience__button {
        &:after {
            content: '\f0d8';
        }
    }
}

@include breakup-breakpoint('wide') {
    .header__util__item--active {
        background: #bc1728;
    }

    .header__util__audience {
        .header__util__staff__list,
        .header__util__students__list {
            position: absolute;
            background: #bc1728;
            padding: 0.5em 1em 1em 0.5em;
            z-index: 10000000;
            width: 250px;
            margin-top: 0;

            li {
                margin: 0 0 0 1.5em;
                border: none;
            }
            a {
                &:focus,
                &:active {
                    background: $white;
                    color: #555;
                }
            }
        }
        .header__util__staff__list {
            width: 150px;
        }
        .header__util__audience__button {
            &:after {
                content: '\f0d7';
            }
        }
    }
}

.header__logo {
    float: left;
    margin: 1em 0 0;

    a,
    a:focus,
    a:active {
        border: none;
        text-decoration: none;
        background: none;
    }
}

.header__logo__img {
    width: 120px;
}

@include breakup-breakpoint('thinish') {
    .header__logo__img {
        width: 150px;
    }
}

@include breakup-breakpoint('wide') {
    .header__logo {
        margin-top: 1em;
    }
    .header__logo__img {
        width: 214px;
    }
}

.header__search {
    .header__search__toggle {
        border: 1px solid #ccc;
        padding: 0.5em 0.9em;
        display: block;
        position: absolute;
        right: 6.5em;
        top: 1em;
        overflow: hidden;
        color: #333;

        &:focus {
            background: #fff;
        }

        .fa {
            font-size: 3em;
            display: block;
        }
    }
}

@include breakup-breakpoint('thinish') {
    .header__search {
        .header__search__toggle {
            padding: 0.75em 1.5em;
            right: 8.3em;
            top: 1em;
        }
    }
}

@include breakup-breakpoint('wide') {
    .header__search {
        .header__search__toggle {
            display: none;
        }
    }
}

.header__search {
    margin: 0;

    fieldset {
        border: none;
        margin: 0;
        padding: 0;
    }
}

@include breakup-breakpoint('wide') {
    .header__search {
        float: right;
        margin: 1em 0;
    }
}

.header__search__form {
    display: none;
}

.header__search--active,
.no-js {
    .header__search__toggle {
        background: #ccc;
        padding-bottom: 2em;

        &:focus {
            background: #ccc;
        }
    }

    .header__search__form {
        display: block;
        position: relative;
        background: #ccc;
        width: 100%;
        padding: 1em 1em;
        overflow: hidden;
        top: 1em;
    }
    fieldset {
        float: right;
    }
    .header__search__button {
        background: $white;
        border: 1px solid $white;
    }
}

@include breakup-breakpoint('thinish') {
    .header__search--active {
        .header__search__form {
            top: 0;
        }
    }
}

@include breakup-breakpoint('wide') {
    .header__search__form {
        display: block;
    }
    .header__search--active {
        .header__search__form {
            position: relative;
            background: transparent;
            margin-top: 0;
            padding: 0;
        }
    }
}

.header__search__input {
    line-height: 1.1em;
    font-size: 1.1em;
    padding: 0.9em 1.1em;
    float: left;
    border: 1px solid #fff;
    width: 82%;
}

.header__search__fieldset {
    input,
    button {
        height: 46px;
    }
}

@include breakup-breakpoint('thinish') {
    .header__search__input {
        width: 89%;
    }
}

@include breakup-breakpoint('thin') {
    .header__search {
        fieldset {
            width: 100%;
        }
    }
}

@include breakup-breakpoint('wide') {
    .header__search__form {
        .header__search__input {
            border-left: 1px solid #cacaca;
            border-top: 1px solid #cacaca;
            border-bottom: 1px solid #cacaca;
            border-right: none;
            border-top-left-radius: 0.5em;
            border-bottom-left-radius: 0.5em;
            width: 220px;
        }
        .header__search__button {
            border-right: 1px solid #cacaca;
            border-top: 1px solid #cacaca;
            border-bottom: 1px solid #cacaca;
            border-left: none;
            border-top-right-radius: 0.5em;
            border-bottom-right-radius: 0.5em;
        }
    }
}

.header__search__button {
    background: none;
    line-height: 1.4em;
    height: 2.35em;
    width: 2em;
    font-size: 1.4em;
    font-weight: bold;
    vertical-align: middle;
    float: left;
    cursor: pointer;
}

.global-nav__wrapper {
    .global-nav__toggle {
        border: 1px solid #ccc;
        padding: 0.5em 1.2em;
        display: block;
        position: absolute;
        right: 10px;
        top: 1em;
        color: #333;

        &:focus {
            background: #fff;
        }

        .fa {
            font-size: 3em;
            display: block;
        }
    }
}

@include breakup-breakpoint('thinish') {
    .global-nav__wrapper {
        .global-nav__toggle {
            padding: 0.75em 1.7em;
            top: 1em;
        }
    }
}

@include breakup-breakpoint('wide') {
    .global-nav__wrapper {
        .global-nav__toggle {
            display: none;
        }
    }
}

.navigation-wrapper {
    display: none;
}

@include breakup-breakpoint('wide') {
    .navigation-wrapper {
        display: block;
    }
}

.global-nav__wrapper {
    clear: right;
    float: right;
    padding: 0;
    width: 100%;
    margin-top: 1em;

    .global-nav {
        list-style-type: none;
        padding: 0;
        margin: 0;

        @include breakup-breakpoint('wide') {
            font-size: 0.95em;
        }
        @include breakup-breakpoint('wider') {
            font-size: 1em;
        }
    }
    li {
        display: inline-block;
        margin: 0 0 0 1em;
    }
}

@include breakup-breakpoint('thinish') {
    .global-nav__wrapper {
        margin-top: 0;
    }
}

@include breakup-breakpoint('wide') {
    .global-nav__wrapper {
        margin-top: 2em;
        width: auto;
    }
}

.global-nav__menu-item__link {
    font-size: 1.2em;
    margin-right: 0.5em;
    font-weight: bold;
    margin-left: 0.5em;

    &:link {
        border: none;
        color: $white;
    }
    &:visited {
        color: $white;
    }
    &:focus,
    &:active {
        background: $white;
        color: #555;
        border-bottom: 1px solid $white;
    }
}

.global-nav__menu-item {
    .global-nav__menu-item__toggle-icon {
        font-family: 'FontAwesome';
        display: block;
        float: right;
        background-color: #c00f22;
        color: white;
        padding: 0.3em 0.3em;
        vertical-align: middle;
        margin-right: 13px;
        margin-top: -0.25em;
        font-size: 2.4em;

        &:before {
            content: '\f107';
        }
    }

    .global-nav__menu-item__toggle {
        outline: none;
    }
}

.global-nav__wrapper--active,
.no-js {
    .global-nav__toggle {
        color: $white;
        border: 1px solid #8d182b;
        background: #8d182b;
        padding: 0.3em 1.2em 7em 1.2em;

        &:focus {
            background: #8d182b;
        }

        .fa:before {
            content: '\f00d';
            font-family: 'FontAwesome';
        }
    }

    @include breakup-breakpoint('thinish') {
        .global-nav__toggle {
            padding: 0.75em 1.6em 2em;
        }
    }

    .navigation-wrapper {
        display: block;
        background-color: #8d182b;
        position: relative;
        overflow: hidden;
    }
    .global-nav__menu-item {
        display: block;
        border-bottom: 1px solid $white;
        padding: 1em 0 1.2em 0;
        margin: 0;
    }
    .header__util__misc--responsive {
        width: 100%;
        padding: 0;

        a {
            margin: 0;
        }
    }

    .header__util__misc__item {
        width: 50%;
        height: 75px;
        padding: 0 15px;
        text-align: center;
        background-color: #c00f22;
        color: #fff;
        display: table;
        border: none;

        &:hover {
            .header__util__misc__item__span {
                border-bottom: 1px solid #fff;
                text-decoration: none;
            }
        }

        &:focus,
        &:active {
            .header__util__misc__item__span {
                background: $white;
                color: #555;
            }
        }

        @include breakup-breakpoint('thinish') {
            height: 60px;
        }

        &__text {
            display: table-cell;
            vertical-align: middle;
        }

        &--unit {
            float: left;
            border-right: 2px solid #90162d;
        }
        &--directory {
            float: right;
        }
    }

    .header__util__audience--responsive {
        margin-bottom: 1em;
        width: 100%;

        .header__util__students,
        .header__util__staff {
            display: block;
            float: none;
            border: none;
            margin: 0;
        }

        .header__util__audience__button {
            padding: 1em 0 0 0;
        }
    }
    .global-nav__menu-item__link,
    .global-nav__drop a {
        &:hover {
            border-bottom: 1px solid $white;
        }
    }
}

@include breakup-breakpoint('wide') {
    .global-nav__wrapper--active {
        background-color: transparent;
        margin: 2em 0 0 0;
        padding: 0;
    }

    .no-js .header-nav-wrapper,
    .header-nav-wrapper {
        .global-nav__wrapper {
            background: transparent;
            padding: 0;
        }
        .navigation-wrapper {
            background: transparent;
            position: initial;
            margin: 0 0 -1em 0;
            padding-bottom: 0;
        }
        .global-nav__menu-item {
            display: inline-block;
            margin-right: 0.5em;
            padding: 1em 0 0 0;

            &:hover {
                .global-nav__menu-item__link,
                .global-nav__menu-item__toggle-icon {
                    color: $red2;
                }
                .global-nav__menu-item__link {
                    border-bottom: 1px solid $red2;
                }
            }

            .global-nav__menu-item__toggle-icon {
                background: transparent;
                color: #595959;
                padding: 0.4em 0;
                margin: 0;
                font-size: 1.2em;
                line-height: 1.2em;
                position: relative;
                width: 100%;
                top: -26px;
                right: 0;
                text-align: right;
                z-index: 1000;
                display: block;
                border: 1px dotted transparent;

                &:before {
                    content: '\f0d7';
                    padding: 0.2em 0.3em;
                }
            }

            .global-nav__menu-item__toggle:focus {
                .global-nav__menu-item__toggle-icon {
                    border-color: #333;
                }
            }

            .global-nav__menu-item__link {
                color: #595959;
            }
        }
    }

    .header__util__misc--responsive,
    .header__util__audience--responsive {
        display: none;
    }
}

.global-nav__drop {
    z-index: 10000;

    display: none;

    ul {
        list-style-type: none;
    }
}

.global-nav__drop__heading {
    display: none;
}

.no-js {
    .header__util__audience__button-link,
    .global-nav__menu-item__toggle {
        display: none;
        @include breakup-breakpoint('wide') {
            display: inline;
        }
    }

    .global-nav__menu-item {
        .global-nav__drop {
            display: none;
            @include breakup-breakpoint('wide') {
                display: block;
                visibility: hidden;
                opacity: 0;
                transition: opacity 1s, visibility 1s;
            }
        }

        &:hover {
            @include breakup-breakpoint('wide') {
                .global-nav__drop {
                    opacity: 1;
                    transition: opacity 0.2s, visibility 0.2s;
                    visibility: visible;
                }
            }
        }
    }
}

//.no-js .global-nav__menu-item:hover,
.global-nav__menu-item--active {
    .global-nav__menu-item__link {
        background: transparent;
        color: $white;
        border: none;
    }
    .global-nav__menu-item__toggle-icon {
        &:before {
            content: '\f106';
        }
    }
    .global-nav__drop {
        display: block;
    }
}

.global-nav__drop {
    left: 0;
    right: 0;
    padding: 1em 0 0 0;
    margin: 1.1em auto -1.5em;
    position: relative;
    background: #a34654;

    .container > div > div:last-child {
        li:last-child {
            border: none;
        }
    }
    ul {
        margin: 0 0 -0.5em 0;
    }
    li,
    .global-nav__drop__column-heading {
        display: none;
        margin: 0 0 0.8em 0;
        padding-bottom: 0.7em;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        padding-left: 0.5em;
    }
    .global-nav__drop__column-heading {
        padding-top: 0.3em;
    }
    .global-nav__drop__mobile,
    .global-nav__drop__column-heading {
        display: block;
    }
    .global-nav__drop__mobile--first {
        padding-top: 7px;

        @include breakup-breakpoint('wide') {
            padding-top: 0;
        }
    }
    .container > div > div:last-child .global-nav__drop__column-heading {
        border: none;
    }
    a {
        color: $white;
        border: none;
        font-size: 1.2em;

        &:visited {
        }
        &:hover {
        }
        &:focus,
        &:active {
            background: $white;
            color: #555;
        }
    }
}

@include breakup-breakpoint('wide') {
    .global-nav__menu-item__link {
        color: #555;
    }

    .global-nav__drop {
        margin: 1.35em auto 0;
        position: absolute;
        padding: 1.8em;
        border-bottom: 1px solid #c9c9c9;
        border-top: 1px solid #c9c9c9;
        background: rgba(250, 250, 250, 0.98);

        ul {
            margin: 0;
        }

        li,
        .global-nav__drop__column-heading {
            border-bottom: 1px solid #ccc;
            padding-bottom: 0.5em;
            display: block;
            padding-left: 0;
        }

        .container > div > div:last-child {
            li:last-child {
                border-bottom: 1px solid #ccc;
            }
        }

        a {
            color: #333;
            font-size: 1em;

            &:hover {
                color: $red2;
            }

            &:focus,
            &:active {
                background: $red2;
                color: $white;
            }
        }
    }
}

@include breakup-breakpoint('wide') {
    .header-nav-wrapper {
        .global-nav__menu-item {
            border: 1px solid transparent;
            border-bottom-width: 0;
            padding: 0.7em 0.4em 0 0.4em;
            margin-right: 0;
            margin-left: 0em;

            .global-nav__drop {
                .global-nav__drop__heading,
                .global-nav__drop__column-heading {
                    display: inline-block;
                    margin: 0 0 1em 0;
                    font-weight: bold;
                    font-size: 1.4em;
                    padding: 0;
                    border: none;

                    a {
                        &:hover {
                            border-bottom: 1px solid $red2;
                        }
                    }

                    &:hover {
                        border-bottom: 1px solid transparent;
                        margin-bottom: 0.95em;
                    }

                    &:focus,
                    &:active {
                        background: $red2;
                        color: $white;
                    }
                }
                .global-nav__drop__heading:hover {
                    border-bottom: 1px solid;
                }
            }
        }

        .no-js & .global-nav__menu-item:hover,
        .global-nav__menu-item--active {
            border-color: #c9c9c9;
            background: rgb(250, 250, 250);

            .global-nav__menu-item__toggle-icon {
                border-color: #333;
            }
        }
    }
}

// will need to clean up when we cleanup the html
.slice-title,
.slice-title--non-url {
    display: none;
}

.no-js .slice-title {
    display: block;
    padding: 1.2em 0.5em 1em 0.5em;
    border-bottom: 1px solid #ccc;
}

@include breakup-breakpoint('wide') {
    .slice-title {
        display: block;
        margin: 0 0 0.3em;

        &,
        .no-js & {
            padding: 0;
            border-bottom: none;
        }

        @include font-family-meta-sans;

        a {
            font-size: 1.8em;
            text-decoration: none;
            margin-bottom: 0.5em;

            &:link,
            &:visited {
                color: $black;
                border: none;
            }

            &:hover {
                border-bottom: 1px solid $red2;
                color: $red2;
            }
            &:focus,
            &:active {
                color: $white;
            }
        }
    }

    .slice-title--non-url {
        display: block;
        margin: 0 0 0.3em;
        font-size: 1.8em;
        color: $black;
    }
}

@include breakup-breakpoint('wider') {
    .js .scroll-down {
        .header {
            top: 0;
            left: 0;
            position: fixed;
            z-index: 1000;
            width: 100%;
            background: white;
            padding: 0;
            margin: 0;
        }

        .header__util {
            display: none;
        }
        .global-nav__wrapper {
            float: left;
            font-size: 1em;
            clear: none;
            margin-top: 13px;
            margin-left: 10px;
        }

        .header__logo img {
            width: 80px;
        }

        .header__search {
            margin-bottom: 0;
        }

        #container {
            margin-top: 235px;
        }
    }
}

.home {
    #section-nav {
        display: none;
    }
}

.no-js {
    .header__search {
        display: block;
        float: left;
        margin-left: 20px;
    }

    .header__search__toggle,
    .global-nav__toggle {
        display: none;
    }

    .global-nav__menu-item {
        margin-bottom: 1.2em;
        display: inline-block;
    }

    .global-nav__wrapper {
        width: 100%;
    }

    .header__util__audience li a {
        color: white;
        .header__util__audience__button {
            color: white;
        }
    }

    .header__util__audience__button {
        display: inline-block;
    }

    .header__util__staff__list,
    .header__util__students__list {
        z-index: 9;
    }
}

.header__util__students {
    a {
        display: flex;
        margin: 1.5em 1.2em;
        border: none;

        span {
            align-self: center;
            padding-top: 0.2em;
        }

        &:link,
        &:visited {
            span {
                color: #fff;
                line-height: 1em;
            }
        }

        &:hover {
            span {
                border-bottom: 1px solid $white;
            }
        }

        &:focus,
        &:active {
            span {
                border: none;
                background: $white;
                color: #555;
            }
        }
    }
}
