// action-box--mod vertical
// used on IS-Support for students for now

.action-box--mod-vertical {
    text-decoration: none;
    display: block;
    color: $white;
    padding: 5px;
    margin-bottom: 20px;
    position: relative;
    z-index: 0;
    overflow: hidden;
    height: 100px;

    @include breakup-breakpoint('thinish') {
        height: 150px;
    }
    &,
    &:link,
    &:visited,
    &:hover,
    &:focus,
    &:active {
        border: 0;
    }

    &:hover {
        span.hggroup {
            border-bottom: 1px solid #ccc;
            color: $white;
        }
    }
    &:focus,
    &:active {
        span.hggroup {
            background-color: $white;
            color: #555;
        }
    }

    span.hggroup {
        color: #eee;
        font-size: 16px;
        font-weight: 700;
        margin: 0;
        padding: 0;
        border-bottom: 1px dotted #ccc;
        display: inline;
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;

        @include breakup-breakpoint('wider') {
            font-size: 18px;
        }
    }

    .hggroup {
        display: inline-block;
        display: block;
        position: relative;
        top: 0px;
        margin-top: 5px;
        padding: 5px 10px 0;
    }

    ul {
        margin-left: 15px;
        margin-bottom: 0;
    }

    img {
        border: 0 none;
        margin: 0;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;

        @include breakup-breakpoint('thin') {
            display: inline;
        }

        @include breakup-breakpoint('thinish') {
            height: 150px;
        }
    }

    &.action-box--mod-vertical-1 {
        background-color: #00264a;
        h2 a {
            color: #1c3040;
        }
    }
    &.action-box--mod-vertical-2 {
        background-color: #555;
        h2 a {
            color: $white;
        }
    }
    &.action-box--mod-vertical-3 {
        background-color: #086b60;
        h2 a {
            color: $white;
        }
    }
    &.action-box--mod-vertical-4 {
        background-color: #555;
        h2 a {
            color: $white;
        }
    }
    &.action-box--mod-vertical-5 {
        background-color: #002f35;
        h2 a {
            color: $white;
        }
    }
    &.action-box--mod-vertical-6 {
        background-color: #555;
        h2 a {
            color: $white;
        }
    }
    &.action-box--mod-vertical-7 {
        background-color: #a22833;
        h2 a {
            color: $white;
        }
    }
    &.action-box--mod-vertical-8 {
        background-color: #555;
        h2 a {
            color: $white;
        }
    }
    &.action-box--mod-vertical-9 {
        background-color: #4d2942;
        h2 a {
            color: $white;
        }
    }
    &.action-box--mod-vertical-10 {
        background-color: #555;
        h2 a {
            color: $white;
        }
    }
    .is-support-text {
        color: $white;
        display: none;
        margin-top: 10px;
        float: left;
        margin-left: 10px;

        i,
        span {
            margin-right: 10px;
        }

        @include breakup-breakpoint('thinish_extended_2') {
            display: block;
        }

        .is-support-list {
            font-style: italic;
        }
    }
}

.self-service-button {
    -webkit-border-radius: 4px;
    moz-border-radius: 4px;
    border-radius: 4px;
    background-color: #b42b1c;
    padding: 3px;
    margin: 5px;

    a {
        display: inline;
        color: $white;
        text-decoration: none;
        border-bottom: 1px solid transparent;
        font-weight: bold;

        &:hover {
            border-bottom: 1px solid #eeeeee;
        }

        &:focus,
        &:active {
            background-color: $white;
            color: #555;
        }
    }
}

