// Body reset
// -------------------------

body {
    margin: 0;
    padding: 0 10px;
    font-family: $font-family-base;
    font-size: $font-size-base;
    line-height: $line-height-base;
    color: $text-color;
    background-color: $body-bg;

    -webkit-font-smoothing: antialiased;
}

// Headline placeholders
// ---------------------

%hdln-1 {
    @include font-family-meta-sans;
    @include font-shorthand(2.5em, 600, 1.4em);
    margin: 6px 0 14px;
}

%hdln-2 {
    @include font-family-meta-sans;
    @include font-shorthand(2em, 500, 1.4em);
    margin: 6px 0 8px;
}

%hdln-3 {
    @include font-family-meta-sans;
    @include font-shorthand(1.6em, 500, 1.3em);
    margin: 6px 0 8px;
}

%hdln-4 {
    @include font-family-meta-sans;
    @include font-shorthand(1.2em, 600, 1.3em);
    margin: 6px 0 8px;
}

// Headline classess
// -----------------

.headline {
    &-1 {
        @extend %hdln-1;
    }

    &-2 {
        @extend %hdln-2;
    }

    &-3 {
        @extend %hdln-3;
    }

    &-4 {
        @extend %hdln-4;
    }
}

h1 {
    @extend %hdln-1;
}

h2 {
    @extend %hdln-2;
}

h3 {
    @extend %hdln-3;
}

h4,
h5,
h6 {
    @extend %hdln-4;
}

//main typography/vertical rhythm set up partial
h1,
h2,
h3,
h4,
h5,
h6 {
    color: $headings-color;
    text-rendering: optimizelegibility; // Fix the character spacing for headings

    small {
        font-weight: normal;
        line-height: 1;
        color: $gray-light;
    }
}

hr {
    color: $grey2;
    height: 1px;
    border: none;
    border-top: 1px solid $grey2;
    margin: $line-height-base 0;
}

/*stop editors messing with headings*/
h1 a,
h1 a:link,
h2 a,
h2 a:link,
h3 a,
h3 a:link,
h4 a,
h4 a:link,
h5 a,
h5 a:link,
h6 a,
h6 a:link,
h1 strong,
h2 strong,
h3 strong {
    font-weight: normal;
    border-bottom: none;
    text-decoration: none;
}

small {
    font-size: 85%; // Ex: 14px base font * 85% = about 12px
}

/*allow editors using the EES to make bold anchors*/
strong,
strong a {
    font-weight: bold;
}

em {
    font-style: italic;
}

cite {
    font-style: normal;
}

.standfirst {
    @include font-family-meta-sans;
    @include font-shorthand(1.6em, 300, 1.4em);
    margin: 0 0 10px 0;
    color: $grey5;
}

p {
    overflow: hidden;
    margin: 0 0 14px;
}

.note {
    background-color: #ffffcc;
    border: 1px solid #ffe599;
    padding: 10px;
}

//Lists
ul,
ol {
    margin: 0 0 14px 25px;
    padding: 0;
}

ul ol,
ol ul {
    margin-bottom: 0;
}

ul li,
ol li {
    padding: 0;
    line-height: 20px;
}

ol li {
    list-style: decimal;
    margin: 0 0 0 20px;
}

ol ul li {
    list-style: circle;
}

ul ul {
    margin-bottom: 0;
    list-style: circle;
}

ol ol {
    margin-bottom: 0;
}

dl {
    margin-bottom: $line-height-base;
}

dt {
    padding: 5px;
    background: $grey1;
    margin-bottom: 10px;
    font-family: $font-family-serif;
    font-size: 1.2em;
    line-height: $line-height-base;
    font-weight: bold;
}

dd {
    margin: 0 0 14px 10px;
    line-height: $line-height-base;
}

/*A-Z listings*/
#alpha-pagination {
    padding: 10px;
    border: 1px solid $grey3;
    margin: 0 0 20px 0;
    @include clearfix;
    background: $grey1;
    color: #999;
    font-size: 1.2em;

    a:visited {
        color: $red1;
    }
    a:active {
        background: #ef383f;
        color: #fff;
    }
    a:hover,
    a:focus {
        border-bottom: 1px solid $grey3;
        background: $red1;
        color: #fff;
    }
}

.odd {
    background: $grey1;
}

#content .alpha li {
    margin: 0;
    list-style: none;
    padding: 10px 5px;
}

.notice-message {
    border: 1px solid #aad4ff;
    background: #e3f0fd;
    padding: 10px;
    margin: 0;
}

acronym,
abbr,
dfn {
    border-bottom: 1px $grey6 dotted;
    cursor: help;
}

blockquote {
    font-family: $font-family-serif;
    font-style: italic;
    font-size: 1.1em;
    line-height: 1.4em;
    color: $grey5;
    padding: 0 0 0 40px;
    margin: 20px 0 20px 0;
    background: transparent url('i/quote.png') no-repeat left top;
    clear: both;
    border: none;

    p {
        margin-bottom: 0;
        @include font-shorthand(16px, 300, $line-height-base * 1.25);
    }
    small {
        display: block;
        line-height: $line-height-base;
        color: $gray-light;
        &:before {
            content: '\2014 \00A0';
        }
    }

    // Float right with text-align: right
    &.pull-right {
        float: right;
        padding-right: 15px;
        padding-left: 0;
        border-right: 5px solid $gray-lighter;
        border-left: 0;

        p,
        small {
            text-align: right;
        }
        small {
            &:before {
                content: '';
            }
            &:after {
                content: '\00A0 \2014';
            }
        }
    }
}

q:before,
q:after,
blockquote:before,
blockquote:after {
    content: '';
}

address {
    display: block;
    margin-bottom: $line-height-base;
    font-style: normal;
    line-height: $line-height-base;
}

//Images

img.right {
    margin: 0 0 0 10px;
    float: right;
}

img.left {
    margin: 0 10px 0 0;
    float: left;
}

img.no-border {
    border: none;
}

a img {
    border: none;
}

object,
img {
    margin: 0 0 14px 0;
    max-width: 100%;
}

.pub-date {
    margin: 0;
    font-style: italic;
}

#content.testimonials {
    padding: 0 0 20px 0;
}

#content.standard {
    padding: 0 $responsive-padding;
    table {
        max-width: 500px;
    }
}

#content.image-gallery {
    padding: 0 0 20px 0;
    width: 780px;
    margin: 0;
}

.js #content .youtube,
.js #content .fms {
    display: none;
}

/*styles for labs charts*/
#labs,
.labs {
    display: none;
}

.js #labs,
.js .labs {
    display: block;
    @include loading-fa-icon-content;
    background: #fafafa;
    margin: 0 0 10px 0;
    p {
        padding: 20px;
        display: block;
    }
}
