.course-finder {
    input,
    select {
        font-size: 16px;
    }
}

.course-finder__query-wrapper {
    margin-top: -40px;
    background: #cccccc;
}

.course-finder__form-wrapper {
    background: #eeeeee;
}

.course-finder__query {
    .query-box {
        padding-right: 44px;
        position: relative;
    }
    input {
        padding: 10px 10px 9px 10px;
        width: 100%;
        border: 1px solid #a9a9a9;
        font-size: 16px;
    }
    button {
        position: absolute;
        top: 0;
        right: 0;
        width: 45px;
        height: 45px;
        background: white;
        border: 1px solid #a9a9a9;
    }
}

.course-finder__clear-buttons {
    display: none;
    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        display: block;
    }

    > div {
        display: inline-block;
    }

    a {
        display: inline-block;
        padding: 5px 10px;
        border: 1px solid;
        border-radius: 3px;
        margin: 10px 10px 0 0;

        &:link,
        &:visited {
            background: #b91b1c;
            color: white;
            border-color: #b91b1c;
        }

        &:hover {
            background: #f22428;
            color: white;
            border-color: #f22428;
        }

        &:active,
        &:focus {
            background: white;
            color: #b91b1c;
            border-color: #b91b1c;
        }
    }
}

.course-finder__results {
    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        padding-left: 20px;
    }
}

.course-finder__results__summary {
    font-weight: 600;
    margin-bottom: 14px;
}

.course-finder__results__summary__refresh {
    display: inline-block;
    color: #b91b1c;
    margin-left: 5px;
}

.finder__results__headers {
    padding: 20px;
    font-weight: 600;
}

$course-finder-result-line-height: 28px;
$course-finder-result-grey: #666;
$course-finder-result-grey-fw: 600;

.course-finder__results__item {
    background: white;
    padding: 10px;
    margin-bottom: 30px;
    position: relative;

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        padding: 20px;
    }

    h2 {
        margin: 0 48px 0 0;
        font-size: 1.3em;
        line-height: $course-finder-result-line-height;

        a {
            font-weight: 500;
            @include anchor-base;
            @include anchor(default);
        }
    }
}

.course-finder__results__item--clearing {
    @include topLeftBorderCorner($borderColour: #0377a0);
}

.course-finder__results__item__level {
    padding: 3px 0;
    font-weight: $course-finder-result-grey-fw;
    color: $course-finder-result-grey;
}

.course-finder__results__item__award {
    font-weight: $course-finder-result-grey-fw;
    color: $course-finder-result-grey;
}

.course-finder__results__item__md {
    margin-bottom: 5px;

    > .fa {
        float: left;
        line-height: 21px;
    }

    > div {
        margin-left: 22px;
    }
}

.course-finder__results__item__info {
    display: none;

    margin-top: 10px;
    border-top: 2px solid #eee;

    &.course-finder__results__item__info--open {
        display: block;
    }
}

.course-finder__results__item__description {
    margin: 10px 0;
}

.course-finder__results__item__md span {
    color: $course-finder-result-grey;
}

.course-finder__results__item__md__label {
    font-weight: 600;
}

@include breakup-breakpoint('thinish_extended_2') {
    // 600px
    .course-finder__results__item__info-toggle {
        display: none;
    }

    .course-finder__results__item__info {
        display: block;
    }
}

.course-finder__results__item__info-toggle {
    position: absolute;
    top: 10px;
    right: 10px;

    padding: 5px 15px;
    border-radius: 5px;
    border: none;
    background: #b91b1b;
    color: white;

    transition: color 100ms, background 100ms;

    .fa {
        transition: transform 300ms;
    }

    &:active,
    &:focus {
        background: #eee;
        color: #333;
    }
    &:hover {
        background: #eee;
        color: #b91b1b;
    }
}

.course-finder__filter--clearing {
    font-weight: 600;
    color: #0377a0;
}

.course-finder-test {
    #primary-nav-toggler,
    #slice-navigation {
        display: none;
    }
}

.course-finder__bottom-banner {
    max-height: 250px;
}

.course-finder__bottom-banner__img {
    width: 100%;
    margin: 0;
}
