.image-accordion {
    width: 100%;
    margin: 1em auto 1.5em;
    position: relative;

    div {
        width: 100%;
        float: left;
        overflow: hidden;
        position: relative;
    }

    input[type='radio'] {
        @include opacity(0);
        position: absolute;
        top: 3.9em;
        right: 110%;
    }

    h3 {
        float: left;
        width: 100%;
        display: inline-block;
        padding: 1em 0;
        margin: 0;
    }

    .image-accordion-navigation,
    .image-accordion-text {
        width: 100%;
        float: left;
        padding: 0;
        margin: 0;
        list-style: none;
    }

    .image-accordion-navigation {
        li {
            color: $white;
            width: 31.75%;
            float: left;
            display: inline-block;
            position: relative;
            overflow: hidden;
            margin-left: 2.375%;
            &:first-child {
                margin-left: 0;
            }
        }

        &.image-accordion-navigation--n4 li {
            $margin: 2.375%;
            width: (100% - 3 * $margin) / 4;
            margin-left: $margin;
            &:first-child {
                margin-left: 0;
            }
        }

        img {
            width: auto;
            width: 100%;
            height: auto;
            display: block;
            margin: 0;
            z-index: 1;
        }

        label {
            width: 100%;
            height: 100%;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            cursor: pointer;
            z-index: 2;
        }

        div {
            width: 100%;
            height: 3em;
            position: absolute;
            bottom: 0;
            left: 0;
            line-height: 1.5em;
            font-weight: bold;
            text-align: center;
            text-shadow: 0 0.1em 0.5em $black;
        }
        .tall div {
            height: 4.5em;
        }
        span {
            width: 100%;
            position: absolute;
            left: 0;
            top: 50%;
            @include translate(0, -50%);
        }
    }

    .lt-ie9 & .image-accordion-navigation span {
        top: 0;
    }

    .image-accordion-toggle.checked ~ .image-accordion-navigation li,
    .image-accordion-toggle.checked ~ .image-accordion-navigation div {
        height: 4.5em;
    }

    .image-accordion-text {
        position: relative;
        > li {
            background: $sandyGrey;
            width: auto;
            padding: 1em;
            margin-top: 1em;
            display: none;
            position: relative;
        }

        ul {
            list-style-type: disc;
            padding-left: 1em;
            padding-right: 3em;
            margin: 0;
        }

        p {
            padding-right: 3em;
        }
    }

    .position-1.checked ~ .image-accordion-navigation .position-1,
    .position-2.checked ~ .image-accordion-navigation .position-2,
    .position-3.checked ~ .image-accordion-navigation .position-3,
    .position-4.checked ~ .image-accordion-navigation .position-4 {
        background-color: $red1;
        color: $white;
    }

    .position-1.checked ~ .image-accordion-text > .position-1,
    .position-2.checked ~ .image-accordion-text > .position-2,
    .position-3.checked ~ .image-accordion-text > .position-3,
    .position-4.checked ~ .image-accordion-text > .position-4 {
        display: block;
    }

    .image-accordion-cancel {
        background-color: $red1;
        color: $white;
        width: 30px;
        height: 30px;
        font-size: 2em;
        line-height: 28px;
        text-align: center;
        display: block;
        position: absolute;
        top: 0.5em;
        right: 0.5em;
        @include border-radius(3px);
        cursor: pointer;
        &:hover,
        &:focus {
            background-color: $logo-red;
        }
    }

    @media (max-width: 600px) {
        .image-accordion-navigation {
            li {
                color: $gray;
                width: 100%;
                height: 3em;
                background-color: $sandyGrey;
                background-image: none;
                border-bottom: 1px solid $white;
                margin-left: 0;
            }
            div {
                height: 3em !important;
                text-shadow: none;
                text-align: left;
            }
            span {
                width: 100%;
                padding: 0 2.5%;
            }
            img {
                display: none;
            }
        }
        .image-accordion-toggle.checked ~ .image-accordion-navigation li {
            height: 3em;
        }
    }

    @media (min-width: 601px) {
        .image-accordion-navigation {
            div {
                background-color: $gray;
                background-color: rgba($gray, 0.5);
                color: $white;
            }
        }
        .position-1.checked ~ .image-accordion-navigation .position-1,
        .position-2.checked ~ .image-accordion-navigation .position-2,
        .position-3.checked ~ .image-accordion-navigation .position-3,
        .position-4.checked ~ .image-accordion-navigation .position-4 {
            div {
                background-color: $red1;
                background-color: rgba($red1, 0.75);
            }
        }
    }
}
