@keyframes backgroundFadeIn {
    from {
        background-color: transparent;
    }
    to {
        background-color: rgba(141, 24, 42, 0.3);
    }
}
@keyframes userFadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.directory {
    margin: 0 0 4.5em;
}

.search-result--directory {
    list-style-type: none;
    margin-left: 0;
}

.directory-users {
    list-style-type: none;
    margin: 0 0 5em 0;

    .loading-fa-icon {
        text-align: center;
        font-size: 50px;
        color: #b02a1a;
        width: 100%;
    }
}

.directory-results-msg {
    margin: 2em 0;
    font-size: 1.1em;
}

.directory-user {
    margin-bottom: 4em;

    @include breakup-breakpoint('medium') {
        margin-bottom: 4em;
    }
}

.directory-user--loading {
    animation: 2s userFadeIn;
}

.directory-user__info {
    display: block;
    margin-bottom: 0.5em;
    font-size: 1.1em;
    margin-left: 0.6em;

    .fa {
        margin-right: 1em;
        font-size: 1.4em;
        width: 1em;
        text-align: center;
        vertical-align: middle;
    }
}

.directory-user__info--job {
    margin-bottom: 1em;
    margin-left: 0;
}

.directory-user__name {
    border-bottom: 2px solid #eee;
    padding-bottom: 0;
    margin-bottom: 0.9em;
    font-size: 1.6em;
}

.directory-user__name,
.directory-user__info--job,
.directory-user__info--room,
.directory-user__info--department,
.directory-results-msg {
    strong {
        background-color: rgba(141, 24, 42, 0.3);
        padding: 4px 8px 2px 8px;
        animation: 6s backgroundFadeIn;
    }
}

.search-form--directory {
    .search-query {
        border: 2px solid #ccc;
    }
    .search-form__query {
        width: 85%;
        float: left;
        font-size: 1.2em;
        padding: 0em 0.7em;

        @include breakup-breakpoint('thinish') {
            width: 90%;
            font-size: 1.5em;
        }
        @include breakup-breakpoint('wide') {
            width: 95%;
        }
    }
    .search-form__button {
        float: right;
        width: 15%;
        @include breakup-breakpoint('thinish') {
            width: 10%;
        }
        @include breakup-breakpoint('wide') {
            width: 5%;
        }
    }
    .search-form__icon {
        font-size: 1.3em;
    }
}

.directory .load-more {
    margin: 0;
}

.directory .fa-edit {
    float: left;
    font-size: 2em;
}

.directory__amend {
    margin-left: 4em;
}
