////
/// Course Base Styles
/// @group courses
////

/// @type Length
$measure-smaller: 5px;
/// @type Length
$measure-small: 10px;
/// @type Length
$measure-default: 20px; // equals Bootstrap gutter width
/// @type Length
$measure-large: 30px;
/// @type Length
$measure-larger: 40px;

$programme-specification-color: #a31c36;

.course {
    margin-top: -40px;

    &.fb-best-bet {
        margin-top: 0;
    }

    .banner-media {
        position: relative;
    }
}

.course h1 {
    margin-bottom: 0;
}

.course__subheader {
    @include font-family-meta-sans;
    @include font-shorthand(1.2em);

    $after-height: 6px;

    position: relative;
    margin-bottom: $measure-large - $after-height;

    &:after {
        content: '';
        display: block;
        width: ($measure-larger * 4);
        height: $after-height;

        clear: both;
        margin-top: $measure-small;
    }
}

.course__subheader__sep {
    color: #dddddd;
    margin: 0 $measure-default;
    padding-bottom: $measure-default;
}

.course__summary {
    @extend .standfirst;
}

.course__school {
    ul {
        list-style-type: none;
        margin-left: 0;

        li {
            margin-bottom: ($measure-small * 1.5);

            a {
                margin-left: 10px;

                &.course__school__noicon {
                    margin-left: 21px;
                }
            }
        }
    }

    &.course__school--inline li {
        display: inline-block;
    }
}

.course__clearing-cta {
    padding: $measure-default $measure-default 25px $measure-default;
    margin-bottom: 15px;

    color: $white;
    background: #0377a0;

    h2 {
        font-size: 1.7em;
        margin: 0;
        a {
            vertical-align: middle;
            font-size: 17px;
        }
    }

    a {
        @include anchor(course__cta);
        @include anchor(dark-background);
    }
}

.course__clearing-cta__tariff {
    margin-top: 1em;
    margin-bottom: 0;
}

.course__clearing-cta__tariff__ucas {
    display: block;
    font-weight: bold;
}

.course__clearing-cta__tariff__accepted {
    margin-left: 0.5em;
}

.course__key-information {
    color: $white;
    padding: $measure-default;

    h2 {
        font-size: 1.4em;
    }

    h3,
    h4 {
        @include font-family-sans-serif;

        font-size: 1em;
        font-weight: 600;
        color: #eee;
    }

    h4 {
        margin: 0;
    }

    h3 {
        margin-top: ($measure-small * 1.5);
        padding-bottom: $measure-smaller;
        border-bottom: 1px solid transparent;
        margin-bottom: $measure-smaller;
    }
}

.course__contact {
    display: flex;
}

.course__contact__item {
    display: inline-block;
    margin-top: $measure-small;

    & + & {
        margin-left: 1em;
    }
}

.course__contact__item__title {
    @include font-family-sans-serif;
    font-weight: 500;
    margin-bottom: $measure-smaller;
}

.course__contact__item__tel {
    position: relative;

    .fa-phone {
        position: absolute;
        top: 0;
        left: 0;
        line-height: 21px;
    }

    a {
        display: inline-block;
        margin-left: 21px;
        clear: both;

        &:before {
            content: '';
            width: 100%;
            display: block;
        }
    }
}

.course__contact__item__email {
    .course__contact__item__tel + & {
        margin-top: 0.5em;
    }
}

/// KIS widget

.course__kis {
    position: relative;
    padding: ($measure-default + 15px) 0 ($measure-default);
    overflow: hidden;
    min-height: 230px;
}

.course__kis__image {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;

    .course__kis--vertical & {
        max-width: none;
    }
}

.course__kis__content {
    position: relative;

    iframe {
        position: relative;
        left: 50%;
        margin-left: -307px;
        display: block;
    }

    .course__kis--vertical & iframe {
        margin-left: -95px;
    }
}

.course__news-item__cat {
    color: $white;
    margin-top: -25px;
    text-transform: uppercase;
    padding: 0 $measure-smaller;
    font-size: 0.8em;
    position: absolute;
}

.course__event {
    clear: both;
    margin-bottom: $measure-default;
}

.course__event__date {
    padding: $measure-smaller $measure-small;

    text-align: center;
    color: $white;
    font-weight: 600;

    float: left;
}

.course__event__text {
    margin-left: 60px;
}

.course__event__text__ww {
    color: #555555;
}

.course__section__separator {
    padding-bottom: $measure-default;
    border-bottom: 6px solid #eeeeee;
    margin-bottom: $measure-large;
}

.course__related__course {
    background-color: #fff;
    padding: 20px 20px;
    margin: 10px 0;

    @include breakup-breakpoint('medium') {
        min-height: 210px;
        margin: 10px 10px 10px 0;
    }
}

.course__related__header {
    width: 100%;
    text-align: center;
}

.course__related__course__title {
    margin-bottom: $measure-smaller;
    font-size: 1.1em;
}

.course__related__course__description {
    color: #555555;
}

.course__related__course__footnote {
    color: #555555;
    margin-top: 20px;
}

@import 'course-base-profiles';

.course__employment {
    margin-top: $measure-default;
}

.course__employment__info {
    margin-top: $measure-larger;

    h3 {
        font-size: 1.2em;
    }
}

.course__employment {
    position: relative;
}

.course__employment__main {
    position: absolute;
    z-index: 1;
    margin: 30% 25% 0;

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        margin-top: 20%;
    }

    @include breakup-breakpoint('wide') {
        // 980px
        margin-top: 12%;
    }
    @include breakup-breakpoint('wider') {
        // 1200px
        margin-top: 25%;
    }
}

.course__employment__main__text,
.course__employment__main__value {
    display: block;
    text-align: center;
}

.course__employment__main__value {
    font-size: 2.5em;
    margin-bottom: $measure-smaller;
}

.course__employment__main__text {
    color: #555555;
    font-size: 0.9em;
    font-weight: 600;
}

.course__employment__chart {
    position: relative;
    padding-bottom: 100%;

    /*
    google visualization tooltip flickering
    https://github.com/google/google-visualization-issues/issues/2162
    */
    div.google-visualization-tooltip {
        pointer-events: none;
    }
    svg > g > g:last-child {
        pointer-events: none;
    }
}

.course__employment__chart__container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.course .fw-section-wrapper {
    padding: $measure-larger 10px;

    &.course__banner-section {
        padding-top: 0;
    }

    &.usps__wrapper {
        padding: 0 10px;
        @include breakup-breakpoint('thinish_extended_2') {
            // 600px
            padding: $measure-default 10px;
        }
    }

    .section-heading {
        text-align: center;
        margin-top: -90px;
        margin-bottom: 30px;
        clear: both;

        i {
            color: white;
            font-size: 23px;
            position: relative;
            z-index: 2;
            top: -41px;
        }

        // Position adjustment due to slight differences in icon size
        i.fa-graduation-cap {
            left: -53px;
        }
        i.fa-clipboard {
            left: -50px;
        }
        i.fa-briefcase {
            left: -52px;
        }

        span {
            font-size: 2em;
            font-weight: 500;
            line-height: 1.4em;
            margin: -22px 0 -7px;
            display: block;
            font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                sans-serif;
        }

        .section-break {
            background: #b92a26;
            height: 4px;
            width: 60px;
            display: inline-block;
        }
    }
}

.phd-projects.phd-projects {
    h2,
    h3 {
        font-weight: 400;

        a {
            border-bottom-width: 0;

            span {
                border-bottom: 0 solid $anchor-color;
            }

            &:hover,
            &:focus,
            &:active {
                text-decoration: none;
            }

            &:hover {
                color: $anchor-color;

                span {
                    border-bottom-width: 2px;
                }
            }

            &:focus,
            &:active {
                background-color: transparent;
                color: $anchor-color;

                span {
                    border-bottom-width: 2px;
                    border-bottom-style: dotted;
                    border-bottom-color: $grey5;
                }
            }

            .fa {
                margin-left: 0.5rem;
                border-bottom-width: 0 !important;
            }
        }

        .fa-chevron-circle-right {
            float: right;
            margin-top: 0.3em;
        }
    }

    h2 {
        display: inline-block;
        margin-bottom: 0;
        font-size: 34px;

        a {
            color: $black;
        }

        .fa-chevron-circle-right {
            font-size: 28px;
        }
    }
}

.course__white-box {
    background-color: $white;
    padding: $measure-small $measure-default;
    margin-bottom: $measure-default;

    &.course__white-box--top-media {
        padding-top: 0;
    }
}

.course__white-box__media-label {
    font-style: italic;
    margin-top: -10px;
}

.course__white-box__content--read-more {
    max-height: 100px;
    overflow: hidden;
    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        max-height: none;
    }
}

.course__white-box__read-more {
    margin-top: -35px;

    .course__white-box__read-more__bg {
        height: 35px;
        position: relative;
        background-color: transparent;
        background-image: linear-gradient(
            to bottom,
            rgba(255, 255, 255, 0),
            rgba(255, 255, 255, 1)
        );
    }
    a {
        display: block;
        text-align: center;
        font-weight: 600;

        &:link,
        &:visited,
        &:hover {
            border-bottom: none;
        }

        padding: 10px 0;
        border-top: 1px solid #ccc;

        span {
            border-bottom: 1px dotted;
        }
        &:hover span {
            border-bottom: 1px solid #670003;
        }
        &:link span,
        &:visited span {
            border: none;
        }
    }

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        display: none;
    }
}

.course__white-box__media {
    margin: 0 (-$measure-default) $measure-default;

    img {
        margin: 0;
        width: 100%;
    }
}

.course .accordion.ui-accordion {
    @include accordion(16px, 16px, 14px);
}

.course__entries {
    display: inline-block;

    select {
        background-color: #eee;
    }
}

.course__accredited-by__img {
    height: 80px;
    margin: 0 $measure-small $measure-default 0;
}

.course .fw-section-wrapper.course__bottom-ctas {
    padding-top: 0;
    background: #333333;
    color: white;

    h2 {
        margin: $measure-larger 0 $measure-small;
        padding: 0 0 $measure-small;
        border: 0;
        font-size: 20px;
        line-height: normal;
        font-weight: 400;
        @include font-family-meta-sans;
    }

    i {
        margin-right: $measure-smaller;
    }
}

.course {
    .anchor-separator {
        height: 0;
        overflow: visible;
        > div {
            position: relative;
        }

        a {
            display: block;
            position: absolute;
            width: 30px;
            height: 30px;
            border: 1px solid #efefef;
            right: 0;
            top: -15px;
            text-align: center;
            line-height: 30px;
            background: white;
            font-size: 20px;

            &:link,
            &:visited {
                color: black;
            }
            &:hover,
            &:focus {
                color: #b91b1b;
            }
            &:active {
                color: white;
                background: #b91b1b;
            }
        }
    }

    .anchor-separator--line {
        height: 1px;
        background: #efefef;
    }

    .anchor-separator--nfw-line {
        height: 5px;
        background: #eee;
        z-index: -1;
    }
}

.course__menu__wrapper {
    margin: ($measure-large) 0;
}

.course__menu {
    max-width: 300px;
    border: 1px solid #cdcdcd;
    background: #eee;
    padding: $measure-smaller $measure-small;
}

.course__menu__title a {
    float: right;
}

.course__menu__content {
    padding-top: $measure-small;
    clear: both;
}

.js {
    .course__assessment__list {
        display: none;
    }

    .course__menu__content {
        display: none;

        li {
            margin-bottom: $measure-smaller;
        }
    }

    .course__apply-dialog-section {
        display: none;
    }
}

.no-js {
    .course__entries {
        select {
            display: none;
        }

        a {
            margin-left: $measure-small;
        }
    }

    .course__entries__selected {
        color: #333333;
        font-weight: 600;
        border: 0;
    }

    .course__menu__title a {
        display: none;
    }

    .course__profiles__item {
        display: inline-block;
    }
}

/* Programme specification download links */
.course__modules__reading--v2 {
    border: 1px solid $programme-specification-color;
    display: flex;
    margin-bottom: 15px;
}

.programme__specification__container.icon {
    padding: 1% 2%;
    font-size: 18px;
    color: $programme-specification-color;
}

.programme__specification__container.text {
    border-left: 1px solid $programme-specification-color;
    padding: 1.4% 2%;
}
