$slp-sass-1: #f15f22;
$slp-sass-2: #f8b141;
$slp-health-1: #00284c;
$slp-health-2: #0077a0;
$slp-smcse-1: #00444d;
$slp-smcse-2: #649a44;
$slp-law-1: #401663;
$slp-law-2: #842b91;

.link-list--school-landing-page {
    padding-top: 10px;
    margin-bottom: 30px;

    @mixin slp-ll-hl($c1, $c2) {
        h2 {
            a:link,
            a:visited {
                color: #333;
                border-bottom: 2px solid $c1;
            }

            &:first-child {
                a:link,
                a:visited {
                    border-bottom-color: $c2;
                }
                a:hover {
                    border-bottom-color: #000;
                }

                a:focus,
                a:active {
                    border-bottom-color: #999;
                }
            }

            a:hover {
                color: #333;
                border-bottom-color: $c2;
            }

            a:focus,
            a:active {
                border-bottom: #999 dotted 2px;
                background: none;
            }
        }
    }

    h2 {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;

        .fa {
            margin-left: 20px;
        }
    }

    &.link-list--school-landing-page--sass {
        @include slp-ll-hl($slp-sass-1, $slp-sass-2);
    }

    &.link-list--school-landing-page--health {
        @include slp-ll-hl($slp-health-1, $slp-health-2);
    }

    &.link-list--school-landing-page--smcse {
        @include slp-ll-hl($slp-smcse-1, $slp-smcse-2);
    }

    &.link-list--school-landing-page--law {
        @include slp-ll-hl($slp-law-1, $slp-law-2);
    }

    ul:not(:last-child) {
        margin-bottom: 30px;
    }
}

.school-landing-ctas {
    padding: 10px 20px 10px;
    background: #eee;
    h2 {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        margin-bottom: 20px;
    }

    &.school-landing-ctas--sass {
        a {
            display: block;
            margin-bottom: 16px;
            padding: 8px;
            font-size: 1.1em;
            font-family: Arial, Helvetica, sans-serif;
            font-weight: 500;
            background: #fff;
            border: 2px solid #fff;

            .fa {
                color: $slp-sass-1;
                margin-right: 10px;
            }

            &:link,
            &:visited {
                color: #333;
            }

            &:hover {
                .label {
                    border-bottom: #000 solid 1px;
                }
            }

            &:focus,
            &:active {
                border-color: $slp-sass-1;
            }
        }
    }

    &.school-landing-ctas--smcse {
        a {
            display: block;
            margin-bottom: 16px;
            padding: 8px 8px 8px 2em;
            font-size: 1.1em;
            font-family: Arial, Helvetica, sans-serif;
            font-weight: 500;
            background: #fff;
            border: 2px solid #fff;

            .fa {
                position: absolute;
                color: $slp-smcse-1;
                margin-right: 10px;
                top: 15px;
                left: 20px;
            }

            &:link,
            &:visited {
                color: #333;
            }

            &:hover {
                .label {
                    border-bottom: #000 solid 1px;
                }
            }

            &:focus,
            &:active {
                border-color: $slp-smcse-1;
            }
        }
    }

    &.school-landing-ctas--health {
        background: $slp-health-1;
    }

    &.school-landing-ctas--law {
        background: $slp-law-1;
    }

    &.school-landing-ctas--health,
    &.school-landing-ctas--law {
        h2 {
            color: white;
        }

        a {
            display: block;
            position: relative;
            background: none;
            margin-bottom: 20px;
            color: white;
            border: none;
            font-size: 1.1em;

            img {
                margin: 0;
                display: none;
            }

            &:link {
                color: white;
                span {
                    border-bottom: 1px dotted white;
                }
            }

            &:hover {
                span {
                    border-bottom: 1px solid white;
                }
            }

            &:focus,
            &:active {
                span {
                    border-bottom: 1px solid white;
                    background: white;
                    color: #555;
                }
            }
        }
    }
}

.section-white-cards--sass {
    .cta-label-icon .fa {
