#promo-wrapper .banner-n.banner--slp {
    text-align: center;

    .banner-text {
        position: relative;
        display: inline-block;
        max-width: 80%;
        top: auto;
        left: auto;
        background: #eee;
        color: #000;
        padding-left: 15px;
        padding-right: 15px;

        img {
            max-width: 200px;
            margin-bottom: 0;
        }

        margin-top: -12%;

        @include breakup-breakpoint('thinish_extended_2') {
            // 600px
            padding-left: 40px;
            padding-right: 40px;

            margin-top: -8%;
        }
    }

    &.banner--slp--sass {
        .ghost-cta {
            &:hover {
                color: #0f0602;
                background: #ff7b42;
                border-color: #bd4a1c;
            }

            &:focus,
            &:active {
                color: #0f0602;
                background: #f3753f;
                border-color: #eee;
            }
        }
    }

    &.banner--slp--health {
        .ghost-cta {
            &:hover {
                color: white;
                background: $slp-health-1;
                border-color: $slp-health-1;
            }

            &:focus,
            &:active {
                color: white;
                background: $slp-health-2;
                border-color: #eee;
            }
        }
    }

    &.banner--slp--smcse {
        .ghost-cta {
            &:hover {
                color: white;
                background: $slp-smcse-1;
                border-color: $slp-smcse-1;
            }

            &:focus,
            &:active {
                color: white;
                background: $slp-smcse-2;
                border-color: #eee;
            }
        }
    }
    &.banner--slp--law {
        .ghost-cta {
            &:hover {
                color: white;
                background: $slp-law-1;
                border-color: $slp-law-1;
            }

            &:focus,
            &:active {
                color: white;
                background: $slp-law-2;
                border-color: #eee;
            }
        }
    }
}

.usps__wrapper.usps__wrapper--slp {
    padding-bottom: 0;
}
