///
// Job Outlook Progress Bar
///

$progress-human-width: 25px;
$progress-bg: 'i/cityventures/progress-bg';

.job-outlook {
    appearance: none;
    height: 34px;
    display: block;
    position: relative;

    @for $max from 1 through 10 {
        $width: $max * $progress-human-width;

        &[max='#{$max}'] {
            width: $width;
        }
    }

    &:before {
        content: '';
        background-image: url($progress-bg);
        opacity: 0.5;
        width: 100%;
        height: 100%;
        display: block;
        position: absolute;
        top: 0;
        left: 0;
    }

    &::-webkit-progress-bar {
        background-color: transparent;
    }

    &::-webkit-progress-value {
        background-color: transparent;
        background-image: url($progress-bg);
    }

    // fix widget appearance
    .widget & + h2 {
        background-color: transparent;
        padding-left: 0;
        padding-right: 0;
    }
}
