.image-carousel {
    margin-top: 0;

    position: relative;

    .bx-controls {
        display: block;
    }

    .bx-controls-direction {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;

        height: 0;
        margin-top: 28%;

        a {
            display: block;
            position: absolute;
            top: -24px;
            border: none;
            font-size: 16px;
            padding: 12px 16px;
            color: white;

            &.bx-prev {
                left: 0;
            }
            &.bx-next {
                right: 0;
            }

            &:link,
            &:visited {
                background: #243c51;
                color: white;
            }

            &:active,
            &:focus {
                background: #30516f;
            }
        }
        @include breakup-breakpoint('thinish') {
            // 480px
            a {
                top: -30px;
                font-size: 20px;
                padding: 15px 20px;
            }
        }

        @include breakup-breakpoint('thinish_extended_2') {
            // 600px
            margin-top: 14%;

            a {
                font-size: 30px;
                padding: 15px 25px;
                opacity: 0.6;
                top: -42px;

                &:hover {
                    opacity: 1;
                }

                &.bx-prev {
                    left: auto;
                    right: 75%;
                    margin-right: 15px;
                }
                &.bx-next {
                    right: auto;
                    left: 75%;
                    margin-left: 15px;
                }
            }
        }
    }
}

.image-carouse__item {
    position: relative;
    margin-bottom: 10px;
    padding: 0;

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        padding: 0 10px;
    }

    .bx-viewport & {
        //margin-left: 25%;
    }

    img {
        width: 100%;
        margin: 0;
    }

    h3 {
        margin: 5px 0 3px;
    }

    transition: opacity 0.1s;
    opacity: 0.3;

    .image-carousel__item_textbox {
        opacity: 0;
    }

    &.image-carouse__item--active {
        opacity: 1;

        .image-carousel__item_textbox {
            opacity: 1;
        }
    }
}
