#promo-wrapper {
    margin: 20px 0 10px 0;
    font-size: 0.9em;
    .banner {
        @include gradient-vertical(#333, #000);
        overflow: hidden;
        color: $white;
        display: block;
        padding: 7px 7px 3px 7px;
        margin-bottom: -8px;
        h2 {
            font-size: 1.3em;
        }
    }
    .banner-media {
        float: left;
        display: none;
    }
    .banner-text {
        float: left;

        a {
            color: $white;
        }
    }
    &.promo-wrapper--single {
        margin-bottom: 0;
    }
}

.js .bxslider,
.js .bx-controls {
    display: none;
}

.loading-fa-icon {
    display: none;
}

.carousel-loading {
    height: 200px;
    position: relative;

    .loading-fa-icon {
        display: block;
        @include loading-fa-icon;
    }
}

.no-js .carousel-loading {
    height: auto;
    background-image: none;
}

/* carousel */
.bx-wrapper,
.no-js .bxslider {
    position: relative;
    padding: 0;
    *zoom: 1;
}

#promo-wrapper,
.no-js #promo-wrapper {
    position: relative;

    .bx-viewport,
    li {
        @include gradient-vertical(#333, #000);
    }
    &.custom-carousel {
        .bx-viewport,
        li {
            background: none;
        }
    }
    #promo-area {
        transform: none;
        background: #fff;
        -webkit-font-smoothing: subpixel-antialiased;

        margin: 0;
    }

    // .bx-controls contains all carousel controls
    .bx-controls {
        padding: 10px;
        font-weight: bold;
        background-color: $black;
        @include clearfix;

        a {
            border-bottom: none;
        }
        .bx-prev {
            left: 0px;
            margin-right: 2px;
        }
        .bx-next {
            right: 0px;
        }
    }

    // pager (numbers)
    .bx-pager {
        float: right;
        color: #fff;
        @include clearfix;

        &.bx-default-pager a {
            background: $gray;
            display: block;
            outline: 0;
            color: white;
            padding: 9px 12px;
            margin: 0 1px;
            font-weight: bold;

            &:hover,
            &.active {
                background: #b02a1a;
            }
        }
    }
    .bx-pager-item,
    .bx-controls-auto-item {
        display: inline-block;
        float: left;
    }

    // next / prev buttons
    .bx-controls-direction {
        float: left;
        a {
            bottom: 0;
            outline: 0;
            z-index: 1;
            padding: 0 12px 0 12px;
            background-color: #333;
            display: block;
            color: #fff;
            font-size: 2em;
            float: left;
            text-align: center;

            &:hover {
                background-color: $link-color;
            }
        }
    }
    // start / stop buttons
    .bx-controls-auto {
        float: right;

        a {
            margin: 0 1px;
            padding: 6px 10px;
            display: block;
            background-color: $grey6;

            i,
            span.fa {
                color: #fff;
            }
        }
        .bx-stop:hover,
        .bx-stop.active {
            background-color: $link-color;
        }
        .bx-start:hover,
        .bx-start.active {
            background-color: $link-color;
        }
    }

    .bx-caption {
        position: absolute;
        bottom: 0;
        left: 0;
        background: #666;
        background: rgba(80, 80, 80, 0.75);
        width: 100%;
    }

    .bx-caption span {
        color: #fff;
        font-family: Arial;
        display: block;
        font-size: 0.85em;
        padding: 10px;
        top: 18px;
    }
    .carousel-text {
        padding: 10px 15px 0;
        background-color: transparent;
        h2 {
            font-size: 1.3em;
        }
    }
    .date {
        margin-top: 5px;
        font-size: 0.9em;
        width: 58px;
        margin-right: 10px;
        .date-day-no {
            font-size: 1.6em;
        }
    }

    //not sure .banner-content exists anymore.
    //needs removing??
    .banner-content {
        color: $white;
        p {
            margin-bottom: 5px;
            overflow: visible;
        }
        a {
            color: $white;
        }
    }
    .carousel-media {
        padding: 10px;
        p {
            margin: 0;
        }

        img {
            padding: 0;
            margin: 0;
            height: auto;
        }
        a {
            border: none;
            background-color: transparent;
        }
        display: none;
    }
}

.no-js .bxslider {
    list-style-type: none;
    margin-left: 0;

    li {
        padding-bottom: 15px;
        margin-bottom: 5px;
        overflow: hidden;
    }
}

.carousel-box-design {
    .bx-viewport,
    li {
        background-image: linear-gradient(
            to bottom,
            #fff 0%,
            #fff 100%
        ) !important;
    }

    .bx-controls-auto,
    .bx-pager-item {
        display: none !important;
    }

    .bx-controls-direction {
        a {
            background-color: rgba(249, 247, 247, 0.53) !important;
            border: none !important;
            color: #0d0c0c !important;
        }
    }

    .bx-controls {
        background-color: transparent !important;
    }

    .box-content-centered {
        display: table-cell;
        vertical-align: middle;
    }

    .boxes,
    .boxes-large,
    .boxes-small {
        text-align: center;
        float: left;
    }

    .box-2,
    .box-3,
    .box-4,
    .box-5 {
        color: #fff;
        display: table;
        margin: 10px;
        width: 100%;
        padding: 15px 10px;
    }

    .box-1 {
        background: #fff;
        margin: 0;
    }

    .box-2 {
        background: #464646;
    }

    .box-3 {
        background: #390334;
    }

    .box-4 {
        background: #ba2634;
    }

    .box-5 {
        background: #030303;
    }
}

@include breakup-breakpoint('thin') {
    #promo-wrapper {
        .bx-controls-auto {
            margin-right: 5px;

            a {
                padding: 9px 12px;
            }
        }

        .bx-pager-item a {
            padding: 13px 10px;
        }
    }

    .carousel-box-design {
        .boxes-small,
        .boxes {
            width: 100%;
        }
        li {
            height: 1300px;
        }
    }
}

@include breakup-breakpoint('thinish') {
    // 480px
    .no-js #promo-wrapper .carousel-text {
        padding: 10px 50px 0;

        h2 {
            font-size: 1.6em;
        }
    }
    #promo-wrapper {
        .bx-wrapper {
            padding: 0 10px;
        }
        .carousel-text {
            padding: 10px 50px 0;
            margin-top: 0px;
            h2 {
                font-size: 1.6em;
            }
        }
        .date {
            margin-top: 10px;
            font-size: small;
            width: 70px;
            margin-right: 20px;

            .date-day-no {
                font-size: 2.2em/1.1em Georgia, serif;
            }
        }
        .bx-controls-direction {
            float: none;

            a {
                top: 38.7%;
                bottom: auto;
                border: 1px solid $white;
                position: absolute;
                width: auto;
                height: 45px;
                padding: 2px 17px;
            }
        }
        .bx-controls {
            padding: 0px 12px 12px;
            .bx-next {
                right: -1px;
            }
            .bx-prev {
                left: -1px;
            }
        }
        &.custom-carousel {
            .bx-wrapper {
                padding: 0;
            }
        }
    }
    .carousel-box-design {
        li {
            height: 1200px;
            margin-left: 35px;
        }
        .boxes {
            width: 50%;
        }
        .boxes-wrap {
            width: 81%;
        }
        .boxes-small {
            margin-left: 10px;
            width: 30%;
        }
        .box1-slide2 {
            height: 564px;
        }
        .box-1 {
            height: 538px;
        }
    }
}

@include breakup-breakpoint('wide') {
    // 980px
    #promo-wrapper {
        .carousel-media {
            display: block;
            img {
                margin-top: 30px;
            }
        }
        .carousel-text {
            //margin-top: 20px;
            padding-left: 10px;
        }
        .bx-pager {
            position: relative;
        }
        .bx-controls-auto {
            position: relative;
        }
        .bx-controls {
            padding: 54px 12px 0;
        }
        .bx-controls-direction {
            a {
                top: 42%;
            }
        }
        .banner {
            margin-bottom: 0;

            .banner-media {
                display: block;
                margin-right: 40px;

                img {
                    margin-bottom: 0;
                }
            }
            .banner-text {
                margin-top: 20px;
                float: none;
                width: 95%;
            }
            h2 {
                font-size: 1.6em;
            }
        }
        .bx-controls {
            overflow: visible;
        }
        .bx-pager,
        .bx-controls-auto {
            bottom: 50px;
        }
    }

    .carousel-box-design {
        li {
            height: 900px;
            margin-left: 45px;
        }
        .box1-slide2 {
            height: 476px;
        }
        .box-1 {
            height: 442px;
        }
    }

    #promo-wrapper,
    .no-js #promo-wrapper {
        .carousel-media {
            display: block;
        }
    }
}

@include breakup-breakpoint('wide') {
    // 980px
    #promo-wrapper,
    .no-js #promo-wrapper {
        margin-bottom: -40px;
        overflow: hidden;
        width: 100%;
        padding-bottom: 0;
        font-size: 1em;
        .banner {
            margin-bottom: 40px;
        }
        .carousel-media {
            img {
                margin-top: 0px;
            }
        }
        h2 {
            font-size: 1.8em;
            margin: 0 0 2.04% 0;
        }
        .carousel-text {
            margin-top: 50px;
            padding-left: 0;
        }
        .bx-controls {
            background-color: transparent;
            overflow: inherit;
            padding: 0px 12px 0;
        }
        .bx-pager {
            position: relative;
            bottom: 56px;
        }
        .bx-controls-auto {
            position: relative;
            bottom: 56px;
        }
        .date {
            margin-top: 5px;
        }
        &.custom-carousel {
            margin-bottom: 0;
        }
    }
    .no-js #promo-wrapper {
        margin-bottom: 0px;
        .banner {
            margin-bottom: 0px;
        }
    }
    .carousel-box-design {
        margin-bottom: 0 !important;

        li {
            margin-left: 35px;
            height: 700px;
        }
        .boxes {
            width: 34%;
            margin-left: 10px;
        }
        .boxes-large {
            width: 50%;
        }
        .boxes-small {
            width: 20%;
        }
        .box1-slide2 {
            height: 557px;
        }
        .box5-slide2 {
            height: 218px;
        }
        .box6-slide2 {
            height: 178px;
        }
        .box7-slide2 {
            height: 142px;
        }
        .box5-slide1 {
            height: 157px;
        }
        .box6-slide1 {
            height: 184px;
        }
        .box7-slide1 {
            height: 142px;
        }
        .box-1 {
            height: 542px;
        }
        .box2-slide1 {
            height: 157px;
        }
    }
}

@include breakup-breakpoint('wide_extended') {
    .carousel-box-design {
        .box5-slide2 {
            height: 198px;
        }
    }
}

@include breakup-breakpoint('wider') {
    // 1200px
    #promo-wrapper,
    .no-js #promo-wrapper {
        .banner .banner-text {
            width: 90%;
        }
    }

    .carousel-box-design {
        .box3-slide1 {
            height: 184px;
        }
        .box7-slide1 {
            height: 143px;
        }
        .box5-slide2 {
            height: 143px;
        }
        .box7-slide2 {
            height: 123px;
        }
    }
}

.js .no-js-pager {
    display: none;
}

.no-js-pager {
    position: absolute;
    color: #fff;
    @include clearfix;
    border: 10px;
    font-weight: bold;
    left: 10px;
    bottom: 10px;

    span,
    a {
        display: inline-block;
        border: none;
        outline: 0;
        color: white;
        padding: 6px 10px;
        margin: 0 1px 0 0;
        font-weight: bold;
    }
    span {
        background: black;
    }
    a {
        background: $gray;

        &:hover,
        &.active {
            background: #b02a1a;
        }
    }
}

#promo-wrapper.carousel-box-design.accommodation-fair {
    img {
        margin: 0;
    }

    @include breakup-breakpoint('thin') {
        li {
            height: 1500px;
        }
    }

    @include breakup-breakpoint('thinish') {
        li {
            height: 1200px;
        }
    }

    @include breakup-breakpoint('medium') {
        li {
            height: 1000px;
        }
    }

    @include breakup-breakpoint('wide') {
        li {
            height: 650px;
        }

        .box5-slide1 {
            height: 162px;
        }

        .box6-slide1 {
            height: 227px;
        }

        .box7-slide1 {
            height: 217px;
        }
    }

    @include breakup-breakpoint('wide_extended') {
        // 1035px

        .box6-slide1 {
            height: 192px;
        }
    }

    @include breakup-breakpoint('wider') {
        // 1200px

        li {
            height: 600px;
        }

        .box7-slide1 {
            height: 197px;
        }
    }
}

// This needs to be fixed. It is way too overqualified because of legacy IDs.
.image-banner#promo-wrapper .banner {
    padding: 0;

    .banner-text {
        background-color: #00284c;
        color: $white;
        width: 100%;
        padding: 20px;
        margin: 0;
        @include breakup-breakpoint('wide') {
            background-color: rgba(#00284c, 0.7);
            width: auto;
            max-width: 50%;
            position: absolute;
            top: 1.429em;
            left: 1.429em;
        }
    }

    .banner-media {
        display: block;
        float: none;
        margin: 0;

        img {
            margin: 0;
        }
    }

    h2 {
        margin: 0;
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-weight: 200;
        font-size: 1.571em;
        line-height: 1.182em;
        @include breakup-breakpoint('wide') {
            font-size: 2.286em;
            line-height: 1.25em;
        }
        a {
            font-weight: inherit;
            &:hover,
            &:focus,
            &:active {
                border-bottom: 1px solid $white;
                text-decoration: none;
            }
        }
    }
    p {
        margin: 1em 0 0;
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-size: 1.143em;
        @include breakup-breakpoint('wide') {
            font-size: 1.286em;
            font-weight: 600;
        }
    }

    &.banner-red-bg .banner-text {
        background: #a22833;
    }

    &.banner-text-box-small .banner-text {
        h2 {
            font-size: 1.6em;
        }

        @include breakup-breakpoint('wide') {
            max-width: 30%;
        }
    }

    &.banner-text-box-very-small .banner-text {
        h2 {
            font-size: 1.3em;
        }

        @include breakup-breakpoint('wide') {
            max-width: 30%;
        }
    }
}

#promo-wrapper {
    margin-bottom: 0;

    .banner-n {
        background: none;
        border-radius: 0;
        height: auto;
        margin-bottom: 0;

        .banner-title,
        .banner-headline {
            @include font-family-meta-sans();
            font-size: 22px;
            margin-bottom: 14px;
        }

        .banner-title {
            font-weight: 400;
        }
        .banner-headline {
            font-weight: 200;
            line-height: 1.25em;
        }

        .banner-text {
            display: none;
            width: auto;
            float: none;
            padding: 10px 10px 0;
            background: #a22833;
            color: white;
        }

        .banner-image {
            img {
                margin: 0;
                width: 100%;
            }
            display: block;
            float: none;
            width: 100%;
        }
    }

    @include breakup-breakpoint('wide') {
        .banner-n {
            position: relative;

            .banner-text {
                display: block;
                position: absolute;
                top: 20px;
                left: 20px;
                padding: 20px 20px 6px;
                max-width: 30%;
            }
        }
    }
}
