#statistic-carousel {
    overflow: hidden;
    padding: 0 70px;

    &.carousel-loading {
        background-color: #fff;

        .bxslider {
            display: none;
        }
        .bx-controls {
            display: none;
        }
        .all-profiles {
            display: none;
        }
    }
    .bxslider {
        margin: 0;
        display: block;
    }
    .bx-wrapper ul {
        margin: 0;
        height: 500px;
    }
    .bx-viewport {
        z-index: 1;
    }
    .statistic {
        padding: 0;
        @include font-family-serif;
        font-size: 1.1em;
        overflow: hidden;

        a:hover {
            border: none;
        }
    }
    .statistic-inner-wrapper {
        padding: 10px 20px;
    }
    .stat-value-wrapper {
        padding: 10px 10px 0 10px;
        text-align: center;
        display: block;
        font-family: 'Times New Roman', Times, serif;

        span {
            display: block;
        }
    }
    .stat-description-wrapper {
        clear: both;
        @include font-serif();
    }
    .stat-value {
        font-size: 3.6em;
        line-height: 1.5em;
        margin: 3px 0 5px;
        font-weight: bold;
        font-style: italic;
    }
    .stat-source {
        color: #333;
        margin-top: 15px;
        display: inline;

        i {
            margin-left: 5px;
            vertical-align: middle;
        }
    }
    // styling for previous/next controls
    .bx-controls {
        display: block;
        position: absolute;
        top: 42%;
        width: 100%;
    }
    .bx-prev,
    .bx-next {
        display: block;
        width: 50px;
        height: 50px;
        position: relative;
        background-color: #333333;
        border-bottom: none;
        text-align: center;
        line-height: 61px;
        font-size: 2em;

        i {
            color: $white;
            font-size: 1.5em;
        }
    }
    .bx-prev {
        float: left;
        left: -70px;

        &.disabled {
            cursor: default;
            background-color: #d7d7d7;
        }
    }
    .bx-next {
        float: right;
        right: -70px;

        &.disabled {
            cursor: default;
            background-color: #d7d7d7;
        }
    }
    &.no-carousel {
        padding: 0;

        .bxslider {
            list-style-type: none;
            margin: 0;
        }
        .statistic {
            padding: 0;
            width: 100%;
            margin-bottom: 15px;
        }
    }

    @include breakup-breakpoint('thin') {
        // 320px
    }
    @include breakup-breakpoint('thinish') {
        // 480px
        &.no-carousel {
            .statistic-odd {
                float: left;
                width: 45%;
                clear: left;
            }
            .statistic-even {
                float: right;
                width: 45%;
            }
        }
    }
    @include breakup-breakpoint('thinish_extended') {
        // 500px
    }
    @include breakup-breakpoint('medium') {
        // 767px
        &.no-carousel {
            .statistic {
                float: left;
                width: 23%;
                margin-right: 14px;
                clear: none;
            }
        }
    }
    @include breakup-breakpoint('wide') {
        // 980px
        .statistic-inner-wrapper {
            margin: 0 10px;
        }
    }
    @include breakup-breakpoint('wide_extended') {
        // 1035px
    }
    @include breakup-breakpoint('wider') {
        // 1200px
        .statistic-inner-wrapper {
            margin: 10px 30px;
        }
    }
}
