.white-card {
    background-color: $white;
    padding: 10px 20px;
    margin-bottom: 20px;

    &.white-card--top-media {
        padding-top: 0;
    }

    h2 {
        font-size: 1.6em;
        font-weight: 500;
    }

    h3 {
        font-size: 1.4em;
        font-weight: 500;
    }
}

.white-card__media-label {
    font-style: italic;
    margin-top: -10px;
}

.white-card__content--read-more {
    max-height: 100px;
    overflow: hidden;
    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        max-height: none;
    }
}

.white-card__read-more {
    margin-top: -35px;

    .white-card__read-more__bg {
        height: 35px;
        position: relative;
        background-color: transparent;
        background-image: linear-gradient(
            to bottom,
            rgba(255, 255, 255, 0),
            rgba(255, 255, 255, 1)
        );
    }
    a {
        display: block;
        text-align: center;

        padding: 10px 0;
        border-top: 1px solid #ccc;
        border-bottom: none;

        &:link,
        &:visited {
            color: #222;
        }

        &:hover span {
            border-bottom: 1px solid #222;
        }
        &:active span,
        &:focus span {
            border-bottom: 1px solid #222;
        }
    }

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        display: none;
    }
}

.white-card__media {
    margin: 0 -20px 20px;

    img {
        margin: 0;
        width: 100%;
    }

    .embed-container {
        padding-top: 0;

        a {
            position: static;
            &:before {
                @extend .fa;
                @extend .fa-play;
                display: block;
                position: absolute;
                z-index: 1;
                left: 0;
                bottom: 0;
                font-size: 2em;
                padding: 0.5em;
                background: white;
            }

            &:link,
            &:visited {
                color: #333;
            }

            &:hover {
                color: #666;

                img {
                    opacity: 0.85;
                }
            }
        }
    }
}
