.courses-list {
    .results {
        list-style-type: none;
        margin: 0;
    }
    .course {
        margin-bottom: -1px;
        background-color: #f7f7f7;
        border: 1px solid #e7e7e7;
        padding: 15px 20px;

        &:hover,
        &:active {
            background-color: #eae9e7;
        }
    }
    .title {
        font-size: 1.2em;
    }
    .level {
        color: #666;
    }
    .summary {
        margin: 10px 0;
    }
    .course-link {
        background-color: #b02a1a;
        border: 0 none;
        border-radius: 2px;
        color: #fff;
        height: 2.45em;
        line-height: 2.45em;
        padding: 0;
        text-align: center;
        transition: all 0.2s ease 0s;
        display: inline;
        font-size: 0.85em;
    }
}

/* CSS for the CPD Course Search and Results */

#cpd-finder {
    /* Search Form */
    #fb-queryform {
        background: #eae9e7;
        margin: 10px 0 10px 0;
        padding: 10px;

        form {
            fieldset {
                border-style: none;

                p {
                    margin-top: 20px;
                }

                input.query {
                    width: 50%;
                }
            }
        }
    }

    /* Results Summary */
    #fb-matching {
        font-size: 0.95em;
    }

    /* Main Results Area */
    #fb-wrapper {
        ol {
            background: #f7f7f7;
            padding: 0;
            margin-left: 0;
            border: 1px solid #e7e7e7;

            li {
                position: relative;
                display: block;
                border-bottom: solid 1px #e7e7e7;
                margin-left: 0;

                a {
                    position: relative;
                    display: block;
                    color: #b02a1a;
                    text-decoration: none;
                    font-weight: bold;
                    padding: 7px 10px 7px 120px;
                    border-bottom: none;

                    &:hover,
                    &:focus,
                    &:active {
                        color: #333;
                        background: #e7e7e7;
                    }

                    span.level {
                        position: absolute;
                        left: 0;
                        top: 0;
                        font-weight: bold;
                        color: #666;
                        padding: 7px 10px 7px 10px;
                    }

                    span.award {
                        color: #666;
                        font-size: 0.8em;
                    }
                }
            }
        }

        /* Search Results Navigation */
        p.fb-page-nav {
            border-top: 1px solid #e7e7e7;
            font-size: 0.95em;
            padding-top: 10px;

            a {
                font-weight: bold;
                padding: 3px 5px;

                &:visited {
                    color: #b02a1a;
                }
                &:hover {
                    text-decoration: underline;
                }
            }

            .fb-previous-result-page {
                margin-right: 10px;
            }

            .fb-next-result-page {
                margin-left: 10px;
            }

            span {
                padding: 3px 5px;
            }
        }
    }
}

.course__assessment {
    margin: 0 0 100px;
}

/* stacked bar charts */

.courses__barchart {
    .courses__barchart__header {
        h3 {
            margin-bottom: 0;
        }

        margin-top: 30px;
    }
}

.bar-chart {
    width: 95%;
    margin-bottom: 60px;
}

.bar-chart__items {
    height: 30px;
    display: inline-block;
    color: white;
    border-right: 2px solid #fff;
}

.bar-chart__items--first {
    background-color: #da0060;
    float: left;
}

.bar-chart__items--second {
    background-color: #871e91;
    float: left;
}

.bar-chart__items--third {
    background-color: #00284b;
    float: left;
}

.bar-chart__items--fourth {
    float: left;
}

.bar-chart--variant-1 {
    .bar-chart__items--first {
        background-color: #008d2b;
    }

    .bar-chart__items--second {
        background-color: #00294a;
    }

    .bar-chart__items--third {
        background-color: #00505b;
    }

    .bar-chart__items--fourth {
        background-color: #cab133;
    }
}
