//
// new search form styles
//
.search-form {
    padding: 20px;
    @include gradient-vertical(#f5f5f5, #f1f1f1);
    margin: 1em 0 3em 0;
    border: none;
}

.search-form__fieldset {
    border: none;
    padding-top: 0;
    padding-bottom: 0;
}

.search-form__label {
    float: left;
    font-size: 1.5em;
    margin: 10px 10px 0 0;
    line-height: 1.2em;
}

.search-form__inputs {
    float: left;
    margin: 0;
    border: 1px solid #cccccc;
    margin-top: 40px;
    width: 207px;

    @include breakup-breakpoint('thinish') {
        margin-top: 0;
        width: 318px;
    }
    @include breakup-breakpoint('medium') {
        width: 608px;
    }
    @include breakup-breakpoint('wide') {
        width: 792px;
    }
}

.search-form__query {
    height: 40px;
    background: $white;
    padding: 5px;
    font-size: 1.4em;
    float: left;
    border: none;
    width: 167px;
    border-radius: 0;
    position: relative;

    @include breakup-breakpoint('thinish') {
        width: 277px;
    }
    @include breakup-breakpoint('medium') {
        width: 567px;
    }
    @include breakup-breakpoint('wide') {
        width: 752px;
    }
}

.search-form__button {
    height: 40px;
    color: #b02a1a;
    border: none;
    cursor: pointer;
    background-color: $white;
    padding: 0;
    width: 38px;
    position: relative;
}

.search-form__icon {
    font-size: 16px;
}

.search-result__query-result {
    font-size: 1.3em;
    margin: 1em 0 1em;
}

.search-result__extra-indication {
    border-top: 2px solid #ccc;
    padding-top: 2em;
    font-size: 1.3em;
    margin: 2em 0 1em;
}

.search-result__initial-search {
    font-weight: bold;
}

.search-result {
    list-style-type: none;
    margin-left: 0;
    margin-top: 3em;
    margin-bottom: 4em;
    width: 80%;
}

.search-result__list {
    margin: 0;
}

.search-result__item {
    margin-bottom: 2em;
    list-style: none;
}

.search-result__title {
    margin-bottom: 0;
}

.search-result__link {
}

.search-result__summary {
    margin-bottom: 0;
}

.search-result__display-url {
    color: #999;
    font-size: 0.9em;
    margin: 0;
}

.search-result__item--doc:before,
.search-result__item--pdf:before {
    display: block;
    content: '';
    width: 18px;
    height: 24px;
    float: left;
    margin-right: 0.5em;
}

.search-result__item--doc,
.search-result__item--docx {
    &:before {
        @include fa-pseudo-icon(file-o) margin-top: 2px;
        text-align: center;
    }
}

.search-result__item--pdf:before {
    background: url('//s1.city.ac.uk/cityr/i/icon-pdf.gif') no-repeat center
        center;
}

.search-form--directory {
    padding-left: 0;

    .search-form__inputs {
        margin-top: 0;
        width: 100%;
    }

    .search-form__query {
        width: 90.8%;
    }
}

.search-result--main-search {
    width: auto;

    .search-result__item {
        margin-left: 0;
    }
}

.search {
    .large-search {
        padding: 20px;
        @include gradient-vertical(#f5f5f5, #f1f1f1);
        margin: 10px 0 20px 0;
        border: none;

        p {
            margin: 0;
            border: 1px solid #cccccc;
            margin-top: 40px;
            width: 207px;
        }
        .query {
            height: 40px;
            background: $white;
            padding: 5px;
            font-size: 1.4em;
            float: left;
            border: none;
            width: 167px;
            border-radius: 0;
        }
        label {
            float: left;
            font-size: 1.5em;
            margin: 10px 10px 0 0;
        }
        .s-button {
            height: 40px;
            color: #b02a1a;
            border: none;
            cursor: pointer;
            background-color: $white;
            padding: 0;
            width: 38px;
            i {
                font-size: 16px;
            }
        }
    }
    #fb-matching {
        font-size: 1.3em;
        margin: 30px 0 40px;
        line-height: 1.3em;
        font-family: $font-family-sans;
        strong {
            font-weight: bold;
        }
    }
    #fb-spelling {
        display: block;
        margin: -10px 0 30px 0;
        font-size: 1.1em;
    }
    #fb-results {
        margin-left: 0;
        li {
            list-style-type: none;
            margin: 0 0 25px 0;
        }
        h3 {
            margin-bottom: 2px;
        }
        .summary {
            margin-bottom: 0;
        }
        .url {
            color: #999999;
            font-size: 0.9em;
            margin: 0;
        }
        i {
            float: left;
            margin-right: 10px;
            font-size: 1.2em;
            line-height: 1.5em;
            color: #666666;
        }
        .fb-filetype {
            float: left;
            margin-right: 10px;
            display: none;
        }
        .fb-filetype.fb-pdf {
            display: block;
            background: url('i/icon-pdf.gif') no-repeat center center;
            text-indent: -9999px;
            width: 18px;
        }
        i.icon-pdf {
            display: none;
        }
    }
    .fb-page-nav {
        line-height: 40px;
        color: #5b5a5a;
        margin: 30px 0;
        padding-left: 3px;

        a {
            @include border-radius(3px);
            padding: 10px 8px;
            font-size: 15px;
            border: none;
            font-weight: bold;
        }
        a:visited {
            color: $red1;
        }
        a:hover,
        a:active,
        a:focus {
            color: $white;
            background: $red1;
        }
    }
    .fb-current-result-page {
        font-size: 15px;
        padding: 2px 5px;
    }
    .fb-previous-result-page,
    .fb-next-result-page {
        margin: 0;
        display: inline-block;
        padding: 0;
        text-align: center;
        line-height: 25px;
    }
    #fb-facets,
    #fb-contextual-navigation-type,
    #fb-contextual-navigation-topic {
        margin: 0 0 20px 0;

        ul {
            list-style-type: none;
            margin-left: 0;
        }
        .facetLabel,
        h3 {
            font-family: Georgia, 'Times New Roman', Times, serif;
        }
        .category {
            margin-bottom: 5px;
        }
    }
    #fb-facets {
        margin-top: 30px;
    }

    @include breakup-breakpoint('thinish') {
        // 480px
        .large-search {
            p {
                margin-top: 0;
                width: 318px;
            }
            #search-query {
                width: 277px;
            }
        }
        .fb-page-nav {
            padding-left: 0;
            margin-left: -3px;

            a {
                padding: 2px 5px;
            }
        }
        .fb-previous-result-page,
        .fb-next-result-page {
            margin: 0;
            width: auto;
            height: auto;
            padding: 0;
            text-align: center;
        }
        .fb-previous-result-page {
            margin-right: 15px;
        }
        .fb-next-result-page {
            margin-left: 15px;
        }
    }
    @include breakup-breakpoint('thinish_extended') {
        // 500px
    }
    @include breakup-breakpoint('medium') {
        // 767px
        .large-search {
            p {
                width: 608px;
            }
            #search-query {
                width: 567px;
            }
        }
        #fb-wrapper {
            //watch out, we are hacking the grid so the filters
            //display after the results, although they appear before in the code
            .filter {
                float: left;
                margin-left: 0;
            }
            .results {
                float: right;
            }
        }
    }
    @include breakup-breakpoint('wide') {
        // 980px
        .large-search {
            p {
                width: 792px;
            }
            #search-query {
                width: 752px;
            }
        }
    }
    @include breakup-breakpoint('wide_extended') {
        // 1035px
    }
    @include breakup-breakpoint('wider') {
        // 1200px
    }
}

.fb-best-bet {
    background: #f7f7f7;
    padding: 20px;
    margin: 0 0 20px 0;
    position: relative;
    border: 1px solid #e7e7e7;

    span {
        font-size: 0.8em;
        position: absolute;
        top: 5px;
        right: 10px;
        color: #999;
    }
    h3 {
        margin: 0;
    }
    .summary {
        margin-bottom: 0;
    }
    .url {
        color: #999999;
        font-size: 0.9em;
        margin-bottom: 0;
    }
}

.search-feedback-survey {
    background: $city-purple;
    color: $white;
    font-family: 'ff-meta-web', Arial, Helvetica, sans-serif;
    font-size: 1.5em;
    padding: 1em;
    a {
        display: inline-block;
        border-bottom-width: 2px;
        border-bottom-style: dotted;
        text-decoration: none;
        &:link,
        &:visited {
            color: $gray-lighter;
            border-bottom-color: $grey3;
        }
        &:hover {
            color: $white;
            border-bottom-color: $white;
            border-bottom-style: solid;
        }
        &:focus,
        &:active {
            background-color: $white;
            color: $grey5;
            border-bottom-color: transparent;
        }
    }
}

.search-page {
    .search-form__query {
        width: 95%;
    }

    .search-form__button {
        width: 5%;
    }
}
