.subject-area {
    h2 {
        color: #444444;
        font-size: 2em;
        margin: 20px 0 15px;
    }
    h3 {
        margin: 15px 0;
    }

    .main-thumbnail {
        display: none;
        position: relative;

        img {
            display: block;
            margin: 0;
        }
    }

    #statistic-carousel {
        border-top: 5px solid #333;
        border-bottom: 5px solid #eae9e7;
        margin: 45px 0;
        padding-top: 50px;
        padding-bottom: 50px;

        &.no-carousel {
            margin: 10px 0;
        }
    }
    .future-student-button,
    .opendays-tours-button,
    .prospectus-button,
    .academics-button {
        padding: 14px 8px 14px 8px;
        margin-bottom: 5px;
        display: block;
        color: $white;
        border-bottom: none;
        @include transition(background-color 0.5s ease-out);

        span {
            border-left: 1px solid $white;
            padding-left: 10px;
            margin-left: 25px;
            display: block;
        }
        i {
            font-size: 1.2em;
            float: left;
        }
        &:after {
            @include fa-pseudo-icon(chevron-right);
            float: right;
            line-height: 0;
            margin-top: -9px;
        }
    }
    .future-student-button,
    .academics-button {
        background-color: #333333;

        &:hover {
            background-color: #505050;
        }
    }
    .opendays-tours-button {
        background-color: #285373;

        &:hover {
            background-color: #476c87;
        }
    }
    .prospectus-button {
        background-color: #4b0943;

        &:hover {
            background-color: #652c5e;
        }
    }
    .unit-lists {
        border: 1px solid #eae9e7;
        margin: 50px 0 0;
    }
    .research {
        border-top: 5px solid #eae9e7;
        border-bottom: 5px solid #eae9e7;
        margin: 60px 0;
        padding: 20px 0 45px;

        .unit-lists {
            margin-top: 0;
            margin-bottom: 20px;
        }
    }
    .accordion {
        margin-bottom: 20px;

        h2 {
            font-size: 1em;
        }
    }
    .contact-list {
        margin-top: 30px;
    }

    @include breakup-breakpoint('thinish_extended') {
        // 500px
        .standfirst {
            //@inlcude gradient-vertical(#fff, #000);
        }
        .person {
            margin-top: 0;
        }
        .description {
            font-size: 1.1em;
        }
    }
    @include breakup-breakpoint('medium') {
        // 767px
        position: relative;

        .main-thumbnail {
            margin: 0;
            display: block;
        }
        .presentation {
            margin-top: 20px;
        }
        .heading {
            background-color: rgba(0, 0, 0, 0.7);
            color: $white;
            display: inline-block;
            padding: 0 20px 0 10px;
            top: 80px;
            position: absolute;
        }
        h1 {
            padding: 5px 0;
            margin: 0;
        }
        .related-subjects {
            a {
                color: $white;

                &:hover {
                    border: none;
                }
            }
        }
        .person {
            text-align: center;

            .thumbnail {
                float: none;
                margin: 0;
            }
            .title {
                text-align: left;
                margin-bottom: 5px;
            }
            .description {
                text-align: left;
                margin-left: 10px;
                margin-bottom: 15px;
            }
            .all-profiles {
                text-align: left;
                margin-left: 10px;
                font-size: 0.9em;
            }
        }
    }
    @include breakup-breakpoint('wide') {
        // 980px
        .heading {
            top: 125px;
        }
        .presentation {
            width: 70%;
            margin-top: 20px;
        }
    }
    @include breakup-breakpoint('wider') {
        // 1200px
        .heading {
            top: 170px;
        }
    }
}

.js .subject-area {
    .unit-lists {
        .unit-lists-title {
            background-color: #eae9e7;
            padding: 11px 30px 12px 5px;
            cursor: pointer;
            margin: 0;
            position: relative;
            font-size: 1.3em;

            &:after {
                @include fa-pseudo-icon(caret-down);
                float: right;
                position: absolute;
                right: 21px;
                top: 10px;
            }
        }
        &.unit-lists-expanded {
            .unit-lists-title {
                &:after {
                    @include fa-pseudo-icon(sort-asc);
                }
            }
        }
        .unit-list,
        ul,
        ol,
        .unit-lists-content {
            padding: 0 10px;
            display: none;
            list-style-type: none;
            margin: 10px 0;
        }
        &.unit-lists-expanded {
            .unit-list,
            ul,
            ol,
            .unit-lists-content {
                display: block;
            }
        }
    }
}

.map-component {
    overflow: hidden;
    border-top: 5px solid #eae9e7;
    margin-top: 70px;

    h2 {
        padding-top: 26px;
    }

    .static-map-link {
        background-color: $white;
        padding: 5px 10px;
        float: left;

        a {
            color: #333333;
            border: none;
        }
        i {
            margin-left: 5px;
        }

        @include breakup-breakpoint('medium') {
            // 767px
            float: right;
        }
    }
}

.related-subjects {
    font-weight: bold;
    font-size: 1em;
}

