#subject-area-list {
    overflow: hidden;
    list-style-type: none;
    margin: 0;

    li {
        position: relative;
        margin-bottom: 20px;
    }
    a {
        border: none;
    }
    img {
        margin: 0;
        //margin: 0 0 0 30px;
    }
    h2 {
        background-color: rgba(0, 0, 0, 0.7);
        color: $white;
        margin: 0;
        padding: 10px 15px;
        bottom: 20px;
    }

    @include breakup-breakpoint('medium') {
        // 767px
        img {
            margin: 0 0 0 30px;
        }
        h2 {
            display: inline;
            position: absolute;
        }
    }
}

.subject-areas-lp {
    h1,
    h2 {
        font-family: 'ff-meta-web', Helvetica;
    }

    #subject-area-list2 {
        list-style-type: none;
        margin: 0 0 60px;
        border-top: 4px solid $gray-lighter;
        padding-top: 12px;

        a {
            font-size: 0.8em;
            font-weight: 600;

            color: #333;
            &:hover {
                color: $red1;
            }
            &:active,
            &:focus {
                color: white;
            }
        }

        h2 {
            margin: 0;
        }

        li {
            padding-bottom: 12px;
            border-bottom: 2px solid $gray-lighter;
            margin-bottom: 12px;
        }
    }

    #subject-area-course-finder {
        .label-line {
            height: 22px;
            font-style: italic;
        }

        #course-search-keyword {
            padding-left: 10px;
            line-height: 34px;
            color: #0377a0;
            font-size: 20px;
            width: 100%;

            &::-webkit-input-placeholder {
                /* WebKit browsers */
                color: #80dfff;
                font-weight: normal;
            }
            &:-moz-placeholder {
                /* Mozilla Firefox 4 to 18 */
                color: #80dfff;
                opacity: 1;
                font-weight: normal;
            }
            &::-moz-placeholder {
                /* Mozilla Firefox 19+ */
                color: #80dfff;
                opacity: 1;
                font-weight: normal;
            }
            &:-ms-input-placeholder {
                /* Internet Explorer 10+ */
                color: #80dfff;
                font-weight: normal;
            }
        }

        #course-search-level {
            padding: 10px;
        }

        button {
            border: none;
            padding: 10px 15px;
            width: 100%;
            text-align: left;

            color: white;
            background: #0377a0;
            border-radius: 10px;

            &:hover {
                background: #0488b0;
            }

            &:active,
            &:focus {
                background: #04a0d7;
            }

            i {
                float: right;
            }
        }
    }

    .new-widget {
        padding: 14px 20px 6px;
        margin-bottom: 20px;

        h2 {
            font-size: 1.2em;

            i {
                float: right;
            }
        }
    }

    .new-widget-blue {
        background: #0377a0;
        color: white;

        a {
            color: white;

            &:hover,
            &:active,
            &:focus {
                color: #bcc4ce;
            }
        }
    }

    .new-widget-gray {
        h2 {
            font-weight: 600;
        }

        background: #eae9e5;
    }
}
