////
/// Library Admin
/// @group sites
////

/**
 * New Library Admin-style Buttons
 */
.modal__box--delete button,
.event__add-exception,
.event__edit-exception {
    background-color: $gray;
    color: $white;
    display: block;
    margin: 0 auto;
    padding: 0.5em;
    border: 0;
    border-radius: 0.25em;
    cursor: pointer;

    &:hover,
    &:focus,
    &:active {
        text-decoration: underline;
    }

    &[disabled] {
        opacity: 0.25;
    }
}

/**
 * Main Calendar Layout
 */
.calendar {
    table-layout: fixed;

    table {
        border-collapse: separate;
        border-spacing: 0 0;
    }

    &.calendar--loading {
        background: url('i/ajax-loader-small-red.gif') no-repeat center center
            $white;
        height: 480px;

        #calendarTable,
        .prev-next-month {
            display: none;
        }
    }
    &.calendar--empty {
        &:after {
            content: 'No library id supplied.';
            display: block;
        }

        #calendarTable {
            display: none;
        }
    }

    th,
    td {
        border: 1px solid $grey4;
    }

    th {
        background-color: $grey3;
        padding: 1em;
        text-align: left;
    }

    td {
        padding: 2.5em 1em 1em;
        position: relative;
        font-style: italic;
        line-height: 1.5;
        text-shadow: 0 0 1px $white;

        &.hover {
            cursor: pointer;
        }
        &.hover,
        &.active {
            box-shadow: inset 0 0 0 4px rgba($black, 0.5);
        }

        &[data-special='closed'] {
            &.hover,
            &.active {
                box-shadow: inset 0 0 0 4px rgba($red2, 0.75);
            }
        }
        &[data-special='open24hours'] {
            &.hover,
            &.active {
                box-shadow: inset 0 0 0 4px rgba($green, 0.75);
            }
        }

        &[data-label='bank-holiday'] {
            background-image: repeating-linear-gradient(
                45deg,
                transparent,
                transparent 35px,
                rgba($white, 0.5) 35px,
                rgba($white, 0.5) 70px
            );
        }
        &[data-label='christmas-holiday'] {
            background-image: linear-gradient(
                    90deg,
                    rgba($white, 0.25) 50%,
                    transparent 50%
                ),
                linear-gradient(rgba($white, 0.25) 50%, transparent 50%);
            background-size: 50px 50px;
        }
        &[data-label='easter-holiday'] {
            background-image: radial-gradient(
                    rgba($white, 0.35) 15%,
                    transparent 16%
                ),
                radial-gradient(rgba($white, 0.35) 15%, transparent 16%);
            background-size: 50px 50px;
            background-position: 0 0, 25px 25px;
        }
        &[data-label='exam-period'] {
            background-image: linear-gradient(
                    45deg,
                    rgba($black, 0.1) 25%,
                    transparent 25%,
                    transparent 75%,
                    rgba($black, 0.1) 75%,
                    rgba($black, 0.1)
                ),
                linear-gradient(
                    -45deg,
                    rgba($black, 0.1) 25%,
                    transparent 25%,
                    transparent 75%,
                    rgba($black, 0.1) 75%,
                    rgba($black, 0.1)
                );
            background-size: 50px 50px;
        }

        &.today {
            .dateLink:after,
            .event-day:after {
                content: ' (Today)';
                color: $red;
            }
        }

        .event-day,
        .dateLink {
            position: absolute;
            top: 0.5em;
            left: 1em;
            font-weight: 600;
            font-style: normal;
        }

        .dateLink {
            &,
            &:hover {
                border: none;
            }
        }

        .event-times {
            font-size: 1.143em; // 16px
        }

        .event__add-exception,
        .event__edit-exception {
            margin-top: 1em;
            font-size: 0.857em;
        }

        // This could be solved some other way...
        br {
            display: none;
        }
    }

    .dayLabel {
        text-align: center;
    }

    .date {
        width: auto;
        display: table-cell;
        float: none;
        cursor: pointer;

        &:not(.eventDate):hover {
            background-color: $grey3;
        }
    }

    // Apply colors from around the color wheel to each unique Event, in order
    $num-unique-colors: 12;
    $slice-degree: 360 / $num-unique-colors;
    @for $i from 1 through $num-unique-colors {
        $hue: $slice-degree * $i;

        .event--#{$i} {
            background-color: hsl($hue, 50%, 75%);
        }
    }

    .event__add-exception,
    .event__edit-exception {
        opacity: 0.5;

        &:hover,
        &:focus,
        &:active {
            opacity: 1;
        }
    }
}

/**
 * Main Modal Styles
 */
.modal {
    background-color: rgba($white, 0.9);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.5em;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 420;

    &:not([class*='active']) {
        @extend .sr-only;
    }
}
.modal__box {
    background-color: $white;
    width: 100%;
    max-width: 28em;
    max-height: 100%;
    border-radius: 0.25em;
    position: relative;
    overflow-y: auto;
    box-shadow: 0 0 0.5em rgba($black, 0.5);
    z-index: 440;

    legend {
        color: $white;
        width: 100%;
        display: block;
        padding: 1.5em;
        border-radius: 0.25em 0.25em 0 0;
        font-style: italic;
        text-align: center;

        &.event--existing {
            background-color: $gray;
        }

        &.event--new {
            background-color: $green;
        }

        &.event--exception {
            background-color: $blue;
        }

        &.event--delete {
            background-color: $red;
        }
    }

    fieldset {
        padding: 1.5em 0;
        margin: 0 1.5em;
        border: 0;
        // This breaks the fluid nature of the modal box:
        // width: 28em;

        & ~ fieldset {
            border-top: 1px dotted $gray;
        }

        &.event--exception {
            &.modal__box__dates {
                span,
                .modal__box__end-day {
                    display: none;
                }
            }

            &.modal__box__recurrence-fieldset {
                display: none;
            }
        }

        &.text-center {
            text-align: center;
        }
    }

    legend + fieldset {
        padding-top: 1em;
    }

    select {
        border: 1px solid $gray;
        min-width: 6em;
        height: 2em;
    }

    label + select {
        float: right;
    }

    &.modal__box--loading {
        background: url('i/ajax-loader-small-red.gif') no-repeat center center
            $white;

        fieldset {
            opacity: 0.1;
        }
    }
}
.modal__close {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 430;
}

/**
 * Deletion-Variety Modal Box
 */
.modal--delete {
    @extend .modal;
    background-color: $black;
    z-index: 520;
}
.modal__box--delete {
    @extend .modal__box;
    max-width: 500px;
    z-index: 540;

    legend {
        font-weight: 600;
    }

    p:last-child,
    ol:last-child {
        margin-bottom: 0;
    }

    button {
        background-color: $red;
        padding: 1em;
        font-weight: 600;
    }
}
.modal__close--delete {
    @extend .modal__close;
    z-index: 530;
}
.modal__box--delete__exceptions {
    // Don't show the exceptions be default, until we know there are some—
    // this will be changed to `block` by the equivalent JS
    display: none;
}
