.fw-section-wrapper.usps__wrapper {
    padding: 0;
    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        padding: 20px 0;
    }
}

.usps__header {
    text-align: center;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 600;
    font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica, sans-serif;
    color: #000;
}

.usps__header__text {
    display: inline-block;
    padding: 0 40px;
    min-width: 200px;
    border-bottom: 2px solid #ccc;
}

.usps__wrapper__items {
    background: #eee;
    padding-bottom: 30px;
}

.usps__items {
    background: #fff;
}

.usps__item {
    text-align: center;
    font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica, sans-serif;

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
    }
}

.usps__item__value {
    padding: 20px 30px 5px;
    $usp-color: #000;
    font-weight: 300;
    font-size: 2.33em;

    @include breakup-breakpoint('thinish') {
        // 480px
        font-size: 3em;
    }
    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        font-size: 1.67em;
    }
    @include breakup-breakpoint('medium') {
        // 768px
        font-size: 2.33em;
    }
    @include breakup-breakpoint('wide') {
        // 980px
        font-size: 3em;
    }

    sup {
        font-size: 0.5em;
    }
}

.usps__item__text {
    padding: 0 30px 30px;
    color: #000;
    font-weight: 400;
}
