/*person module*/

/* styles to prevent elements from jumping around when js is available */
.js {
    .apply-filter {
        display: none;
    }
    .js-load-hide {
        @include loading-fa-icon-content;
        height: 200px;
        li {
            display: none;
        }
    }
}

/* profiles index pages */
.subfamily-choice {
    margin: 35px 0 0 0;
    padding: 0;
    overflow: hidden;
    font-size: 0.9em;

    li {
        display: block;
        float: left;
        margin-bottom: 20px;
    }
    a {
        color: $black;
        @include gradient-vertical(#f5f5f5, #f1f1f1);
        text-indent: 0;
        padding: 8px 25px;
        margin-right: 10px;
        display: block;
        @include border-radius(2px);
        border: 1px solid #ccc;
        font-weight: bold;
        font-size: 14px;
        letter-spacing: 0.02em;
        position: relative;

        &:hover,
        &.active {
            @include gradient-vertical(#333333, #333333);
            color: $white;
            border-color: $black;
            &:after {
                content: ' ';
                height: 0;
                position: absolute;
                width: 0;
                border: 18px solid transparent;
                border-top-color: #333333;
                top: 28px;
                right: 37%;
            }
        }
        &.active {
            padding-right: 15px;
            padding-left: 20px;
        }
        &:focus {
        }
        .fa-times {
            margin: 0 -8px 0 7px;
        }
    }
}

.display-filters {
    margin-left: 0;
    overflow: hidden;
    padding: 5px 0;
    li {
        float: left;
        list-style-type: none;
        margin-bottom: 10px;
        margin-top: 2px;
    }
    a {
        margin-right: 10px;
        cursor: pointer;
        background: none repeat scroll 0 0 #333333;
        border-radius: 3px;
        padding: 6px 10px;
        color: white;
        border-bottom: none;
    }
    .fa-times {
        margin: 0 0 0 7px;
    }
}

/* end profiles index pages */

.person {
    .name {
        clear: both;
        margin-bottom: 5px;
        margin-left: 5px;
    }
    .course-and-award {
        margin: 2px 5px 15px;
        display: inline-block;
        font-size: 1.2em;
    }
    .course-title {
        margin-right: 5px;
    }
    .jobTitle {
        font-weight: bold;
        margin-bottom: 0;
    }
    h3 {
        clear: both;
        margin-bottom: 15px;
        font-size: 1.5em;
    }
    .thumbnail-wrapper {
        border: 1px solid #cccccc;
        padding: 5px;
        box-shadow: 0 8px 6px -6px rgba(0, 0, 0, 0.2);
        text-align: center;
        background-color: $white;
        clear: both;
    }
    .thumbnail {
        margin-bottom: -5px;
    }
    .fa-user {
        font-size: 7em;
        color: #ccc;
        text-align: center;
    }
    .tag-list-title {
        font-size: 1.1em;
        font-weight: bold;
        margin-bottom: 6px;
        i {
            margin-left: 10px;
        }
    }
    .tag-list,
    .tag-list-no-icon {
        margin: 2px 0 20px 0;
        overflow: hidden;

        li {
            list-style-type: none;

            .city,
            .region,
            .country {
                margin-right: 0;
            }
        }
        a,
        span {
            @include gradient-vertical(#f5f5f5, #f1f1f1);
            border: 1px solid rgba(0, 0, 0, 0.1);
            border-radius: 3px;
            color: #444444;
            display: block;
            padding: 2px 9px 2px 6px;
            margin-bottom: 5px;
            margin-right: 10px;
            float: left;
        }
        i {
            display: none;
            color: #666666;
            margin-right: 7px;
        }
    }
    .extra-tags {
        border-top: 1px solid $grey2;
        padding-top: 15px;
        margin-top: 20px;
    }
    .region,
    .country,
    .city {
        float: left;
        margin-right: 10px;
    }
    .description {
        font-family: georgia;
        font-size: 1.5em;
        font-style: italic;
        line-height: 1.2em;
        margin: 22px 0 22px;
    }
    .video-testimonial {
        margin-bottom: 40px;
    }
    .map {
        display: block;
        margin-bottom: 0px;
    }
    .profile-content-tags {
        padding: 10px;
        background-color: #f9f9f9;
        margin-left: 7px;
        margin-right: 5px;
        clear: left;
        .company {
            float: none;
            width: auto;
            margin: 0;
            background: none;
            padding: 0;
            .name {
                padding: 0;
                margin-left: 0;
                font-weight: normal;
            }
        }
    }
    .company {
        background: none repeat scroll 0 0 #e3f0fd;
        margin-bottom: 20px;
        padding: 13px 0 10px 0;
        .name {
            font-weight: bold;
            font-size: 1em;
        }
        p {
            padding: 0 10px 10px 10px;
            margin: 0;
        }
        .description {
            font-size: 1em;
            line-height: 1.4em;
            font-style: inherit;
            font-family: inherit;
        }
    }
    .profile-tabs {
        margin-top: 40px;
    }
    .previous-page {
        display: block;
        line-height: 28px;
    }

    @include breakup-breakpoint('thinish') {
        // 480px
        .thumbnail-wrapper {
            width: 40%;
            float: right;
            margin-top: -7px;
        }
        .profile-content-tags {
            margin-left: 0;
        }
    }
    @include breakup-breakpoint('thinish_extended') {
        // 500px
        .company {
            float: right;
            width: 240px;
            margin-left: 20px;
        }
    }
    @include breakup-breakpoint('medium') {
        // 767px
        .thumbnail-wrapper {
            width: auto;
            float: none;
            margin-top: 0;
        }
        .profile-content-tags {
            margin-left: 7px;
        }
        .description {
            margin: 0 0 20px;
        }
        .tag-list li {
            clear: both;
        }
    }
    @include breakup-breakpoint('wide') {
        // 980px
    }
    @include breakup-breakpoint('wide_extended') {
        // 1035px
    }
    @include breakup-breakpoint('wider') {
        // 1200px
    }
}

/*people profile when shown in listing pages*/
.profile-listing {
    li {
        list-style-type: none;
        margin: 0;
    }

    .person {
        float: left;
        overflow: hidden;
        background: none repeat scroll 0 0 #f9f9f9;
        border: 1px solid #ccc;
        width: 100%;
        margin: 0 0 20px;

        .fa-user {
            margin: 20px auto 18px;
            display: block;
        }
        .thumbnail-wrapper {
            box-shadow: none;
            border: none;
            background-color: transparent;
            display: block;
            overflow: hidden;
        }
        .thumbnail {
            max-height: 183px;
        }
        .description {
            color: #333333;
            font-family: inherit;
            font-size: 1em;
            font-style: inherit;
            margin-bottom: 0;
            padding: 0 10px 0 10px;
        }

        @include breakup-breakpoint('thinish_extended') {
            // 500px

            height: 370px;
            margin-right: 0;

            &.even {
                float: right;
            }
            .thumbnail-wrapper {
                float: left;
                margin: 0 auto 5px;
            }
            .fa-user {
                font-size: 5em;
                clear: both;
            }
            .description {
                padding-top: 10px;
            }
            .tag-list {
                margin: 10px 0;
                clear: both;

                li {
                    clear: none;
                }
            }
        }
        @include breakup-breakpoint('medium') {
            // 767px

            height: 450px;

            .thumbnail-wrapper {
                float: none;
            }
            .fa-user {
                font-size: 7em;
                float: none;
            }
        }
        @include breakup-breakpoint('wide') {
            // 980px
            height: 480px;
            .fa-user {
                font-size: 7.5em;
                margin: 27px auto;
            }
            .tag-list li {
                clear: both;
            }
        }
        @include breakup-breakpoint('wider') {
            // 1200px

            .fa-user {
                font-size: 8em;
                margin: 42px auto;
            }
        }
    }
    .tag-list {
        padding: 5px 10px 10px 0;

        li {
            float: left;
            margin: 5px 0 0 10px;
        }
        i {
            display: inline;
        }
        .active,
        a:hover {
            @include gradient-vertical(#333333, #333333);
            border-color: $black;
            color: $white;

            i {
                color: $white;
            }
        }
        .remove-filter {
            margin: 0 0 0 10px;
            float: right;
            padding: 0;
            @include gradient-vertical(#333333, #333333);
        }
        .fa-times {
            margin-right: 0;
        }
    }
    .name {
        margin-left: 10px;
        font-size: 1.15em;
        font-weight: bold;
        @include font-family-sans-serif();
        .url {
            border-bottom: 1px solid #ccc;
            &:hover {
                border: none;
            }
        }
    }
    .no-results {
        list-style-type: none;
        margin-left: 0;
    }
}

.fb-page-nav {
    clear: both;
}

.social-list {
    margin: 15px 0 15px;
    overflow: hidden;

    li {
        list-style-type: none;
        float: left;
        width: 35px;
    }
    a {
        border-bottom: none;

        &:focus {
            background-color: transparent;
        }
    }
    i {
        font-size: 1.7em;
        color: #666;

        &:hover {
            color: $black;
        }
    }
}

// carousel profiles

#profiles-carousel {
    background-color: #333;
    padding: 0 40px 1px;
    overflow: hidden;
    -webkit-text-size-adjust: none;

    &.carousel-loading {
        background-color: #fff;

        .bxslider {
            display: none;
        }
        .bx-controls {
            display: none;
        }
        .all-profiles,
        .featured-title {
            display: none;
        }
    }
    .bxslider {
        margin: 0;
        display: block;
        list-style-type: none;
    }
    .featured-title {
        background-color: #333;
        color: $white;
        font-size: 1.5em;
        display: block;
        margin-bottom: 0;
    }
    .person {
        color: $white;

        a {
            color: $white;
            border: none;
        }
        .thumbnail-wrapper {
            border: none;
            box-shadow: none;
            background-color: #333;
        }
        .thumbnail {
            border-bottom: none;
            margin-top: 20px;
            max-height: 160px;
        }
        .title {
            color: $white;
            border-bottom: none;
            display: block;
            width: auto;
            margin-top: 10px;
            padding: 0 1px;
        }
        .description {
            font-size: 0.9em;
            font-style: normal;
            font-family: $font-family-sans;
            margin: 0;
            padding: 0 1px;
        }
    }
    .all-profiles {
        color: $white;
        padding: 10px 0 5px;
        display: block;

        a {
            color: $white;

            &:hover {
                border: none;
            }
        }
    }
    .bx-controls {
        display: block;
        text-align: center;
    }
    .bx-pager {
        overflow: hidden;
        margin: 24px 0 10px;
        display: inline-block;

        .bx-pager-item {
            float: left;

            .bx-pager-link {
                background: none repeat scroll 0 0 #666666;
                border-radius: 5px;
                display: block;
                height: 10px;
                margin: 0 5px;
                outline: 0 none;
                text-indent: -9999px;
                width: 10px;
                cursor: pointer;
                border-bottom: none;

                &:hover,
                &.active {
                    background-color: $white;
                }
            }
        }
    }
    .bx-prev,
    .bx-next {
        width: 60px;
        height: 100%;
        border-bottom: none;
        text-align: center;
        font-size: 2em;
        position: absolute;
        bottom: -80px;

        &.disabled i {
            cursor: default;
            color: #5b5b5b;
        }
        &:focus {
            background-color: transparent;
        }
        i {
            color: $white;
            font-size: 1.5em;
            line-height: 100px;
        }
    }
    .bx-prev {
        left: -46px;
    }
    .bx-next {
        right: -47px;
    }

    @include breakup-breakpoint('thinish') {
        // 480px
        .title {
            padding-top: 20px;
        }
    }
    @include breakup-breakpoint('thinish_extended') {
        // 500px
        .person {
            .title {
                font-size: 1.3em;
            }
            .description {
                font-size: 1em;
            }
        }
        .all-profiles {
            font-size: 1em;
        }
    }
    @include breakup-breakpoint('medium') {
        // 767px
        .title {
            padding-top: 0;
        }
    }
}

// end carousel profiles
