.hallway-links {
    margin-top: 20px;

    a {
        color: #333;
        text-decoration: none;
        border: none;

        &:hover {
            color: #b02a1a;
        }

        &:active,
        &:focus {
            color: white;
            background: #b91b1b;
        }
    }

    .hallway-links-image {
        img {
            width: 100%;
            margin: 0;
        }
    }

    .hallway-links-content {
        padding: 5px 0 0;
        background: white;
        margin-bottom: 50px;

        @include breakup-breakpoint('thinish_extended_2') {
            margin-bottom: 0;
        }

        p {
            margin-right: 15px;
            margin-bottom: 0;
        }

        h1,
        h2,
        h3 {
            font-family: 'ff-meta-web', Georgia, sans-serif;
        }

        ul {
            list-style-type: none;
            list-style-position: outside;
            margin: 0;
        }

        li {
            padding-bottom: 6px;
            border-bottom: 1px solid #ccc;
            margin-bottom: 6px;
        }

        h2 {
            margin: 15px 0;
            font-size: 1.4em;
            font-weight: 600;

            a {
                color: black;
                font-weight: 600;
                text-decoration: none;

                &:hover {
                    color: #b02a1a;
                    border-bottom: solid 1px #b02a1a;
                }
                &:active,
                &:focus {
                    color: white;
                    background: #b91b1b;
                    border-bottom: solid 1px white;
                }
            }
        }
    }
}
