$tx-time: 0.2s ease;
$home-controls-bottom: 20px;
$home-controls-hpos: 20px;
$home-controls-button-size: 40px;
$home-controls-button-icon-size: 16px;
$home-controls-button-hmargin: 0px;
$home-controls-pager-size: 20px;
$home-controls-pager-hmargin: 5px;
$home-controls-pager-hmargin-sm: 5px;

.home {
    #container {
        margin-bottom: 15px;
    }

    #header .site-home a {
        cursor: default;
        &:hover,
        &:focus {
            border: 0;
            text-decoration: none;
            color: inherit;
            background: inherit;
        }
    }

    h2,
    h3 {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        color: black;
        font-weight: 400;
        a {
            color: black;
            &:hover,
            &:focus {
                text-decoration: underline;
            }
        }
        i.fa-chevron-right,
        span.fa-chevron-right {
            color: black;
            float: right;
            font-size: 0.7em;
            margin-top: 0.3em;
        }
    }

    .home-news-events div,
    .home-business,
    .home-research {
        h2 {
            margin-bottom: 0;
            > a {
                .fa {
                    font-size: 0.8em;
                }

                &:hover {
                    text-decoration: none;
                    span {
                        color: $anchor-color;
                        border-bottom: 2px solid;
                        border-bottom-color: $anchor-color;
                    }
                    .fa {
                        border-bottom: none;
                    }
                }

                &:active,
                &:focus {
                    text-decoration: none;
                    background: $white;
                    span {
                        color: $anchor-color;
                        border-bottom: 2px dotted;
                        border-bottom-color: $grey5;
                    }
                    .fa {
                        border-bottom: none;
                    }
                }
            }
        }
    }

    .home-item {
        a {
            border-bottom: 0;
        }

        > a {
            display: block; /* a's containing blocks */
            &:focus {
                background: inherit;
            }
        }
    }

    h2 {
        font-size: 34px;
    }
    h3 {
        font-size: 22px;
    }

    img {
        margin: 0;
        backface-visibility: hidden;
        -moz-backface-visibility: hidden;
    }

    #slice-navigation,
    #primary-nav-toggler,
    .home-sidebar {
        display: none;
    }

    .responsive-wrapper {
        padding-top: 0;
    }

    #promo-wrapper {
        margin-bottom: 0;

        .bx-wrapper {
            padding: 0;
            position: relative;
            background: #ececee;
        }

        .bx-viewport,
        li {
            background: none;
        }

        .bx-controls {
            position: static;
            background: transparent;
            text-align: center;
            overflow: visible;
            height: 55px;

            a,
            a:focus {
                background: none;
                outline: 0;

                i,
                span.fa {
                    transition: color $tx-time;
                }
            }

            @include breakup-breakpoint('medium') {
                height: 0;
            }
        }

        .col-md-12 {
            float: none;
        }

        .bx-controls-direction {
            display: none;
        }

        .bx-pager {
            float: none;
            display: block;
            position: absolute;
            bottom: $home-controls-bottom;
            left: $home-controls-hpos;

            .bx-pager-item {
                margin: 0;

                a {
                    display: inline-block;
                    color: #ddd;
                    height: $home-controls-pager-size;
                    line-height: $home-controls-pager-size;
                    font-size: $home-controls-pager-size;
                    margin: 0 $home-controls-pager-hmargin-sm;

                    &:hover {
                        color: white;
                    }

                    &.active {
                        color: $red1;
                    }

                    @include breakup-breakpoint('medium') {
                        margin: 0 $home-controls-pager-hmargin;
                    }
                }
            }
        }

        .bx-controls-auto,
        .bx-controls-direction {
            display: block;
            position: absolute;
            margin: 0;
            bottom: $home-controls-bottom - 7px;
            height: $home-controls-button-size;

            a {
                &,
                &.active {
                    &,
                    &:focus,
                    &:hover {
                        background: none;
                        border: 0;
                        padding: 0;
                    }
                }

                height: $home-controls-button-size;
                width: $home-controls-button-size;
                top: 0;

                line-height: $home-controls-button-size + 2;
                font-size: $home-controls-button-size;
                margin: 0 $home-controls-button-hmargin;

                i,
                span.fa {
                    position: absolute;

                    &.bg {
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        color: white;
                        line-height: inherit;
                    }
                    &.ic {
                        top: 50%;
                        left: 50%;
                        width: $home-controls-button-icon-size;
                        height: $home-controls-button-icon-size;
                        line-height: $home-controls-button-icon-size;
                        font-size: $home-controls-button-icon-size;
                        margin-left: -0.5 * $home-controls-button-icon-size;
                        margin-top: 1 + -0.5 * $home-controls-button-icon-size;
                        color: $red1;

                        &.fa-chevron-right,
                        &.fa-chevron-left {
                            margin-top: -0.5 * $home-controls-button-icon-size +
                                2;
                        }

                        &.fa-play,
                        &.fa-chevron-right {
                            margin-left: -0.5 * $home-controls-button-icon-size +
                                1;
                        }
                        &.fa-chevron-left {
                            margin-left: -0.5 * $home-controls-button-icon-size -
                                1;
                        }
                    }
                }
                &:hover i,
                &.active i,
                &:hover span,
                &.active span {
                    &.bg {
                        color: $red1;
                    }
                    &.ic {
                        color: white;
                    }
                }

                &:hover {
                    color: white;
                }

                &.active {
                    color: $red1;
                }
            }
            .bx-next {
                margin-left: 0.35em; // 14px
            }
        }

        .bx-controls-direction {
            display: none;

            a {
                display: inline-block;
                position: relative;
                left: 0;
            }
        }

        .bx-controls-auto {
            right: $home-controls-hpos;
            a {
                display: block;
                position: absolute;
                right: 0;

                &.active {
                    display: none;
                }
            }
        }

        @include breakup-breakpoint('medium') {
            .bx-controls {
                padding: 0;
            }

            .bx-pager {
                left: 11em;
            }

            .bx-controls-direction {
                display: block;
                left: $home-controls-hpos;
            }
        }

        .carousel-media {
            width: 100%;
            padding: 0;
            display: block;
            height: 195px;
            position: relative;
            overflow: hidden;

            img {
                position: absolute;
                top: 0;
                right: 0;
                left: auto;
                max-width: none;
                height: 100%;
            }

            @include breakup-breakpoint('medium') {
                height: auto;

                img {
                    height: auto;
                    max-width: 100%;
                    position: static;
                    margin-top: 0;
                }
            }
        }

        li {
            position: relative;
        }

        .carousel-text {
            display: block;
            padding: 0;
        }

        .banner-content {
            -webkit-transform: translateZ(0);
            height: auto;
            color: black;
            padding: 10px;

            h2 {
                display: none;
                margin-bottom: 5px;
            }
            sup {
                line-height: 0.5em;
            }
            p,
            a {
                color: black;
                font-size: 18px;
                font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                    sans-serif;
                line-height: 1.5em;
                //font-weight: 300;
                border: none;
            }
            a:hover {
                border-bottom: 1px solid $black;
            }
            @include breakup-breakpoint('thinish') {
                p,
                a {
                    font-size: 22px;
                }
            }

            @include breakup-breakpoint('medium') {
                // 767px
                h2 {
                    display: block;
                    color: $red1;
                    margin: 0 0 5px 0;
                    font-size: 14px;
                }
                p,
                a {
                    font-size: 20px;
                }
            }
            @include breakup-breakpoint('wide_extended') {
                // 1035px
                h2 {
                    font-size: 16px;
                }
                p,
                a {
                    font-size: 24px;
                }
            }
        }

        @include breakup-breakpoint('medium') {
            position: relative;

            .row {
                position: static;
                width: 100%;
                display: block;
                margin: 0;

                &::before {
                    display: none;
                }

                > div.col-md-12 {
                    position: static;
                    width: 100%;
                    height: auto;
                    min-height: 0;
                    display: block;
                    margin: 0;
                    padding: 0;
                }
            }

            .carousel-text {
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;

                display: block;

                width: 100%;
                height: 100%;

                margin: 0;
                padding: 0;
            }

            .banner-content {
                background: #efefef;
                background: rgba(255, 255, 255, 0.9);
                position: absolute;
                max-width: 60%;
                top: 2em;
                left: 2em;
                padding: 0.714em;
            }
        }
        @include breakup-breakpoint('wide') {
            .banner-content {
                padding: 20px;
                max-width: 40%;
            }
        }
    }

    .home-news {
        h2,
        h3 {
            font-size: 16px;
            line-height: 1.4em;
        }
    }

    .header__logo {
        img {
            margin: 0 0 14px 0;
        }
    }
}

$home-section-background: white;

.home-background {
    position: relative;
    background: $home-section-background;
    padding: 2px 10px 0;
    margin: 0 -10px 20px -10px;
    @include breakup-breakpoint('wide') {
        padding: 2px 0 0;
        margin: 0 0 20px 0;
    }
}

.home-city-connect {
    margin: 22px 0 20px;
}

.home-news-events {
    margin-top: 2px;
    margin-bottom: 20px;
}

.home-studying {
    margin-top: 20px;
    margin-bottom: 20px;
}

.home-motto {
    padding: 20px 0;
    text-align: center;
    p {
        margin: 0;
        color: $gray-light;
        font-weight: bold;
    }
    h3 {
        margin-top: 0;
    }
}

.home-news-events {
    .submenu {
        height: 30px;
        font-style: italic;
    }

    .dropdown-select {
        display: inline-block;
        width: auto;
        height: auto;
        line-height: inherit;
        position: relative;

        a.display {
            &,
            &:focus {
                border: 0;
            }
            padding: 0;
            display: inline;
            font-weight: normal;
            span {
                color: black;
            }
            i {
                margin-left: 5px;
                position: static;
                font-size: 0.8em;
                margin-top: 0.1em;
            }
        }

        .options {
            width: auto;
            padding: 5px;
            border: 1px solid $grey3;
            border-radius: 5px;

            a {
                display: block;
                white-space: nowrap;
                width: 100%;
            }
        }
    }

    /* TODO: remove .bx-wrapper, replaced by .news-events-strip .bx-wrapper */
    .bx-wrapper {
        position: relative;

        .bx-controls {
            display: block;
        }

        .bx-pager {
            display: none;
        }

        .bx-controls-direction {
            display: block;
            position: static;
            width: 0;
            height: 0;

            a {
                &,
                &.active {
                    &,
                    &:focus {
                        background: none;
                        border: 0;
                        text-decoration: none;
                    }
                }

                transition: background $tx-time;
                &:hover {
                    background: rgba(255, 255, 255, 0.3);
                }

                position: absolute;
                top: 0;
                margin: 0;
                width: 50px;
                padding: 0 0 66.85%;
                height: 0;
                border: 0;
                display: block;

                &.bx-prev {
                    left: 0;
                }
                &.bx-next {
                    right: 0;
                }

                i,
                span.fa {
                    position: absolute;
                    text-align: center;
                    transition: color $tx-time;

                    &.bg {
                        top: 50%;
                        left: 50%;
                        height: $home-controls-button-size;
                        width: $home-controls-button-size;

                        line-height: $home-controls-button-size + 2;
                        font-size: $home-controls-button-size;
                        margin: (-0.5 * $home-controls-button-size) auto auto
                            (-0.5 * $home-controls-button-size);
                        color: white;
                    }
                    &.ic {
                        top: 50%;
                        left: 50%;
                        width: $home-controls-button-icon-size;
                        height: $home-controls-button-icon-size;
                        line-height: $home-controls-button-icon-size;
                        font-size: $home-controls-button-icon-size;
                        margin-left: -0.5 * $home-controls-button-icon-size;
                        margin-top: -0.5 * $home-controls-button-icon-size + 1;
                        color: $red1;

                        &.fa-chevron-right,
                        &.fa-chevron-left {
                            margin-top: -0.5 * $home-controls-button-icon-size +
                                2;
                        }

                        &.fa-play,
                        &.fa-chevron-right {
                            margin-left: -0.5 * $home-controls-button-icon-size +
                                1;
                        }
                        &.fa-chevron-left {
                            margin-left: -0.5 * $home-controls-button-icon-size -
                                1;
                        }
                    }
                }
                &:hover i,
                &.active i,
                &:hover span,
                &.active span {
                    &.bg {
                        color: $red1;
                    }
                    &.ic {
                        color: white;
                    }
                }

                &:hover {
                    color: white;
                }

                &.active {
                    color: $red1;
                }
            }
        }
    }

    .news-card-content .news-card-content__title {
        font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
    }
}

/* TODO: remove .home-events (replaced by .news-events-strip__events) */
.home-events {
    > :last-child .event {
        border-bottom-width: 0;
    }

    .event {
        padding-bottom: 15px;
        border-bottom: 1px solid $gray-light;
        margin-bottom: 15px;

        .date {
            width: 50px;
            margin: 0;
        }
        .date-month,
        .date-day-no {
            background: $red2;
            border: 0;
            color: white;
            font-size: small;
            line-height: 1em;
            font-weight: 600;
            font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                sans-serif;
        }

        .date-month {
            background: $red2;
            padding: 8px 0 6px;
        }
        .date-day-no {
            background: $red1;
            border: 0;
            color: white;
            font-size: 14px;
            padding: 6px 0;
        }

        .event-details {
            margin-left: 70px;
            min-height: 53px;

            h2,
            h3 {
                font-size: 16px;
                margin: 0 0 0.2em;
                line-height: 1.4em;
                font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
            }
            p {
                font-size: 15px;
                font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
                font-weight: bold;
                margin-bottom: 0;
                color: #555;
            }

            &,
            h2 a,
            h3 a,
            p {
                line-height: 1.2em;
            }
        }
    }
}

.home-studying-tweens {
    padding-top: 0;

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px, sm
        padding-top: 20px;
    }

    @include breakup-breakpoint('medium') {
        // 767px, md
        padding-top: 0;
    }

    .home-item {
        border-bottom: 1px solid $gray-light;
        h2 {
            font-size: 22px;
            line-height: 1.4em;
            color: #000;
            margin: 6px 15px 0 0;
        }

        position: relative;

        img {
            margin: 0;
        }
        h3 {
            color: #000;
            margin: 6px 0;
        }

        h2,
        h3,
        p {
            @include transition(color $tx-time);
            color: black;
        }

        p {
            font-size: 16px;
            line-height: 1.4em;
            color: #444;
            margin: 0;
            a i {
                color: $red1;
                float: right;
            }
            a,
            a:focus {
                font-weight: bold;
                color: inherit;
                @include transition(color $tx-time);
            }
        }

        img,
        p {
            display: none;
        }

        &:hover {
            p,
            h2,
            h3 {
                color: #777;
            }
            p a,
            p a:focus {
                color: $red1;
            }
        }
        .image-credit {
            display: none;
            z-index: 10;
        }

        @include breakup-breakpoint('thinish_extended_2') {
            border-bottom: none;
            .fa-chevron-right {
                display: none;
            }
            .image-credit {
                display: block;
            }
        }
    }

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px, sm
        .home-item {
            div {
                border: 0;
            }

            h3 {
                margin: 6px 0 0;
            }

            h3 i,
            h3 span {
                display: none;
            }

            img,
            p {
                display: block;
            }
        }
    }
}

.home-research,
.home-business {
    .home-item {
        margin-bottom: 20px;

        img,
        h2 {
            margin: 0;
        }

        h2,
        h3 {
            color: black;
            @include transition(color 200ms);
            font-size: 22px;
            margin-right: 15px;
            position: relative;

            i,
            span.fa {
                position: absolute;
                right: -15px;
                bottom: 6px;
            }
        }
        p {
            font-size: 16px;
            color: #444;
            display: none;

            @include breakup-breakpoint('medium') {
                // 767px
                display: block;
            }
        }

        .content {
            position: relative;
            z-index: 2;
            margin: -27px 15px 0;
            padding: 6px 16px 0;
            background: $home-section-background;

            p {
                margin-right: 16px;
            }
        }

        &:hover {
            h2,
            h2 i {
                color: #b02a1a;
            }
        }
    }
}

.home-research,
.home-business,
.home-studying-tweens {
    .home-item {
        .image {
            position: relative;
            img {
                width: 100%;
                opacity: 1;
                @include transition(opacity $tx-time);
            }

            &:after {
                content: '';
                background: $white;
                opacity: 0;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 2; // MIGHT NEED TO FIDDLE WITH THIS A BIT
                @include transition(opacity 0.2s ease);
            }
        }

        a:hover {
            .image img {
                opacity: 0.75;
            }
        }
    }
}

.home-business-bottom {
    $y: 10%;
    $x: 20%;
    $h: 2.4%;

    position: relative;
    padding-bottom: $h;
    overflow: hidden;

    > div {
        position: absolute;
        width: 100%;
        padding-bottom: $y;
        bottom: 0;
        left: 0;

        > div {
            background-color: $home-section-background;
            width: 100% + $x;
            height: 100%;
            position: absolute;
            left: -$x / 2;
            border-radius: 0 0 50% 50%;
        }
    }
}

.home .home-course-finder {
    color: #333;
    background: #ccc;
    font-size: 16px;
    padding: 20px;

    h2,
    h3,
    legend {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-weight: 600;
        margin: 6px 0px 8px 0px;
        font-size: 22px;
        line-height: 1.3em;
        color: #444;
        font-weight: 600;
    }

    input[type='text'],
    select {
        width: 100%;
        margin-bottom: 20px;
    }
    input[type='text'] {
        @include filter-field($active-bg: false);
    }
    input[type='text'],
    select {
        @include filter-field($active-bg: true);
    }

    .home-course-finder-submit button {
        padding: 15px 20px;
        line-height: 1em;
    }
    .home-course-finder-submit a {
        padding: 5px 0 0;
        display: inline-block;
        float: left;
    }
    a.home-course-finder-submit-text__clearing {
        color: #fff !important;
        background: #0377a0;
        border-radius: 5px;
        padding: 5px 10px;
        margin-top: 10px;
        margin-bottom: 10px;
        clear: both;
    }

    .home-course-finder-submit {
        a {
            color: inherit;
            border: 0;

            &:hover {
                text-decoration: none;
                border-bottom: 2px solid $anchor-color;
                color: $anchor-color;
            }
            &:focus,
            &:active {
                color: $anchor-color;
                background: none;
                text-decoration: none;
                border-bottom: 2px dotted $grey5;
            }
        }

        button {
            width: 100%;
            text-align: left;
            cursor: pointer;
            float: right;
            background: #555;
            color: white;
            font-weight: bold;
            border: 0;
            i,
            span.fa {
                float: right;
            }
            @include transition(background $tx-time);
            &:hover {
                background: #888;
            }
        }

        .home-course-finder-submit-text {
            //display: none;

            @include breakup-breakpoint('medium') {
                display: block;
            }
        }
    }
}

.home-media-ribbon-wrapper {
    margin: 40px -10px 0;

    @include breakup-breakpoint('wide') {
        margin: 40px 0 0;
    }

    /** overwrite until html can be updated */
    &.section-fw-purple-wrapper {
        background: #8d182a;
    }
}

.home-media-ribbon {
    .text {
        p,
        a {
            color: white;
        }

        /* a {
             text-decoration: none;
             border-bottom: 1px dashed white;

             &:hover {
                 border-bottom: 1px solid white;
             }
         }*/

        @include section(#8d182a);
        @include section-white-links(#8d182a);
    }
}

.home .footer2016 {
    padding-top: 0;
}

.home-city-connect {
    position: relative;

    .background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        overflow: hidden;

        img {
            max-width: none;
        }
    }

    .content {
        position: relative;
        padding: 20px 10px;
        .home-text-container {
            text-align: center;

            h3,
            p,
            a {
                color: white;
                margin-bottom: 10px;
            }

            a {
                display: inline-block;
                background: #27040d; /*ie8*/
                background: rgba(0, 0, 0, 0.5);
                @include transition(background $tx-time);
                font-weight: bold;
                padding: 10px 15px;
                border-radius: 5px;
                border: 0;

                i,
                span.fa {
                    margin-left: 60px;
                }

                &:hover {
                    background: #360412; /*ie8*/
                    background: rgba(0, 0, 0, 0.3);
                }
            }
        }
    }

    $text-width: 0.5;
    $video-width: 0.45;
    $video-aspect-ratio: 0.562963;

    .home-video {
        padding: 0;

        .home-video-spacer {
            width: 100%;
            padding-top: 0;
            padding-bottom: $video-aspect-ratio * 100%;
            margin-bottom: 0;

            .youtube-preview {
                height: 100%;
            }
        }

        img {
            margin: 0;
        }
    }

    @include breakup-breakpoint('medium') {
        overflow: hidden;
        padding-bottom: ($video-width * $video-aspect-ratio) * 100%;

        .background {
            height: 101%;
        }

        .content {
            position: absolute;
            width: $text-width * 100%;
            top: 0;
            left: 0;
            height: 100%;
            padding: 0;

            .home-text-container {
                height: 100%;
                text-align: center;

                &::before {
                    height: 100%;
                    content: '';
                    display: inline-block;
                    vertical-align: middle;
                    width: 0;
                }
                .text {
                    display: inline-block;
                    max-width: 90%;
                    margin: 0 auto;
                    vertical-align: middle;
                }
            }
        }

        .home-video {
            position: absolute;
            top: 0;
            left: $text-width * 100%;
            width: $video-width * 100%;
        }
    }

    @include breakup-tweakpoint('(min-width: 850px)', 'medium', true) {
        .content .home-text-container .text {
            max-width: 300px;
        }
    }
}

.home .news-card-media {
    padding-bottom: 66.53%;

    img {
        position: absolute;
        max-width: none;
        min-width: 100%;
        height: 100%;
        width: auto;
        right: 0;
