// Override Bootstrap's make-row() mixin
@mixin make-row($gutter: $grid-gutter-width) {
    @include clearfix();
    margin-left: ($gutter / -2) !important;
    margin-right: ($gutter / -2) !important;
}

img[align] {
    margin-top: 0;
    margin-bottom: 1.5em;
}

img[align='left'] {
    float: left;
    margin-right: 1.5em;
}

img[align='right'] {
    float: right;
    margin-left: 1.5em;
}

.hidden {
    display: none;
    visibility: hidden;
}

.center-text {
    text-align: center;
}

.ie6 img {
    width: 100%;
}

/*elastic embed*/
.embed-wrapper {
    max-width: 100%;
}

.embed-container {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 30px;
    height: 0;
    overflow: hidden;
    margin-bottom: 14px;
}

.embed-container iframe,
.embed-container object,
.embed-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
}

@mixin background-opacity($colour, $opacity) {
    /* Fallback for web browsers that doesn't support RGBa */
    background-color: $colour;
    /* RGBa with $opacity opacity */
    background: rgba(red($colour), green($colour), blue($colour), $opacity);
}

@mixin visuallyhidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

// Hide only visually, but have it available for screenreaders: h5bp.com/v
.visuallyhidden {
    @include visuallyhidden;
}

// Extends the .visuallyhidden class to allow the element to be focusable when navigated to via the keyboard: h5bp.com/p
.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
}

//feedback button
// used current students for now
#feedback {
    background-color: #ffffff;
    font-size: 18px;
    font-weight: bold;
    height: 47px;
    padding: 0 4px;
    right: -39px;
    padding-top: 4px;
    position: fixed;
    text-align: center;
    text-decoration: none;
    top: 750px;
    transform: rotate(270deg);
    z-index: 100000;

    .cta {
        margin-bottom: 0;
    }
    a {
        padding: 10px;
        border-radius: 0;
    }
}

// animation mixin
// used for loading cogs
@-moz-keyframes infinite-spinning-clock {
    0% {
        -moz-transform: rotate(0deg);
    }
    100% {
        -moz-transform: rotate(360deg);
    }
}

@-moz-keyframes infinite-spinning-anti-clock {
    0% {
        -moz-transform: rotate(360deg);
    }
    100% {
        -moz-transform: rotate(0deg);
    }
}

@-ms-keyframes infinite-spinning-clock {
    0% {
        -ms-transform: rotate(0deg);
    }
    100% {
        -ms-transform: rotate(360deg);
    }
}

@-ms-keyframes infinite-spinning-anti-clock {
    0% {
        -ms-transform: rotate(360deg);
    }
    100% {
        -ms-transform: rotate(0deg);
    }
}

@-webkit-keyframes infinite-spinning-clock {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}

@-webkit-keyframes infinite-spinning-anti-clock {
    0% {
        -webkit-transform: rotate(360deg);
    }
    100% {
        -webkit-transform: rotate(0deg);
    }
}

@-o-keyframes infinite-spinning-clock {
    0% {
        -o-transform: rotate(0deg);
    }
    100% {
        -o-transform: rotate(360deg);
    }
}

@-o-keyframes infinite-spinning-anti-clock {
    0% {
        -o-transform: rotate(360deg);
    }
    100% {
        -o-transform: rotate(0deg);
    }
}

@keyframes infinite-spinning-clock {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

@keyframes infinite-spinning-anti-clock {
    0% {
        transform: rotate(360deg);
    }
    100% {
        transform: rotate(0deg);
    }
}

@mixin city-animation(
    $animName,
    $animDuration,
    $animIterationCount,
    $animTimingFct
) {
    -moz-animation-name: $animName;
    -moz-animation-duration: $animDuration;
    -moz-animation-iteration-count: $animIterationCount;
    -moz-animation-timing-function: $animTimingFct;

    -ms-animation-name: $animName;
    -ms-animation-duration: $animDuration;
    -ms-animation-iteration-count: $animIterationCount;
    -ms-animation-timing-function: $animTimingFct;

    -webkit-animation-name: $animName;
    -webkit-animation-duration: $animDuration;
    -webkit-animation-iteration-count: $animIterationCount;
    -webkit-animation-timing-function: $animTimingFct;

    -o-animation-name: $animName;
    -o-animation-duration: $animDuration;
    -o-animation-iteration-count: $animIterationCount;
    -o-animation-timing-function: $animTimingFct;

    animation-name: $animName;
    animation-duration: $animDuration;
    animation-iteration-count: $animIterationCount;
    animation-timing-function: $animTimingFct;
}

.fade-out {
    opacity: 0;
    pointer-events: none;
    user-select: none;
    animation: fade-out 1s linear;
}
@keyframes fade-out {
    0%,
    75% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
