$meta-font-styles: (
    ('Light', 300, normal),
    ('Normal', 400, normal),
    ('Book', 500, normal),
    ('Medium', 600, normal),
    ('Bold', 700, normal),
    ('LightIta', 300, italic),
    ('NormalIta', 400, italic),
    ('BookIta', 500, italic),
    ('MediumIta', 600, italic),
    ('BoldIta', 700, italic)
);

@mixin meta-font-style($prefix, $weight, $style) {
    @font-face {
        font-family: 'ff-meta-web';
        font-style: #{$style};
        font-weight: #{$weight};
        src: url('../fonts/metasans/MetaWeb-#{$prefix}.woff?city20150610')
            format('woff');
    }
}

@each $style in $meta-font-styles {
    @include meta-font-style(nth($style, 1), nth($style, 2), nth($style, 3));
}
