.cookie-notice {
    $background: rgba(0, 0, 0, 0.85);
    padding: 20px 0;
    background: $background;
    color: white;
    font-size: 15px;

    @include section-white-links($background);
}

.cookie-notice__text {
    margin: 0 30px;

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        margin-right: 150px;
    }
}

.cookie-notice__inner {
    position: relative;
}

.cookie-notice__dismiss {
    display: none;

    position: relative;

    margin-left: 10px;

    button {
        border: none;
        padding: 10px 15px;
        text-align: left;
        cursor: pointer;

        color: white;
        background: #b91b1b;
        border-radius: 10px;

        transition: color 100ms, background 100ms;

        &:hover {
            background: #eee;
            color: #333;
        }
        &:active,
        &:focus {
            background: #eee;
            color: #b91b1b;
        }
    }

    margin-top: 20px;
    text-align: center;

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        position: absolute;
        top: 0;
        margin-top: 0;
        right: 30px;
    }
}

.js {
    .cookie-notice {
        display: none;
        position: fixed;
        z-index: 1000;
        width: 100%;
        bottom: 0;
        left: 0;
    }

    .cookie-notice__dismiss {
        display: block;
    }
}
