/**
 * Select Element Replacement Dropdowns
 */
.dropdown-select {
    width: 100%;
    height: auto;
    position: relative;
    z-index: 1000;
    line-height: 2.357em;
    /*a*/
}

.dropdown-select .display,
.dropdown-select .display:focus,
.dropdown-select .options {
    background-color: #ffffff;
    border: 1px solid #cccccc;
    z-index: 1000;
}

.dropdown-select .display {
    color: #b02a1a;
    display: block;
    padding: 0 1em;
    overflow: hidden;
    font-weight: bold;
    white-space: nowrap;
    text-overflow: ellipsis;
    /*i*/
}

.dropdown-select .display i {
    position: absolute;
    top: 1px;
    right: 1em;
    font-weight: bold;
    line-height: inherit;
}

.dropdown-select .display .open {
    display: none;
}

.dropdown-select.active .display {
    /*i*/
    /*i*/
}

.dropdown-select.active .display .open {
    display: inline-block;
}

.dropdown-select.active .display .closed {
    display: none;
}

.dropdown-select.disabled .display {
    color: #e7e7e7;
}

.dropdown-select .options {
    background-color: #ffffff;
    opacity: 0;
    width: 100%;
    visibility: hidden;
    border-top: 0;
    position: absolute;
    transition: opacity 0.1s ease, visibility linear 0.1s;
    z-index: 1000;
}

.dropdown-select .options a {
    color: #b02a1a;
    display: block;
    padding: 0.5535em 1em;
    border-bottom: 1px solid #cccccc;
    line-height: 1.25em;
}

.dropdown-select .options a:last-child {
    border-bottom: 0;
}

.dropdown-select .options a:hover, .dropdown-select .options a:focus, .dropdown-select .options a:active {
    background-color: #e7e7e7;
}

.dropdown-select .options a.selected {
    background-color: #b02a1a;
    color: #ffffff;
    font-weight: bold;
}

.dropdown-select.active .options {
    opacity: 1;
    visibility: visible;
    transition: opacity 0.1s ease;
}

.dropdown-select.disabled .options {
    display: none;
}

