}

#content .wForm p {
    margin: 0 0 14px 0;
}

label {
    white-space: normal;
    margin: 0 0 5px 0;
}

.wForm form .label {
    font-size: medium;
}

/* .wForm form Sections ========================================= */
/* Fieldset */
.wForm form fieldset {
    border: 1px solid #cccccc;
    padding: 10px;
    background: #fafafa;
    margin-bottom: 10px;
    display: block;
    clear: both;
}

.wForm form fieldset legend {
    color: #333333;
    font-family: Georgia, "Times New Roman", Times, serif;
    font-size: 1.4em;
    padding: 10px;
