    top: -0.5em;
    left: 0;
}

/* Nested Fieldsets */
.wForm form fieldset fieldset {
    background-color: #fafafa;
    margin-top: 10px;
}

.wForm form fieldset fieldset legend {
    font-size: 1.2em;
}

/* Grid Section */
.wForm form table {
