}

/* Inputs ======================================================================== */
.wForm input,
.wForm textarea,
.wForm select {
    border: 1px solid #cccccc;
    box-shadow: "inset 0 0 8px  rgba(0,0,0,0.1), 0 0 16px rgba(0,0,0,0.1)";
