    padding: 5px !important;
}

.wForm textarea {
    width: 90%;
    max-width: 410px;
}

td input,
td textarea,
td select {
    width: 90%;
}

/* Buttons ======================================================================= */
/* Submit & Cancel buttons */
.wForm form .actions {
    font-size: 0.9em;
}

.wForm form .actions input {
    font-family: Arial, Helvetica, sans-serif;
}

.wForm form .actions:after {
    content: '.';
