/**
 * Pagination
 */
.pagination {
    width: 100%;
    margin: 1em 0px 1em;
    text-align: center;
    display: inline-block;
    list-style: outside none none;
    line-height: 100%;
    font-weight: 600;
}

.pagination .previous,
.pagination .number,
.pagination .after,
.pagination .ellipsis {
    padding: 4px 8px;
    margin: 10px 0 10px 5px;
    background-color: #eeeeee;
}

.pagination .previous,
.pagination .number,
.pagination .after {
    border: 1px solid transparent;
}

.pagination .number,
.pagination .ellipsis {
    display: none;
}

.pagination .selected {
    display: inline;
    margin: 10px 0 10px 5px;
    color: #6b6b6b;
    border: 1px solid transparent;
}

.pagination .selected:before {
    content: 'Page ';
}

.pagination .previous:before {
    content: '< ';
}

.pagination .after:after {
    content: ' >';
}

.pagination a {
    text-decoration: none;
    color: #000000;
    background-color: #eeeeee;
    border: 1px solid transparent;
    display: inline;
}

.pagination a:link {
    color: #000000;
}

.pagination a:visited {
    color: #000000;
}

.pagination a:hover {
    color: #b91b1b;
    background-color: #eeeeee;
    border: 1px solid #b91b1b;
}

.pagination a:active, .pagination a:focus {
    color: white;
    background-color: #b91b1b;
    border: 1px solid transparent;
}

.pagination .disabled {
    color: #6b6b6b;
    background-color: #eeeeee;
}

.pagination .ellipsis {
    background-color: transparent;
    margin: 0;
}

@media only screen and (min-width: 480px) {
    .pagination .ellipsis,
    .pagination .first-number,
    .pagination .last-number,
    .pagination .display {
        display: inline;
    }
    .pagination .number.extended {
        display: none;
    }
    .pagination .selected {
        border: 1px solid #6b6b6b;
    }
    .pagination .selected:before {
        content: '';
    }
    .pagination .previous:before {
        content: '';
    }
    .pagination .after:after {
        content: '';
    }
}

@media only screen and (min-width: 500px) {
    .pagination .number {
        display: inline;
    }
}

@media only screen and (min-width: 600px) {
    .pagination .number.extended {
        display: inline;
    }
}

.pagination.pagination--white-buttons .previous,
.pagination.pagination--white-buttons .number,
.pagination.pagination--white-buttons .after {
    background-color: #ffffff;
}

.pagination.pagination--white-buttons a {
    color: #000000;
    background-color: #ffffff;
}

.pagination.pagination--white-buttons a:link {
    color: #000000;
}

.pagination.pagination--white-buttons a:visited {
    color: #000000;
}

.pagination.pagination--white-buttons a:hover {
    color: #b91b1b;
    background-color: #ffffff;
}

.pagination.pagination--white-buttons .disabled {
    color: #6b6b6b;
    background-color: transparent;
}

.pagination--news-intranet {
    margin: 0 0 5em;
}

.pagination--news-intranet .after:hover,
.pagination--news-intranet .previous:hover {
    border: 1px solid #b91b1b;
    cursor: pointer;
}

.pagination--news-intranet .after:hover a,
.pagination--news-intranet .previous:hover a {
