/**
 * Main Modal Styles
 */
.modal, .modal--delete {
    background-color: rgba(255, 255, 255, 0.9);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.5em;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 420;
}

.modal__box, .modal__box--delete {
    background-color: #ffffff;
    width: 100%;
    max-width: 28em;
    max-height: 100%;
    border-radius: 0.25em;
    position: relative;
    overflow-y: auto;
    box-shadow: 0 0 0.5em rgba(51, 51, 51, 0.5);
    z-index: 440;
}

