    background-color: #333333;
}

.modal__box legend.event--new, .modal__box--delete legend.event--new {
    background-color: #669933;
}

.modal__box legend.event--exception, .modal__box--delete legend.event--exception {
    background-color: #00739b;
}

.modal__box legend.event--delete, .modal__box--delete legend.event--delete {
    background-color: #cc3333;
}

.modal__box fieldset, .modal__box--delete fieldset {
    padding: 1.5em 0;
    margin: 0 1.5em;
    border: 0;
}

.modal__box fieldset ~ fieldset, .modal__box--delete fieldset ~ fieldset {
