}

.modal__box fieldset.event--exception.modal__box__recurrence-fieldset, .modal__box--delete fieldset.event--exception.modal__box__recurrence-fieldset {
    display: none;
}

.modal__box fieldset.text-center, .modal__box--delete fieldset.text-center {
    text-align: center;
}

.modal__box legend + fieldset, .modal__box--delete legend + fieldset {
