//
// MIXINS
//

/// Simple translate3d mixin
@mixin translate3d($x, $y, $z) {
    transform: translate3d($x, $y, $z);
}

/// Apply `layout` to elements
// Master mixin, use this one
@mixin has-layout($approach: zoom) {
    @if $approach == block {
        @include has-layout-block;
    } @else {
        @include has-layout-zoom;
    }
}
// Simple layout application tailored only for IE using zoom
@mixin has-layout-zoom {
    // This breaks validation
    *zoom: 1;
}
// Simple layout application by toggling `display`
@mixin has-layout-block {
    // This makes IE6 get layout
    display: inline-block;
    // And this puts it back to block
    & {
        display: block;
    }
}

/// Properly clear elements (legacy Compass stuff)
// Older-style clearfix
@mixin pie-clearfix {
    &:after {
        content: '';
        display: table;
        clear: both;
    }
    @include has-layout;
}
// Legacy-style clearfix
@mixin legacy-pie-clearfix {
    &:after {
        content: '/0020';
        height: 0;
        display: block;
        clear: both;
        overflow: hidden;
        visibility: hidden;
    }
    @include has-layout;
}

/// Simple application of `background-size`
@mixin background-size($size...) {
    @if (length($size) == 0) {
        $size: '100% auto';
    }
    background-size: $size;
}

//
// Bootstrap 2 Legacy Mixins
//

@mixin font-family-serif() {
    font-family: $font-family-serif;
}
@mixin font-family-sans-serif() {
    font-family: $font-family-sans;
}
@mixin font-family-meta-sans() {
    font-family: $font-family-meta-sans;
}
@mixin font-shorthand(
    $size: $font-size-base,
    $weight: normal,
    $lineHeight: $line-height-base
) {
    font-size: $size;
    font-weight: $weight;
    line-height: $lineHeight;
}
@mixin font-serif(
    $size: $font-size-base,
    $weight: normal,
    $lineHeight: $line-height-base
) {
    @include font-family-serif();
    @include font-shorthand($size, $weight, $lineHeight);
}
@mixin font-sans-serif(
    $size: $font-size-base,
    $weight: normal,
    $lineHeight: $line-height-base
) {
    @include font-family-sans-serif();
    @include font-shorthand($size, $weight, $lineHeight);
}

@mixin border-radius($radius) {
    -webkit-border-radius: $radius;
    -moz-border-radius: $radius;
    border-radius: $radius;
}

@mixin input-block-level {
    @include box-sizing(
        border-box
    ); // Makes inputs behave like true block-level elements
    display: block;
    width: 100%;
    min-height: $inputHeight; // Make inputs at least the height of their button counterpart (base line-height + padding + border)
}

@mixin ie7-inline-block() {
    *display: inline; /* IE7 inline-block hack */
    *zoom: 1;
}
@mixin ie7-restore-left-whitespace() {
    *margin-left: 0.3em;

    &:first-child {
        *margin-left: 0;
    }
}
@mixin ie7-restore-right-whitespace() {
    *margin-right: 0.3em;
}

@mixin buttonBackground(
    $startColor,
    $endColor,
    $text-color: #fff,
    $textShadow: 0 -1px 0 rgba(0, 0, 0, 0.25)
) {
    // gradientBar will set the background to a pleasing blend of these, to support IE<=9
    @include gradientBar($startColor, $endColor, $text-color, $textShadow);
    *background-color: $endColor; /* Darken IE7 buttons by default so they stand out more given they won't have borders */
    @include reset-filter();

    // in these cases the gradient won't cover the background, so we override
    &:hover,
    &:active,
    &.active,
    &[disabled],
    &.disabled {
        color: $text-color;
        background-color: $endColor;
        *background-color: darken($endColor, 5%);
    }

    // IE 7 + 8 can't handle box-shadow to show active, so we darken a bit ourselves
    &:active,
    &.active {
        background-color: darken($endColor, 10%) \9;
    }
}

@mixin gradientBar(
    $primaryColor,
    $secondaryColor,
    $text-color: #fff,
    $textShadow: 0 -1px 0 rgba(0, 0, 0, 0.25)
) {
    @include gradient-vertical($primaryColor, $secondaryColor);
    color: $text-color;
    text-shadow: $textShadow;
    border-color: $secondaryColor $secondaryColor darken($secondaryColor, 15%);
    border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1)
        fade-in(rgba(0, 0, 0, 0.1), 0.15);
}

@mixin nav-divider($top: #e5e5e5, $bottom: $white) {
    // IE7 needs a set width since we gave a height. Restricting just
    // to IE7 to keep the 1px left/right space in other browsers.
    // It is unclear where IE is getting the extra space that we need
    // to negative-margin away, but so it goes.
    *width: 100%;
    height: 1px;
    margin: (($line-height-base / 2) - 1) 1px; // 8px 1px
    *margin: -5px 0 5px;
    overflow: hidden;
    background-color: $top;
    border-bottom: 1px solid $bottom;
}

///
/// Visually hide an element properly.
///
/// @example scss
///   @include visually-hidden;
///   // width:  1px !important;
///   // height: 1px !important;
///   // padding: 0 !important;
///   // border:  0 !important;
///   // position: absolute !important;
///   // overflow: hidden !important;
///   // clip: rect(1px, 1px, 1px, 1px) !important;
///
@mixin visually-hidden() {
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    border: 0 !important;
    position: absolute !important;
    overflow: hidden !important;
    clip: rect(1px 1px 1px 1px) !important; /* IE6, IE7 */
    clip: rect(1px, 1px, 1px, 1px) !important;
}

@import 'loading-fa-icon';

@mixin topLeftBorderCorner($borderWidth: 8, $borderColour: $black) {
    // used to create the bordered corner on an element
    position: relative;

    &:before {
        content: '';
        display: block;
        background: transparent;
        width: #{$borderWidth * 6}px;
        height: #{$borderWidth * 6}px;
        border-top: #{$borderWidth}px solid $borderColour;
        border-left: #{$borderWidth}px solid $borderColour;
        position: absolute;
        top: #{-1 * $borderWidth}px;
        left: #{-1 * $borderWidth}px;

        @include breakup-breakpoint('thinish_extended_2') {
            width: #{$borderWidth * 9}px;
            height: #{$borderWidth * 9}px;
        }
    }
}
