$white: #ffffff;
$section-color-gray: #eeeeee;
$section-color-blue: #00284c;
$section-color-purple: #3a0033;
$section-color-thisiscity: #3a0033;
$section-color-red: #a7281d;
$gold: #ba9f61;
$black: #333333;

$section-colors: white white #333, gray $section-color-gray #333,
    blue $section-color-blue $white, purple $section-color-purple $white,
    thisiscity $section-color-thisiscity $white, red $section-color-red $white;

$section-padding: 20px;
$section-padding-large: 70px;
$section-padding-large-mobile: 45px;

body {
    -moz-font-feature-settings: 'lnum' 1;
    -moz-font-feature-settings: 'lnum=1';
    -ms-font-feature-settings: 'lnum' 1;
    -o-font-feature-settings: 'lnum' 1;
    -webkit-font-feature-settings: 'lnum' 1;
    font-feature-settings: 'lnum' 1;
}

@mixin section($color: white, $arrow: null) {
    padding-top: $section-padding;
    padding-bottom: $section-padding;
    @include breakup-breakpoint('wide') {
        padding-top: $section-padding;
        padding-bottom: $section-padding;
    }

    /*    h2, h2 > a {
        font-family: "ff-meta-web", "Helvetica Neue", Arial, Helvetica, sans-serif;
        font-size: 24px;
        line-height: 30px;
        font-weight: 600;
    }*/

    p.stand-first {
        font-size: 18px;
        line-height: 24px;
        font-weight: 500;
        padding-bottom: 25px;
    }

    @each $section-color in $section-colors {
        @if ($color == nth($section-color, 1)) {
            $backgroundColor: nth($section-color, 2);
            $textColor: nth($section-color, 3);
            background-color: #{$backgroundColor};
            color: #{$backgroundColor};

            & > * {
                color: #{$textColor};
            }
        }
    }

    @if ($arrow) {
        position: relative;
        &:after {
            content: '';
            width: 0;
            height: 0;
            border-top: 1em solid currentColor;
            border-left: 1em solid transparent;
            border-right: 1em solid transparent;
            position: absolute;
            top: 100%;
            left: 50%;
            margin-left: -1em;
        }
    }
}

@mixin section-white-links($bg) {
    a {
        &:link,
        &:visited {
            color: #eee;
            border-bottom: 2px dotted #ccc;
        }

        &:hover {
            color: white;
            border-bottom: 2px solid white;
        }

        &:focus,
        &:active {
            background-color: white;
            color: #555;
            border-bottom: 2px solid white;
        }
    }
}

.gray-section {
    position: relative;
    background: $gray-lighter;

    padding: 2px 10px 0;
    margin: 0 -10px;

    .responsive-wrapper {
        padding-top: 0;
    }

    @include breakup-breakpoint('wide') {
        padding: 2px 0 0;
        margin: 0 0;
    }
}

.section-fw-white-wrapper,
.section-normal {
    @include section;
}

.section-normal-first {
    padding-top: 0;
}

.fw-section-wrapper {
    margin: 0 -10px;
    padding: 20px 10px;

    @include breakup-breakpoint('wide') {
        padding: 20px 0;
        margin: 0;
    }

    &.section-no-padding {
        padding: 0 10px;
        @include breakup-breakpoint('wide') {
            padding: 0;
        }
    }
}

.section-fw-gray-wrapper {
    @include section(gray);
}

.section-fw-gray-arrow-wrapper {
    @include section(gray, true);
}

.section-fw-blueribbon-wrapper {
    @include section(blue);
    @include section-white-links(blue);
}

.section-fw-purple-wrapper {
    @include section(purple);
    @include section-white-links(purple);
}

.section-fw-thisiscity-wrapper {
    @include section(thisiscity);
}

.section-fw-blueribbon-wrapper,
.section-fw-purple-wrapper,
.section-fw-thisiscity-wrapper {
    padding-top: $section-padding-large-mobile;
    padding-bottom: $section-padding-large-mobile;
    @include breakup-breakpoint('wide') {
        padding-top: $section-padding-large;
        padding-bottom: $section-padding-large;
    }
}

.media-ribbon-wrapper {
    padding-top: 2em;
    padding-bottom: 2em;
    @include breakup-breakpoint('medium') {
        padding-top: 0;
        padding-bottom: 0;
    }
}

.media-ribbon {
    .text-wrapper {
        @include breakup-breakpoint('medium') {
            padding-bottom: 27.2%;
        }
        position: relative;
    }
    .text-center {
        @include breakup-breakpoint('medium') {
            height: 100%;
            position: absolute;
        }
        vertical-align: middle;
        width: auto;
        &:before {
            height: 100%;
            content: '';
            display: inline-block;
            vertical-align: middle;
            width: 0;
        }
        .text {
            display: inline-block;
            max-width: 90%;
            margin: 0 auto;
            vertical-align: middle;
            &,
            & h2 {
                font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                    sans-serif;
                font-size: 22px;
                line-height: 1.5em;
                font-weight: 400;
            }
        }
    }
    .embed-container {
        padding-top: 0;
        margin-bottom: 0;
        img {
            display: block;
            margin-bottom: 0;
        }
    }
}

.media-ribbon--img-only-wrapper {
    padding: 0;
    margin: 0;
    .text-wrapper {
        @include breakup-breakpoint('medium') {
            padding-bottom: 27.2%;
        }
        position: relative;
        height: 100%;
    }
    .text-center {
        @include breakup-breakpoint('medium') {
            height: 100%;
            position: absolute;
        }
        vertical-align: middle;
        width: auto;
        &:before {
            height: 100%;
            content: '';
            display: inline-block;
            vertical-align: middle;
            width: 0;
        }
        .text {
            display: inline-block;
            max-width: 90%;
            margin: 0 auto;
            vertical-align: middle;
        }
    }
    img {
        display: block;
        @include breakup-breakpoint('medium') {
            margin-bottom: 40px;
        }
        @include breakup-breakpoint('wide') {
            margin: 0;
        }
    }
}

.thisiscity-media-ribbon-wrapper {
    .fa {
        color: #ba9f61;
        font-size: 44px;
        margin-bottom: 1em;
        padding-right: 0.5em;
        display: block;
        height: 44px;
        float: left;
    }

    .thisiscity-media-ribbon__heading {
        float: left;
        font-size: 14px;
        font-weight: 700;
        line-height: 14px;
        margin-bottom: 1em;
        padding: 16px 0 14px 0;
        height: 44px;
        display: block;
        text-transform: uppercase;

        & + * {
            clear: left;
        }
    }

    a {
        border-bottom: 3px solid $gold;
        color: $section-color-gray;
        cursor: pointer;

        &:link {
            border-bottom: 3px solid $gold;
        }

        &:hover,
        &:focus {
            border-bottom: 3px solid $gold;
            background-color: $gold;
            color: $section-color-gray;
        }

        &:active {
            border-bottom: 3px solid $section-color-gray;
            background-color: $section-color-gray;
            color: $black;
        }
    }
}

.section-fw-imgbg-wrapper {
    position: relative;
    overflow: hidden;

    padding-top: 70px;
    padding-bottom: 70px;

    color: white;
}

.section-fw-imgbg__bg {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
    max-width: none;
}
