.international-country-list {
    margin-top: 30px;
    border-top: 2px solid lightgray;
    padding-top: 30px;

    h2,
    .letter-list {
        text-align: center;
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-weight: 600;
    }

    .letter-list {
        a,
        span {
            padding: 5px 10px;
            margin: 0 5px;
            font-weight: 600;
            display: inline-block;
        }

        span {
            color: lightgray;
        }

        a {
            color: black;
            transition: background 0.2s, color 0.2s;
            border: none;
            border-radius: 4px;

            &:hover,
            &.selected {
                background: $red1;
                color: white;
            }
        }
    }

    h3 {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        background: lightgray;
        padding: 5px 5px 1px;
        margin-bottom: 25px;
        font-weight: 600;

        .go-up {
            font-size: 0.7em;
            float: right;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    .item {
        a {
            display: block;
            text-align: center;
            border: none;
            color: black;
            transition: color 0.2s;

            img {
                display: none;
                margin: 0;
                transition: opacity 0.2s;
                @include breakup-breakpoint('thinish_extended_2') {
                    // 600px
                    display: inline;
                }
            }

            h4 {
                font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                    sans-serif;
                font-size: 1.1em;
                text-align: center;
            }

            &:hover {
                color: $red1;

                img {
                    opacity: 0.75;
                }
            }

            &:active,
            &:focus {
                color: white;
            }
        }
    }
}

.js .international-country-list {
    .go-up {
        display: none;
    }

    .letter-group.selected .go-up {
        display: block;
    }

    .letter-group.unselected {
        display: none;
    }
}

.my-country {
    h1 {
        img {
            margin-left: 20px;
        }

        .connect-with-us {
            display: inline-block;
            float: right;
        }
    }

    h3 {
        margin-top: 40px;
    }

    .widget h3 {
        margin-top: 6px;
    }

    .my-country-visit,
    .my-country-studying,
    .my-country-requirements,
    .my-country-englishreq,
    .my-country-scholarship,
    .my-country-community,
    .my-country-links {
        margin-top: 30px;
    }

    .my-country-community {
        img {
            float: left;
            margin: 0 20px 20px 0;
        }
    }

    .my-country-two-column-list-left {
        margin-bottom: 0;

        @include breakup-breakpoint('thinish_extended_2') {
            // 600px
            margin-bottom: 14px;
        }
    }

    #contact-widget {
        background: #00284b;
        padding: 20px 0;
        font-family: 'ff-meta-web', Arial, Helvetica, sans-serif;

        h2,
        .widget-content,
        .widget-title {
            color: white;
            background: none;
            border: none;
            font-size: 1em;
        }

        p,
        h2 {
            margin-bottom: 1em;
        }

        .widget-content,
        .vcard {
            padding: 0;
        }

        h2 {
            padding: 0 20px;
        }

        a {
            color: #ccc;
            border-bottom: 2px dotted #ccc;

            &:hover {
                color: white;
                border-bottom: 2px solid white;
            }

            &:focus,
            &:active {
                background: white;
                color: #00284b;
                border: none;
            }
        }
    }

    .mycountry-hotcourse {
        margin-bottom: 20px;

        .action-button {
            padding: 15px 5px;
            display: block;
            background: #db2232;
            border-radius: 0;
            text-shadow: none;
            box-shadow: none;
            text-align: center;
        }
    }

    .my-country-links {
        a {
            font-size: 1.2em;
            font-weight: 600;
        }
    }

    .connect-with-us {
        margin-top: 10px;

        .social-icon {
            float: none;
            display: inline-block;
        }
    }
}

.media-ribbon.my-country-ribbon .text-center .text {
    font-size: 1.1em;
}

p.disclaimer {
    margin: 24px 0;
    padding: 3px 0 3px 14px;
    border-left: 5px solid grey;
}

table.visa-event-table {
    width: auto;

    tr th,
    tr td {
        white-space: nowrap;
        background: none;
    }
}

.international-lil-wrapper {
    padding: 0;
}

.international-lil {
    margin-top: 20px;
    margin-bottom: 20px;
    padding-top: 20px;
    padding-bottom: 20px;

    &.international-lil-first {
        margin-top: 0;
        padding-top: 0;
    }

    .international-london-left {
        img,
        .embed-wrapper {
            margin: 0 20px 0 0;
        }

        .quote {
            font-family: Georgia, 'Times New Roman', Times, serif;
            font-style: italic;
            color: #555;
            font-size: 1.2em;

            .mark {
                color: $red1;
                font-size: 1.2em;
                font-weight: 600;
            }
        }
    }

    .international-london-right {
        border-left: 3px solid #eee;
        padding-left: 15px;

        p {
            font-weight: 600;
        }
    }
}

.section-fw-gray.international-lil .international-london-right {
    border-left-color: #ccc;
}

p.left-float-image {
    @include breakup-breakpoint('thinish_extended') {
        // 500px
        float: left;
        margin: 0 20px 0 0;
    }
}
