.destinations {
    margin-top: -30px;
    margin-bottom: 15px;
}

.destinations__form {
    background: #ccc;
    padding: 40px 0;
    text-align: center;
    position: relative;

    &:after {
        content: ' ';
        width: 0;
        height: 0;
        border-left: 15px solid transparent;
        border-right: 15px solid transparent;

        border-top: 15px solid #ccc;
        position: absolute;
        bottom: -15px;
        right: 50%;
    }
}

.destinations__select {
    width: 80%;
    padding: 10px;
    background: white;
}

.destinations__course {
    margin-top: 40px;
}

.destinations__course__charts {
    margin-top: 30px;
    border: 1px solid #ccc;
    padding: 30px 0 0;
}

.destinations__charts__chart {
    padding: 0 20px;
}

@include breakup-breakpoint('medium') {
    // 768px
    .destinations__course__charts > .row {
        div:not(:last-child) .destinations__charts__chart__wrapper {
            position: relative;
            &:after {
                content: ' ';
                position: absolute;
                height: 60%;
                top: 20%;
                right: -21px;
                width: 2px;
                background: #ccc;
            }
        }

        .destinations__charts__chart {
            padding: 0 10px;
        }

        div:first-child .destinations__charts__chart {
            padding-left: 20px;
        }
        div:last-child .destinations__charts__chart {
            padding-right: 20px;
        }
    }
}

.js {
    .destinations__charts__chart__table,
    .destinations__submit {
        display: none;
    }
}

.destinations__charts__chart__title {
    color: #888;
    margin: 0;
    font-weight: 400;
    font-size: 2em;
}

.destinations__course__tables {
    margin-top: 30px;
}

.destinations__course__salary {
    margin-top: 40px;

    span {
        color: #666;
    }
}
