.ui-helper-hidden {
    display: none;
}
.ui-helper-hidden-accessible {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}
.ui-helper-reset {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    line-height: 1.3;
    text-decoration: none;
    font-size: 100%;
    list-style: none;
}

.ui-helper-clearfix {
    display: inline-block;
    &:after {
        content: '.';
        display: block;
        height: 0;
        clear: both;
        visibility: hidden;
    }
}

* html .ui-helper-clearfix {
    height: 1%;
}

.ui-helper-clearfix {
    display: block;
}

.ui-helper-zfix {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    opacity: 0;
    filter: Alpha(Opacity = 0);
}

.ui-state-disabled {
    cursor: default !important;
}

.ui-icon {
    display: block;
    text-indent: -99999px;
    overflow: hidden;
    background-repeat: no-repeat;
}

.ui-widget-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.ui-widget {
    /*font-size: 1.1em; this rules is messed with my sizes yo*/
    .ui-widget,
    input,
    select,
    textarea,
    button {
        font-size: 1em;
    }
}

.ui-widget-content {
    background: none;
}

.ui-widget-header {
    border: none;
    background: $white;
    color: white;
    font-weight: bold;
    a {
        color: white;
    }
}

.ui-state-default,
.ui-widget-content .ui-state-default {
    background-color: #eae9e7;
    font-weight: bold;
    color: $red1;
}

.ui-state-default a {
    color: $grey6;
    text-decoration: none;
    &:link,
    &:visited {
        color: $grey6;
        text-decoration: none;
    }
}

.ui-state-hover,
.ui-widget-content .ui-state-hover,
.ui-widget-content .ui-state-focus {
}

.ui-state-hover a {
    color: $grey2;
    text-decoration: none;
    &:hover {
        color: #333;
        text-decoration: none;
        background-color: $grey2;
    }
}

.ui-state-active,
.ui-widget-content .ui-state-active {
}

.ui-state-active a {
    color: $grey2;
    text-decoration: none;
    &:link,
    &:visited {
        color: $grey2;
        text-decoration: none;
    }
}

.ui-widget :active {
    outline: none;
}

.ui-state-highlight,
.ui-widget-content .ui-state-highlight {
    border: 1px solid $grey2;
    background: white;
    color: #363636;
}

.ui-state-highlight a,
.ui-widget-content .ui-state-highlight a {
    color: #363636;
}

.ui-state-error,
.ui-widget-content .ui-state-error {
    border: 1px solid #cd0a0a;
    background: #b81900 url('i/ui-bg_diagonals-thick_18_b81900_40x40.png') 50%
        50% repeat;
    color: white;
}

.ui-state-error a,
.ui-widget-content .ui-state-error a,
.ui-state-error-text,
.ui-widget-content .ui-state-error-text {
    color: white;
}

.ui-priority-primary,
.ui-widget-content .ui-priority-primary {
    font-weight: bold;
}

.ui-priority-secondary,
.ui-widget-content .ui-priority-secondary {
    opacity: 0.7;
    filter: Alpha(Opacity = 70);
    font-weight: normal;
}

.ui-state-disabled,
.ui-widget-content .ui-state-disabled {
    opacity: 0.35;
    filter: Alpha(Opacity = 35);
    background-image: none;
}

.ui-icon {
    width: 18px;
    height: 18px;
    background-image: url('i/ui-icons_222222_256x240.png');
}

.ui-widget-content .ui-icon {
    background-image: url('i/ui-icons_222222_256x240.png');
}

.ui-widget-header .ui-icon {
    background-image: url('i/ui-icons_ffffff_256x240.png');
}

.ui-state-default .ui-icon,
.ui-state-hover .ui-icon,
.ui-state-focus .ui-icon,
.ui-state-active .ui-icon {
    background-image: url('i/ui-icons_222222_256x240.png');
}

.ui-state-highlight .ui-icon {
    background-image: url('i/ui-icons_228ef1_256x240.png');
}

.ui-state-error .ui-icon,
.ui-state-error-text .ui-icon {
    background-image: url('i/ui-icons_ffd27a_256x240.png');
}

