//accordion

.ui-accordion {
    .ui-accordion-header {
        font-family: Arial, Helvetica, sans-serif;
        cursor: pointer;
        position: relative;
        margin: 0;
        border-bottom: 1px solid #fff;
        zoom: 1;

        &:hover {
            border-bottom: 1px solid #fff;
        }
    }

    .ui-accordion-li-fix {
        display: inline;
    }

    .ui-accordion-header-active {
        border-bottom: 0 !important;
    }

    .ui-accordion-header a {
        display: block;
        padding: 8px;
        border: none;
    }
} //end ui-accordion

.ui-accordion-icons .ui-accordion-header a {
    padding-left: 1.5em;

    &:hover {
        background: #e0dbd7;
    }
}

.ui-accordion {
    .ui-accordion-header {
        a {
            &:focus,
            &:active {
                background-color: #fff;
                outline: none;
            }
        }
        .ui-icon {
            position: absolute;
            right: 0.5em;
            top: 50%;
            margin-top: -8px;
        }
    }
    .ui-accordion-content {
        padding: 1em;
        border-top: 0;
        margin-top: -2px;
        position: relative;
        top: 1px;
        margin-bottom: 2px;
        overflow: auto;
        display: none;
        zoom: 1;
    }
    .ui-state-active {
        background: #fff;

        a {
            color: #353535;
            border: 1px solid #e0dbd7;
            border-bottom: none;

            &:focus,
            &:active {
                background-color: #fff;
                outline: none;
            }
        }
    }
    .ui-accordion-content-active {
        display: block;
        border: 1px solid #e0dbd7;
        border-top: none;
    }
} //end ui-accordion

.ui-autocomplete {
    position: absolute;
    cursor: default;
    box-shadow: 0 5px 4px rgba(0, 0, 0, 0.5);
    z-index: 1000 !important;
}

* html .ui-autocomplete {
    width: 1px;
}

.ui-menu {
    background: #333;
    list-style: none;
    padding: 0 0 10px 0;
    margin: 0;
    width: 254px;
    display: block;

    .ui-menu-item {
        background-color: #333;
        margin: 0;
        padding: 0;
        width: 100%;

        a {
            text-decoration: none;
            display: block;
            padding: 0.3em 0.4em;
            margin: 0 8px;
            line-height: 1.5;
            color: $white;
            border: none;
            font-weight: bold;
            cursor: pointer;

            &.ui-state-focus,
            &.ui-state-active {
                text-decoration: underline;
            }
            &.ui-state-focus {
                background-color: $white;
                color: $red1;
            }
        }
    }
}

.ui-autocomplete li.ui-menu-item {
}

li.ui-menu-item:first-child {
    background: #333 url('i/autocomplete-down.png') no-repeat 25px 0;
    padding-top: 15px;
}

//ui-button

.ui-button {
    display: inline-block;
    position: relative;
    padding: 0;
    margin-right: 0.1em;
    text-decoration: none !important;
    cursor: pointer;
    text-align: center;
    zoom: 1;
    overflow: visible;
}

.ui-button-icon-only {
    width: 2.2em;
}

button.ui-button-icon-only {
    width: 2.4em;
}

.ui-button-icons-only {
    width: 3em;
}

button.ui-button-icons-only {
    width: 3.2em;
}

.ui-button .ui-button-text {
    display: block;
    line-height: 1.4;
}

.ui-button-text-only .ui-button-text {
    padding: 0.4em 1em;
}

.ui-button-icon-only .ui-button-text,
.ui-button-icons-only .ui-button-text {
    padding: 0;
    text-indent: -9999999px;
}

.ui-button-text-icon .ui-button-text {
    padding: 0.4em 1em 0.4em 1.8em;
}

.ui-button-text-icons .ui-button-text {
    padding: 0.4em 1em 0.4em 1.8em;
    padding-right: 1.8em;
}

input.ui-button {
    padding: 0.1em 0.1em;
}

.ui-button-icon-only .ui-icon,
.ui-button-text-icon .ui-icon,
.ui-button-text-icons .ui-icon,
.ui-button-icons-only .ui-icon {
    position: absolute;
    top: 50%;
    margin-top: -9px;
}

.ui-button-icon-only .ui-icon {
    left: 50%;
    margin-left: -8px;
}

.ui-button-text-icon .ui-icon-primary,
.ui-button-text-icons .ui-icon-primary,
.ui-button-icons-only .ui-icon-primary {
    left: 0.5em;
}

.ui-button-text-icons .ui-button-icon-secondary,
.ui-button-icons-only .ui-button-icon-secondary {
    right: 0.5em;
}

.ui-button-set {
    margin-right: 7px;
    .ui-button {
        margin-left: 0;
        margin-right: -0.3em;
    }
}

button.ui-button::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/*dialog*/

.ui-dialog {
    position: absolute;
    padding: 0.2em;
    width: 300px;
    overflow: visible;
    background: #fff;
    -moz-box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.6);
    -webkit-box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.6);
    -o-box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.6);
    box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.6);

    .ui-dialog-titlebar {
        padding: 0.5em 1em 0.3em;
        position: relative;
    }
    .ui-dialog-title {
        float: left;
        margin: 0.1em 16px 0.2em 0;
        color: $grey6;
        font-family: Georgia, 'Times New Roman', Times, serif;
        font-size: 1.8em;
        line-height: 1.4em;
        font-weight: normal;
        border-bottom: 1px solid #e7e7e7;
    }
    .ui-dialog-titlebar-close {
        margin: -25px -2px 0 0;
        padding: 1px 5px 1px 1px;
        position: absolute;
        top: 42%;
        width: 80px;
        height: 20px;
        right: 0;
        background: transparent url('i/modal-close.png') no-repeat right top;
        color: $red1;
        cursor: pointer;
        border: none;

        span {
            display: block;
            text-indent: 0;
            background: none;
            width: 65px;
            text-transform: uppercase;
        }

        &:hover {
            color: $red2;
            background-position: right -20px;
        } //end hover

        &:focus,
        &:active {
            background-color: transparent;

            span {
                background-position: right -60px;
            }
        }
    } //end ui-dialog-titlebar-close

    .ui-dialog-content {
        position: relative;
        border: 0;
        padding: 0.5em 1em;
        background: none;
        overflow: auto;
        zoom: 1;
    }
    .ui-dialog-buttonpane {
        text-align: left;
        border-width: 1px 0 0 0;
        background-image: none;
        margin: 0.5em 0 0 0;
        padding: 0.3em 1em 0.5em 0.4em;

        button {
            float: right;
            margin: 0.5em 0.4em 0.5em 0;
            cursor: pointer;
            padding: 0.2em 0.6em 0.3em 0.6em;
            line-height: 1.4em;
            width: auto;
            overflow: visible;
        }
    }
    .ui-resizable-se {
        width: 14px;
        height: 14px;
        right: 3px;
        bottom: 3px;
    }
}

.ui-draggable .ui-dialog-titlebar {
    cursor: move;
}

// tabs
#content .ui-tabs .ui-tabs-nav li a {
    font-weight: bold;
    border-bottom: none;
}

.ui-tabs {
    position: relative;
    padding: 0.2em;
    margin: 0 0 20px 0;
    zoom: 1;

    .ui-tabs-nav {
        margin: 0;
        border-bottom: 7px solid #333;
        padding: 0;

        li {
            list-style: none;
            float: left;
            padding: 0;
            white-space: nowrap;
            border-bottom: 32px solid #eae9e7;
            border-right: 24px solid transparent;
            height: 0;
            padding: 0;
            margin-right: -6px;
            cursor: pointer;
            position: relative;
            background: transparent;

            &:hover,
            &:focus {
                border-bottom: 32px solid #e7e7e7;
                a {
                    color: #333;
                    text-decoration: underline;
                }
            }
            &:focus {
                outline: none;
            }

            a {
                padding: 10px 8px 10px 15px;
                line-height: 32px;
                position: relative;
                font-size: 0.9em;
                text-decoration: none;
                cursor: pointer;
                color: #fff;
                position: relative;
                background: transparent;
                color: #333;

                &:link {
                    border-bottom: none;
                }
                &:hover {
                }
                &:focus {
                    border-top: none;
                    border-bottom: none;
                    outline: none;
                    background: transparent;
                }
                &:active {
                }
            } //end a

            &.ui-tabs-active {
                margin-bottom: 0;
                filter: alpha(opacity=100);
                opacity: 1;
                z-index: 100;
                border-bottom: 32px solid #333;

                a {
                    cursor: text;
                    color: $white !important;

                    &:hover {
                        background: transparent;
                    }
                    &:focus {
                        border-top: none;
                        border-bottom: none;
                        outline: none;
                        background: transparent;
                    }
                    &:active {
                    }
                }
            } //end active

            &.ui-state-disabled a,
            &.ui-state-processing a {
                cursor: text;
            } //end disabled
        } //end li
    } // end .ui-tabs-nav

    &.ui-tabs-collapsible .ui-tabs-nav li.ui-tabs-selected a {
        background-color: $grey2;
        cursor: pointer;
    }

    .ui-tabs-panel {
        display: block;
        border: 0;
        padding: 10px;
        background: none;
        border-color: #e0dbd7;
        border-width: 0 1px 1px 1px;
        border-style: solid;
    }
    .ui-tabs-hide {
        display: none !important;
    }
} //end tabs

