}
.ui-datepicker .ui-datepicker-prev-hover {
    top: 2px;
}
.ui-datepicker .ui-datepicker-next-hover {
    top: 2px;
}
.ui-datepicker .ui-datepicker-prev-hover span,
.ui-datepicker .ui-datepicker-next-hover span {
