.cta-strip {
    margin: 30px -10px;

    a.ghost-cta {
        display: block;
    }

    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;

        a {
            display: inline-block;
            flex-basis: 1px;
            flex-grow: 1;
        }

        a.ghost-cta {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
}

#promo-wrapper a.ghost-cta,
a.ghost-cta {
    display: inline-block;
    font-family: 'Arial', sans-serif;
    font-size: 15px;
    text-align: center;
    font-weight: 600;
    padding: 12px 20px;
    margin: 0 10px 15px;

    border: 2px solid;

    transition: background-color 100ms ease-in-out,
        border-color 100ms ease-in-out, color 100ms ease-in-out;

    &:link,
    &:visited {
        background: #fff;
        color: #555;
        border-color: #ccc;
    }

    &:hover {
        color: #111;
        background: #ccc;
        border-color: #999;
    }

    &:focus,
    &:active {
        color: #111;
        background: #ccc;
        border-color: #fff;
    }

    .fa {
        margin-left: 20px;
    }
}
