@import '../breakup';

//Custom styles for any page with a Gmap
.buildingthevision {
    #mapCanvas {
        height: 600px;
        img {
            max-width: none;
        }
    }

    .accordion {
        dt {
            color: $black;
            padding: 5px;
        }
        dt.ui-state-active {
            background-color: $sandyGrey;
        }
    }
}

// dynamic google map
#dynamic-gmap {
    width: 100%;
    height: 350px;
    border: none;

    object,
    img {
        max-width: inherit;
    }
    .infoBox {
        background-color: $white;
        padding: 2px 8px;
        border: 1px solid #ccc;
        letter-spacing: 0.03em;
    }
    .gm-style-iw {
        .marker-title {
            font-weight: bold;
            display: block;
        }
    }
}

@include breakup-breakpoint('thin') {
    // 320px
    .buildingthevision {
        #mapCanvas {
            margin-left: auto;
            margin-right: auto;
            width: 80%;
            height: 400px;
        }
    }
}
