//scoped to the backbone calendar page

.event-calendar-wrap {
    .responsive-toggler {
        @include font-family-sans-serif();
        font-size: 1em;
        line-height: 1.5em;

        a {
            font-weight: bold;
            border-bottom: 1px solid #dcdcdc;
            border: 1px solid rgba(0, 0, 0, 0.1);
        }

        i,
        span.fa {
            margin-left: 5px;
        }
    }

    .filters {
        display: none;
        padding: 2%;
        background: #f6f6f6;
        margin-bottom: 2%;

        &.active {
            display: block;
        }
    }

    // filters
    .filter-list {
        list-style: none;
        margin: 0 0 14px 0;
        padding: 0;
        overflow: hidden;

        li {
            float: left;
            padding: 1px;
            cursor: pointer;
        }
        .filter {
            display: block;
            border-bottom: none;
            padding: 5px;
            border-radius: 5px;
            color: #00739b;

            &.selected {
                background-color: #e3f0fd;

                &:after {
                    @include fa-pseudo-icon(times) background-image: none;
                    background-position: 0 0;
                    background-repeat: repeat;
                    color: #cccccc;
                    display: inline;
                    height: auto;
                    line-height: normal;
                    margin-left: 8px;
                    margin-top: 0;
                    text-decoration: inherit;
                    text-indent: -99999px;
                    vertical-align: baseline;
                    width: auto;
                    cursor: pointer;
                }
                &:hover {
                    &:after {
                        color: #00739b;
                    }
                }
                &:focus {
                    background-color: #e3f0fd;
                }
            }
            &:hover {
                background-color: #e3f0fd;
            }
            &:focus {
                background-color: $white;
            }
        }
    }
    #fb-summary {
        font-weight: bold;
    }

    //each event in the results list
    .event {
        i,
        span.fa {
            color: #666;
        }
        .date {
            margin-bottom: 0;
        }
    }
    .event-top {
        margin-bottom: 14px;
    }
    .event-title {
        font-size: 1.4em;
        margin-bottom: 10px;
    }
    .date {
        margin-bottom: 0;
    }
    .event-summary {
        margin: 0;
    }
}

.filter-group {
    border-top: 1px solid #ccc;

    &.first {
        border-top: none;
    }
}

@include breakup-breakpoint('medium') {
    // 767px

    .js .event-calendar-wrap .filters {
        display: block;
    }

    .event-calendar-wrap {
        .responsive-toggler {
            display: block;
            font-family: $headings-font-family;
            font-size: 1.8em;

            i,
            span.fa {
                display: none;
            }

            a {
                font-weight: normal;
                background: #fff;
                filter: none;
                padding: 0;
                border-radius: 0;
                border: none;
                cursor: default;

                &:hover {
                    border: none;
                }
                &:active {
                    box-shadow: none;
                    text-decoration: none;
                }
            }
        }
        .filters {
            background: transparent;
            padding: 0;
        }
        .filter-list {
            li {
                padding: 1px;
            }
        }
    }
}
