#digital-shoreditch #content {
    font-size: 1em;

    a {
        font-weight: bold;
    }
}
#digital-shoreditch h1 {
    display: none;
}

#digital-shoreditch #digital-twitter h2 a {
    font-weight: normal;
}

#container #digital-search {
    label {
        display: block;
        float: none;
        padding: 0;
        margin: 0 0 14px 0;
    }
    p {
        margin: 0;
    }
    #search-query {
        width: 50%;
    }
}

#digital-shoreditch #competition {
    padding: 15px 0;
}

#coverage {
    margin-bottom: 20px;
    @include clearfix;
    clear: both;

    a {
        font-weight: bold;
    }
}

#digital-map {
    border: 1px solid $grey3;
    padding: 9px;
    background: $grey1;
    margin-bottom: 20px;
}
#container #content #slidedeck_frame {
    width: 1000px;
    height: 340px;
    padding: 10px;
    margin: 0 0 20px 0;
}

#digital-events {
    display: inline;
    float: left;
    margin-right: 70px;
    width: 50%;
}

#digital-search {
    .search-button {
        width: 100px;
    }
}

#digital-twitter iframe {
    height: 720px;
}

#promo-wrapper .carousel-media .techimg {
    margin: 16% 24%;
}

#promo-wrapper ul .c1 li {
    background-image: none;
    background-color: transparent;
}
