/** Responsive code that is not related to any specific component or site */
//@import "../breakup";

@include breakup-breakpoint('thin') {
    //320px
    @include make-grid(xxs);
}

@include breakup-breakpoint('thinish') {
    // 480px
    @include make-grid(xs);
}

@include breakup-breakpoint('thinish_extended') {
    // 500px
    @include make-grid(s);
}

@include breakup-breakpoint('thinish_extended_2') {
    // 600px
    @include make-grid(sm);
}

@include breakup-breakpoint('medium') {
    // 767px

    @include make-grid(md);

    object embed {
        width: 100%;
    }
}

@include breakup-breakpoint('wide') {
    // 980px

    @include make-grid(lg);

    body {
        padding: 0;
    }

    .responsive-wrapper {
        padding-top: 20px;

        #content {
            //padding: 0 0 20px 0;
        }
    }

    #container,
    .container-multiple {
        width: 980px;
    }
}

@include breakup-breakpoint('wide_extended') {
    // 1035px

    @include make-grid(xl);

    .children {
        position: absolute;
    }

    #search-form {
        left: -1px;
    }

    #container,
    .container-multiple {
        width: 1035px;
    }
}

@include breakup-breakpoint('wider') {
    // 1200px
    @include make-grid(xxl);

    #container,
    .container-multiple {
        width: 1200px;
    }
}
