// Component reused accross the site

// anything that is a list component
// for now, used on:
// library site
// /research
// event section

.item-list {
    margin: 0;
    list-style-type: none;

    .article {
        margin: 0;
        list-style-type: none;
        border-bottom: 1px solid #cccccc;
        padding: 20px 0;
        overflow: hidden;
    }
    h2 {
        margin: 0 0 12px 0;
        @include font-sans-serif(1.3em, bold, 1.3em);
    }
    .title {
        font-weight: bold;
    }
    .thumbnail {
        float: left;
        margin: 0 10px 10px 0;
        width: 40%;
    }
    .summary {
        clear: both;
    }

    @include breakup-breakpoint('thinish_extended') {
        // 500px
        .thumbnail {
            width: auto;
            margin: 0 20px 0 0;
        }
        .summary {
            clear: none;
        }
    }
}

//end list compoment

//search box
.module-search {
    clear: left;
    margin-bottom: 20px;
    background: #333333;
    color: #fff;
    overflow: hidden;

    fieldset {
        margin: 0;
        padding: 20px;
        background: #333333;
        border: none;
    }

    label {
        display: inline;
        font-size: 1.35em;
        padding: 9px 0 8px 0;
    }
    p {
        margin: 0;
        overflow: hidden;
    }
    a {
        color: #fff;
    }
    #search-query {
        background-color: #f7f7f7;
        border: medium none;
        display: block;
        float: left;
        clear: both;
        font-size: 1.4em;
        height: 35px;
        line-height: 27px;
        min-height: 27px;
        padding: 4px 5px;
        width: 80%;
    }
    .search-button {
        background-color: #f7f7f7;
        border: none;
        cursor: pointer;
        height: 35px;
        margin: 0;
        width: 35px;

        .fa-search {
            font-size: 1.5em;
            color: #b02a1a;
        }
    }
    .search-advanced {
        text-align: left;
        margin: 10px 0;
    }

    @include breakup-breakpoint('thin') {
        // 320px
        #search-query {
            width: 80%;
        }
    }
    @include breakup-breakpoint('thinish') {
        // 480px
        p {
            clear: both;
        }
        label {
            float: left;
        }
        .search-advanced {
            float: right;
            clear: none;
        }
    }
    @include breakup-breakpoint('thinish_extended') {
        // 500px
        #search-query {
            width: 89%;
        }
    }
    @include breakup-breakpoint('wide') {
        // 980px
        label {
            display: block;
        }
        #search-query {
            margin-top: 1px;
            width: 92%;
        }
        .search-button {
            margin-top: 1px;
            width: 41px;
        }
        .search-advanced {
            float: right;
        }
    }
    @include breakup-breakpoint('wider') {
        // 1200px
        #search-query {
            width: 94%;
        }
        .search-button {
            width: 37px;
        }
    }
}
//end search box

// best bets

.best-bet {
    background: #f7f7f7;
    padding: 20px;
    margin: 0 0 20px 0;
    position: relative;
    border: 1px solid #e7e7e7;

    .best-bet__label {
        font-size: 0.8em;
        position: absolute;
        top: 5px;
        right: 10px;
        color: #999;
    }
    .best-bet__title {
        margin: 0;
    }
    .best-bet__summary {
        margin-bottom: 0;
    }
    .best-bet__url {
        color: #999999;
        font-size: 0.9em;
    }
}

// end best bets

//log in box
.account-log-in {
    @include gradient-vertical($sandyGrey, $sandyGreyDark);
    margin-bottom: 20px;
    height: 118px;

    p {
        padding: 10px 10px 10px 13px;
        margin: 0;
    }
    .cta {
        width: 100%;
        text-align: center;
        padding: 0;
        margin-top: 2%;
    }
    a {
        color: $white;
        border-bottom: none;
        @include gradient-vertical(#ad2817, #991b0d);
        @include border-radius(4px);
        @include box-shadow(0 7px 6px -6px rgba(0, 0, 0, 0.6));
        font-size: 14px;
        line-height: 30px;
        padding: 2px 0;
        width: 30%;
    }

    @include breakup-breakpoint('thinish_extended') {
        // 500px
        p {
            float: none;
            width: auto;
            padding-bottom: 0;
        }
        .hard-cta {
            position: static;
            text-align: center;
            width: 100%;
            padding: 0;

            a {
                width: 50%;
                margin: 0 auto;
            }
        }
    }
}

//end log in box

//opening time
.opening-times {
    padding: 16px 10px 10px;
    background-color: $sandyGrey;
    margin-bottom: 20px;

    h2 {
        float: left;
        margin: 0 0 14px 0;
        padding: 0;
        font-size: 1.35em;
    }
    #todays-date {
        background-color: $white;
        border: 1px solid $grey3;

        float: right;
        padding: 4px 6px;
        position: relative;
        @include border-radius(4px);
    }
    ul {
        clear: both;
        list-style-type: none;
        padding: 0;
        margin: 0;
    }
    li {
        padding: 10px;
        overflow: hidden;
        a {
            float: left;
            margin-right: 13px;
            font-size: 0.95em;
            font-weight: bold;
        }
        p {
            clear: both;
            float: left;
            margin-bottom: 0;
            margin-top: 5px;
            padding: 0;
        }
        &.opening-times-last {
            border: none;
        }
    }
    .odd a:hover {
        border-bottom: 1px solid $grey1;
    }
    .even a:hover {
        border-bottom: 1px solid $sandyGrey;
    }
    p {
        text-align: right;
    }
}

//end opening time box

//contact box
.module-contact {
    @include gradient-vertical(#eae9e7, #e0dbd7);
    padding: 5px 15px;
    overflow: hidden;

    h2 {
        margin-bottom: 0;
    }
    .contact-address,
    .adr {
        margin-top: 10px;
    }
}

//end contact box

