.news-events-aggregate {
    margin: 20px 0;
    background-color: #f5f5f5;
    padding: 5px 10px 20px;

    .events {
        padding-top: 10px;
        background-color: $white;

        .event {
            margin-bottom: 0;
        }
    }
}

@include breakup-breakpoint('thin') {
    // 320px
    .news-events-aggregate {
        .spotlight-on-research,
        .city-news {
            .detail {
                bottom: 0;
                position: absolute;
                width: 100%;
            }
            .description {
                border: 0 none;
                clip: rect(0px, 0px, 0px, 0px);
                display: block;
                height: 1px;
                margin: -1px;
                overflow: hidden;
                position: absolute;
                width: 1px;
            }
            a:hover .description {
                clip: auto;
                height: auto;
                margin: 0;
                overflow: visible;
                position: static;
                width: auto;
            }
        }
    }
}

.article-listing {
    overflow: hidden;

    &.ul {
        list-style-type: none;
    }
    .article {
        margin-bottom: 25px;
        position: relative;
        overflow: hidden;
        border: 1px solid #ededed;

        a,
        a:hover {
            color: #333;
        }
    }
    .detail {
        padding: 5px 0;
    }
    .event {
        background-color: $white;
        padding: 10px;
        border: none;

        .detail {
            background-color: $white;
            float: right;

            i,
            span.fa {
                margin-right: 5px;
            }
        }
        .date {
            margin: 0 10px 5px 0;
            width: 50px;
        }
        .title {
            margin-bottom: 5px;
            font-size: 0.9em;
        }
        .date-day-no {
            border-bottom: 1px solid #ccc;
            padding-bottom: 3px;
        }
        .time,
        .category,
        .audience {
            float: left;
            margin-right: 10px;

            i {
                margin-right: 3px;
            }
        }
        .description {
            clear: both;
            padding: 0;
        }
    }
    .spotlight-on-research,
    .city-news {
        a:hover img {
            opacity: 0.8;
        }
        .detail {
            background-color: $white;

            &:after,
            &:before {
                bottom: 100%;
                border: solid transparent;
                content: ' ';
                height: 0;
                width: 0;
                position: absolute;
                pointer-events: none;
            }
            &:after {
                border-color: rgba(136, 183, 213, 0);
                border-bottom-color: $white;
                border-width: 10px;
                left: 15%;
                margin-left: -10px;
            }
            &:before {
                border-color: rgba(194, 225, 245, 0);
                border-bottom-color: $white;
                border-width: 16px;
                left: 15%;
                margin-left: -16px;
            }
        }
        .title {
            margin: 10px;
        }
    }
    .thumbnail {
        display: block;
        margin: 0;
        width: 100%;
    }
    img {
        margin-bottom: 0;
    }
    .title {
        font-size: 1.15em;
        margin: 0;
        font-family: $font-family-sans;
    }
    .description {
        margin-bottom: 0;
        padding: 0 10px 10px 10px;
    }

    @include breakup-breakpoint('wide') {
        // 980px
        .event .title {
            font-size: 1.15em;
        }
    }
}

.contact-list {
    list-style-type: none;
    margin: 0;
    padding: 10px 0 0;
    overflow: hidden;

    .contact {
        margin-bottom: 20px;
    }
    .name {
        display: block;
        margin-bottom: 10px;
        font-weight: bold;
        border-bottom: 1px solid $red3;
        padding-bottom: 4px;
    }
    .email {
        display: block;
    }
    .tel {
        display: block;
    }
    .fax {
        display: block;
    }
    .name,
    .email,
    .tel,
    .fax {
        span {
            font-weight: bold;
        }
    }
    .address {
        margin-top: 8px;

        span {
            display: block;
        }
    }

    @include breakup-breakpoint('medium') {
        // 767px
        .contact {
            margin-bottom: 0;
        }
    }
}

/**
 * Announcements Box - pulled from academic-units and subject-areas
 */
.announcements {
    background-color: #e3f0fd;
    border: 1px solid #3a87ad;
    color: #3a87ad;
    font-size: 1.2em;
    margin: 0 0 20px 0;
    padding: 10px;
    .subject-area & {
        margin: 20px 0;
    }
}

.standout-warning {
    margin: 24px 0;
    padding: 3px 0 3px 14px;
    border-left: 10px solid #a22833;
    font-size: 1.2em;
}

.image-under {
    position: relative;
    top: 0;
    z-index: -999;
    width: 100%;
    height: 100%;
    display: block;
    margin: 0;
}

.text-over {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    color: $white;
}

/* generic search section */
.search-section {
    padding-bottom: 0.5em;

    .search-section-label {
        margin: 0 0 1em;
    }
    .search-query,
    .dropdown-select .display,
    select {
        border: none;
        width: 100%;
        height: 3em;
        line-height: 3em;
        padding: 0 1em;
        font-size: 1.2em;
        margin-bottom: 0.5em;
        cursor: pointer;
        @include breakup-breakpoint('medium') {
            margin-bottom: 0;
        }
    }
    .search-query {
        cursor: inherit;
    }
    .dropdown-select {
        .fa-chevron-down,
        .fa-chevron-up {
            margin-right: 1em;
        }
        .options {
            width: 91%;
            @include breakup-breakpoint('medium') {
                width: 92.5%;
            }
        }
        a {
            cursor: pointer;
        }
    }
    .search-refine-wrapper {
        margin: 1em 0 0;
    }
    .search-submit {
        background-color: #b02a1a;
        width: 100%;
        border: none;
        height: 3em;
        line-height: 3em;
        color: $white;
        font-size: 1.2em;
        cursor: pointer;
        padding-left: 0.5em;
        text-align: left;
        margin-bottom: 0.5em;
        @include breakup-breakpoint('medium') {
            margin-bottom: 0;
        }

        .fa-search {
            display: block;
            float: right;
            font-size: 1.1em;
            line-height: 2.5em;
            padding-right: 0.2em;
        }
    }
    .search-section-links {
        .search-section-link {
            margin: 0 1em 1em 0;
            @include breakup-breakpoint('thinish_extended_2') {
                margin-top: 1em;
            }
            @include breakup-breakpoint('medium') {
                margin-top: 0;
            }
        }
    }
    .search-section-button {
        display: inline-block;
        clear: both;
        margin-bottom: 1em;
        float: left;
        padding: 0 12px;
        display: block;
        background-color: #ccc;
        height: 3.6em;
        line-height: 3.6em;
        border: none;
        color: $white;
        background-color: #333;
        @include breakup-breakpoint('thinish_extended_2') {
            float: right;
            //margin-top: 0;
        }
    }

    .library-search__content-type {
        color: $red1;
        background: #fff;
        font-weight: 600;
    }
}

/**
 * Image Credits
 *
 * Assumes the parent has `position: relative;`.
 */
.image-credit {
    color: $white;
    width: auto;
    max-width: 100%;
    min-height: 2.5em;
    padding: 0.125em;
    position: absolute;
    top: 0;
    right: 0;
    overflow: hidden;
    z-index: 2;
    background-color: #222;
    line-height: 2em;

    &[aria-expanded='true'] {
        background-color: #222;
    }

    &:hover {
        background-color: #333;
    }

    .news-card & {
        z-index: 4;
    }

    .image-credit__button {
        background-color: transparent;
        color: $white;
        width: 2.25em;
        height: 2.25em;
        float: right;
        position: relative;
        border: 0;
        cursor: pointer;

        // http://fontawesome.io/icon/info-circle/
        // http://fontawesome.io/icon/info
        .fa-info-circle,
        .fa-info {
            color: inherit;
            font-size: 1.357em; // 19px
            font-family: 'FontAwesome';
            transition: all 0.2s ease;
        }
        &[aria-pressed='false'] {
            .fa-info-circle {
                display: none;
            }
        }
        &[aria-pressed='true'] {
            .fa-info-circle {
                display: none;
            }
        }
    }

    .image-credit__text {
        display: table-cell;
        padding: 0.588em; // = 0.5rem / 0.85em
        font-size: 0.85em;
        line-height: 1.5;

        a {
            &:link,
            &:visited {
                color: $white;
                text-decoration: none;
                border-bottom: 1px dotted $white;
            }

            &:active {
                border-bottom-color: transparent;
            }
        }
    }
    .image-credit__button[aria-pressed='false'] {
        & + .image-credit__text {
            @include visually-hidden;
            display: none;
        }
    }
}
.image-credit--bottom-right {
    bottom: 0;
    top: auto;
}
.image-credit--top-left {
    left: 0;
    right: auto;

    .image-credit__button {
        float: left;
    }
}

/**
 * Image Creditation for scp
 *
 */

.credits {
    position: relative;
    padding-top: 1em;
    color: $grey5;
    line-height: 1.5em;
    margin-top: 2em;
    font-size: 12px;
    clear: both;

    &:before {
        content: '';
        background-color: $black;
        width: 10em;
        height: 0.2em;
        position: absolute;
        top: 0;
        left: 0;
    }
}
.credits-items {
    list-style-type: none;
    margin: 0;
}

.credits-title {
    margin: 0 0 1em 0;
    display: inline-block;
    font-size: 1.3em;
    cursor: pointer;

    .fa-info-circle {
        margin-right: 0.2em;
    }

    &:after {
        font-family: 'FontAwesome';
        content: '\f0d7';
        display: inline-block;
        width: auto;
        height: auto;
        margin-left: 0.5em;
    }

    &.credits-title--active {
        &:after {
            content: '\f0d8';
        }
    }

    .fa-info-circle {
        display: none;
    }

    &:hover {
        color: $black;
    }
}

.credits-item {
    clear: both;
    padding: 0.5em 0;
}

.credits-item-thumb {
    float: left;
    margin: 0.3em 0 2em 0;

    img {
        width: 75%;
        margin: 0;
    }
}

.credits-item-author,
.credits-item-source,
.credits-item-licence {
    margin: 0;
}

.credits-item-origin {
    margin-right: 0.5em;
    font-weight: bold;
}

.left-border-item {
    border-left: thick solid #333;
    padding-left: 10px;
}

.cyclic-popup.ui-dialog {
    .ui-dialog-buttonpane {
        padding-left: 14px;
    }

    .ui-dialog-buttonpane .ui-dialog-buttonset button {
        float: left;
        background: none;
        border: none;
        padding: 0;

        span {
            padding: 0;
            color: #666;
        }

        &:nth-child(1) {
            span:before {
                content: '\f060';
                font-family: FontAwesome;
                padding-right: 10px;
            }
        }

        &:nth-child(2) {
            float: right;

            span:after {
                content: '\f061';
                font-family: FontAwesome;
                padding-left: 10px;
            }
        }
    }
}

.js .cyclic-popup-item__detail {
    display: none;
}

.cyclic-popup-item__name {
    font-weight: 600;
    font-size: 1.3em;
}
