@mixin divider {
    height: 4px;
    width: 80px;
    margin: -28px auto 30px;
    position: relative;
}

$profile_width_non_mobile: '95%';

// 2019

// Student level color modifiers
.course.course-ug .fw-section-wrapper.course__testimonials__wrapper .yr_2019 {
    @include breakup-breakpoint('medium') {
        .col-sm-8.col-sm-push-16.col-md-6.col-lg-4 {
            width: $profile_width_non_mobile;
        }
    }
    .course__profiles__item--student {
        .course__profiles__item__text__title {
            color: #a51b36;
        }
        .block {
            background: #a51b36;
            @include divider;
        }
    }
}
.course-pg .fw-section-wrapper.course__testimonials__wrapper .yr_2019 {
    @include breakup-breakpoint('medium') {
        .col-sm-8.col-sm-push-16.col-md-6.col-lg-4 {
            width: $profile_width_non_mobile;
        }
    }
    .course__profiles__item--student {
        .course__profiles__item__text__title {
            color: #4f2842;
        }
        .block {
            background: #4f2842;
            @include divider;
        }
    }
}

.fw-section-wrapper.course__testimonials__wrapper .yr_2019 {
    padding: 0 0 40px;
    margin-top: -40px;
    .course__profiles__item {
        color: black;
        padding: 0 $measure-small 70px;
        background: white;
        text-align: center;
    }

    .course__profiles__item__image {
        background: white;
        text-align: center;
        margin-bottom: $measure-default;

        span.fa {
            padding: 10px 0 0;
            font-size: 8em;
            color: #555;
        }
    }

    .course__profiles__item__image__name {
        padding: 15px 15px 5px;
        font-size: 1.2em;
        font-weight: 600;
    }

    .course__profiles__item {
        background: transparent;
    }

    // Custom academic profile styles
    .course__profiles__item--academic {
        .course__profiles__item__text__title {
            color: #243f53;
            left: 0;
        }
        @include breakup-breakpoint('medium') {
            .course__profiles__item__text__title {
                color: #243f53;
                left: 6%;
            }
        }
        .block {
            @include divider;
            background: #0076a0;
            left: 0;
        }
        @include breakup-breakpoint('medium') {
            .block {
                @include divider;
                color: #243f53;
                left: 6%;
            }
        }
        i {
            background: #0076a0;
            color: white;
            border-radius: 90px;
            padding: 1px;
            font-size: 20px;
            top: 12px;
            position: relative;
            z-index: 100;
            right: 50px;
        }
        .academic__wrapper {
            display: flex;
            .academic__wrapper__thumb {
                width: 120px;
                height: 120px;
                img {
                    width: auto;
                    height: auto;
                }
            }
            .course__profiles__item__image__name {
                text-align: left;
                padding: 15px 15px 15px;
                font-size: 1.2em;
                font-weight: 600;
                a {
                    color: #b91b1b;
                }
            }
            .course__profiles__item__text__further {
                padding: 15px 15px 5px;
                font-size: 1.2em;
            }
        }
        svg {
            position: relative;
            top: 55px;
            z-index: 99;
            left: 16px;
            margin-top: -82px;
        }
        // Larger screen adjustments
        @include breakup-breakpoint('medium') {
            // .academic__wrapper {
            //     display: flex;
            // }
            // Triangle effect
            .col-sm-8.col-sm-push-16.col-md-6.col-lg-4 {
                width: 54% !important;
                margin: 0 20% !important;
            }
            .col-sm-8.col-sm-push-16.col-md-6.col-lg-4::after {
                position: absolute;
                top: 0%;
                right: 43%;
                margin-right: -55%;
                content: '';
                width: 12%;
                height: 100%;
                background: -webkit-gradient(
                    linear,
                    right top,
                    left bottom,
                    color-stop(49.5%, #eae9e7),
                    color-stop(50%, transparent)
                );
                background: -webkit-linear-gradient(
                    right top,
                    #eae9e7 49.5%,
                    transparent 50%
                );
                background: linear-gradient(
                    to right bottom,
                    #fff 49.5%,
                    transparent 50%
                );
            }
        }
    }

    // Title base styles, e.g. 'Student profile'
    .course__profiles__item__text__title {
        @include font-family-meta-sans;
        position: relative;
        font-size: 1.1em;
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-weight: 600;
        text-transform: uppercase;
        margin: 10px 0 0;
        text-align: center;
        &:after {
            position: absolute;
            content: ' ';
            left: 0;
            width: 80px;
            height: 0;
            margin: 24px 44.5%;
        }
    }

    .course__profiles__item__text__strapline {
        font-size: 1.2em;
        @include font-family-meta-sans;
        font-weight: 500;
    }

    .course__profiles__item__text__quote {
        @include font-family-meta-sans;
        font-size: 1.6em;
        line-height: 1.3em;
        width: 90%;
        margin: 0 auto;
        font-weight: 300;
        span {
            font-size: 1.4em;
        }
    }

    .course__profiles__item__text__further {
        font-size: 1.2em;
    }

    .course__profiles {
        position: relative;

        .col-sm-8.col-sm-push-16.col-md-6.col-lg-4 {
            margin: 0 auto;
            float: none;
            left: 0;
            width: 95%;
            background: white;
            padding: 20px;
        }

        ul.items {
            margin: 0;
            li {
                padding: 70px 10px;
            }
        }

        .bx-controls {
            display: block;
        }

        .bx-controls-direction {
            position: absolute;
            left: 50%;
            bottom: 0;
            width: 0;
            height: 0;

            a {
                display: block;
                position: absolute;
                bottom: 0;

                border: 0;
                padding: $measure-default $measure-large;

                color: #ddd;
                background: rgba(0, 0, 0, 0.4);
                font-size: 1.4em;

                &:focus,
                &:hover {
                    background: rgba(0, 0, 0, 0.22);
                }
                &:active {
                    color: white;
                }
            }

            .bx-prev {
                right: 0;
            }
            .bx-next {
                left: 0;
            }
        }
    }

    // Photo cutouts
    .circular__crop {
        max-width: 150px;
        height: auto;
        position: relative;
        overflow: hidden;
        margin: -60px auto 0;
        img {
            border-radius: 50%;
            width: 90px;
            height: 90px;
        }
    }
    .circular__crop__image {
        display: block;
        margin: 0 auto;
        height: 90px;
        width: 90px;
        border-radius: 50%;
        background-size: auto;
    }

    // Testing - image positioning
    #fring.circular__crop__image {
        background: url('https://www.city.ac.uk/__data/assets/image/0011/201071/FRING.jpg')
            center no-repeat;
        background-position: 50% 30%;
    }
    #de-allie.circular__crop__image {
        background: url('https://www.city.ac.uk/__data/assets/image/0007/161269/De-Allie-resized.jpg')
            center no-repeat;
        background-position: 38% 15%;
    }
}

