.no-scroll {
    overflow-y: hidden;
}
.dialog {
    background: rgba(30, 30, 30, 0.7);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;

    button {
        @include fa-icon-extend('times');
        transition: transform 200ms;
        background-image: none;
        color: inherit;
        text-indent: 0;
        font-size: 50px;
        width: 50px;
        height: 50px;
        color: #fff;
        position: absolute;
        top: 10px;
        right: 10px;
        background-color: transparent;
        border: none;

        &:hover {
            cursor: pointer;
            color: #aaa;
        }
    }

    .dialog__box {
        width: 500px;
        display: inline-block;
        flex: auto;
        overflow-y: auto;

        iframe {
            display: block;
            margin-right: auto;
            margin-left: auto;
        }
    }
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.shortcourse-banner {
    .media-block__wrapper {
        max-height: inherit;
        position: relative;

        .video-preview {
            max-height: inherit;
            top: 0;
            left: 0;
            right: 0;

            &:before {
                content: '';
                width: 100%;
                height: 100%;
                position: absolute;
                z-index: 1;
                background: rgba(62, 16, 49, 0.5);
                -moz-transition: background 0.3s linear;
                -webkit-transition: background 0.3s linear;
                -o-transition: background 0.3s linear;
                transition: background 0.3s linear;
                transition: 2s ease;
            }

            &:hover:before {
                opacity: 0.9;
                background: none;
            }

            .video-preview__button {
                position: absolute;
                background-color: transparent;
                z-index: 2;
                border-bottom: none;
                left: 50%;
                top: 35%;

                svg {
                    fill: none;
                    .stroke-dotted {
                        opacity: 0;
                        stroke-dasharray: 1, 8;
                        stroke-width: 4px;
                        stroke-linecap: round;
                        transform-origin: 50% 50%;
                        animation: spin 20s infinite linear;
                        transition: opacity 1s ease, stroke-width 1s ease;
                        stroke: #fff;
                    }

                    .stroke-solid {
                        stroke-dashoffset: 0;
                        stroke-dasharray: 300;

                        stroke-width: 4px;
                        transition: stroke-dashoffset 1s ease, opacity 1s ease;
                        stroke: #fff;
                    }

                    .icon {
                        transform-origin: 50% 50%;
                        transition: transform 200ms ease-out;
                        fill: #fff;
                    }

                    .circle {
                        fill: #fff;
                        fill-opacity: 0.2;
                    }

                    &:hover {
                        .stroke-dotted {
                            stroke-width: 4px;
                            opacity: 1;
                        }
                        .stroke-solid {
                            opacity: 0;
                            stroke-dashoffset: 200;
                        }
                        .icon {
                            transform: scale(1.3);
                        }
                        .circle {
                            fill: #fff;
                            fill-opacity: 0;
                        }
                    }
                }
            }

            @include breakup-breakpoint('thin') {
                .video-preview__button {
                    left: 43%;
                    top: 25%;

                    svg {
                        width: 50px;
                        height: 50px;
                    }
                }
            }

            @include breakup-breakpoint('medium') {
                .video-preview__button {
                    left: 43%;
                    top: 35%;

                    svg {
                        width: 80px;
                        height: 80px;
                    }
                }
            }

            @include breakup-breakpoint('wider') {
                .video-preview__button {
                    left: 50%;
                    top: 35%;

                    svg {
                        width: 100px;
                        height: 100px;
                    }
                }
            }
        }
    }
}
