.profile-hallway {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    > div {
        display: flex;
        flex-direction: column;
    }
}

.profile-hallway__item {
    margin-bottom: 20px;

    img {
        width: 100%;
    }

    display: flex;
    flex-grow: 1;
    flex-direction: column;

    a {
        display: flex;
        flex-grow: 1;
        flex-direction: column;
        border: none;
        background: none;

        .profile-hallway__item__title-text {
            border-bottom: 1px dotted white;
        }

        &:hover .profile-hallway__item__title-text {
            border-bottom: 1px solid white;
        }

        &:active,
        &:focus {
            .profile-hallway__item__title-text {
                background: white;
                color: #333;
            }
        }
    }
}

.profile-hallway__item__info {
    flex-grow: 1;
}
