.footer2016 {
    background: #fff;
    margin: 0 -10px;
    padding: 0 0 10px 0;
    position: relative;

    @include breakup-breakpoint('wide') {
        margin: 0;
    }

    .container-multiple {
        max-width: 100%;
    }

    &__social,
    &__location {
        background: #a22833;
        color: #fff;
        text-align: center;

        @include breakup-breakpoint('medium') {
            text-align: left;
        }
    }

    &__social {
        padding: 20px 10px;

        @include breakup-breakpoint('medium') {
            padding-top: 15px;
        }
    }

    &__location {
        padding: 15px 10px;
        @include breakup-breakpoint('medium') {
            padding-top: 30px;
        }
    }

    &__misc,
    &__info {
        background: #fff;
        color: #000;
        padding: 10px 10px;
    }

    .location-panel {
        margin-bottom: 20px;

        &__heading {
            color: #fff;
        }

        p {
            margin-bottom: 5px;
        }
    }

    .social-panel {
        border-bottom: 2px solid rgba(190, 105, 113, 0.2);
        padding-bottom: 10px;
    }

    .social-panel__icons {
        margin: 10px 0 0 0;

        @include breakup-breakpoint('medium') {
            display: inline;
            margin: 0 0 0 40px;
        }
    }

    .quick-panel,
    .promo-panel {
        text-align: center;
        padding: 10px 0;
        margin-bottom: 0;
    }

    .promo-panel {
        border-bottom: 2px solid rgba(170, 170, 170, 0.2);
        padding: 10px 0 25px 0;

        &__image {
            margin: 0;
        }
    }

    .info-copyright {
        font-weight: bold;
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .info-logo {
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin: 0 0 10px 0;
    }

    .link {
        border: none;

        &,
        &:link,
        &:visited {
            color: #000;
        }

        &:hover {
            border-bottom: 1px solid #000;
        }

        &:focus,
        &:active {
            background: #fff;
            border-bottom: 1px dotted #000;
        }
    }

    .link--social-text,
    .link--location,
    .link--goto {
        &,
        &:link,
        &:visited {
            color: #fff;
        }

        &:hover {
            border-bottom: 1px solid #fff;
        }

        &:focus,
        &:active {
            background: #fff;
            color: #555;
            border: none;
        }
    }

    .link--social-text {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-size: 1.4em;

        &__span {
            margin-left: 10px;
        }
    }

    .link--goto {
        &__span {
            margin-left: 5px;
        }
    }

    .link--goto-mobile {
        @include breakup-breakpoint('wide') {
            display: none;
        }
    }

    .link--goto-desktop {
        display: none;

        @include breakup-breakpoint('wide') {
            display: inline-block;
            float: right;
            margin-right: 20px;
            margin-top: 7px;
        }
    }

    .link--social-icon {
        font-size: 1.7em;
        margin: 0 23px 5px 0;
        display: inline-block;

        &,
        &:link,
        &:visited {
            color: #fff;
        }

        &:hover {
            color: #000;
            border: none;
        }

        &:focus,
        &:active {
            background: #fff;
            color: #555;
            border: none;
        }
    }

    .link--map {
        display: inline-block;
        max-width: 100%;

        &__image {
            margin: 10px 0 5px 0;
            max-height: 150px;
            display: block;
            border: 4px solid #fff;
        }

        &__text {
            color: #fff;
        }

        &:hover {
            border: none;

            .link--map__image {
                opacity: 0.8;
            }

            .link--map__text {
                border-bottom: 1px solid #fff;
            }
        }

        &:focus,
        &:active {
            border: none;

            .link--map__image {
                opacity: 0.8;
            }

            .link--map__text {
                background: #fff;
                color: #555;
            }
        }
    }

    .footer2016__misc,
    .footer2016__info {
        .link--image {
            display: inline-block;
            border: 2px solid transparent;

            &:hover {
                border: 2px solid #ddd;
            }
        }
    }

    .link--quick {
        margin-right: 15px;
        line-height: 2;
        white-space: nowrap;
    }

    .link--offset {
        margin-left: 25px;
    }
}
