.survey-message {
    -webkit-transition-duration: 600ms;
    transition-duration: 600ms;
    background: $city-purple;
    font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica, sans-serif;
    color: $white;
    padding: 1em;
    z-index: 10;
    font-size: 1.3em;
    line-height: 1.4em;
    position: absolute;
    left: 0;
    top: 0;

    .survey-message__title {
        font-size: 1.5em;
    }

    @include breakup-breakpoint('medium') {
        left: 2em;
        top: 4.5em;
        width: 350px;
        position: fixed;
    }

    .survey-message__accept,
    .survey-message__refuse {
        float: right;
    }

    .survey-message__accept a,
    .survey-message__refuse {
        display: inline-block;
        border-bottom-width: 2px;
        border-bottom-style: dotted;
        text-decoration: none;
        cursor: pointer;
        &:link,
        &:visited {
            color: $gray-lighter;
            border-bottom-color: $grey3;
        }
        &:hover {
            color: $white;
            border-bottom-color: $white;
            border-bottom-style: solid;
        }
        &:focus,
        &:active {
            background-color: $white;
            color: $grey5;
            border-bottom-color: transparent;
        }
    }

    .survey-message__accept {
        margin-left: 1em;
    }
}

.survey-message--loading {
    transform: translate(-115%);
}
