////
/// Anchor Variables
////
/// @type Color
$anchor-color: #b91b1b;
/// @type Color
$anchor-color--dark: #670003;
/// @type Color
$anchor-color-alt: #555555;
/// @type Color
$anchor-color-alt--light: #999999;
/// @type Color
$anchor-color-alt--lighter: #cccccc;
/// @type Color
$anchor-color-alt--lightest: #eeeeee;
/// @type Length
$anchor-border-width: 1px;
/// @type Length
$anchor-border-width--heading: 2px;

///
/// Anchor Base Styles
///
/// @param {String} $target - Target of base styles
///
/// @example scss
///
///
@mixin anchor-base($target: self) {
    border-bottom-width: $anchor-border-width;
    border-bottom-style: dotted;
    position: relative;

    @if $target == self {
        &,
        &:hover,
        &:focus {
            text-decoration: none;
        }
    }
}

/// Anchor Types
/// @type Map
$anchor-types: (
    'default': (
        'link': (
            'color': $anchor-color,
            'border-bottom-color': $anchor-color-alt--light,
        ),
        'visited': (
            'color': $anchor-color--dark,
        ),
        'hover': (
            'border-bottom-style': 'solid',
            'border-bottom-color': $anchor-color--dark,
        ),
        'focus': (
            'background-color': $anchor-color,
            'color': $white,
            'border-bottom-color': 'transparent',
        ),
        'active': (
            'background-color': $anchor-color,
            'color': $white,
            'border-bottom-color': 'transparent',
        ),
    ),
    'dark-background': (
        'link': (
            'color': $anchor-color-alt--lightest,
            'border-bottom-color': $anchor-color-alt--lighter,
        ),
        'visited': (
            'color': $anchor-color-alt--lightest,
        ),
        'hover': (
            'color': $white,
            'border-bottom-color': $white,
        ),
        'focus': (
            'background-color': $white,
            'color': $anchor-color-alt,
            'border-bottom-color': 'transparent',
        ),
        'active': (
            'background-color': $white,
            'color': $anchor-color-alt,
            'border-bottom-color': 'transparent',
        ),
    ),
    'simple': (
        'link': (
            'color': $black,
            'border-bottom-color': 'transparent',
        ),
        'visited': (
            'color': $black,
        ),
        'hover': (
            'background-color': 'transparent',
            'border-bottom-style': 'solid',
            'border-bottom-color': $black,
        ),
        'focus': (
            'background-color': 'transparent',
            'border-bottom-style': 'solid',
            'border-bottom-color': $black,
        ),
        'active': (
            'background-color': 'transparent',
            'border-bottom-style': 'solid',
            'border-bottom-color': $black,
        ),
    ),
    'hallway': (
        'link': (
            'color': $black,
            'border-bottom-color': 'transparent',
        ),
        'visited': (
            'color': $black,
        ),
        'hover': (
            'color': $anchor-color,
            'border-bottom-style': 'solid',
            'border-bottom-color': $anchor-color,
        ),
        'focus': (
            'background-color': $white,
            'color': $anchor-color,
            'border-bottom-style': 'dotted',
            'border-bottom-color': $anchor-color-alt,
        ),
        'active': (
            'background-color': $white,
            'color': $anchor-color,
            'border-bottom-style': 'dotted',
            'border-bottom-color': $anchor-color-alt,
        ),
    ),
    'black': (
        'link': (
            'color': $black,
            'border-bottom-color': 'transparent',
        ),
        'visited': (
            'color': $black,
        ),
        'hover': (
            'color': $anchor-color,
            'border-bottom-style': 'solid',
            'border-bottom-color': $anchor-color,
        ),
        'focus': (
            'background-color': $white,
            'color': $anchor-color,
            'border-bottom-style': 'dotted',
            'border-bottom-color': $anchor-color-alt,
        ),
        'active': (
            'background-color': $white,
        ),
    ),
    'team': (
        'link': (
            'color': $black,
            'border-bottom-width': '0',
        ),
        'visited': (
            'color': $black,
        ),
        'hover': (
            'background-color': $grey5,
            'color': $white,
            'border-color': $anchor-color,
        ),
        'focus': (
            'background-color': $anchor-color,
            'color': $white,
            'outline': '5px auto -webkit-focus-ring-color',
        ),
        'active': (
            'background-color': $anchor-color,
            'color': $white,
            'outline': '5px auto -webkit-focus-ring-color',
        ),
    ),
    'service__cta': (
        'link': (
            'color': $anchor-color,
            'border-bottom-width': '2px',
            'border-bottom-style': 'dotted',
            'border-bottom-color': $anchor-color-alt--light,
        ),
        'visited': (
            'color': $red4,
            'border-bottom-color': $anchor-color-alt--light,
        ),
        'hover': (
            'border-bottom-color': $red4,
            'border-bottom-style': 'solid',
        ),
        'focus': (
            'background-color': $anchor-color,
            'color': $white,
            'border-bottom-color': 'transparent',
        ),
        'active': (
            'background-color': $anchor-color,
            'color': $white,
            'border-bottom-color': 'transparent',
        ),
    ),
    'project__cta': (
        'link': (
            'color': $anchor-color,
            'border-bottom-width': '2px',
            'border-bottom-style': 'dotted',
            'border-bottom-color': $anchor-color-alt--light,
        ),
        'visited': (
            'color': $red4,
            'border-bottom-color': $anchor-color-alt--light,
        ),
        'hover': (
            'border-bottom-color': $red4,
            'border-bottom-style': 'solid',
        ),
        'focus': (
            'background-color': $anchor-color,
            'color': $white,
            'border-bottom-color': 'transparent',
        ),
        'active': (
            'background-color': $anchor-color,
            'color': $white,
            'border-bottom-color': 'transparent',
        ),
    ),
    'heading': (
        'link': (
            'border-bottom-width': $anchor-border-width--heading,
        ),
    ),
    'reset': (
        'link': (
            'border-bottom-width': '0',
        ),
        'focus': (
            'background-color': 'transparent',
        ),
        'active': (
            'background-color': 'transparent',
        ),
    ),
);

@mixin anchor($type: default, $child: null) {
    @if not map-has-key($anchor-types, #{$type}) {
        @error "There is no anchor type named `#{$type}` in `$anchor-types`. Anchor Type should be one of `#{map-keys($anchor-types)}`.";
    }

    @if $child {
        @include anchor(reset);

        #{$child} {
            @include anchor-base(child);
        }
    }

    $anchor-type: map-get($anchor-types, #{$type});

    @each $anchor-selector, $anchor-rules in $anchor-type {
        &:#{$anchor-selector} #{$child} {
            @each $anchor-property, $anchor-value in $anchor-rules {
                #{$anchor-property}: #{$anchor-value};
            }
        }
    }
}

.service,
.project {
    a {
        @include anchor-base;
        @include anchor;
    }
    h3 {
        a {
            @include anchor-base;
            @include anchor(heading);
        }
    }

    /** TODO: remove after services uses default link style */
    .hard-cta a {
        &:link,
        &:visited {
            color: #fff;
            border: 1px solid $red3;
        }
        &:hover {
            border: 1px solid $red4;
        }
    }

    %heading a,
    .dark-background a,
    .widget a,
    [class*='widget-'] a {
        @include anchor(heading);
    }

    .dark-background a {
        @include anchor(dark-background);
    }

    a.simple,
    .footer a {
        @include anchor(simple);
    }

    a.hallway {
        @include anchor(black);
        font-weight: 600;
    }

    a.service__header-link,
    a.project__header-link {
        @include anchor(black);
        font-weight: 500;

        i {
            font-size: 0.8em;
            margin-left: 0.3em;
        }
    }

    a.service__cta,
    a.project__cta {
        @include anchor(service__cta);
    }

    .service__team a,
    .project__team a {
        @include anchor(team);
        background-color: $gray-lighter;
        padding: 5px 10px;
        border-radius: 6px;
        font-weight: 500;
    }

    .service__bottom-ctas,
    .project__bottom-ctas {
        &,
        #contact-widget {
            a {
                @include anchor(dark-background);
                border-bottom-width: 1px;
                transition: none;
            }
        }
    }

    .service__key-information a,
    .project__key-information a {
        @include anchor(dark-background);
        border-bottom-width: 1px;

        &:link,
        &:visited {
            border-bottom-style: dotted;
        }
        &:hover {
            border-bottom-style: solid;
        }
    }
    .service__key-information h3 a,
    .project__key-information h3 a {
        float: right;
    }
}
