@import '../breakup';

#library-search {
    fieldset {
        padding: 10px 20px 20px;
    }
    label {
        display: block;
    }

    .search-query-container {
        /* padding-right: 46px;
        position: relative;

        input {
            width: 100%;
        }

        button.search-button {
            position: absolute;
            top: 0px;
            right: 0px;
        }*/
    }

    &.module-search {
        .search-advanced {
            position: relative;
            float: left;

            label {
                white-space: nowrap;
                font-size: 14px;
                margin: 0px 3px;
                padding: 0;

                input {
                    margin: 0px;
                    vertical-align: text-bottom;
                }

                @include breakup-breakpoint('thinish_extended') {
                    float: left;
                }
            }
        }

        .library-catalogue-container {
            float: none;
            clear: left;

            .library-catalogue {
                line-height: normal;
            }

            @include breakup-breakpoint('thin') {
                // 320px
                float: right;
                clear: none;
            }

            @include breakup-breakpoint('thinish') {
                // 480px
                float: none;
                clear: left;
            }

            @include breakup-breakpoint('thinish_extended_2') {
                // 600px
                float: right;
                clear: none;
            }
        }
    }
}

#library-featured {
    clear: both;
    padding: 0 ($grid-gutter-width / 2);

    .ui-tabs-nav {
        border: none;

        li {
            border: none;
            height: auto;
            padding: 10px 0;
            background-color: $sandyGreyDark;
            background-image: none;
            width: 50%;
            @include box-shadow(4px -4px 5px 1px rgba(0, 0, 0, 0.1) inset);
            margin: 0;

            a {
                padding: 0;
                text-decoration: none;
            }
            &.ui-state-hover {
                background-color: #d1cfca;
            }
            &.ui-tabs-active {
                background-color: $sandyGrey;
                @include box-shadow(none);
                filter: none;
            }
        }
    }
    h2 {
        background-image: none;
        color: $grey6;
        padding: 0;
        text-align: center;
        margin: 0;
        border: none;
        @include font-serif(1.6em, normal, 1.4em);
    }
}

#library-services,
#library-resources,
#library-featured .ui-tabs-panel {
    background-color: $sandyGrey;
    border: none;
    padding: 17px 10px 5px 10px;
    overflow: hidden;
    margin: 0;

    li {
        list-style-type: none;
        float: left;
    }
    .featured-thumb {
        margin-bottom: 0;
        float: left;
        width: 100%;
    }
    a {
        border-bottom: none;
    }
    a:hover {
        p {
            background-color: $logo-red;
        }
    }
    p {
        background-color: #b02a1a;
        color: white;
        padding: 7px 0 7px 4px;
        font-size: 1.1em;
        clear: both;
    }
}

.opening-times {
    padding: 16px 10px 10px;
    background-color: $sandyGrey;
    margin-bottom: 20px;

    h2 {
        float: left;
        margin: 0 0 14px 10px;
        padding: 0;
        font-size: 1.35em;
    }
    #todays-date {
        background-color: $white;
        border: 1px solid $grey3;
        float: right;
        padding: 4px 6px;
        position: relative;
        @include border-radius(4px);
        right: 10px;
    }
    ul {
        clear: both;
        list-style-type: none;
        padding: 0;
        margin: 0;
    }
    li {
        padding: 10px;
        overflow: hidden;
        a {
            float: left;
            margin-right: 13px;
            font-size: 0.95em;
            font-weight: bold;
        }
        p {
            clear: both;
            float: left;
            margin-bottom: 0;
            margin-top: 5px;
            padding: 0;
        }
        &.opening-times-last {
            border: none;
        }
    }
    .odd a:hover {
        border-bottom: 1px solid $grey1;
    }
    .even a:hover {
        border-bottom: 1px solid $sandyGrey;
    }
    p {
        text-align: right;
    }
}

.js .opening-times.widget-loading {
    min-height: 352px;
    background-position: center center;

    p,
    ul {
        display: none;
    }
}

#library-news {
    h2 {
        margin-bottom: 5px;
        margin-top: 0;
    }
    .article {
        padding-bottom: 5px;
    }
    .article-1 {
        padding-top: 0;
    }
    .article-3 {
        border: none;
    }
    .cta {
        float: right;
    }
}

#library-news {
    clear: left;
    margin-bottom: 20px;
}

.library-promotional {
    margin-bottom: 30px;

    img {
        margin: 0;
    }
}

#library-media {
    margin-bottom: 30px;
}

#library-upgrade {
    margin-top: 30px;

    a {
        border: none;

        &:focus {
            background-color: transparent;
        }
    }
    img {
        margin-bottom: 0;
    }
}

#library-chat {
    margin-bottom: 30px;
}

#library {
    .site-home {
        margin-top: 44px;
    }
    #announcements {
        background: #e3f0fd;
        list-style: none;
        color: #3a87ad;
        border: 1px solid #3a87ad;
        font-size: 1.2em;
        margin: 0 0 20px;
        clear: both;

        li {
            padding: 15px 10px;
            a {
                //color: $white;
                font-weight: normal;
            }
        }
    }
    .quicksearch {
        @include gradient-vertical(#ad2817, #991b0d);
        border: 1px solid $grey2;
        margin: 10px 0;
        padding: 10px;
        position: relative;

        .qs_label {
            font-size: 17px;
            font-style: italic;
            margin: 0 10px 0 0;
        }
        input {
            font-size: 20px;
            margin-top: 10px;
            width: 400px;
        }
        .loader {
            left: 210px;
            top: 37px;
        }
    }
    #global-nav1 .active-dynamic {
        background-color: $black;
        a {
            border-left: 1px solid $black;
        }
    }
}

#libraries-landing-times {
    list-style-type: none;
    padding: 0;
    margin: 0;

    .library {
        clear: both;
        margin-bottom: 30px;
        display: block;
        position: relative;
        overflow: hidden;
    }
    img {
        border: none;
        margin: 0;
        padding: 0;
    }
    a {
        border-bottom: none;
        display: block;
    }
    h2 {
        padding: 0;
        z-index: 100;
        width: 100%;
        border: none;
        margin: 0;

        span {
            color: #fff;
            padding: 10px;
            display: block;
            background: rgb(176, 42, 26);
            font-size: 0.9em;
            line-height: 1.2em;

            &:hover {
                text-decoration: underline;
            }
        }
    }
    .opening-times-header {
        position: absolute;
        left: -999px;
        th {
            position: absolute;
            left: -999px;
        }
    }
    .library-times-illustration {
        border: none;
        float: left;
    }
    .opening-times-exceptions,
    .opening-times-standard {
        border: 1px solid $grey3;
        margin: 0;
        margin-top: -5px;
        display: table;

        td {
            padding: 1px 5px;
            border-color: $grey3;
            background: rgba(255, 255, 255, 0.98);
        }
        caption {
            background-color: #e0dbd7;
            text-align: left;
            font-weight: bold;
            padding: 11px 5px;
            margin-bottom: -1px;
            font-family: Arial, Helvetica, sans-serif;
            font-size: 12px;
            position: relative;
            width: auto;
        }
        .opening-time {
            text-align: right;
        }
    }
}

// opening hours when they appear in the sidebar
#sidebar-opening-times {
    caption {
        text-align: left;
        padding: 10px 5px;
        font-weight: bold;
        margin-bottom: -1px;
        border: 1px solid $grey3;
        width: auto;
        @include gradient-vertical($sandyGrey, $sandyGreyDark);
        margin-bottom: -2px;
    }
    .opening-times-header th {
        position: absolute !important;
        clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
        clip: rect(1px, 1px, 1px, 1px);
        padding: 0 !important;
        border: 0 !important;
        height: 1px !important;
        width: 1px !important;
        overflow: hidden;
    }
    .opening-times-standard {
        border-left: 1px solid $grey3;
        border-right: 1px solid $grey3;
        td {
            background: $white;
            text-align: left;
            padding: 2px 5px;
            text-align: center;

            &.opening-days {
                text-align: left;
            }
        }
    }
    .opening-times-exceptions {
        border-collapse: collapse;
        border-left: 1px solid #aad4ff;
        border-top: 1px solid #aad4ff;
        margin-bottom: 20px;

        caption {
            filter: none; /* solves issue in ie and table head background color for exceptions*/
            background: #e3f0fd;
            border: 1px solid #aad4ff;
        }
        th {
        }
        td {
            border-right: 1px solid #aad4ff;
            border-bottom: 1px solid #aad4ff;
            background: #e3f0fd;
            text-align: left;
            padding: 2px 5px;
            text-align: center;

            &.opening-days {
                width: 57%;
                text-align: left;
            }
        }
    }
}

@include breakup-breakpoint('thin') {
    // 320px
}

@include breakup-breakpoint('thinish') {
    // 480px
    #library-search .search-advanced {
        float: left;
    }

    #library-opening-times li p {
        float: right;
        clear: none;
        margin-top: 0;
    }
}

@include breakup-breakpoint('thinish_extended') {
    // 500px
    #library-services,
    #library-resources,
    #library-featured .ui-tabs-panel {
        padding: 17px 0 5px;

        li {
            float: left;
            margin-left: 2%;
            width: 47%;
        }
    }
    #libraries-landing-times {
        .library {
            float: left;
            width: 48%;
            clear: right;
        }
        .library-even {
            float: right;
        }
        h2 span {
            position: absolute;
        }
    }
}

@include breakup-breakpoint('medium') {
    // 767px

    #library {
        #mast1 {
            height: 48px;
        }
        #global-nav-toggler {
            display: none;
        }
        .account-log-in {
            height: 157px;
        }
    }
    #library-services,
    #library-resources,
    #library-featured .ui-tabs-panel {
        p {
            font-size: 0.9em;
        }
    }
    #library-opening-times {
        padding-top: 14px;

        li {
            padding: 9px 10px;

            p {
                clear: both;
                float: left;
                margin-top: 5px;
            }
        }
    }
}

@include breakup-breakpoint('wide') {
    // 980px
    #library {
        #mast1 {
            height: 36px;
        }
    }
    #library-search .search-advanced {
        float: left;
    }
    #library-services,
    #library-resources,
    #library-featured .ui-tabs-panel {
        li {
            margin-left: 1.8%;
            width: 31%;
        }
        p {
            font-size: 0.9em;
        }
    }
    #library-opening-times {
        min-height: 318px;
        padding: 14px 0;
        ul {
            padding-right: 10px;
            padding-left: 10px;
        }
        li {
            padding: 10px;
            p {
                float: right;
                clear: none;
                margin-top: 0;
            }
        }
    }
    #library-contact {
        .tel {
            display: block;
        }
        .contact-address {
            float: left;
            margin-top: -41px;
            padding-left: 210px;
        }
    }
    #libraries-landing-times {
        .library {
            float: none;
            width: auto;
        }
        .library-even {
            float: none;
        }
        a {
            display: inherit;
        }
        h2 span {
            bottom: 20px;
        }
        img {
            float: left;
            padding-left: 30px;
        }
        .opening-times-exceptions,
        .opening-times-standard {
            float: left;
            margin-left: -30px;
            margin-top: 20px;
            width: 260px;
            @include box-shadow(0px 0px 5px rgba(0, 0, 0, 0.2));
        }
    }
}

@include breakup-breakpoint('wide_extended') {
    // 1035px
}

@include breakup-breakpoint('wider') {
    // 1200px
    #library-opening-times {
        min-height: 352px;
        padding: 19px 0;

        ul {
            padding-right: 10px;
            padding-left: 10px;
        }
        li {
            padding: 12px;
        }
    }
    #libraries-landing-times {
        h2 span {
            width: 280px;
        }
        img {
            padding-left: 25px;
        }
        .library {
            float: left;
            overflow: visible;
            width: 47%;
        }
        .library-even {
            float: right;
        }
        .opening-times-exceptions,
        .opening-times-standard {
            margin-left: 0;
            position: absolute;
            right: 0;
        }
    }
}
