/**
 * City Health "Microsite"
 */

.city-health__form {
    max-width: 25em;
    margin-bottom: 2em;
}

.city-health__loading {
    width: 100%;
    display: none;
    text-align: center;

    .fa {
        display: inline-block;
        font-size: 4em;
    }
}

.city-health__listing {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
        width: 100%;
        display: block;

        & + li {
            margin-top: 1.5em;
            border-top: 1px solid $grey2;
            padding-top: 1.5em;
        }
    }
}

.city-health__description {
    max-width: 40em;
}

.city-health__button {
    display: inline-block;
    padding: 0.5em 0.75em;
    font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica, sans-serif;
    font-weight: 600;
    border-radius: 0.375em;

    &,
    &:link,
    &:visited {
        background-color: $red;
        color: $white;
    }

    &:hover,
    &:focus,
    &:active {
        text-decoration: underline;
    }

    &:focus,
    &:active {
        background-color: $grey2;
    }

    &:focus {
        color: $grey6;
    }

    &:active {
        color: $red;
    }
}
