// Tables

table {
    width: 99.9%;
    margin-bottom: 14px;
    border-collapse: collapse;
    overflow-x: auto;
    overflow-y: hidden;

    caption {
        width: 100%;
        text-align: right;
        font-family: $font-family-serif;
        font-size: 18px;
        line-height: 1.333;
        margin: 0 0 10px 0;
    }

    th {
        border: 1px solid #e0dbd7;
        text-align: left;
        font-weight: bold;
        background: #eae9e7;
        padding: 5px;
    }

    td {
        border: 1px solid #e0dbd7;
        background: #fff;
        text-align: left;
        padding: 5px;
        display: table-cell;
    }

    &.align-top td {
        vertical-align: top;
    }

    td.alt {
        background: #f5fafa;
        color: #797268;
    }
    tr {
        &:nth-child(2n) {
            td {
                background: #f4f4f4;
            }
        }
    }
}

#content table {
    display: block; //needed to scroll on a small screen
    @include breakup-breakpoint('wide') {
        // 980px
        display: table;
    }
}

/***********************
*   responsive table   *
***********************/
@include breakup-tweakpoint('(max-width: 600px)', 'thin', false) {
    .js {
        .responsive-table {
            display: block;

            thead,
            tbody,
            th,
            td,
            tr {
                display: block;
            }

            thead tr {
                position: absolute;
                top: -9999px;
                left: -9999px;
            }

            tr {
                border: 1px solid #ccc;
            }

            td {
                border: none;
                border-bottom: 1px solid #eee;
                position: relative;
                padding-left: 50%;

                &:before {
                    position: absolute;
                    top: 6px;
                    left: 6px;
                    width: 45%;
                    padding-right: 10px;
                    white-space: nowrap;
                    content: attr(data-th);
                }
            }
        }
    }
}
