.icons-finance,
.icons-hr {
    .fa {
        font-size: 34px;
        text-decoration: none;
        display: inline-block;
    }

    a {
        text-decoration: none;
        border-bottom: none;
        color: #979799;
        text-align: center;
        display: block;
        margin-bottom: 1.5em;

        &:hover {
            color: #333333;
        }

