#intranet {
    .pc-shop-order-form {
        width: 600px;

        .admin-form--header {
            display: inline-block;
            width: 200px;
        }

        .admin-form--checkbox {
            height: 14px;
            font-size: 1.5em;
        }

        .admin-form--footnote {
            font-size: 12px;
            display: inline-block;
            margin-left: 20px;
        }
    }
    @include breakup-breakpoint('medium') {
        .global-nav__wrapper--active {
            margin: 0;
        }
        .header__logo-nav-wrapper {
            padding-bottom: 2em;
        }

        .global-nav__menu-item__link {
            // margin-right: 0.3em;
            // margin-left: 0.3em;
        }
    }
    @include breakup-breakpoint('wide') {
        .header__logo__img {
            width: 150px;
        }
        .global-nav__menu-item {
            padding: 0.7em 0.2em 0 0.2em;
        }
        .global-nav__menu-item__link {
            font-size: 14px;
        }
        .global-nav__wrapper {
            margin-top: 0;
        }
    }
    @include breakup-breakpoint('wider') {
        .header__logo-nav-wrapper {
            padding-bottom: 0;
        }
        .global-nav__wrapper {
            margin-top: 0;
            margin-left: 2em;
        }
    }
}

/*************************
*   Intranet Home Page   *
*************************/
$slider-controls-button-size: 40px;
$slider-controls-button-icon-size: 16px;

.intranet-home {
    h1 {
        @extend .sr-only;
    }
}

.intranet-home__container {
    font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 1.3em;

    .part {
        margin-bottom: 25px;
    }

    .part--side {
        margin-left: 0;
        margin-right: 0;

        &__title {
            padding-bottom: 0;
            margin-bottom: 15px;
            border-bottom: 2px solid #ccc;
            font-size: 1.1em;
            font-weight: 500;
        }

        @include breakup-breakpoint('wide') {
            margin-right: 5px;

            &__title {
                margin-right: 20px;
            }
        }
    }

    .icon-link-wrapper {
        padding-left: 2px;
        margin-bottom: 17px;

        @include breakup-breakpoint('wide') {
            margin-bottom: 14px;
            font-size: 0.87em;
        }

        &:nth-child(2n + 1) {
            clear: both;
        }
    }

    .icon-link {
        &__icon,
        &__text {
            display: table-cell;
            vertical-align: top;
        }

        &__icon {
            color: #4d2942;
            min-width: 30px;

            img {
                margin-bottom: 0;
            }
        }

        &__body {
            display: table;
            line-height: 1.1em;
            text-decoration: none;
            border: none;

            &:link,
            &:visited {
                .icon-link__span {
                    color: #222;
                    border: none;
                    background: none;
                }
            }

            &:hover {
                .icon-link__span {
                    border-bottom: 1px solid;
                }
            }

            &:focus,
            &:active {
                background: none;

                .icon-link__span {
                    border-bottom: 1px dotted;
                }
            }
        }
    }

    .box-link-wrapper {
        margin-bottom: 20px;
    }

    .box-link {
        &__pointer,
        &__text {
            display: table-cell;
            vertical-align: middle;
            padding-right: 4px;
        }

        &__pointer {
            text-align: right;
            font-size: 0.8em;
        }

        &__body {
            display: table;
            width: 100%;
            background: #370332;
            color: #fff;
            height: 75px;
            padding: 0 15px;
            font-size: 1.1em;
            line-height: 1.2em;

            &:hover {
                .box-link__span {
                    border-bottom: 1px solid #fff;
                }
            }

            &:focus,
            &:active {
                .box-link__span {
                    color: #370332;
                    background: #fff;
                }
            }
        }
    }

    .line-link-wrapper {
        margin-bottom: 20px;
    }

    .line-link {
        display: table;
        width: 100%;
        border-bottom: 1px solid #ccc;
        font-family: Arial, Helvetica, sans-serif;
        font-size: 0.9em;

        &__pointer,
        &__text {
            display: table-cell;
            vertical-align: middle;
            padding-right: 4px;
        }

        &__pointer {
            text-align: right;
            font-size: 0.8em;
        }

        &__text {
            a:link,
            a:visited {
                color: #222;
                border-bottom: none;
            }

            a:hover {
                color: #b02a1a;
            }

            a:focus,
            a:active {
                color: #fff;
                background: #b02a1a;
            }
        }
    }

    .news-yammer-wrapper {
        padding-left: 0;
        padding-right: 0;
        margin-bottom: 20px;
    }

    .intranet-yammer {
        height: 505px;
        border: 10px #ddd solid;
        background: #f3f5f8;

        &__header {
            background: #396b9a;
            color: #fff;
            border: 1px solid #dde0e6;
            margin: 0 0 0.5em;
            padding: 8px 8px 8px 12px;
            font-family: Helvetica, Arial, sans-serif;
            font-weight: 500;
        }

        &__skip {
            left: -99999px;
            position: absolute;

            &:focus {
                font-size: 0.8em;
                background-color: #ff0;
                color: #000;
                float: right;
                padding: 0 5px;
                position: static;
            }
        }

        &__feed {
            height: 430px;
        }
    }

    .intranet-news {
        background: #ddd;

        &__header {
            padding: 0 5px 5px 1px;
            background: #fff;
        }

        &__heading {
            font-size: 1.8em;
            line-height: 1em;
            color: #222;
            border: none;

            &:hover,
            &:focus {
                text-decoration: underline;
                border: none;
            }
        }

        &__subheading {
            font-size: 0.8em;
            color: #b02a1a;
            border: none;
            float: right;

            &:hover {
                text-decoration: underline;
                border: none;
            }

            &:focus,
            &:active {
                background: #b02a1a;
                color: #fff;
            }
        }

        &__submessage {
            font-family: Arial, Helvetica, sans-serif;
            font-style: italic;
            font-size: 0.8em;
            margin: 0 0 0 0;
        }

        &__media {
            width: 100%;
        }

        &__headline-wrapper {
            margin: 0 10px 0 10px;
        }

        &__headline {
            font-weight: 500;
            line-height: 1.3em;
            color: #222;
            border-bottom: 1px dotted;
        }

        &__summary {
            padding: 5px 10px 5px 10px;
            line-height: 1.3em;
        }

        &__body:hover,
        &__body:focus,
        &__body:active {
            .intranet-news__media {
                opacity: 0.8;
            }

            .intranet-news__headline {
                border-bottom: 1px solid;
            }
        }
    }

    .bx-wrapper {
        position: relative;

        .bxslider {
            a:hover,
            a:focus,
            a:active {
                background: none;
            }
        }

        .bx-controls {
            display: block;
        }

        .bx-pager {
            display: none;
        }

        .bx-controls-direction {
            display: block;
            position: static;
            width: 0;
            height: 0;

            a {
                &,
                &.active {
                    &,
                    &:focus {
                        background: none;
                        border: 0;
                        text-decoration: none;
                    }
                }

                transition: background $tx-time;
                &:hover {
                    background: rgba(255, 255, 255, 0.3);
                }

                position: absolute;
                top: 0;
                margin: 0;
                width: 50px;
                padding: 0 0 66.85%;
                height: 0;
                border: 0;
                display: block;

                &.bx-prev {
                    left: 0;
                }
                &.bx-next {
                    right: 0;
                }

                .slider-control {
                    position: absolute;
                    text-align: center;
                    transition: color $tx-time;

                    &.bg {
                        top: 50%;
                        left: 50%;
                        height: $slider-controls-button-size;
                        width: $slider-controls-button-size;

                        line-height: $slider-controls-button-size + 2;
                        font-size: $slider-controls-button-size;
                        margin: (-0.5 * $slider-controls-button-size) auto auto
                            (-0.5 * $slider-controls-button-size);
                        color: white;
                        opacity: 0.9;
                    }
                    &.ic {
                        top: 50%;
                        left: 50%;
                        width: $slider-controls-button-icon-size;
                        height: $slider-controls-button-icon-size;
                        line-height: $slider-controls-button-icon-size;
                        font-size: $slider-controls-button-icon-size;
                        margin-left: -0.5 * $slider-controls-button-icon-size;
                        margin-top: -0.5 * $slider-controls-button-icon-size + 1;
                        color: $red1;
                        opacity: 0.9;

                        &.fa-chevron-right,
                        &.fa-chevron-left {
                            margin-top: -0.5 * $slider-controls-button-icon-size +
                                2;
                        }

                        &.fa-play,
                        &.fa-chevron-right {
                            margin-left: -0.5 *
                                $slider-controls-button-icon-size + 1;
                        }
                        &.fa-chevron-left {
                            margin-left: -0.5 *
                                $slider-controls-button-icon-size - 1;
                        }
                    }
                }
                &:hover .slider-control,
                &.active .slider-control {
                    &.bg {
                        color: $red1;
                    }
                    &.ic {
                        color: white;
                    }
                }

                &:hover {
                    color: white;
                }

                &.active {
                    color: $red1;
                }
            }
        }
    }
}

