////
/// City Ventures
/// @group sites
////

.city-ventures--home {
    .relative-parent {
        margin-bottom: 4rem;
        position: relative;
    }

    .standfirst {
        margin-bottom: 0;

        @include breakup-breakpoint('thinish_extended_2') {
            // Evaluated width of the social icons list
            margin-right: (130px + $grid-gutter-width);
        }
    }

    .social-icons-list {
        margin: 1.5rem 0 0;

        @include breakup-breakpoint('thinish_extended_2') {
            margin-top: 0;
            position: absolute;
            top: 0;
            right: 0;
        }
    }
}

.meta-boxes {
    width: 100%;
    float: left;
}
.meta-box {
    background-color: $grey2;
    padding: $grid-gutter-width;
    margin-bottom: $grid-gutter-width;
    overflow: hidden;

    @include breakup-breakpoint('medium') {
        min-height: 13.5em;
    }

    h3 {
        width: 100%;
    }

    p {
        margin-bottom: 0;
    }

    .meta-box__contact-list {
        margin: 0;
        list-style: none;

        li {
            display: block;

            & + li {
                margin-top: 0.5rem;
            }
        }

        span,
        a {
            display: block;
            padding-left: 2.5rem;
            position: relative;

            &:link,
            &:visited {
                border-bottom-width: 0;
            }

            &:before {
                color: $grey5;
                position: absolute;
                top: 0;
                left: 0;
                font-family: 'FontAwesome';
                font-size: 1.25em;
                vertical-align: middle;
            }

            &.meta-box__contact-list__telephone:before {
                // fa-telephone
                content: '\f095';
            }
            &.meta-box__contact-list__email:before {
                // fa-envelope
                content: '\f0e0';
            }
            &.meta-box__contact-list__location:before {
                // fa-map-marker
                content: '\f041';
                top: 2px;
            }
        }

        & + p {
            margin-top: 1em;
        }
    }
}

.tile-title {
    font-size: 2rem;

    &:link,
    &:visited {
        color: $black;
    }

    &:hover,
    &:focus {
        border-bottom: 1px dotted currentColor;
    }

    &:after {
        content: '\f105';
        padding-left: 0.5em;
        font-family: 'FontAwesome';
    }
}

.tile-wrapper--city-ventures {
    margin-top: 0;

    .tile-listing--city-ventures {
        .tile {
            display: inline-block;
            margin-bottom: $grid-gutter-width;

            &:link,
            &:visited {
                color: $gray;
                border-bottom-width: 0;

                h4 {
                    display: inline-block;
                    border-bottom: 1px dotted transparent;
                }
            }

            &:hover,
            &:focus {
                h4 {
                    border-bottom-color: currentColor;
                }
            }

            &:focus,
            &:active {
                background-color: transparent;
                color: $gray;
            }

            .tile-subject-icon {
                top: auto;
                bottom: 0;
                font-size: 1.5em;
            }

            &.tile--folder {
                .tile-subject-icon {
                    background-color: $blue;
                }
            }

            &.tile--trophy {
                .tile-subject-icon {
                    background-color: $city-yellow;
                    color: $gray;
                }
            }

            &.tile--calendar {
                .tile-subject-icon {
                    background-color: $city-purple;
                }
            }

            p {
                margin-bottom: 0;
            }
        }
    }
}
