/*overwrite padding*/
#content .wFormContainer {
    padding: 0;
    margin-bottom: 20px;
}

/*specific rule for short course payment form*/
#content .wFormContainer .wForm form #tfa_Coursefee-H {
    display: block;
}

.wFormContainer .wForm form {
    margin: 0;
    padding: 10px;
    border: 1px solid $grey3;
    @include font-family-sans-serif;
    font-size: 1em;
}

.wForm form div {
    padding: 0;
    margin: 0;
    clear: both;
}

#content .wForm p {
    margin: 0 0 14px 0;
}

label {
    white-space: normal;
    margin: 0 0 5px 0;
}

.wForm form .label {
    font-size: medium;
}

/* .wForm form Sections ========================================= */

/* Fieldset */
.wForm form fieldset {
    border: 1px solid $grey3;
    padding: 10px;
    background: #fafafa;
    margin-bottom: 10px;
    display: block;
    clear: both;
}

.wForm form fieldset legend {
    color: $grey6;
    @include font-family-serif;
    font-size: 1.4em;
    padding: 10px;
    padding: 0; /* overrides main wforms-layout.css */
}

/* IE7 */
* html .wFormContainer .wForm form fieldset legend {
    top: -0.5em;
    left: 0;
}

/* Nested Fieldsets */
.wForm form fieldset fieldset {
    background-color: #fafafa;
    margin-top: 10px;
}

.wForm form fieldset fieldset legend {
    font-size: 1.2em;
}

/* Grid Section */
.wForm form table {
    border-collapse: collapse;
    width: 200px;
    margin-top: 14px;
}

.wForm form tr td {
    padding: 0.5em;
    border: 0;
}

.wForm form tr td .oneField {
    margin-top: 0;
}

.wForm form th {
    padding: 0 4px;
}

.wForm form .alternate-1 {
    background-color: #f2e1e0;
}

.wForm form .alternate-0 {
}

/* html section */
.wForm form div.htmlsection {
    padding: 0;
}

/* div when direct child of form */
.wForm form > div.oneField {
    padding: 10px 10px 0 10px;
}

.wForm .labelsAbove > div.oneField label {
    width: 90%;
}

/* Field Labels ======================================================================== */

/* Base */
.wForm form label {
    display: block;
    margin: 0 0 4px 0 !important;
}

.wForm label.preField {
    display: inline-block;
    background-color: inherit;
    font-weight: bold;
}

.wForm .labelsAbove label.preField {
    display: block;
}

/* Text inputs / Select label */
label.preField {
}

/* Individual Checkbox / Radio label */
.wForm form label.postField {
    margin: 0 10px 0 0 !important;
}

br {
}

/* Inputs ======================================================================== */

.wForm input,
.wForm textarea,
.wForm select {
    border: 1px solid $grey3;
    @include box-shadow(
        'inset 0 0 8px  rgba(0,0,0,0.1), 0 0 16px rgba(0,0,0,0.1)'
    );
    padding: 5px !important;
    background: #fff;
    background: rgba(255, 255, 255, 0.5);
    display: block;
    clear: left;
}

.wForm select {
    padding: 5px !important;
}

.wForm textarea {
    width: 90%;
    max-width: 410px;
}

td input,
td textarea,
td select {
    width: 90%;
}

textarea {
}

input {
}

select {
}

/* Buttons ======================================================================= */

/* Submit & Cancel buttons */
.wForm form .actions {
    font-size: 0.9em;
}

.wForm form .actions input {
    @include font-family-sans-serif;
}

.wForm form .actions:after {
    content: '.';
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

.wForm form div.actions .primaryAction {
    /* forms specific */
    width: auto;
    border: none;
    display: inline;
    /* verbatim from compass generated website.css */
    background: $red1 url('i/icon-action-arrow.png') no-repeat right center;
    padding: 5px 20px 5px 5px !important;
    color: $white !important;
    font-weight: bold;
    @include border-radius(2px);
    @include transition(background 0.3s ease);
    text-shadow: 1px 1px 1px $red2;
    @include box-shadow(0 2px 2px rgba(0, 0, 0, 0.5));
    margin-right: 20px;
    font-size: 1em;
}

.wForm form .actions .primaryAction:hover {
    background-color: $red2;
}

.wForm form .actions .primaryAction:hover {
    text-decoration: underline;
    color: $white;
}

/*cancel button*/
.wForm form .actions .secondaryAction {
    /* verbatim from compass generated website.css */
    background: $grey6;
    padding: 5px 8px !important;
    color: $white !important;
    border: none;
    @include border-radius(2px);
    @include transition(background 0.3s ease);
    text-shadow: 1px 1px 1px $red2;
    @include box-shadow(0 2px 2px rgba(0, 0, 0, 0.5));
}

/*to overwrite a rule from website.css*/
#content .secondaryAction {
    border-bottom: none;
    font-weight: bold;
    font-size: 1em;
    margin-left: 0 !important;
}

.wForm form .primaryAction:hover,
.wForm form .secondaryAction:hover {
    color: $grey5;
}

.wForm label.preField,
.wForm .labelsLeftAligned label.preField,
.wForm .labelsRightAligned label.preField {
    min-width: inherit !important;
}

/*Paging Buttons */
.wForm .wfPagePreviousButton,
.wForm .wfPageNextButton {
    cursor: pointer;
    color: $red1;
}

/* .wForm form Builder Related Rules ===================================================== */

/* .wForm form or Fieldset instruction paragraph. */
.wForm form .instructions {
}

/* Asterisk on required fields */
.wForm form .reqMark {
    font-size: medium !important;
    margin: 0 !important;
    color: #333;
}

.wForm form span.reqMark {
    color: #f00;
}

#previewContainer #form_preview {
    font-size: 14px;
}

#content .wForm form table {
    font-size: 1em;
    max-width: none;
    display: block;
}

#content.standard .wForm form table {
    max-width: 460px;
}

.wForm form table {
    width: 100%;
    margin-bottom: 14px;
    border-left: 1px solid #c1dad7;
    border-top: 1px solid #c1dad7;
    font-size: 0.8em;
    border-collapse: collapse;

    caption {
        width: 100%;
        text-align: right;
        @include font-family-serif;
        margin: 0 0 10px 0;
    }

    th {
        border: 1px solid #c1dad7;
        border-left: none;
        border-top: none;
        text-align: left;
        font-weight: bold;
        background: $sandyGrey;
        @include gradient-vertical($sandyGrey, $sandyGreyDark);
        padding: 5px;
    }

    td {
        border-right: 1px solid #c1dad7;
        border-bottom: 1px solid #c1dad7;
        background: #fff;
        text-align: left;
        padding: 5px;
        display: table-cell;
    }

    td.alt {
        background: #f5fafa;
        color: #797268;
    }
}

/* w.wForm forms Related Rules ========================================================== */

.wForm form div.oneField {
    padding: 0;
    margin-bottom: 0 !important;
}

/* Field w/ a validation error */
.wForm form div.errFld {
    /*background-color:#fff !important;*/
    margin: 0 -3px;
    padding: 0 3px;
    border: none !important;
}

.wForm form div.errFld input,
.wForm form div.errFld textarea,
.wForm form div.errFld select {
    border: 1px solid #cc3333;
    background-color: #feb1ad;
    color: $grey6;
}

/* Styling for message associated with a validation error.
   starts as span.errMsg, becomes div.errMsg when displaying errors inline */
.wForm form div.errMsg {
    margin-bottom: 10px;
}

/* Field-Hint with focus */
.wFormContainer .wForm form .field-hint span {
    color: $black;
}

/* Field-Hint without focus */
.wFormContainer .wForm form .field-hint-inactive {
    color: #aaa;
}

/* Repeat Behavior divs (when not set on a fieldset) */
.wForm form div.repeat {
    border: 1px dotted #ced1d3;
}

.wForm form div.removeable {
    border: 1px dotted #ced1d3;
    background-color: #f5f5f5;
}

/* Links generated by the Repeat Behavior */
.wForm form .duplicateLink {
    line-height: 260%;
    color: #414d59;
}

.wForm form .removeLink {
    line-height: 260%;
    color: #f00;
}

.wForm form td .duplicateLink,
.wForm form td .removeLink {
    margin: 0;
    line-height: 100%;
}

.oneChoice input {
    @include size(18px, 18px);
}

/* common settings checkbox */
input[type='checkbox'] {
    @include size(18px, 18px);
    border: none;
    background-position-x: left;
    margin-right: 5px;
}

input[type='checkbox']:hover {
    background-position-y: -40px;
}

input[type='checkbox']:checked {
    background-position-y: -20px;
}

input[type='checkbox']:checked:hover {
    background-position-y: -60px;
}

/* disabled settings for checkbox */
input[type='checkbox']:disabled {
    opacity: 0.3;
}

.wForm input[type='radio'] {
    border: none;
    margin-right: 5px;
    text-shadow: none !important;
    @include box-shadow(none);
    -moz-box-shadow: none;
    background: inherit;
}

.wForm form .oneField .oneChoice:last-child {
    margin-bottom: 14px;
}

.wForm form .oneChoice label {
    vertical-align: top !important;
}

.wForm form .oneChoice input {
    float: left;
}

.wForm form fieldset .oneField .choices {
}

/* Inline Section ========================================================== */

.wForm form .inlineSection div.oneField {
    display: block !important;
    clear: none;
    margin-bottom: 14px !important;
}

.wForm form .inlineSection {
    margin-bottom: 14px;
    padding: 0 !important;
}

.wForm form .inlineSection .preField {
}

.wForm form .inlineSection .oneField select {
    width: auto;
    float: left;
}

.wForm form .inlineSection .oneField label {
}

/* Label when inside an inline section */
.inlineSection .oneField .inlineLabel {
    float: left;
}

.wForm form .inlineSection .oneField {
}

.wForm form .inlineSection .oneField input {
    float: left;
}

.inlineSection select {
    float: right;
}

.wForm form .inlineSection .oneField input {
}

#cff-single-donation,
#cff-regular-donation {
    overflow: hidden;
    padding: 0 0 10px;
    background-color: #eae9e7;
    margin-bottom: 20px;
    label {
        display: block;
        margin: 10px 0;
    }
    legend {
        background-color: #eae9e7;
        background: linear-gradient(to bottom, #eae9e7 0%, #e0dbd7 100%);
        border-bottom: 1px solid white;
        border-top: medium none;
        color: #333333;
        font-family: arial, sans-serif;
        font-size: 1em;
        font-weight: bold;
        line-height: 1.6em;
        margin: 0;
        padding: 10px 0;
        text-indent: 10px;
        width: 100%;
        span {
            color: #b02a1a;
        }
    }
    fieldset {
        margin-left: 10px;
        margin-bottom: 15px;
        border: none;
    }
    .donation-submit {
        background-color: #b02a1a;
        background: url('i/icon-action-arrow.png') no-repeat 77px center,
            linear-gradient(to bottom, #b02a1a 0%, #96190b 100%);
        border-bottom: medium none;
        border-radius: 4px;
        box-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
        color: #ffffff;
        display: block;
        float: left;
        font-size: 0.8em;
        font-weight: bold;
        line-height: 1.3em;
        margin: 15px 20px 5px 10px;
        padding: 8px 35px 8px 13px;
        text-align: center;
        text-shadow: 1px 1px 1px #96190b;
        cursor: pointer;
        border: none;
    }
}

#cff-regular-donation {
    .donate-appeal {
        display: none;
    }
}

.donation-other-amount {
    display: none;
    input {
        background: none repeat scroll 0 0 #f7f7f7;
        border: medium none;
        width: 60px;
        font-size: 1em;
        height: auto;
        line-height: 18px;
        margin: 0;
        min-height: 18px;
        padding: 4px 5px;
        margin: 5px 0 0;
    }
}

#wow-widget {
    div {
        margin-bottom: 10px;
    }
    label {
        margin: 0 10px;
    }
    #submit-nomination {
        background-color: #b02a1a;
        background: url('i/icon-action-arrow.png') no-repeat 120px center,
            linear-gradient(to bottom, #b02a1a 0%, #96190b 100%);
        border-bottom: medium none;
        border-radius: 4px;
        box-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
        color: #ffffff;
        display: block;
        float: left;
        font-size: 0.8em;
        font-weight: bold;
        line-height: 1.3em;
        padding: 8px 35px 8px 13px;
        text-align: center;
        text-shadow: 1px 1px 1px #96190b;
        cursor: pointer;
        border: none;
    }
    small.notice {
        display: block;
        width: 50%;
        margin-top: 40px;
    }
}

@include breakup-breakpoint('thinish') {
    // 480px
    .wForm {
        input,
        textarea,
        select {
            width: auto;
        }
    }
}
