.people-listing {
    display: flex;
    flex-wrap: wrap;

    > div {
        display: flex;
    }
}

.people-listing__person {
    background: none repeat scroll 0 0 #f9f9f9;
    border: 1px solid #ccc;
    margin-bottom: 20px;
    padding: 10px;
    width: 100%;
}

.people-listing__person__thumb {
    text-align: center;

    margin: -5px -5px 0;

    img {
        max-height: 183px;
        max-width: 100%;
        margin-bottom: 0;
    }

    span {
        color: #ccc;
        font-size: 6em;
        margin: 20px auto 19px;
    }
}

.people-listing__person__link {
    @include anchor-base('h2>span');
    @include anchor(default, 'h2>span');
}

.people-listing__person__name {
    font-size: 1.2em;
}

.people-listing__person__tag-list {
    margin: 10px 0;
    clear: both;
    list-style: none;

    li {
        margin-bottom: 10px;
    }

    a {
        border-radius: 3px;
        padding: 2px 9px 2px 6px;

        &,
        &:link,
        &:visited {
            @include anchor-base();
            background: #f1f1f1;
            border: 1px solid rgba(0, 0, 0, 0.1);
            color: #444;
        }

        &:hover {
            background: #444;
            border: 1px solid #444;
            color: white;
        }

        &:active,
        &:focus {
            border: 1px solid #b91b1b;
            background: #b91b1b;
            color: white;
        }
    }

    i {
        margin-right: 7px;
    }
}
