$color-tumblr-alt: #e2992e;

@mixin social-media-icon($imageUrl, $offset: '-2px') {
    content: '';
    width: 23px;
    height: 16px;
    float: left;
    margin: 0 #{$offset} 0 0;
    background: url($imageUrl) no-repeat;
}

/******************************************
* Elements common to the social media hub *
******************************************/
.social-media-hub {
    .socialmedia-navigation-list {
        margin: 0;
        padding: 0;
        list-style-type: none;
        text-align: left;
        margin-bottom: 20px;

        li {
            display: inline;
            margin: 2px;
            text-align: center;
        }

        a {
            background: #f2f2f2;
            padding: 3px 10px;
            color: #000;
            text-decoration: none;
            font-size: 17px;
            display: inline-block;
            margin: 0px;
            border: solid 1px #aaa;
            width: 90px;
        }

        a.current {
            color: #fff;
            background: #000;
        }

        a:hover {
            color: #fff;
            background: #000;
        }
    }

    h1 {
        @extend .sr-only;
    }
}

/***************************************
* Elements specific to the Social Wall *
***************************************/
.social-wall-wrapper {
    .load-more {
        text-align: center;
        margin-top: 40px;

        a:link,
        a:visited {
            background: #f2f2f2;
            padding: 10px 50px;
            color: #000;
            text-decoration: none;
            font-size: 14px;
            font-weight: 700;
            display: inline-block;
            border: solid 1px #aaa;
            cursor: pointer;
        }

        a:hover {
            color: #fff;
            background: #000;
        }

        span {
            margin-right: 20px;
        }
    }
}

.social-wall {
    max-width: 1200px;
    margin: 0px auto;

    .item {
        margin-bottom: 20px;
        border: solid 1px #e6e6e6;
        padding: 10px 0 0 0;
        width: 100%;
        background: #f2f2f2 none repeat scroll 0 0;
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;

        @media screen and (min-width: 400px) {
            width: 46.25%;
        }

        @media screen and (min-width: 900px) {
            width: 31.48%;
        }
    }

    .item-heading {
        padding: 0 10px;
        line-height: 1.3em;
        font-weight: 600;
        font-size: 1.35em;
    }

    .item-media {
        margin: 5px 0 0px 0;
        padding: 0 5px;
    }

    .item-media-image {
        .static {
            display: block;
            margin: auto;
            width: 100%;
        }
    }

    .item-media-video {
        padding-bottom: 64%;

        .preview {
            display: block;
            margin-top: -20px;
            width: 100%;
        }

        .play-button {
            width: 100%;
            height: 100%;
            position: absolute;
            opacity: 0.7;
            cursor: pointer;
            background: rgba(0, 0, 0, 0)
                url('https://www.city.ac.uk/__data/assets/image/0010/291196/youtube-dark.png')
                50% 50% no-repeat;

            &:hover,
            &:focus {
                background: rgba(0, 0, 0, 0)
                    url('https://www.city.ac.uk/__data/assets/image/0011/291197/youtube-color.png')
                    50% 50% no-repeat;
            }
        }
    }

    .item-text {
        padding: 5px 10px;
        font-size: 1.1em;
        word-wrap: break-word;

        a:link {
            color: #b91b1b;
            border-bottom: 1px dotted #999;
        }

        a:visited {
            color: #670003;
            border-bottom: 1px dotted #999;
        }

        a:hover {
            color: #b91b1b;
            border-bottom: 1px solid #670003;
        }

        a:focus,
        a:active {
            color: #fff;
            background-color: #b91b1b;
        }
    }

    .item-footer {
        margin-top: 5px;
        padding: 5px 10px 3px 10px;
        border-top: solid 3px #e6e6e6;
        font-size: 0.9em;

        a:link,
        a:visited {
            color: #670003;
            border-bottom: none;
        }

        a:hover {
            color: #670003;
            border-bottom: 1px solid #670003;
        }

        a:focus,
        a:active {
            color: #fff;
            background-color: #b91b1b;
        }

        .footer-twitter:before {
            @include social-media-icon(
                'https://www.city.ac.uk/__data/assets/image/0017/291203/twitter.png'
            );
        }

        .footer-facebook:before {
            @include social-media-icon(
                'https://www.city.ac.uk/__data/assets/image/0017/291221/facebook.png'
            );
        }

        .footer-instagram:before {
            @include social-media-icon(
                'https://www.city.ac.uk/__data/assets/image/0006/291237/instagram.png'
            );
        }

        .footer-youtube:before {
            @include social-media-icon(
                'https://www.city.ac.uk/__data/assets/image/0007/291238/youtube.png',
                '3px'
            );
        }

        .elapsed-time {
            float: right;
        }
    }
}

/*************************************************
* Elements specific to the Social Media Accounts *
*************************************************/
.smh-accounts {
    .smh-account {
        background: #f2f2f2 none repeat scroll 0 0;
        margin: 10px 0;
        padding: 15px;
        overflow: auto;
        display: inline-block;
        width: 100%;

        @include breakup-breakpoint('medium') {
            width: 49%;
        }
    }

    @include breakup-breakpoint('medium') {
        .smh-account:nth-child(2n + 2) {
            float: right;
        }

        .smh-account:nth-child(2n + 3) {
            float: left;
            clear: both;
        }
    }

    .smh-account__image {
        float: left;
        height: 75px;
        width: 75px;
        border: solid 1px #ccc;
    }

    .smh-account__name,
    .smh-account__desc {
        margin: 0 0 0 95px;
    }

    .smh-account__types {
        margin: 10px 0 0 95px;
    }

    .smh-account__name {
        font-size: 1.7em;
    }

    .smh-account__type {
        background: url('//s1.city.ac.uk/cityr/i/home/social.png') no-repeat
            scroll 0 -30px rgba(0, 0, 0, 0);
        border-bottom: none;
        display: block;
        float: left;
        height: 27px;
        line-height: 27px;
        text-decoration: none;
        margin: 5px 10px 5px 0;
        width: 30px;
    }

    .smh-account__type--facebook {
        background-position: 0 -30px;
        &:hover,
        &:focus {
            background-position: 0 0;
        }
    }

    .smh-account__type--twitter {
        background-position: -32px -29px;
        &:hover,
        &:focus {
            background-position: -32px 0;
        }
    }

    .smh-account__type--youtube {
        background-position: -62px -30px;
        &:hover,
        &:focus {
            background-position: -62px 0;
        }
    }

    .smh-account__type--instagram {
        background-position: -312px -30px;
        &:hover,
        &:focus {
            background-position: -312px 0;
        }
    }

    .smh-account__type--tumblr {
        border: none;
        font-size: 2.2em;
        line-height: 1.3;
        background-color: #f2f2f2;

        &:before {
            content: '\f174';
            font-family: FontAwesome;
            color: #444;
        }

        &:hover:before {
            color: $color-tumblr-alt;
        }

        &:focus {
            border: none;
        }

        &:focus:before {
            color: $color-tumblr-alt;
        }
    }
}
