.link-list {
    ul {
        list-style-type: none;
        margin: 0;

        li {
            padding-bottom: 6px;
            border-bottom: 1px solid #ccc;
            margin-bottom: 6px;
            a {
                color: #333;
                border: none;
                transition: color 0.1s, background 0.1s;

                &:hover {
                    color: $red1;
                    background: transparent;
                }
                &:active,
                &:focus {
                    background: $red1;
                    color: white;
                }
            }
        }
    }
}
