@import 'font-awesome-extend';

.accordion {
    margin: 0 0 20px;
}

@mixin accordion(
    $header-font-size,
    $header-padding-vertical,
    $header-padding-horizontal,
    $lateral-border: 0px
) {
    @include breakup-tweakpoint('(max-width: 420px)', 'thin', false) {
        .ui-accordion-content {
            img,
            img.left,
            img.right {
                display: block;
                float: none;
            }
        }
    }

    // new

    .ui-accordion-header {
        color: $black;
        font-size: $header-font-size;
        background-color: $gray-lighter !important;
        border-bottom: 2px solid #cccccc !important;
        margin-bottom: 5px;
        border-top: 0;
        padding: 0;

        a {
            padding: 12px 2em 12px 14px !important;
            border: 0 !important;
            background: none !important;
            font-weight: 600;

            &,
            &:link {
                color: #333333;
            }

            &:hover {
                text-decoration: underline;
            }

            &:focus {
                outline: 5px auto -webkit-focus-ring-color;
            }
        }

        &.ui-state-focus,
        &.ui-state-active,
        &.ui-state-hover {
            &,
            a {
                color: #666;
            }
        }
        &.ui-state-active {
            border-bottom-width: 1px !important;
        }

        .ui-icon {
            @include fa-icon-extend('chevron-down');
            transition: transform 200ms;
            background-image: none;
            color: inherit;
            text-indent: 0;
        }

        &.ui-accordion-header-active {
            margin-bottom: 0;

            .ui-icon {
                @include fa-icon-extend('chevron-down', 'rotate-180');
            }
        }
    }

    .ui-accordion-content {
        background-color: #f8f8f8;
        margin: 0 0 5px !important;
        border: 0 !important;
        border-bottom: 2px solid #cccccc !important;
        top: 0 !important;
    }
}

//overwrite for accordion headers and content
.ui-accordion {
    @include accordion(16px, 16px, 14px);
}

//strategic plan accordians
.strategic {
    .ui-accordion-header__ {
        font-size: 1.2em;
        line-height: 1.5em;
        padding: 0.15em 1.5em 0.15em 0.5em;
        color: white;
        color: $white;
        background: none;
        background-color: $darkBrown;

        i {
            padding: 5px;
            position: absolute;
            right: 0px;
            clear: both;
            margin: 0px;
        }

        &.education {
            background: $education;
            color: $white;
            background: none;
            background-color: $education;
        }
        &.research {
            background: $research;
            background: $research;
            color: $white;
            background: none;
            background-color: $research;
        }
        &.enterprise {
            background: $enterprise;
            background: $enterprise;
            color: $white;
            background: none;
            background-color: $enterprise;
        }
        &.internationalisation {
            background: $internationalism;
            background: $internationalism;
            color: $white;
            background: none;
            background-color: $internationalism;
        }
    }

    .enabling-theme {
        h3 {
            color: $white;
            background: $deepPurple;
        }
    }

    .key-commits {
        .ui-accordion-header {
            color: $black;
            background: transparent;
        }

        .ui-accordion-content-active {
            border: none;
        }
    }

    .accordion {
        table {
            border-collapse: separate;
            border-spacing: 0;
        }

        td,
        th {
            padding: 0.25em;
            border: {
                width: 0 0 0 2px;
                style: solid;
            }
        }

        th {
            background: none;
        }

        tr:first-child td {
            border: none;
        }
    }
}
