/*
migrated from static for http://www.city.ac.uk/research/research-publications/search-form
*/

#fb-queryform-publications {
    .large-search {
        background: #eee;
        margin: 10px 0 20px;
        overflow: hidden;
        padding: 20px;
        position: relative;
        border: none;
    }
    .large-search h1,
    .large-search h2 {
        float: left;
    }
    .large-search p {
        border: 1px solid #cccccc;
        margin: 0 !important;
        overflow: hidden;
        width: 512px;
    }
    .large-search .action-p {
        border: medium none;
        margin-top: 7px;
        overflow: visible;
        position: absolute;
        right: 20px;
        top: 27px;
        width: auto;
    }
    .large-search fieldset {
        float: left;
        margin: 0;
        width: 800px;
    }
    .large-search #index-query,
    .large-search .query {
        background: #f7f7f7;
        border: medium none;
        display: block;
        float: left;
        font-size: 1.4em;
        height: 27px;
        line-height: 27px;
        min-height: 35px;
        padding: 4px 5px;
        width: 475px;
    }
    .large-search label {
        display: inline;
        float: left;
        font-size: 1.5em;
        margin: 10px 10px 0 0;
    }
    .large-search #search-button,
    .large-search .s-button {
        background: url('i/skin/search-red.png') no-repeat scroll center center
            #f7f7f7;
        border: medium none;
        cursor: pointer;
        display: inline;
        float: left;
        font-size: 0;
        height: 35px;
        line-height: 0;
        margin: 0;
        overflow: hidden;
        text-indent: -99999px;
        width: 35px;
    }
}

#fb-advanced-publications {
    fieldset {
        border: none;

        padding: 20px;
        background: #eae9e7;
        background-image: linear-gradient(to bottom, #eae9e7 0%, #e0dbd7 100%);
        margin: 10px 0 20px 0;
        @include clearfix;
        position: relative;
    }

    legend,
    div {
        float: left;
    }

    legend {
        font-size: 1.4em;
        margin: 2px 10px 0 0;
    }

    div {
        padding-left: 1em;
    }

    label {
        float: left;
        display: inline;
        font-size: 1.2em;
        margin: 6px 10px 0 0;
    }

    .query {
        $height: 31px;
        min-height: $height;
        height: $height;
        line-height: $height;

        background: #f7f7f7;
        padding: 4px 5px;
        font-size: 1.2em;
        float: left;
        display: block;
        border: none;
    }

    .s-button {
        height: 33px;
        width: 33px;
        background: url('i/skin/search-red.png') no-repeat scroll center center
            #f7f7f7;
        border: medium none;
        cursor: pointer;
        display: inline;
        float: left;
        font-size: 0;
        line-height: 0;
        margin: 0;
        overflow: hidden;
        text-indent: -99999px;
    }
}
