.business-contact-us-profiles {
    .profile {
        padding: 10px 0;
        border-top: 2px solid #ccc;
        margin: 20px 0;

        h2 {
            font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                sans-serif;
        }

        .name {
            font-weight: 600;
        }

        a {
            border: 0;

            &:hover {
                text-decoration: underline;
            }

            &:focus,
            &:active {
                color: white;
            }
        }

        .phone,
        .email {
            margin: 0;
        }

        .contact,
        .job-title {
            margin: 7px 0;
        }
    }
}

.business-contact-us-ribbon {
    p {
        font-size: 22px;
        font-family: 'ff-meta-web', Arial, Helvetica, sans-serif;
        line-height: 1.5em;
    }
}

.business-case-study-listing {
    .case-study {
        margin-bottom: 40px;

        img {
            margin: 0;
        }

        h2,
        h3 {
            font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
                sans-serif;
            font-size: 22px;
            margin-right: 15px;
            position: relative;

            a {
                color: black;
                transition: color 0.2s;

                &:hover {
                    color: $red1;
                }

                &:active,
                &:focus {
                    color: white;
                }
            }
        }

        p {
            display: none;

            @include breakup-breakpoint('medium') {
                // 767px
                display: block;
            }
        }
    }
}

.business-case-study {
    h1,
    .standfirst {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
    }

    .standfirst {
        font-style: normal;
    }

    ul.social-icons-list {
        margin: 30px 0;
        border-bottom: 2px solid #e7e7e7;
        padding-bottom: 30px;
    }

    .partner-logo {
        float: left;
        margin: 0 20px 20px 0;

        img {
            margin: 0;
        }
    }
}

.business-case-partner {
    img {
        margin: 0;
    }

    background: #243d51;

    .partner-text {
        padding: 30px 20px;
        color: #f0f0f0;
    }

    .partner-quote {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
        font-size: 1.2em;
        font-weight: 600;
    }

    .partner-member-of-staff {
        margin: 0;
        font-weight: 600;
        font-size: 0.9em;
    }

    .partner-job-title {
        font-size: 0.9em;
        font-style: italic;
    }

    .partner-profile {
        font-weight: 600;
        margin: 30px 0;
    }

    .partner-industry,
    .partner-country,
    .partner-employees,
    .partner-website {
        margin: 0;
    }

    a {
        color: white;
        text-decoration: underline;
        border: none;
        transition: color 0.1s;

        &:hover {
            color: #bcc4ce;
        }

        &:focus,
        &:active {
            color: white;
        }
    }
}

.business-case-related {
    margin-top: 60px;
    border-top: 5px solid $red1;
    padding-top: 20px;

    h2 {
        font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica,
            sans-serif;
    }

    ul {
        margin: 0;
        list-style-type: none;
    }

    li {
        clear: both;

        a {
            border: none;
            color: black;

            &:hover {
                color: $red1;
                transition: color 0.2s;
                text-decoration: underline;
            }

            &:active,
            &:focus {
                color: white;
            }

            display: block;
            img {
                width: 30%;
                margin-right: 10px;
                float: left;
            }
        }
    }
}
