.strategic {
    /*Counter the default row styles applied for the homepage */
    .row {
        clear: both;
        margin-left: 0px;
        padding: 0px;
    }

    .widget img {
        width: 100%;
        height: 100%;
    }

    .performance {
        h2 {
            color: $deepPurple;
        }
        h1 {
            color: $deepPurple;
            border-bottom: 5px solid;
        }
    }

    .overview div .widget {
        h2 {
            background: $darkBrown;
            color: $white;
        }
        a {
            color: $black;
        }
    }

    .themes {
        h1 {
            color: $darkBrown;
            border-bottom: 5px solid $darkBrown;
        }
        .widget {
            h2 {
                background-image: none;
                background-color: $darkBrown;
                color: $white;
                filter: none;
            }
        }
    }

    p {
        overflow: visible;
    }

    .equal-height-children .hp-box {
        min-height: 250px;
    }

    .hp-box {
        background: $grey3;
        background-position: bottom right;
        background-repeat: no-repeat;
        div {
            padding: $padding-large;
        }
    }
    .action-p {
        padding: 5px 1px 5px 1px;
        .action-button {
            color: $white;
            border: none;
        }
        .action-button:hover {
            text-decoration: underline;
        }
    }
}

.download-strategic-plan-button {
    float: right;
    width: 280px;
    background-color: $red3;
    margin-left: 2em;
    overflow: hidden;

    $space-around-text: 10px;

    img {
        margin-bottom: $space-around-text;
    }

    a {
        color: white;
        display: block;
        text-align: center;
        font-weight: bolder;
        padding-bottom: $space-around-text;
    }

    a:hover,
    a:active,
    a:focus {
        background-color: $red2;
        color: $grey1;
        text-decoration: underline;
    }
}

.strategic {
    .widget h2 {
        filter: none;
    }

    hr {
        margin: {
            top: 20px;
            bottom: 0px;
        }
    }

    .hp-facts {
        position: relative;

        h3 {
            position: absolute;
        }

        dt {
            background: none;
        }

        .hideshow {
            position: relative;
            font-size: 1em;

            span {
                font-size: 1em;
                display: block;
                position: absolute;
                bottom: 0;
                right: 0;
                cursor: pointer;
            }
        }
    }

    .hp-fact-text {
        dt {
            font-size: 10em;
            line-height: 1.2em;
        }
        dt.hideshow {
            font-size: 1em;
        }
    }

    @mixin build-colour-selector($colour) {
        .widget.key-commits h2 {
            background-image: none;
            background-color: $colour;
            color: $white;
        }

        .widget {
            h2 {
                background-image: none;
                background-color: $colour;
                color: $white;
            }
            h1 {
                color: $colour;
                border-bottom: 5px solid $colour;
            }
        }
    }

    section.education {
        @include build-colour-selector($education);
        h1 {
            color: $education;
            border-bottom: 5px solid $education;
        }
    }
    section.research {
        @include build-colour-selector($research);
        h1 {
            color: $research;
            border-bottom: 5px solid $research;
        }
    }
    section.enterprise {
        @include build-colour-selector($enterprise);
        h1 {
            color: $enterprise;
            border-bottom: 5px solid $enterprise;
        }
    }
    section.internationalisation {
        @include build-colour-selector($internationalism);
        h1 {
            color: $internationalism;
            border-bottom: 5px solid $internationalism;
        }
    }

    .accordion.education {
        td,
        th {
            border-color: $education;
        }
    }

    .accordion.research {
        td,
        th {
            border-color: $research;
        }
    }

    .accordion.enterprise {
        td,
        th {
            border-color: $enterprise;
        }
    }

    .accordion.internationalism {
        td,
        th {
            border-color: $internationalism;
        }
    }
}
