.footer {
    padding-top: 10px;

    a {
        color: black;
        border: none;

        &:focus {
            //background: none;
        }

        &:hover {
            text-decoration: underline;
        }
    }

    p {
        line-height: 1.2em;
        margin-bottom: 5px;
    }

    img {
        margin: 0;
    }

    h3 {
        font-size: 22px;
        font-family: 'ff-meta-web', Arial, Helvetica, sans-serif;
        line-height: 1.5em;
    }

    .footer-contact > div {
        margin-bottom: 20px;
    }

    .footer-links {
        margin-bottom: 20px;

        .cr {
            display: inline-block;
            font-weight: bold;
        }

        .links {
            display: block;
        }

        @include breakup-breakpoint('medium') {
            .cr {
                float: left;
            }
            .links {
                margin-left: 230px;
            }
        }

        a {
            margin-right: 15px;
        }
    }

    .footer-qaahr {
        margin: 20px 0;
        border-bottom: 1px solid #aaa;

        img:hover {
            outline: 2px solid $white;
        }

        a {
            display: inline-block;
            margin: 0 30px 30px 0;

            &:focus,
            &:active {
                background-color: transparent;
            }
        }
    }
}
