.js {
    .responsive-tabs {
        $breakpoint: 'thinish_extended';
        margin-bottom: 20px;

        @mixin tablink() {
            color: black;
            font-weight: 800;
            font-family: 'ff-meta-web', Arial, Helvetica, sans-serif;
            display: block;
            padding: 10px;
            background: #f5f5f5;

            &:hover {
                background: #a22833;
                color: white;
            }

            &:focus {
                background: #a22833;
                color: white;
                span {
                    border-bottom: 1px dotted white;
                }
            }

            &:active {
                background: white;
                color: #555;
                span {
                    border-bottom: 1px dotted black;
                }
            }
        }
        @mixin tablink-selected() {
            color: black;
            display: block;
            border-bottom-color: white;
            background: white;
            span {
                border: none;
            }
        }

        > ul {
            margin: 0;
            padding: 0 10px;
            border-bottom: 1px solid #ddd;

            li {
                display: inline-block;
                margin-bottom: -1px;

                a {
                    border: 1px solid #ddd;

                    @include tablink();

                    &.selected {
                        @include tablink-selected();
                    }
                }
            }
        }

        .tab-panel {
            border: 1px solid #ddd;

            .accordion-toogle {
                background: #f5f5f5;
                margin: 0;
                font-size: 1.2em;

                a {
                    @include tablink();

                    &:after {
                        @include fa-pseudo-icon(chevron-down) float: right;
                    }
                }
            }

            .panel-content {
                border-top: 1px solid #ddd;
                padding: 10px;
            }

            &.selected .accordion-toogle a {
                @include tablink-selected();
                &:after {
                    content: '\f077';
                }
            }
        }

        // accordion mode
        > ul {
            display: none;
        }

        .tab-panel {
            .accordion-toogle {
                display: block;
            }

            .panel-content {
                display: none;
                border: none;
                padding: 10px;
            }

            &.selected {
                .accordion-toogle {
                    border-bottom: 1px solid #ddd;
                }

                .panel-content {
                    display: block;
                    tab-index: 0;
                }
            }
        }

        // tabs mode
        @include breakup-breakpoint($breakpoint) {
            > ul {
                display: block;
            }

            .tab-panel {
                display: none;
                border-top-width: 0;
                padding: 10px;

                &.selected,
                &.default {
                    display: block;
                }

                .accordion-toogle {
                    position: absolute;
                    width: 1px;
                    height: 1px;
                    margin: -1px;
                    padding: 0;
                    overflow: hidden;
                    clip: rect(0, 0, 0, 0);
                    border: 0;
                }

                .panel-content {
                    display: block;
                }
            }
        }

        @include breakup-breakpoint('medium') {
            margin-bottom: 0;
        }
    }
}
