////
/// Coloured Action Boxes
/// @group components
/// @example html
///   <ul class="coloured-action-boxes">
///     <li class="coloured-action-box  coloured-action-box--COLOUR">
///       <a href="#" title="TITLE">
///         <h3 class="coloured-action-box__title">TITLE</h3>
///         <p class="coloured-action-box__description">DESCRIPTION</p>
///         <span class="coloured-action-box__icon  fa  fa-ICON" aria-hidden="true"></span>
///         <span class="coloured-action-box__label">LABEL</span>
///       </a>
///     </li>
///   </ul>
////

.coloured-action-boxes {
    width: 100%;
    max-width: $screen-lg;
    padding: 0;
    margin: 0 auto 4rem;
    list-style: none;
    font-size: 0;

    li {
        display: inline-block;
        margin-bottom: $grid-gutter-width;
        font-size: $font-size-base;
    }
}

$colored-box-start-color: #dc1a30;
$colored-box-launch-color: #7e1e91;
$colored-box-scale-color: #18739f;
$colored-box-connect-color: #a39995;

$cta-link-color--dark-blue: #00294a;
$cta-link-color--light-blue: #00b1e1;
$cta-link-color--green: #649c4d;
$cta-link-color--red: #dd0036;
$cta-link-color--dark-purple: #401661;
$cta-link-color--light-purple: #842a8f;
$cta-link-color--maroon: #390031;
$cta-link-color--teal: #00454c;

$colored-box-start-image: 'i/cityventures/start.png';
$colored-box-launch-image: 'i/cityventures/launch.png';
$colored-box-scale-image: 'i/cityventures/scale.png';
$colored-box-connect-image: 'i/cityventures/connect.png';

.coloured-action-box {
    a {
        color: inherit;
        display: block;
        text-decoration: none;

        &:link,
        &:visited {
            border-width: 0;
        }

        &:hover,
        &:focus,
        &:active {
            background-color: transparent;
        }

        span {
            font-weight: bold;
            text-decoration: underline;
        }
    }

    .coloured-action-box__title {
        background-position: right 1em center;
        background-repeat: no-repeat;
        color: $white;
        height: 6em;
        display: block;
        padding: 1em;
        position: relative;
        font-weight: bold;
        line-height: 4;

        @include breakup-breakpoint('wide') {
            background-position: right 1em bottom 1em;
            height: 8em;
            line-height: 1;
        }

        &:before {
            content: '/';
        }
    }

    &.coloured-action-box--start {
        .coloured-action-box__title {
            background-color: $colored-box-start-color;
            background-image: url($colored-box-start-image);
        }

        a span {
            color: $colored-box-start-color;
        }
    }
    &.coloured-action-box--launch {
        .coloured-action-box__title {
            background-color: $colored-box-launch-color;
            background-image: url($colored-box-launch-image);
        }

        a span {
            color: $colored-box-launch-color;
        }
    }
    &.coloured-action-box--scale {
        .coloured-action-box__title {
            background-color: $colored-box-scale-color;
            background-image: url($colored-box-scale-image);
        }

        a span {
            color: $colored-box-scale-color;
        }
    }
    &.coloured-action-box--connect {
        .coloured-action-box__title {
            background-color: $colored-box-connect-color;
            background-image: url($colored-box-connect-image);
        }

        a span {
            color: $colored-box-connect-color;
        }
    }

    .coloured-action-box__description {
        margin: 1.5em 0 0.5em;
    }
}

/// City Brand colour variants
.coloured-action-box {
    &.coloured-action-box--dark-blue {
        a span {
            color: $cta-link-color--dark-blue;
        }
    }

    &.coloured-action-box--light-blue {
        a span {
            color: $cta-link-color--light-blue;
        }
    }

    &.coloured-action-box--green {
        a span {
            color: $cta-link-color--green;
        }
    }

    &.coloured-action-box--red {
        a span {
            color: $cta-link-color--red;
        }
    }

    &.coloured-action-box--dark-purple {
        a span {
            color: $cta-link-color--dark-purple;
        }
    }

    &.coloured-action-box--light-purple {
        a span {
            color: $cta-link-color--light-purple;
        }
    }

    &.coloured-action-box--maroon {
        a span {
            color: $cta-link-color--maroon;
        }
    }

    &.coloured-action-box--teal {
        a span {
            color: $cta-link-color--teal;
        }
    }
}
