.hallway {
    margin-top: 10px;

    .hallway-item {
        position: relative;
        overflow: hidden;
        height: 150px;
    }

    &.three-columns,
    &.equal,
    &.equalWithWidget {
        .col-xs-24 {
            margin-bottom: 2.2%;
        }
    }

    &.hallway-right-4,
    &.hallway-right-5,
    &.hallway-left-4,
    &.hallway-left-5 {
        hgroup {
            left: 0;
        }
        .col-md-12,
        .col-md-7 {
            hgroup {
                left: 10px;
            }
        }
        .hallway-item {
            margin-bottom: 2%;
        }
    }

    img {
        margin: 0;
        vertical-align: middle;
        width: 100%;
    }
    hgroup {
        top: 0;
        width: 100%;
    }
    a {
        border: none;
        color: $white;
        &:focus {
            background-color: #a0271f;
        }
    }
    h2,
    h3 {
        background-color: $red3;
        font-family: Arial, sans-serif;
        border: none;
        margin: 0;
        padding: 5px 6px;
        font-weight: 700;
        font-size: 1em;
    }

    h3 {
        color: #ccc;
        margin-top: -10px;
    }
}

@include breakup-breakpoint('thinish') {
    .hallway {
        .hallway-item {
            overflow: hidden;
            height: auto;
            position: auto;
        }
        img {
            display: inline;
        }
        hgroup {
            width: auto;
            position: absolute;
        }
        a {
            text-decoration: none;
            border: none;
            display: block;
            background-color: transparent;

            &:hover,
            &:link,
            &:focus {
                text-decoration: none;
                border: none;
                background-color: transparent;
            }
        }
        &.hallway-right-4,
        &.hallway-right-5,
        &.hallway-left-4,
        &.hallway-left-5 {
            .col-md-17 {
                margin-bottom: 2.7%;
                .hallway-item {
                    margin-right: -10px;
                    margin-bottom: 2.7%;
                }
                .hallway-item.col-md-12 {
                    padding-right: 0;
                    margin-right: 0;
                }
            }
            .col-md-7 {
                padding-right: 1px;
            }
        }
    }
}

@include breakup-breakpoint('medium') {
    .hallway {
        &.hallway-right-4,
        &.hallway-right-5,
        &.hallway-left-4,
        &.hallway-left-5 {
            .col-md-17 {
                margin-bottom: 2%;

                .hallway-item {
                    margin-bottom: 2%;
                }
            }
            .col-md-7 {
                padding-right: 5px;
            }
        }
    }
}

@include breakup-breakpoint('wide') {
    .hallway {
        margin-top: 0;

        h2,
        h3 {
            padding: 6px 10px;
            font-size: 1.3em;
        }
        &.hallway-right-4,
        &.hallway-right-5,
        &.hallway-left-4,
        &.hallway-left-5 {
            .col-md-17 {
                margin-bottom: 2.4%;

                .hallway-item {
                    margin-bottom: 2.4%;
                }
            }
            .col-md-7 {
                padding-right: 7px;
            }
        }
    }
}
