@import '../breakup';

@mixin nav-link-styles {
    &:focus,
    &:hover {
        color: #000;
        text-decoration: underline;
        background-color: #fff;
        border-color: #ccc;
    }
    &:active {
        text-decoration: none;
    }
}

@mixin clear-arrow {
    &:after,
    &:before {
        content: none;
    }
}

@mixin arrow {
    &:after,
    &:before {
        top: -1px;
        border: solid transparent;
        content: ' ';
        height: 0;
        width: 0;
        position: absolute;
        pointer-events: none;
    }

    &:after {
        border-color: rgba(255, 255, 255, 0);
        border-top-color: #fff;
        border-width: 10px;
        left: 5%;
        margin-left: -10px;
    }
    &:before {
        border-color: rgba(204, 204, 204, 0);
        border-top-color: #cccccc;
        border-width: 11px;
        left: 5%;
        margin-left: -11px;
    }
}

@mixin arrow-select {
    &:after {
        width: 0;
        height: 0;
        border-bottom: 12px solid transparent;
        border-top: 13px solid transparent;
        border-left: 13px solid #fff;
        font-size: 0;
        line-height: 0;
        left: 100%;
        margin-left: 0px;
        top: 0px;
    }

    &:before {
        border: transparent;
    }
}

@mixin arrow-nav {
    &:after {
        width: 0;
        height: 0;
        border-bottom: 12px solid transparent;
        border-top: 13px solid transparent;
        border-left: 13px solid #f7f7f7;
        font-size: 0;
        line-height: 0;
        left: 100%;
        margin-left: 0px;
        top: 0px;
    }

    &:before {
        border: transparent;
    }
}

#primary-nav-toggler {
    cursor: pointer;
    font-weight: normal;
    font-family: $font-family-serif;
    font-size: 1.4em;
    width: 100%;
    display: inline-block;
    text-align: center;
    margin: 0;
    border-top: 3px solid #8d182a;
    border-left: 3px solid #8d182a;
    border-right: 3px solid #8d182a;
    background: #8d182a;

    a {
        color: $white;
        display: block;
        line-height: 20px;
        padding: 0.6em 0;
        font-family: $font-family-meta-sans;
        border: none;

        &:before {
            content: '\f0c9';
            font-family: 'FontAwesome';
            margin-right: 0.7em;
        }
        &:hover {
            border: none;
        }
        &:focus,
        &:active {
            background: #8d182a;
        }
    }

    &.active {
        a {
            background: #fff;
            color: #333;
        }
    }
    @include breakup-breakpoint('wide') {
        // 980px
        display: none;
    }
}

#slice-navigation {
    &.active {
        margin-top: -7px;
    }
}

.js #slice-navigation {
    display: none;

    &.active {
        display: block;
    }
}

#slice-navigation.superbreadcrumb {
    li {
        display: block;

        &:last-child {
            a {
                border-bottom: none;
            }
        }
        &:first-child a {
            @include clear-arrow;
        }
        //home page:
        &:first-child.current {
            display: none;
        }
    }
    a {
        border-bottom: 1px solid #ccc;
        margin-top: 0;
        @include arrow;
    }
    .current span {
        padding: 15px 10px 10px;
        display: block;
        @include arrow;
        position: relative;
        color: #000;
        font-size: 1.2em;
    }
}

@include breakup-breakpoint('wide') {
    // 980px
    .js #slice-navigation {
        display: block;
    }
    #slice-navigation {
        background: $sandyGrey;
        padding: 0;
        border: none;
        #secondary-nav-siblings {
            li.selected {
                a {
                    margin-bottom: 0px;
                    padding-bottom: 7px;
                    z-index: 1;
                }
            }
        }
        #secondary-nav-siblings li a,
        #secondary-nav-branch li a {
            position: relative;
            bottom: 0px;
            span {
                padding-bottom: 2px;
            }
        }
        #secondary-nav > li.selected:first-child {
            margin-top: 0;
        }
        // super breadcrumb styles - lots of overrides
        &.superbreadcrumb {
            background-color: #eae9e7;
            overflow: hidden;
            li {
                float: left;
                &:first-child a {
                    padding-left: 20px;
                    color: #333;
                    &::before,
                    &::after {
                        content: ' ';
                    }
                }
            }
            // the last element has a class .current and always contains a span rather than a link
            .current {
                span {
                    display: block;
                    float: left;
                    font-weight: bold;
                    font-size: 1em;
                    padding: 10px 0 10px 55px;
                    border: none;
                    @include clear-arrow;
                    &:before {
                        content: none !important;
                    }
                }
            }
            a {
                display: block;
                float: left;
                font-weight: bold;
                padding: 10px 30px 10px 50px;
                margin: 0;
                border-bottom: none;
                color: #333;
                &:hover {
                    text-decoration: none;
                    background: #e0dbd7;
                    color: #333;
                }
                &:hover:after {
                    border-left-color: #e0dbd7 !important;
                }
                &:visited {
                    color: #333;
                }
                span {
                    display: inline;
                    padding: 0;
                    border-right: none;
                }
            }
            .current span::after,
            a::after {
                content: ' ';
                display: block;
                width: 0;
                height: 0;
                border-top: 21px solid transparent; /* Go big on the size, and let overflow hide */
                border-bottom: 21px solid transparent;
                border-left: 12px solid #eae9e7;
                position: absolute;
                top: 50%;
                margin-top: -21px;
                margin-left: -1px;
                left: 100%;
                z-index: 2;
            }
            a::before {
                content: ' ';
                display: block;
                width: 0;
                height: 0;
                border-top: 21px solid transparent;
                border-bottom: 21px solid transparent;
                border-left: 12px solid white;
                position: absolute;
                top: 50%;
                margin-top: -21px;
                margin-left: 0;
                left: 100%;
                z-index: 1;
            }
        }
    }
}

/*primary nav*/
#primary-nav {
    margin: 0;
    clear: right;
    @include legacy-pie-clearfix;

    li {
        margin: 0;
        padding: 0;
        list-style: none;
        display: none;
        list-style: none;

        &.selected,
        &.nav-home {
            display: block;
        }
        &.nav-home {
            a {
                background-image: none;
            }
        }
        &.nav-home.selected {
            a {
                @include clear-arrow;
            }
        }
        a {
            display: block;
            //font-size: 1.2em;
            position: relative;
            text-decoration: none;
            width: auto;
            padding: 1.2em 0.5em 1em 0.5em;
            border-bottom: none;
            border-right: none;
            //@include nav-link-styles;
        }
        &.selected,
        &.current {
            display: block;
            a {
                //font-weight: bold;
                border-bottom: 1px solid #ccc;
                @include arrow;
            }
        }
        &.current.parent {
            a {
                font-weight: normal;
            }
        }
    }
    li:last-child a span {
        border-right: none;
    }
}

//styles only applied on section homepage
#primary-nav.top-level {
    li {
        display: block;

        a {
            background-color: #fff;
            border-bottom: 1px solid #ccc;
        }
        &.nav-home {
            a {
                border-bottom: none;
            }
        }
    }
    .selected {
        a {
            font-weight: bold;
            background-color: #fff;
        }
    }
    li:first-child + li {
        a {
            @include arrow;

            &:active,
            &:focus {
                color: $logo-red;
                text-decoration: underline;
            }
            &:hover {
                background-color: #fff;
            }
        }
    }
}

@include breakup-breakpoint('wide') {
    // 980px

    #primary-nav {
        padding: 0;
        border: none;

        li {
            display: block;
            float: left;

            a {
                margin-left: -1px;
                background: transparent;
                margin-bottom: 0;
                padding: 5px 0;
                line-height: 20px;
                height: auto;
                font-weight: bold;
                color: #333;
                @include clear-arrow;
                font-size: 1em;

                &:visited {
                    color: #333;
                }
                &:focus,
                &:active,
                &:hover {
                    background: transparent;
                    color: #333;
                    text-decoration: underline;
                }
                span {
                    border-right: 1px solid #777;
                }
            }
            &.current,
            &.selected {
                a {
                    background: #333;
                    padding-bottom: 5px;
                    margin-bottom: 0px;
                    padding-top: 10px;
                    margin-top: -5px;
                    color: #fff;
                    border-bottom: none;
                    @include clear-arrow;

                    span {
                        border-right: none;
                    }
                }
            }
            &.nav-home {
                border-bottom: none;
            }
            &:last-child {
                a {
                    border-right: none;
                }
            }
            &.current.parent {
                a {
                    font-weight: bold;
                }
            }

            &.nav-home,
            &:first-child {
                a {
                    left: 1px;
                    span {
                        padding-left: 5px;
                    }
                }
            }
        }
        span {
            padding: 0 6px;
        }
    }
    //only the primary-nav is shown if has class .top-level - no secondary nav
    #primary-nav.top-level {
        li {
            a {
                background: transparent;
                padding: 5px 0;
                border-bottom: none;
                &:hover {
                    background: transparent;
                }
            }
            &.current,
            &.selected {
                a {
                    background: #333;
                    padding-bottom: 5px;
                    margin-bottom: -5px;
                    padding-top: 10px;
                    margin-top: -5px;
                }
            }
        }
        li:first-child + li {
            a {
                background: none;
                margin-top: 0;
                @include clear-arrow;
                &:hover {
                    background-color: transparent;
                }
            }
        }
        li:last-child {
            a span {
                border-right: none;
            }
        }
    }
}

#secondary-nav {
    margin: 0;
    @include legacy-pie-clearfix;
    clear: both;

    li {
        list-style: none;
        margin: 0;
        padding: 0;
        overflow: visible;

        &.end,
        li {
            overflow: visible;
        }
        &.end a {
        }
        a {
            font-size: 0.9em;
            display: block;
            padding: 15px 10px 10px;
            position: relative;
            @include arrow;
            @include nav-link-styles;
        }
    }
    .selected {
        a {
            font-weight: bold;

            &:hover {
                border-bottom: 1px solid #ccc;
            }
        }
    }
}

#secondary-nav-siblings,
#secondary-nav-branch {
    margin: 0 0 0 20px;
    padding: 0;

    li {
        a {
            border-bottom: 1px solid #ccc;
            margin-top: 0;
            @include clear-arrow;
        }
    }
    li:last-child {
        a {
            border-bottom: none;
        }
    }
    li:first-child {
        a {
            @include arrow;
        }
    }
}

@include breakup-breakpoint('wide') {
    // 980px
    #secondary-nav {
        padding: 5px 0 2px 4px;
        background: #333;
        margin-top: 0;

        .no-sec-nav & {
            display: none;
        }

        li {
            float: left;
            line-height: normal;
            padding-right: 17px;
            a {
                padding: 5px 10px;
                border-bottom: none;
                @include arrow-nav;
                background: #f7f7f7;
                font-weight: bold;
                display: block;
                margin-top: 0px;
                color: #333;
                &:visited {
                    color: #333;
                }
                &:hover,
                &:focus,
                &:active {
                    background-color: #f7f7f7;
                    color: #333;
                    text-decoration: underline;
                }
            }
        }
        .selected {
            a {
                background-color: #fff;
                background-position: right -14px;
                color: #333;
                @include arrow-select;
                &:hover,
                &:visited {
                    border-bottom: none;
                    background-color: #fff;
                }
            }
        }
        > li:first-child {
            border-left: none;
        }
        > li:first-child.selected {
            border-left: none;
        }
        li.end {
            margin-bottom: 0px;
            margin-right: 0;
            margin-top: 0px;
            a {
                background-position: right -14px;
            }
        }
        li.selected-join {
            a {
                background-position: right -134px;
            }
        }
    }
    //tab style
    #secondary-nav-branch,
    #secondary-nav-siblings {
        margin: 0;
        padding: 0;
        li {
            margin-bottom: 0;
            padding-right: 0;
            a {
                padding: 5px 0;
                background: transparent;
                @include clear-arrow;
                //this hides the border left on selected element span
                margin-left: -1px;
                color: #fff;

                &:hover,
                &:focus,
                &:active,
                &:visited {
                    color: #fff;
                    background-color: transparent;
                }
            }
            a span {
                border-left: 1px solid #999;
                padding: 0 6px;
                display: block;
            }
            &:first-child {
                a span {
                    border-left: none;
                }
            }
        }
        //overrides styles from mobile first menu
        li:first-child {
            a {
                background: transparent;
                @include clear-arrow;
            }
        }
        li.selected {
            margin-bottom: -2px;
            a {
                background: #fff;
                color: #333;
                padding-bottom: 9px;
                margin-bottom: -9px;
                position: relative;
                @include clear-arrow;
                &:hover,
                &:active,
                &:focus {
                    background-color: #fff;
                }
                span {
                    border-color: #fff;
                }
            }
        }
    }
}
