.action-box {
    border: 1px solid #ccc;
    overflow: hidden;
    margin-bottom: 20px;

    &.action-box-2 {
        a {
            border-color: $city-blue;
        }
    }
    &.action-box-3 {
        a {
            border-color: $city-green;
        }
    }
    &.action-box-4 {
        a {
            border-color: $city-orange;
        }
    }
    &.action-box-5 {
        a {
            border-color: #00284d;
        }
    }
    &.action-box-6 {
        a {
            border-color: #00444d;
        }
    }
    &.action-box-7 {
        a {
            border-color: $blue-dark;
        }
    }
    &.action-box-8 {
        a {
            border-color: $city-blue-light;
        }
    }
    &.action-box-9 {
        a {
            border-color: #8f2b8c;
        }
    }

    h2 {
        margin-top: 0;
    }

    a {
        color: #333;
        display: block;
        border-bottom: none;
        border-left: 10px solid $red1;
        padding: 20px;
        -webkit-transition: all 0.1s ease-in-out;
        -moz-transition: all 0.1s ease-in-out;
        transition: all 0.1s ease-in-out;
        -webkit-transform: translateX(-5px);
        -moz-transform: translateX(-5px);
        transform: translateX(-5px);

        &:hover,
        &:focus {
            -webkit-transform: translateX(0px);
            -moz-transform: translateX(0px);
            transform: translateX(0px);
            background: #f9f9f9;
        }

        &:active {
            background: #fff;
        }
    }

    .fake-link {
        color: $red;
        border-bottom: 1px solid #ccc;

        &:hover {
            border-bottom-color: #fff;
        }
    }
}

// action-box vertical
// used on intranet for now, but using Cara's News section design
// do we need to change this class at some point?
.action-box-vertical {
    border-left: 1px solid #ebebeb;
    border-right: 1px solid #ebebeb;
    border-bottom: 1px solid #ebebeb;
    padding: 10px;
    margin-bottom: 20px;
    position: relative;

    &:before {
        content: '';
        left: -1px;
        position: absolute;
        top: 0;
        width: 100.3%;
        border-top-width: 8px;
        border-top-style: solid;
    }

    h2 {
        @include font-family-sans-serif();
        font-size: 0.9em;
        text-transform: uppercase;
        border-bottom: 1px solid $grey3;
        padding-bottom: 5px;
        margin-bottom: 20px;
    }

    ul {
        margin-left: 12px;
    }

    &.action-box-vertical-1 {
        &:before {
            border-top-color: $red;
        }
        h2 a {
            color: $red;
        }
    }
    &.action-box-vertical-2 {
        &:before {
            border-top-color: #401663;
        }
        h2 a {
            color: #401663;
        }
    }
    &.action-box-vertical-3 {
        &:before {
            border-top-color: #00284d;
        }
        h2 a {
            color: #00284d;
        }
    }
    &.action-box-vertical-4 {
        &:before {
            border-top-color: #00444d;
        }
        h2 a {
            color: #00444d;
        }
    }
    &.action-box-vertical-5 {
        &:before {
            border-top-color: #979799;
        }
        h2 a {
            color: #979799;
        }
    }
    &.action-box-vertical-6 {
        &:before {
            border-top-color: $city-orange;
        }
        h2 a {
            color: $city-orange;
        }
    }
    &.action-box-vertical-7 {
        &:before {
            border-top-color: $city-green;
        }
        h2 a {
            color: $city-green;
        }
    }
    &.action-box-vertical-8 {
        &:before {
            border-top-color: $city-blue-light;
        }
        h2 a {
            color: $city-blue-light;
        }
    }
    &.action-box-vertical-9 {
        &:before {
            border-top-color: $blue-dark;
        }
        h2 a {
            color: $blue-dark;
        }
    }
    &.action-box-vertical-10 {
        &:before {
            border-top-color: deeppink;
        }
        h2 a {
            color: deeppink;
        }
    }

    h2 a {
        &:hover {
            font-weight: bold;
        }
        &:active,
        &:focus {
            background-color: $red;
            color: #fff;
        }
    }
}

