$color-shortcourse-primary: #3e1031;
$color-shortcourse-primary-gradient: #774266;
$color-shortcourse-secondary: #d82231;
$color-shortcourse-secondary-gradient: #4d0432;

$color-cpd-primary: #00434c;
$color-cpd-primary-gradient: #1f666f;

.shortcourse {
    margin-top: -50px;

    .responsive-tabs {
        margin-bottom: 30px;

        @include breakup-breakpoint('medium') {
            margin-bottom: 10px;
        }
    }
}

.shortcourse-solid {
    background-color: $color-shortcourse-primary;
    color: $white;
}

.shortcourse-header {
    margin: 0 -10px;
    padding: 0px 10px;
    font-family: 'ff-meta-web', 'Helvetica Neue', Arial, Helvetica, sans-serif;

    h1 {
        display: inline-block;
    }

    @include breakup-breakpoint('wide') {
        margin-left: 0;
        margin-right: 0;
    }
}

.shortcourse-header-wrapper {
    padding-top: 20px;

    @include breakup-breakpoint('medium') {
        padding-bottom: 10px;
    }

    @include breakup-breakpoint('wide') {
        padding-bottom: 20px;
    }
}

.shortcourse-header__title {
    margin-right: 20px;
}

.shortcourse-header__award {
    font-size: 0.55em;
    font-weight: normal;
    line-height: 1;
    display: inline-block;
}

.shortcourse-banner {
    margin: 0 -10px;
    max-height: 300px;
    overflow: hidden;

    @include breakup-breakpoint('wide') {
        margin-left: 0;
        margin-right: 0;
    }

    @include breakup-breakpoint('wider') {
        max-height: 320px;
    }

    img {
        width: 100%;
        margin: 0;

        @include breakup-breakpoint('wider') {
            transform: translateY(-5%);
        }
    }
}

.shortcourse-selection {
    background: $sandyGrey;
    color: #333;
    padding: 20px 10px;
    margin: 20px -10px 0 -10px;
    z-index: 100;

    select {
        display: block;
        width: 100%;
        max-width: 300px;
        margin-bottom: 20px;
        font-size: 1.3em;
        padding: 5px;
        font-family: $font-family-sans;
        border-radius: 4px;
    }

    .cta {
        display: block;
        max-width: 300px;
        font-size: 1.3em;

        a {
            display: block;
        }
    }

    @include breakup-breakpoint('medium') {
        position: absolute;
        margin: 0;
        right: 10px;
        left: 10px;
        width: auto;

        &:after {
            position: absolute;
            top: 100%;
            right: 50%;
            margin-right: -50%;
            content: '';
            width: 100%;
            height: 120px;
            background: linear-gradient(
                to left bottom,
                $sandyGrey 49.5%,
                transparent 50%
            );
        }

        select {
            font-size: 1em;
            padding: 8px 5px;
        }
    }

    @include breakup-breakpoint('wide') {
        padding: 20px;

        &:after {
            height: 140px;
        }

        select {
            font-size: 1.2em;
            max-width: 100%;
            padding: 12px 5px;
        }

        .cta {
            font-size: 1.3em;
            max-width: 100%;

            a {
                padding: 10px;
            }
        }
    }
}

.shortcourse-selection__title {
    font-size: 1.4em;
    font-weight: 600;
    margin-bottom: 10px;
}

.shortcourse-intro {
    margin-bottom: 10px;

    @include breakup-breakpoint('medium') {
        margin-top: 30px;
        margin-bottom: 20px;
    }
}

.shortcourse-keyinfo {
    margin-left: -10px;
    margin-right: -10px;
    margin-bottom: 20px;
    padding: 20px;

    @include breakup-breakpoint('medium') {
        margin: 0;
        padding: 20px;
    }

    h2 {
        margin-bottom: 20px;
    }

    h2:after {
        content: '';
        display: block;
        max-width: 300px;
        padding-top: 0;
        border-bottom: 1px solid $white;

        @include breakup-breakpoint('wide') {
            max-width: 100%;
        }
    }

    .row {
        margin-bottom: 10px;
    }

    .cta {
        display: block;
        max-width: 300px;
        font-size: 1.3em;

        @include breakup-breakpoint('wide') {
            max-width: 100%;
        }

        a {
            display: block;
            color: #333;
        }
    }
}

.shortcourse-keyinfo__details {
    margin-bottom: 30px;
}

.shortcourse-cta {
    a {
        background-color: $white;
        color: #333;
        border: 1px solid #dcdcdc;

        &:hover {
            background-color: $sandyGrey;
        }

        &:focus {
            background-color: $sandyGrey;
        }
    }
}

.js .shortcourse-testimonials-block {
    display: none;
}

.shortcourse-testimonials-block {
    margin-left: -10px;
    margin-right: -10px;
    padding-left: 0;
    padding-right: 0;

    @include breakup-breakpoint('medium') {
        margin-left: auto;
        margin-right: auto;
    }
}

.shortcourse-testimonials {
    background: linear-gradient(
        to left bottom,
        $color-shortcourse-primary,
        $color-shortcourse-primary-gradient
    );
    padding: 0;
    color: $white;
    margin-bottom: 10px;

    @include breakup-breakpoint('medium') {
        margin-bottom: 20px;
    }

    .course__profiles__item {
        padding-bottom: 0;
    }

    .bx-controls-direction {
        left: 25%;

        a {
            padding: 10px 20px;
        }

        @include breakup-breakpoint('thinish') {
            left: 50%;
        }
    }
}

.shortcourse-testimonials--empty {
    @include breakup-breakpoint('medium') {
        height: 5px;
        width: 100%;
        background-color: $color-shortcourse-primary;
    }
}

.shortcourse-testimonials__image-wrapper {
    padding: 0;

    img {
        width: 100%;
        height: auto;
        margin: 0;
        filter: grayscale(0.7);
        opacity: 0.8;
    }
}

.shortcourse-testimonials__text {
    text-align: right;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;

    @include breakup-breakpoint('medium') {
        padding-left: 10px;
        padding-right: 20px;
        padding-top: 20px;
    }

    @include breakup-breakpoint('wider') {
        padding-right: 30px;
        padding-top: 30px;
    }
}

.shortcourse-testimonials__quote {
    font-style: italic;
    font-size: 1.3em;
    line-height: 1.3em;
    margin-bottom: 20px;

    span {
        font-style: normal;
        margin-bottom: 5px;
    }

    @include breakup-breakpoint('medium') {
        font-size: 1.5em;
        margin-bottom: 30px;
    }
}

.shortcourse-testimonials__name,
.shortcourse-testimonials__type {
    margin-bottom: 0;

    @include breakup-breakpoint('wide') {
        font-size: 1.1em;
        margin-bottom: 2px;
    }
}

.shortcourse-testimonials__type {
    padding-bottom: 20px;

    @include breakup-breakpoint('wider') {
        font-size: 1.1em;
        margin-bottom: 40px;
    }
}

.shortcourse-tutor-wrapper {
    padding-left: 0;
    padding-right: 0;
    margin-left: -10px;
    margin-right: -10px;
    position: relative;
    background: linear-gradient(to right, white 0%, white 100%);

    @include breakup-breakpoint('medium') {
        margin-bottom: 20px;

        &:after {
            content: '';
            width: 100%;
            height: 35px;
            position: absolute;
            background: inherit;
            z-index: 1;
            bottom: -35px;
            transform-origin: left top;
            transform: skewY(-5deg);
        }
    }
}

.shortcourse-tutor-block {
    background: linear-gradient(
        to bottom,
        $color-shortcourse-secondary,
        $color-shortcourse-secondary-gradient
    );
    padding: 30px 0 0 0;
    color: $white;

    @include breakup-breakpoint('medium') {
        margin-left: 10px;
        margin-right: 10px;
    }

    @include breakup-breakpoint('wide') {
        padding: 30px 0 0 0;
    }
}

.shortcourse-tutor-block {
    h2 {
        margin-bottom: 30px;
        padding-left: 10px;
        padding-right: 10px;

        @include breakup-breakpoint('wide') {
            padding-left: 20px;
            padding-right: 20px;
        }

        &:after {
            content: '';
            display: block;
            max-width: 190px;
            padding-top: 0;
            border-bottom: 1px solid $white;
        }
    }
}

.shortcourse-tutor-block {
    a {
        color: $white;
        border-bottom: 1px dotted $white;
        font-style: normal;
    }

    a:hover {
        border-bottom: 1px solid $white;
    }

    a:focus {
        background: $white;
        color: #333;
    }
}

.shortcourse-tutor-block {
    .bx-controls {
        display: block;
    }

    .bx-controls-direction {
        position: absolute;
        left: 50%;
        top: -77px;
        width: 0;
        height: 0;

        a {
            display: block;
            position: absolute;
            bottom: 0;
            border: 0;
            padding: 10px 20px;
            color: #ddd;
            background: rgba(0, 0, 0, 0.4);
            font-size: 1.4em;
        }

        a:focus,
        a:hover {
            background: rgba(0, 0, 0, 0.22);
        }

        a:active {
            color: white;
        }

        .bx-prev {
            right: 0;
        }

        .bx-next {
            left: 0;
        }
    }
}

.shortcourse-tutor-selection {
    .shortcourse-tutor-block {
        padding-top: 50px;
    }
}

.shortcourse-tutor-items {
    list-style-type: none;
    margin-left: 0;
    margin-bottom: 0;
}

.shortcourse-tutor {
    img {
        margin-bottom: 0;
        max-height: 300px;

        @include breakup-breakpoint('thin') {
            max-width: 300px;
        }

        @include breakup-breakpoint('medium') {
            max-width: 100%;
        }

        @include breakup-breakpoint('wide') {
            max-width: 90%;
        }
    }
}

.shortcourse-tutor__name {
    padding-left: 10px;
    padding-right: 10px;
    font-size: 1.4em;
    font-weight: 500;

    @include breakup-breakpoint('wide') {
        padding-left: 20px;
        padding-right: 20px;
    }
}

.shortcourse-tutor__bio {
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 20px;

    @include breakup-breakpoint('wide') {
        padding-left: 20px;
        padding-right: 20px;
    }
}

.shortcourse-tutor__bio:last-child {
    padding-bottom: 40px;
}

.shortcourse-promo {
    position: relative;
    overflow: hidden;

    img {
        margin-bottom: 0;
        height: 100%;
        max-width: none;

        @include breakup-breakpoint('wider') {
            width: 100%;
        }
    }
}

.shortcourse-promo__text {
    position: absolute;
    left: 20px;
    top: 30px;
    z-index: 1;
    font-size: 1.8em;
    @include font-family-meta-sans;

    p {
        margin-bottom: 0;
        line-height: 1.3;
        color: white;
    }

    a {
        display: inline-block;
        margin-top: 20px;
        border-bottom: none;
        color: #e31b25;
        font-size: 1.1em;
    }

    .fa {
        margin-left: 10px;
        vertical-align: middle;
        font-size: 1.2em;
    }

    @include breakup-breakpoint('wide') {
        left: 30px;
    }
}

.shortcourse-promo__text {
    a:hover {
        span:not([class]) {
            border-bottom: 2px solid #e31b25;
        }
    }

    a:focus {
        color: white;
    }
}

.shortcourse-promo--mobile {
    padding-left: 0;
    padding-right: 0;
    margin-left: -10px;
    margin-right: -10px;
    margin-bottom: 20px;

    @include breakup-breakpoint('medium') {
        display: none;
    }
}

.shortcourse-promo--desktop {
    display: none;

    @include breakup-breakpoint('medium') {
        display: block;
        margin-left: 0;
        margin-right: 0;
        margin-top: 20px;
    }
}

.shortcourse-next-course-block {
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 30px;
    padding-bottom: 20px;
    margin-left: -10px;
    margin-right: -10px;
    margin-bottom: 20px;

    @include breakup-breakpoint('wide') {
        margin-left: 0;
        margin-right: 0;
    }

    h2 {
        margin-bottom: 30px;
    }

    h2:after {
        content: '';
        display: block;
        width: 100%;
        border-bottom: 1px solid $white;

        @include breakup-breakpoint('thin') {
            width: 200px;
        }

        @include breakup-breakpoint('medium') {
            width: 250px;
        }

        @include breakup-breakpoint('wide') {
            width: 300px;
        }
    }
}

.shortcourse-next-course {
    padding-bottom: 20px;
    padding-right: 20px;
}

.shortcourse-next-course__title {
    font-weight: 600;
    margin-bottom: 10px;
    font-size: 1.1em;

    a {
        color: $white;
        border-bottom: 2px solid $white;
    }

    a:hover {
        border-bottom: none;
    }
}

.shortcourse-related-course-block {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-bottom: 30px;

    h2 {
        margin-bottom: 30px;
        display: inline-block;
        border-bottom: 1px solid #ccc;
    }
}

.shortcourse-related-course {
    margin-bottom: 10px;
}

.shortcourse-twitter {
    border: 1px solid #eee;
    border-radius: 5px;
    height: 400px;
    overflow-y: auto;
}

.shortcourse-twitter--mobile {
    margin-bottom: 40px;
    @include breakup-breakpoint('medium') {
        display: none;
    }
}

.shortcourse-twitter--desktop {
    display: none;

    @include breakup-breakpoint('medium') {
        display: block;
        margin-bottom: 30px;

        .shortcourse-tutor-wrapper + & {
            margin-top: 40px;
        }
    }
}

/*****************
 *      CPD      *
 *****************/
.shortcourse-header__award.shortcourse-header__award--cpd {
    display: block;
    margin-top: 10px;
}

.shortcourse-selection.shortcourse-selection--cpd {
    &:after {
        background: none;
        position: static;
    }

    @include breakup-breakpoint('medium') {
        position: static;
        padding-bottom: 0;
    }
}

.shortcourse-keyinfo.shortcourse-keyinfo--cpd {
    .shortcourse-keyinfo__action {
        max-width: 300px;
    }

    .cta {
        display: inline-block;
        font-size: 1.3em;
        width: 48%;

        &:nth-child(2) {
            margin-left: 5px;
        }
    }

    @include breakup-breakpoint('medium') {
        .cta {
            width: 100%;

            &:nth-child(2) {
                margin-left: 0;
            }
        }
    }

    @include breakup-breakpoint('wide') {
        .cta {
            width: 48%;

            &:nth-child(2) {
                margin-left: 5px;
            }
        }
    }
}

.shortcourse-keyinfo.shortcourse-keyinfo--cpd {
    .shortcourse-keyinfo__details {
        a {
            color: #eee;
            border-bottom: 1px dotted #eee;
            font-style: normal;
        }

        a:hover {
            border-bottom: 1px solid $white;
        }

        a:focus {
            background: $white;
            color: #666;
        }
    }
}

.shortcourse-testimonials-block.shortcourse-testimonials-block--cpd {
    background-color: #eee;
    margin-top: 30px;
    margin-bottom: 10px;

    @include breakup-breakpoint('medium') {
        margin-left: -10px;
        margin-right: -10px;
    }

    @include breakup-breakpoint('wide') {
        margin-left: 0;
        margin-right: 0;
    }

    .shortcourse-testimonials,
    .shortcourse-testimonials--empty {
        background: none;
        background-color: #eee;
    }

    .shortcourse-testimonials {
        margin-bottom: 0;
    }

    .shortcourse-testimonials--empty {
        height: 20px;
    }

    .shortcourse-testimonials__text {
        text-align: center;

        img {
            max-width: 200px;
        }
    }

    .shortcourse-testimonials__quote {
        font-style: normal;
        margin-bottom: 15px;
    }

    .shortcourse-testimonials__name {
        color: #333;
    }

    .course__profiles__item {
        color: $color-cpd-primary;
    }

    .bx-wrapper {
        padding-top: 10px;
        padding-bottom: 60px;
    }

    .bx-controls-direction {
        left: 50%;
    }
}

.shortcourse-tutor-wrapper.shortcourse-tutor-wrapper--cpd {
    @include breakup-breakpoint('medium') {
        &:after {
            bottom: 0;
            transform: skewY(5deg);
        }

        &.shortcourse-tutor-selection:after {
            bottom: 0;
        }
    }

    .shortcourse-tutor-block {
        background: linear-gradient(
            to bottom,
            $color-cpd-primary,
            $color-cpd-primary-gradient
        );
    }

    .shortcourse-tutor__img-wrapper {
        padding-left: 10px;

        @include breakup-breakpoint('medium') {
            text-align: right;
            padding-right: 10px;
        }

        @include breakup-breakpoint('wide') {
            padding-right: 20px;
        }
    }
}

.cpd-solid {
    background-color: $color-cpd-primary;
    color: $white;
}

.cpd-which-programmes {
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-left: -10px;
    margin-right: -10px;

    a {
        color: #eee;
        border-bottom: 2px dotted #ccc;
    }

    a:hover {
        border-bottom: 2px solid $white;
    }

    @include breakup-breakpoint('medium') {
        margin-left: 0;
        margin-right: 0;
    }

    @include breakup-breakpoint('wide') {
        padding-left: 20px;
        padding-right: 20px;
    }
}

.cpd-which-programme__title {
    font-weight: 400;
    margin-bottom: 10px;
    font-size: 1em;
}
