#map-container {
    position: relative;

    &.loading {
        @include loading-fa-icon-content-bg;
    }
}

#map-canvas img {
    max-width: none;
}

#map-controls {
    padding-bottom: 5px;

    input {
        width: 100%;
        margin-bottom: 5px;
        height: 39px;
        padding: 4px;
    }

    button {
        position: absolute;
        right: 15px;
        top: 1px;
        background: transparent;
        color: $red1;
        border: none;
        height: 37px;
    }
    a {
        display: inline-block;
        padding: 4px 10px;
        margin-right: 3px;
        background: $red1;
        border-radius: 5px;
        color: white;
        height: 39px;
        line-height: 31px;

        i {
            display: none;
        }

        &.enabled {
            background: $gray-darker;
            color: white;
            font-weight: bold;
            i {
                display: inline-block;
            }
        }
    }
}

#journey-planner {
    margin-top: 10px;
    margin-bottom: 10px;
}

.visit #filter {
    .ui-accordion-content {
        max-height: 400px;
        overflow-y: auto !important;
    }
    ul {
        margin-left: 0;
    }
    li {
        list-style: outside none none;
        a {
            padding: 10px 0 10px 40px;
            margin: 5px 0;
            background: transparent url('i/skin/map-icons/university.png')
                no-repeat left center;
            display: block;
            border: none;

            &:focus {
                color: $red1;
            }
        }
    }
}
